import Label = cc.Label;
import Node = cc.Node;
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import {battleScene} from "../../../../../Scenes/BattleScene";
import HeroWithAvatar from "../../HeroWithAvatar";

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroCompositingDisplay extends HeroWithAvatar {

    @property(Label)
    nameLabel: Label = null;
    @property(Sprite)
    qualitySprite: Sprite = null;
    @property(SpriteFrame)
    qualityPictures: SpriteFrame[] = [];

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    protected onItemChanged() {
        if (!this.item) return;
        super.onItemChanged();
        this.nameLabel.string = this.item.name;
        this.qualitySprite.spriteFrame = this.qualityPictures[this.item.quality];
    }

    update (dt) {
        this.updateMoreInfo();
    }

    // region 

    @property(Node)
    moreInfoNode: Node = null;
    @property(Label)
    levelLabel: Label = null;
    @property([Node])
    starNodes: Node[] = [];
    @property(Node)
    selectedNode: Node = null;
    @property(Node)
    fightingNode: Node = null;

    public isSelected: boolean = false;

    private updateMoreInfo() {
        if (!this.moreInfoNode) return;
        this.levelLabel.string = this.item.level.level.toString();
        this.starNodes.forEach((s, i) => s.active = i < this.item.star.level);
        this.selectedNode.active = this.isSelected;
        this.fightingNode.active = battleScene().heroesInTeam.find(h => h == this.item) != undefined;
    }

    // endregion

}
