import {SelectableObject} from "../../../../CoreModule/Components/SelectableObject";
import HeroCompositingContainer from "./HeroCompositingContainer";
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import LabelOutline = cc.LabelOutline;
import Color = cc.Color;

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroQualitySiftSelection extends SelectableObject {

    @property(Number)
    quality: number = -1;
    @property(Sprite)
    picture: Sprite = null;
    @property(LabelOutline)
    outline: LabelOutline = null;
    @property([SpriteFrame])
    pictures: SpriteFrame[] = [];
    public container: HeroCompositingContainer = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onDeselected(): void {
        this.picture.spriteFrame = this.pictures[1];
        this.outline.color = new Color(104, 104, 104);
    }

    onSelected(): void {
        this.picture.spriteFrame = this.pictures[0];
        this.outline.color = new Color(134, 118, 79);
        this.container?.onSelectionChange();
    }

}
