import {rewardProcessor, RewardProcessor} from "../../PlayerModule/Utils/RewardProcessor";
import {MoneyType, PlayerMoney} from "../Data/PlayerMoney";
import {RewardType} from "../../PlayerModule/Data/Reward";
import {playerMgr} from "../../PlayerModule/Managers/PlayerManager";

abstract class MoneyRewardProcessor extends RewardProcessor {

	protected abstract get type(): MoneyType;

	public invoke(rate = 1) {
		const pMoney = playerMgr().getPlayerData(PlayerMoney);
		pMoney.gain(this.type, this.value * rate);
	}

}

@rewardProcessor(RewardType.Gold)
export class GoldRewardProcessor extends MoneyRewardProcessor {

	protected get type(): MoneyType { return "gold"; }

}

@rewardProcessor(RewardType.Diamond)
export class DiamondRewardProcessor extends MoneyRewardProcessor {

	protected get type(): MoneyType { return "diamond"; }

}

@rewardProcessor(RewardType.Medal)
export class MedalRewardProcessor extends MoneyRewardProcessor {

	protected get type(): MoneyType { return "medal"; }

}

@rewardProcessor(RewardType.Fragment)
export class FragmentRewardProcessor extends MoneyRewardProcessor {

	protected get type(): MoneyType { return "fragment"; }

}
