import {assetField, field, GameData} from "../../CoreModule/Data/BaseData";
import {BundleName} from "../../CoreModule/Managers/AssetBundleManager";
import SkeletonData = sp.SkeletonData;
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {Constructor} from "../../../Utils/BaseContext";
import {data, StaticData} from "../../CoreModule/Data/StaticData";
import {AttackMotionGroupId} from "../../BattleModule/Data/MotionGroup";
import {IEntity} from "../../BattleModule/Data/Battler";
import {paramRepo} from "../../HeroModule/Data/Param";
import {MathUtils} from "../../../Utils/MathUtils";
import {RestraintType} from "../../HeroModule/Data/Hero";

export enum EnemyType {
	Normal, Boss
}

@data("Enemy")
export class Enemy extends StaticData implements IEntity {

	@field
	public type: EnemyType = EnemyType.Normal;
	@field
	public level: number = 1;
	@field
	public name: string = "";
	@field
	public restraint: RestraintType = RestraintType.Red;
	@field
	public skeletonName: string = "";
	@field
	public params: {[T: number]: number} = {};
	@field
	public score: number = 1;

	@field(Number)
	public attackMotionId: number = AttackMotionGroupId;

	@assetField(SkeletonData, BundleName.Sprites,
		"parts/${skeletonName}")
	public skeleton: SkeletonData;

	/**
	 * 
	 */
	get battleLevel(): number { return this.level; }

	/**
	 * 
	 */
	private _battleParams: number[];
	get battleParams(): number[] {
		if (!this._battleParams) {
			const params = paramRepo().find();

			this._battleParams = [];
			params.forEach(p => {
				const k = p.id;
				const val = this.params[k] || p.default_;
				this._battleParams[k] = MathUtils.clamp(val, p.min, p.max);
			})
		}
		return this._battleParams;
	}
}

export function enemyRepo() {
	return getRepository(EnemyRepo);
}

@repository
export class EnemyRepo extends BaseRepository<Enemy> {

	get clazz(): Constructor<Enemy> {
		return Enemy;
	}

	public constructor() {
		super();
	}

}
