	/**
	 * 
	 */
	protected update(dt: number) {
		this.updateScale();
		if (this.recording) {
			this.skeleton.setAnimation(0, DefaultStand, true);
			// @ts-ignore
			this.skeleton.update(this.images.length / 60);
			this.images.push(this.shot());
		}
	}

	public skeletonSize: number = 300;

	private updateScale(){
		const xRate = this.skeleton.node.scaleX / this.skeletonSize;
		const yRate = this.skeleton.node.scaleY / this.skeletonSize;
		let k = Math.max(
			this.skeleton.node.width * xRate,
			this.skeleton.node.height * yRate);
		if (k > 1) {
			this.skeleton.node.scaleX /= k;
			this.skeleton.node.scaleY /= k;
		}
	}

