	@property(EditBox)
	nickname: EditBox = null;

	@showLoading
	async createNFT(){
		const picture = this.shot();

		this.startRecording();
		let recordingTime = Math.floor(this.skeleton.findAnimation(DefaultStand).duration * 60);
		await PromiseUtils.waitFor(() => this.images.length >= recordingTime);
		this.recording = false;
		const gif = await GIFUtils.makeGIF(this.images.filter((e, i) => i < recordingTime));

		console.log("this.item.nftPartIds", this.item.nftPartIds)

		if (Web3Enable) {
			const creator = new NFTCreator();
			creator.name = this.nickname.string;
			creator.picture = picture;
			creator.gif = gif;
			creator.partIds = this.item.nftPartIds;
			creator.create().then(res => {
				this.createdWindow.setPicture(creator.picture);
				this.createdWindow.setQuality(Number(res.rawData.quality));
				this.createdWindow.show();
			});
		} else {
			// TODO: 
			console.log("");
			const hero = Hero.create(playerMgr().player.account,
				this.item.heroTypeId, this.item.partSettings, this.nickname.string);

			HeroInterface.CreateHero({hero}).then(res => {
				const hero: Hero = res.hero;
				this.createdWindow.setPicture(picture);
				// 
				this.createdWindow.setQuality(QualityType.UR - hero.quality + 1);
				this.createdWindow.show();
			})
		}
		// .catch(e => {
		// 	alertMgr().alert("", e, AlertDialogType.OK).then();
		// 	console.error(e);
		// });
	}

