	@property(Camera)
	camera: Camera = null;

	texture: RenderTexture = null;
	windowSize: number = Math.max(cc.winSize.width, cc.winSize.height);
	targetSize: number = 600;

	initScreenShot() {
		this.camera.enabled = false;
		this.texture = new RenderTexture();
		this.texture.initWithSize(this.windowSize, this.windowSize, cc.RenderTexture.DepthStencilFormat.RB_FMT_S8)
		this.camera.targetTexture = this.texture;
	}

	shot(): string {
		// 
		this.camera.render(undefined);

		// 
		let texture = this.texture;
		let data = texture.readPixels();
		let width = texture.width;
		let height = texture.height;
		let canvasTemp = document.createElement('canvas');
		let ctxTemp = canvasTemp.getContext('2d');
		canvasTemp.width = width;
		canvasTemp.height = height;

		// 
		let rowBytes = width * 4;
		for (let row = 0; row < height; row++) {
			let srow = height - 1 - row;
			let imageData = ctxTemp.createImageData(width, 1);
			let start = srow*width*4;
			for (let i = 0; i < rowBytes; i++)
				imageData.data[i] = data[start+i];
			ctxTemp.putImageData(imageData, 0, row);
		}

		// 
		let canvas = document.createElement('canvas');
		let ctx = canvas.getContext('2d');
		canvas.width = this.targetSize;
		canvas.height = this.targetSize;
		ctx.drawImage(canvasTemp, (this.windowSize - this.targetSize) / 2, (this.windowSize - this.targetSize) / 2, this.targetSize, this.targetSize, 0, 0, this.targetSize, this.targetSize);

		// base64URL
		return canvas.toDataURL("image/png");
	}

	downloadScreenShot(url: string){
		// download
		document.location.href = url.replace(
			/^data:image[^;]*/, "data:image/octet-stream");
	}

	recording: boolean = false;
	images: string[] = [];

	private startRecording() {
		this.images = [];
		this.recording = true;
	}

