import ccclass = cc._decorator.ccclass;
import Button = cc.Button;
import property = cc._decorator.property;
import {getLanguage, i18nMgr, Languages} from "../Managers/I18nManager";
import Toggle = cc.Toggle;
import Node = cc.Node;
import ToggleGroup = cc.ToggleGroup;
import {gameMgr} from "../Managers/GameManager";
import Label = cc.Label;


@ccclass
export default class LanguageSwitch extends cc.Component {

    @property(Node)
    langSwitchWindow: Node;

    @property([Toggle])
    toggles: Toggle[] = [];// iToggleLanguagesi

    @property(Label)
    languageLabel: Label;// label

    protected onEnable() {
        let langIdx = getLanguage(i18nMgr().getCurrentLanguage());
        this.toggles[langIdx].check();
        this.onClick(this.toggles[langIdx]);
    }

    /**
     * Toggle
     * @param e Toggle
     */
    public onClick(e: Toggle) {
        if (!e.isChecked) return; // toggle
        let idx=this.toggles.indexOf(e);
        i18nMgr().setLanguage(Languages[idx]);
        this.languageLabel.string = e.getComponentInChildren(Label).string;
    }

    public setWindowVisible(e: any, visible: string) {
        console.log(visible)
        this.langSwitchWindow.active = visible == 'true';
    }
}