import HeroInfoDisplay from "../HeroInfoDisplay";
import Label = cc.Label;
import CultivateWindowController from "../CultivateWindowController";
import {battleScene} from "../../../../../Scenes/BattleScene";
import Slider = cc.Slider;

const {ccclass, property} = cc._decorator;

@ccclass
export default class SummonWindowDisplay extends cc.Component {

    // region Background

    @property(HeroInfoDisplay)
    heroInfo: HeroInfoDisplay = null;
    @property(Label)
    stablePrice: Label = null;

    // endregion

    // region Content

    @property(Label)
    USTDValues: Label[] = [];
    @property(Label)
    USTDBalance: Label = null;
    @property(Label)
    HCTValues: Label[] = [];
    @property(Label)
    HCTBalance: Label = null;
    @property(Label)
    pricePercent: Label = null;
    @property(Slider)
    priceSlider: Slider = null;

    // endregion

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    private cultivateController: CultivateWindowController = null;

    start () {
        this.cultivateController = battleScene().cultivateWindow;
    }

    // region 

    update (dt) {
        this.updateLabels();
        this.updatePictures();
    }

    updateLabels() {
        let percent = this.priceSlider.progress;
        this.USTDValues.forEach(v => v.string = Math.floor(20 * percent).toString());
        this.HCTValues.forEach(v => v.string = Math.floor(400 * percent).toString());

        this.pricePercent.string = Math.floor(100 * percent) + "%";
    }

    updatePictures() {

    }

    protected onEnable() {
        this.heroInfo.node.active = false;
    }

    // endregion

    // region 

    summonBtn() {

    }

    // endregion
}
