import Node = cc.Node;
import CultivateSelection from "./CultivateSelection";
import CultivateSelectionGroup from "./CultivateSelectionGroup";
import {Hero} from "../../../HeroModule/Data/Hero";
import Animation = cc.Animation;

const {ccclass, property} = cc._decorator;

@ccclass
export default class CultivateWindowController extends cc.Component {

    @property(Node)
    window: Node = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () { }

    // region 

    @property([CultivateSelection])
    selectionCards: CultivateSelection[] = [];
    selectionGroup: CultivateSelectionGroup = null;
    @property([Node])
    outNodes: Node[] = [];
    @property([Node])
    inNodes: Node[] = [];

    initSelectionGroup() {
        this.selectionGroup = new CultivateSelectionGroup(false);
        this.selectionCards.forEach(c => {
            c.selectableGroup = this.selectionGroup;
            c.controller = this;
            this.selectionGroup.addElement(c);
        })
    }

    onSelectionCardChange() {
        let selected = this.selectionCards.findIndex(c => c.selectableGroup.isSelected(c));
        this.outNodes.forEach((n, i) => n.active = i == selected);
        this.inNodes.forEach((n, i) => n.active = i == selected);
        this.refreshHero = true;
    }

    // endregion

    // update (dt) {}

    public currentHero: Hero = null;
    public refreshHero: boolean = false;

    open(hero: Hero) {
        if (!this.selectionGroup)
            this.initSelectionGroup();
        this.currentHero = hero;
        this.refreshHero = true;
        this.selectionGroup.select(this.selectionCards[0]);
        this.window.active = true;
        this.getComponent(Animation).play();
    }

    quit() {
        this.window.active = false;
    }
}
