import {error, get, post} from "../../CoreModule/Common/CoreCommon";

export class EconomicThrow {

    @error(400001, "") static NotEnoughMoney(d?) {}
    @error(400002, "") static NotEnoughFragment(d?) {}
    @error(400003, "") static NotEnoughReturn(d?) {}
    @error(400011, "") static CanNotUnlockJob(d?) {}
    @error(400012, "") static JobAlreadyUnlocked(d?) {}
    @error(400013, "") static JobNotUnlocked(d?) {}
    @error(400014, "") static ReachJobMaxLevel(d?) {}
    @error(400015, "") static NotJobProfitTime(d?) {}
    @error(400016, "") static UnlockJobError(d?) {}
    @error(400017, "") static UpgradeJobError(d?) {}
    @error(400018, "") static ProfitJobError(d?) {}

}

export class EconomicInterface {

    @post("/job/job/unlock") static UnlockJob(d?): any {}
    @post("/job/job/upgrade") static UpgradeJob(d?): any {}
    @post("/job/job/profit") static ProfitJob(d?): any {}

}
