import {SelectableObject} from "../../../../Modules/CoreModule/Components/SelectableObject";
import PartsDisplay from "./PartsDisplay";
import PartsContainer from "../ItemContainers/PartsContainer";
import PartColorsContainer from "../ItemContainers/PartColorsContainer";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PartsSelection extends SelectableObject {

    display: PartsDisplay = null;
    container: PartsContainer | PartColorsContainer = null;

    //region 

    onDeselected(): void {
        this.display.onSelectChanged(false);
        if (this.selectableGroup.selectedIndex == -1)
            this.container.onSelectCancel(this.display);
    }

    onSelected(): void {
        this.display.onSelectChanged(true);
        this.container.onSelectChanged(this.display);
    }

    toggleSelection() {
        if (this.container instanceof PartsContainer)
            this.container.switchColorSelector();
        super.toggleSelection();
    }

    //endregion

}
