import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import {Hero} from "../../../HeroModule/Data/Hero";
import RankItemDisplay from "./RankItemDisplay";
import {waitForDataLoad} from "../../../CoreModule/Managers/DataManager";
import {waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import {RankInterface} from "../../../RankModule/Common/RankCommon";
import {ScrollviewFilter} from "../../../CoreModule/Utils/ScrollviewFilter";
import Node = cc.Node;
import {DataLoader} from "../../../CoreModule/Utils/DataLoader";

const {ccclass, property} = cc._decorator;

@ccclass
export default class RankItemContainer extends ItemContainer<Hero, RankItemDisplay> {

    start () {
        this.setup().then();
    }

    @waitForDataLoad
    @waitForLogin
    private async setup() {
        let nfts: [] = (await RankInterface.GetPVPRank()).nfts;
        let items: Hero[] = nfts.map(nft => DataLoader.load(Hero, nft)).sort((a, b) => a.rank - b.rank);
        this.setItems(items);
    }

    update (dt) {
    }

    onItemsChanged() {
        super.onItemsChanged();
        let nodes : Node[] = [];
        this.itemsComponents.forEach(item => { if(item.node.active == true) nodes.push(item.node) })
        ScrollviewFilter.filter("vertical", this.container, nodes, 5, this.node.height, 170);
    }
}
