
import {Result} from "../Data/Result";
import {Battler} from "../Data/Battler";

export type MotionState = 'not_started' | 'playing' | 'ended' | 'finished';

export class ResultProcessor {

	public battler: Battler;
	public result: Result;

	public static create(battler: Battler, result: Result) {
		const res = new ResultProcessor();
		res.result = result;
		res.battler = battler;
		return res;
	}

	public process() {
		this.doProcess();
	}

	private doProcess() {

	}
}
