	// region 

	public static load<T extends BaseData>(
		type: Constructor<T>, data = null,
		index: number = null, parent: BaseData = null) : T {
		if (data == null) return data;
		let res = new type(index, parent);

		if (data) {
			const properties = this.getProperties(type);
			for (const key in properties) if (key in data)
				this.loadProp(res, key, properties[key], data[key]);
		}

		const assetFields = this.getAssetFields(type);
		for (const key in assetFields)
			this.loadAsset(res, key, assetFields[key]);

		Promise.all(res.loadTasks())
			.then(() => res.isLoaded = true);

		res.onCreated();
		return res;
	}

	public static async loadFromAssetBundle<T extends GameData>(
		bundleName: string, assetPath: string, type: Constructor<T>) : Promise<Object> {

		let data: any = await assetMgr().loadAsset(
			JsonAsset, bundleName, assetPath);
		if (!data) return {};

		return this.loadFromJSON(data.json, type);
	}
	public static loadFromJSON<T extends GameData>(
		json: object[], type: Constructor<T>) : Object {

		if (!json) console.error(json, type);

		let list: T[] = json ? json.map(this.load.bind(this, type)) : [];

		let res = {};
		list.forEach(d => res[d._id] = d);

		return res;
	}

	private static loadProp(res: BaseData, key: string, prop, data) {
		try {
			let pType = prop.type || prop.default?.constructor;

			res[key] = this.loadType(pType, data, res);
		} catch (e) {
			console.error(e, res, key, data, prop);
		}
	}
	private static loadAsset(res: BaseData, key: string, asset) {
		res.addLoadTask(asset.type, key, asset.bundleName, asset.assetPath);
	}

	private static loadType(type, data, parent, index = null) {
		if (data === null) return null;

		if (type instanceof Array) {
			this.ensureType(Array, data);

			const eType = type[0]; // 
			return data.map((d, idx) =>
				this.loadType(eType, d, parent, idx));
		}

		this.ensureType(type, data);
		return cc.js.isChildClassOf(type, BaseData) ?
			this.load(type, data, index, parent) : data;
	}

	private static ensureType(type, data) {
		if (!type) return;

		const isData = cc.js.isChildClassOf(type, BaseData);
		if (isData && data.constructor != Object)
			CoreThrow.SystemError(`Data type error: value = ${data}type = ${type}`);
			// throw `Data type error: value = ${data}type = ${type}`;
			// throw new DataLoaderError(type, data);


		if (!isData && !isChildClassOf(data.constructor, type))
			CoreThrow.SystemError(`Data type error: value = ${data}type = ${type}`);
		// if (!isData && type != data.constructor)
		// 	CoreThrow.SystemError(`Data type error: value = ${data}type = ${type}`);
			// throw `Data type error: value = ${data}type = ${type}`;
			// throw new DataLoaderError(type, data);
	}

	// endregion

	// region 

	public static convert<T extends BaseData>(
		typeOrData: Constructor<T> | any, data?: any): Partial<T> {
		let type: Constructor<T>;
		if (typeOrData instanceof Function)
			type = typeOrData;
		else {
			data = typeOrData;
			if (typeOrData instanceof BaseData)
				type = data.constructor as Constructor<T>;
		}

		if (!data) return null;

		const res = {};
		if (type) { //  type
			const properties = this.getProperties(type);
			for (const key in properties) if (key in data)
				this.convertProp(res, key, properties[key], data[key]);

		} else { //  type
			const keys = Object.keys(data);
			for (const key of keys)
				this.convertProp(res, key, null, data[key]);
		}

		return res;
	}

	private static convertProp(res: object, key: string, prop, data) {
		try {
			let pType = prop?.type || prop?.default?.constructor || data?.constructor;

			res[key] = this.convertType(pType, data);
		} catch (e) {
			console.error(e, res, key, data, prop);
		}
	}

	private static convertType(type, data) {
		if (data === null) return null;

		if (type instanceof Array)
			// this.ensureType(Array, data); // Convert
			return data.map(d => this.convertType(type[0], d));

		// this.ensureType(type, data); // Convert
		return isChildClassOf(type, BaseData) ?
			this.convert(data) : data;
	}

