import {Hero} from "./Hero";
import {BaseData, field} from "../../CoreModule/Data/BaseData";
import {cFun, cGte, ConditionGroup, ConditionType} from "../../PlayerModule/Data/Condition";
import {HeroSystemConfig} from "../Configs/HeroSystemConfig";
import {ParamType} from "./Param";
import {MathUtils} from "../../../Utils/MathUtils";
import {config} from "../../CoreModule/Data/BaseConfig";
import {configMgr} from "../../CoreModule/Managers/ConfigManager";

export abstract class HeroSystem extends BaseData {

	@field
	public level: number = 0;

	hero: Hero

	constructor(index, parent) {
		super(index);
		this.hero = parent;
	}

	onCreated() {
		super.onCreated();
		this.level = MathUtils.clamp(this.level, this.minLevel, this.maxLevel);
	}

	// region 

	public get minLevel(): number { return 0; }
	public abstract get maxLevel(): number

	protected get affectParams(): ParamType[] { return []; }

	// endregion

	// region 

	public isMax() { return this.level >= this.maxLevel; }
	public isMin() { return this.level <= this.minLevel; }

	// endregion

	// region 

	public abstract costs();

	public conditions() {
		return ConditionGroup.create(
			cFun(() => this.level < this.maxLevel),
			this.costs()
		);
	}

	public upgrade() {
		this.level = Math.min(this.level + 1, this.maxLevel);
	}

	// endregion

	// region 

	public calc(val, paramId: ParamType) {
		//  this.affectParams  0
		if (this.affectParams.length == 0 ||
			this.affectParams.includes(paramId))
			return this.doCalc(val, paramId);
		return val;
	}

	protected abstract doCalc(val, paramId: ParamType);

	// endregion

}

export abstract class ConfigurableHeroSystem extends HeroSystem {

	abstract get systemName();
	protected get config() {
		return configMgr().config(HeroSystemConfig)[this.systemName]
	}

	public get minLevel(): number { return this.config.min; }
	public get maxLevel(): number { return this.config.max; }

	protected get affectParams(): ParamType[] {
		return Object.keys(this.config.params).map(k => Number.parseInt(k));
	}

	public costs() {
		const res = new ConditionGroup();

		for (const key in this.config.cost) {
			const formula = this.config.cost[key];
			const val = this.eval(formula);
			val && res.add(cGte(key as ConditionType, val));
		}

		return res;
	}

	protected doCalc(val, paramId: ParamType) {
		const formula = this.config.params[paramId];
		const res = this.eval(formula, val);
		return res === undefined ? val : res;
	}

	protected eval(formula, val?) {
		const i = this.level, c = this.config;
		try {
			return eval(formula);
		} catch (e) {
			console.error("", this, val, formula, e);
		}
	}
}

export class LevelData extends ConfigurableHeroSystem {

	@field
	public curMaxLevel: number =
		configMgr().config(HeroSystemConfig).level.defaultMax;

	get systemName() { return "level"; }

	conditions(): ConditionGroup {
		return ConditionGroup.create(
			cFun(() => this.level < this.curMaxLevel),
			super.conditions()
		)
	}

	public upgrade() {
		if (this.level >= this.curMaxLevel) return;
		super.upgrade();
	}

	public breakEnable() {
		return this.level >= this.curMaxLevel && this.curMaxLevel <= this.maxLevel
	}

	public break() {
		this.curMaxLevel = Math.min(this.maxLevel, this.curMaxLevel +
			configMgr().config(HeroSystemConfig).break.breakLevel);
	}

}
export class BreakData extends ConfigurableHeroSystem {

	get systemName() { return "break" }

	private get heroLevel() { return this.hero.level; }

	conditions(): ConditionGroup {
		return ConditionGroup.create(
			cFun(() => this.heroLevel.breakEnable()),
			super.conditions()
		);
	}

	upgrade() {
		super.upgrade();
		this.hero.level.break();
	}

}
export class StrengthenData extends ConfigurableHeroSystem {

	get systemName() { return "strengthen" }

}

export class StarData extends ConfigurableHeroSystem {

	get systemName() { return "star" }

	private get costConfig() {
		return this.config.costTable[this.hero.quality][this.level - 1];
	}
	private get paramConfig() {
		return this.config.paramTable[this.hero.quality][this.level - 1];
	}

	protected get affectParams(): ParamType[] {
		return !this.paramConfig ? [] :
			Object.keys(this.paramConfig).map(k => Number.parseInt(k));
	}

	protected doCalc(val, paramId: ParamType) {
		return val + (this.paramConfig?.[paramId] || 0);
	}

	public costs() {
		const res = new ConditionGroup();

		if (!this.costConfig) return res;
		for (const key in this.costConfig)
			res.add(cGte(key as ConditionType, this.costConfig[key]));

		return res;
	}
}
