import {ItemDisplay} from "../../../CoreModule/Components/ItemDisplay";
import {HeroGroup, HeroGroupType} from "../../Data/HeroGroup";
import HeroInBagContainer from "./HeroInBagContainer";
import {QualityType} from "../../../HeroModule/Data/Quality";
import Label = cc.Label;
import Node = cc.Node;
import Sprite = cc.Sprite;
import {RestraintType} from "../../../HeroModule/Data/Hero";
import SpriteFrame = cc.SpriteFrame;
import HeroGroupHeroContainer from "./HeroGroupHeroContainer";
import {ScrollviewFilter} from "../../../CoreModule/Utils/ScrollviewFilter";

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroGroupDisplay extends ItemDisplay<HeroGroup> {
    @property(HeroGroupHeroContainer)
    heroContainer: HeroGroupHeroContainer;
    @property([Node])
    stars: Node[] = [];
    @property([SpriteFrame])
    restraintIcons: SpriteFrame[] = [];
    @property(Label)
    tagLabel: Label;
    @property(Sprite)
    icon: Sprite;

    @property(Node)
    starsNode: Node;
    @property(Node)
    labelNode: Node;
    @property(Node)
    iconNode: Node;

    start() {

    }

    protected onItemChanged() {
        super.onItemChanged();
        this.setTag();
        this.heroContainer.setItems(this.item.heroes);
    }


    private setTag() {
        this.starsNode.active = false;
        this.labelNode.active = false;
        this.iconNode.active = false;

        switch (this.item.groupBy) {
            case HeroGroupType.STAR:

                for (let i = 0; i < 5; i++)
                    this.stars[i].active = i < this.item.value;
                this.starsNode.active = true;
                break;
            case HeroGroupType.QUALITY:
                this.tagLabel.string = QualityType[this.item.value];
                this.labelNode.x = 0;
                this.labelNode.active = true;
                break;
            case HeroGroupType.RESTRAINT:
                this.tagLabel.string = RestraintType[this.item.value];
                this.labelNode.x = 7;
                this.icon.spriteFrame = this.restraintIcons[this.item.value];
                this.labelNode.active = true;
                this.iconNode.active = true;
                break;
        }
    }
}
