import {BundleName} from "../../CoreModule/Managers/AssetBundleManager";
import {assetField, field} from "../../CoreModule/Data/BaseData";
import {DefaultBt1, DefaultBt2} from "../../NFTModule/Managers/NFTManager";
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {Constructor} from "../../../Utils/BaseContext";
import {partRepo} from "./Part";
import {data, StaticData} from "../../CoreModule/Data/StaticData";
import SkeletonData = sp.SkeletonData;

export enum HeroRace {
	Race0, Race1, Race2, Race3, None
}

@data("HeroType")
export class HeroType extends StaticData {

	@field
	public bt1: number = DefaultBt1;
	@field
	public bt2: number = DefaultBt2;

	// @field
	// public name: string = "";

	@field
	public race: HeroRace = HeroRace.Race0;

	@field
	public scale: number = 1;

	@field([Number])
	public defaultParts: number[] = [];
	@field(String)
	public defaultSkeletonName: string = null;

	@field
	public createEnable: boolean = true;

	@assetField(SkeletonData, BundleName.Sprites,
		"parts/${defaultSkeletonName}")
	public defaultSkeleton: SkeletonData;

	public getDefaultParts() {
		return partRepo().list.filter( p => this.defaultParts.indexOf(p.id) >= 0);
		// const types = partTypeRepo().list; // DataUtils.partTypes();
		// return types.filter(t => this.defaultParts.indexOf(t.id) >= 0);
	}

}

export function heroTypeRepo() {
	return getRepository(HeroTypeRepo);
}

@repository
export class HeroTypeRepo extends BaseRepository<HeroType> {

	get clazz(): Constructor<HeroType> { return HeroType; }

}
