import ccclass = cc._decorator.ccclass;
import Label = cc.Label;
import property = cc._decorator.property;
import Button = cc.Button;
import Node = cc.Node;
import {AlertDialogType, alertMgr, AlertResult, AlertTask} from "../Managers/AlertManager";
import Layout = cc.Layout;


@ccclass
export default class AlertWindow extends cc.Component {

    @property(Node)
    window: Node;

    @property(Label)
    titleLabel: Label;

    @property(Label)
    messageLabel: Label;

    @property(Node)
    buttonGroup: Node;

    @property(Button)
    okButton: Button;

    @property(Button)
    cancelButton: Button;

    currentTask: AlertTask;

    update(dt: number) {
        if (alertMgr().hasAlert() && !this.isShowing()) {
            this.currentTask = alertMgr().popAlertTask();
            this.show();
            this.titleLabel.string = this.currentTask.title;
            this.messageLabel.string = this.currentTask.message;
            this.setupType(this.currentTask.type);
        }
    }

    private show() {
        this.window.active = true;
    }

    private dismiss() {
        this.window.active = false;
    }

    private isShowing() {
        return this.window.active;
    }

    private setupType(type: AlertDialogType) {
        switch (type) {
            case AlertDialogType.Prompt:
                this.buttonGroup.active = false;
                break;
            case AlertDialogType.OK:
                this.buttonGroup.active = true;
                this.okButton.node.active = true;
                this.cancelButton.node.active = false;
                break;
            case AlertDialogType.OKCancel:
                this.buttonGroup.active = true;
                this.okButton.node.active = true;
                this.cancelButton.node.active = true;
                break;
        }

        //
        this.window.getComponentsInChildren(Layout).forEach(val => {
            val.updateLayout();
        });
    }


    /////////////////////////////////////////
    btnOKClicked() {
        this.currentTask.feedback(AlertResult.OK);
        this.dismiss();
    }

    btnCancelClicked() {
        this.currentTask.feedback(AlertResult.CANCEL);
        this.dismiss();
    }

    backgroundClicked() {
        if (this.currentTask.type == AlertDialogType.Prompt) {
            this.dismiss();
            this.currentTask.feedback(AlertResult.OK);
        }
    }

    ////////////////////////////////////////////
}
