import {BaseManager, getManager, manager} from "./BaseManager";

import director = cc.director;

export type LoadingOptions = {
	enable?: boolean,
	code?: number,
	title?: string,
	detail?: string,
	maxValue?: number,
	showImmediately?: boolean
}

export class GameLoading implements LoadingOptions {

	public code: number;
	public title: string
	public detail: string
	public maxValue: number
	public showImmediately: boolean

	constructor(code: number, title?: string,
							detail?: string, maxValue: number = 1,
							showImmediately: boolean = false) {
		this.code = code;
		this.title = title;
		this.detail = detail;
		this.maxValue = maxValue;
		this.showImmediately = showImmediately;
	}
}

export function showLoading(settingOrObj?: LoadingParam | object,
														detailOrKey?: string, desc?: any): any {

	// startLoading
	// let setting: LoadingSetting; // = CoreLoading.Default();
	const hasSetting = !detailOrKey && !desc;

	const process = (obj, key, desc) => {
		const oriFunc = desc.value;

		desc.value = async function(...p) {
			const setting = hasSetting ?
				(typeof settingOrObj == 'function' ?
					settingOrObj(detailOrKey) : settingOrObj) :
				undefined;

			const info = loadingMgr().startLoading(setting);
			return await oriFunc.apply(this, p)
				.finally(() => info?.finish());
		}
	}

	if (hasSetting) return process;

	process(settingOrObj, detailOrKey, desc);
}

export function blockLoading(obj, key, desc) {
	const oriFunc = desc.value;
	const mgr = loadingMgr();

	desc.value = async function(...p) {
		// return PromiseUtils.funcWrapper(
		// 	oriFunc.bind(this, p),
		// 	() => mgr.isBlocked = true,
		// 	() => mgr.isBlocked = false);
		mgr.isBlocked++;
		try {
			return await oriFunc.apply(this, p);
		} finally {
			mgr.isBlocked--;
		}
	}
}

export function loadingMgr(): LoadingManager {
	return getManager(LoadingManager);
}

export function peekLoadingInfo() {
	return loadingMgr().peekLatestLoadingInfo();
}

export type LoadingParam = LoadingOptions |
	((...any) => LoadingOptions);

@manager
class LoadingManager extends BaseManager {

	private loadingInfo: LoadingInfo[] = [];

	public isBlocked: number = 0;

	/**
	 * @param setting Loading
	 * @return LoadingProgressSetter 
	 */
	public startLoading(setting?: LoadingParam): LoadingProgressSetter {
		setting ||= CoreLoading.Default;

		if (typeof setting == 'function') setting = setting();

		if (this.isBlocked || setting?.enable === false) return null;

		let title = setting.title, detail = setting.detail;

		if (setting.code) { // code
			const key = "LOAD" + setting.code;
			const exist = !!i18nMgr().getKey(key);

			// Titlesettingtitle
			title = exist ? `\\L[${key}]` : title;

			// Detailsettingdetail
			if (!detail) { // settingdetail
				const dKey = key + "_DETAIL";
				const dExist = !!i18nMgr().getKey(dKey);

				detail = dExist ? `\\L[${dKey}]` : "";
			}
		}

		return this.pushLoadingInfo(title, detail,
			setting.maxValue, setting.showImmediately);
	}

	private pushLoadingInfo(title, detail, maxValue, showImmediately) {
		let res = new LoadingInfo(title, detail,
			maxValue, showImmediately);
		this.loadingInfo.push(res);
		return res;
	}

	public peekLatestLoadingInfo(): LoadingInfo {
		return this.loadingInfo[this.loadingInfo.length - 1];
	}

	public popLatestLoadingInfo():void {
		this.loadingInfo.pop();
	}

}

interface LoadingProgressSetter {
	/**
	 * 
	 * @param currentValue 
	 * @param maxValue undefined
	 */
	updateProgress(currentValue: number, maxValue: number): void;

	/**
	 * 
	 * @param currentValue
	 */
	updateProgress(currentValue: number): void;

	/**
	 * 
	 * @param text 
	 */
	updateLoadingText(text: string): void;

	/**
	 * 
	 * @param text 
	 */
	updateReminder(text: string): void;

	/**
	 * 
	 */
	finish(): void;
}

enum LoadingState {
	NOT_STARTED,
	LOADING,
	FINISHED
}

class LoadingInfo implements LoadingProgressSetter, LoadingOptions {

	public title: string;
	public detail: string;
	public currentValue: number;
	public maxValue: number;

	public startTime: number;

	private state: LoadingState;

	constructor(title: string, detail: string, maxValue: number, showImmediately: boolean) {
		this.title = title;
		this.detail = detail;
		this.currentValue = 0;
		this.maxValue = maxValue;
		this.startTime = director.getTotalTime() - (showImmediately ? 1e9 : 0);
		this.state = LoadingState.NOT_STARTED;
	}

	/**
	 * 
	 * @param currentValue 
	 * @param maxValue 
	 */
	public updateProgress(currentValue: number, maxValue: number = undefined): void {
		this.currentValue = currentValue;
		if (maxValue != undefined) this.maxValue = maxValue;
		if (this.currentValue >= this.maxValue) {
			this.markFinished();
		}
	}

	/**
	 * 
	 * @param text 
	 */
	public updateLoadingText(text: string) {
		this.title = text;
	}

	public updateReminder(text: string): void {
		this.detail = text;
	}

	get isNotStarted() {
		return this.state == LoadingState.NOT_STARTED;
	}

	get isLoading() {
		return this.state == LoadingState.LOADING;
	}

	get isFinished() {
		return this.state == LoadingState.FINISHED;
	}

	public markStarted() {
		this.state = LoadingState.LOADING;
	}

	public markFinished() {
		this.state = LoadingState.FINISHED;
	}

	public finish(): void {
		this.updateProgress(this.maxValue);
	}

}

import {CoreLoading} from "../Common/CoreCommon";
import {i18nMgr} from "./I18nManager";
import {InterfaceOptions} from "./NetworkManager";
import {PromiseUtils} from "../../../Utils/PromiseUtils";
