import {SelectableObject} from "../../../../Modules/CoreModule/Components/SelectableObject";
import PartTypeDisplay from "./PartTypeDisplay";
import PartTypeContainer from "../ItemContainers/PartTypeContainer";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PartTypeSelection extends SelectableObject {

    display: PartTypeDisplay = null;
    container: PartTypeContainer = null;

    //region 

    onDeselected(): void {
        this.display.onSelectChanged(false);
    }

    onSelected(): void {
        this.display.onSelectChanged(true);
        this.container.onSelectChanged(this.display);
    }

    //endregion

}
