import {BaseData, field} from "../../CoreModule/Data/BaseData";
import {QualityType} from "../../HeroModule/Data/Quality";
import {RestraintType} from "../../HeroModule/Data/Hero";
import {playerData} from "../../PlayerModule/Managers/PlayerManager";
import {PlayerData} from "../../PlayerModule/Data/Player";
import {cGte, ConditionGroup, ConditionType} from "../../PlayerModule/Data/Condition";

export class RoomNFTInfo extends BaseData {

	@field(String)
	public nftId: string;
	@field(String)
	public name: string;
	@field(String)
	public ownerAccount: string;
	@field(Number)
	public heroTypeId: number;
	@field([Number])
	public partIds: number[];
	@field(Number)
	public level: number;
	@field(Number)
	public star: number;
	@field(Number)
	public quality: QualityType;
	@field(Number)
	public restraint: RestraintType;
	@field(Number)
	public battlePoint: number;
	@field(Number)
	public rank: number;

}

export class PlayerBattleItem extends BaseData {

	@field(Number)
	public roomId: number;
	@field([RoomNFTInfo])
	public opponents: RoomNFTInfo[] = []; // 

}

@playerData("PlayerBattle")
export class PlayerBattle extends PlayerData {

	@field([PlayerBattleItem])
	public battleData: PlayerBattleItem[] = [];
	@field
	public refreshCount: number = 0; // 
	@field
	public battleChance: number = 0;// 

	/**
	 * 
	 */
	public getRoomData(roomId: number) {
		let res = this.battleData.find(s => s.roomId == roomId);
		if (!res) {
			res = new PlayerBattleItem();
			res.roomId = roomId;

			this.battleData.push(res);
		}
		return res;
	}

	// region 

	/**
	 * 
	 * @param cnt
	 */
	public battleBuyCosts(cnt) {
		return ConditionGroup.create(
			cGte(ConditionType.Gold, cnt * 1000)
		);
	}

	public buyBattleChance(cnt) {
		this.battleChance += cnt;
	}

	/**
	 * 
	 */
	public startPVPBattle() {
		this.battleChance = Math.max(0, this.battleChance - 1);
	}

	/**
	 * 
	 */
	public finishPVPBattle(win, nftId, roomId) {
		if (!win) return;
		// 
		const oppos = this.getRoomData(roomId).opponents;
		const index = oppos.findIndex(o => o.nftId == nftId);
		oppos.splice(index, 1);
	}

	// endregion

	// region 

	/**
	 * 
	 * @param cnt
	 */
	public refreshBuyCosts(cnt) {
		return ConditionGroup.create(
			cGte(ConditionType.Gold, cnt * 500)
		);
	}

	/**
	 * 
	 */
	public buyRefreshCount(cnt, refresh = false) {
		this.refreshCount += cnt;
		if (refresh && this.refreshCount > 0)
			this.refreshPVP();
	}

	/**
	 * 
	 */
	public refreshPVP() {
		this.battleData.forEach(bd => bd.opponents = []);
		this.refreshCount = Math.max(0, this.refreshCount - 1);
	}

	// endregion

}
