import {getSingleton, singleton} from "../Modules/CoreModule/Utils/SingletonUtils";
import {BaseScene} from "./BaseScene";
import {Hero} from "../Modules/HeroModule/Data/Hero";
import Node = cc.Node;
import CultivateWindowController from "../Modules/BattleModule/Components/Cultivate/CultivateWindowController";
import SettlementWindowController from "../Modules/BattleModule/Components/SettlementWindowController";
import MyTeamFrame from "./BattleScene/Components/MyTeamFrame";
import {copyMgr} from "../Modules/CopyModule/Managers/CopyManager";
import {playerMgr, waitForLogin} from "../Modules/PlayerModule/Managers/PlayerManager";
import {waitForDataLoad} from "../Modules/CoreModule/Managers/DataManager";
import {battleMgr} from "../Modules/BattleModule/Managers/BattleManager";
import {RoomNFTInfo} from "../Modules/BattleModule/Components/PVP/PlayerCardDisplay";
import MainBattleDisplay from "../Modules/BattleModule/Components/Main/MainBattleDisplay";
import {nftMgr, Web3Enable} from "../Modules/NFTModule/Managers/NFTManager";
import PVPBattleDisplay from "../Modules/BattleModule/Components/PVP/PVPBattleDisplay";
import Prefab = cc.Prefab;

const {ccclass, property} = cc._decorator;

export function battleScene() {
	return getSingleton(BattleScene);
}

@ccclass
@singleton
export default class BattleScene extends BaseScene {

	heroesInTeam: Hero[] = [];
	battleSpeed: number = 1;

	protected onLoad() {
		super.onLoad();
	}

	start() {
		super.start();
		if (Web3Enable) nftMgr().connect().then();
		this.setSingleComponents();
		this.startMainBattle().then();
	}

	update(dt) {
		super.update(dt);
		this.battleSpeed = Date.now() < copyMgr().getMainPlayerCopySettings()?.speedUpTime ? 2 : 1;
		if (this.mainBattle.item?.isEnded && !this.battleEnded)
			this.nextMainBattle();
		// console.log("progress", dataMgr().loadingProgress());
	}

	setSingleComponents() {
		this.myTeamFrame = this.myTeamFrameNode.getComponent("MyTeamFrame");
		this.cultivateWindow = this.cultivateWindowNode.getComponent("CultivateWindowController");
		this.settlementWindow = this.settlementWindowNode.getComponent("SettlementWindowController");
		this.mainBattle = this.mainBattleNode.getComponent("MainBattleDisplay");
		this.pvpBattle = this.pvpBattleNode.getComponent("PVPBattleDisplay");
	}

	@waitForLogin
	@waitForDataLoad
	async startMainBattle() {
		const battle = await battleMgr().startMainBattle();
		this.mainBattle.setItem(battle);
	}

	private battleEnded: boolean = false;

	nextMainBattle() {
		this.battleEnded = true;
		battleMgr().startMainBattle().then(battle => {
			this.mainBattle.setItem(battle);
			this.battleEnded = false;
		})
	}

	@property(Node)
	myTeamFrameNode: Node = null;
	@property(Node)
	cultivateWindowNode: Node = null;
	@property(Node)
	settlementWindowNode: Node = null;
	@property(Node)
	mainBattleNode: Node = null;
	@property(Node)
	pvpBattleNode: Node = null;

	myTeamFrame: MyTeamFrame;
	cultivateWindow: CultivateWindowController;
	settlementWindow: SettlementWindowController;
	mainBattle: MainBattleDisplay;
	pvpBattle: PVPBattleDisplay;

	// region PVP

	currentPVPRoomIndex: number = -1; // 
	roomSelected: boolean = false; // 
	currentTargetSelect: RoomNFTInfo = null; // 
	currentPVPSelect: Hero = null; // 
	heroSelected: boolean = false; // 

	async startPVPBattle(target: string = undefined) {
		if (target) {
			const battle = await battleMgr().startPVPBattle(
				this.currentPVPSelect.nftId, -1, target);
			this.pvpBattleNode.active = true;
			this.pvpBattle.setItem(battle);
			return;
		}
		if (!this.currentPVPSelect.nftId ||
			this.currentPVPRoomIndex < 0 ||
			!this.currentTargetSelect.nftId) return;
		const battle = await battleMgr().startPVPBattle(
			this.currentPVPSelect.nftId,
			this.currentPVPRoomIndex,
			this.currentTargetSelect.nftId);
		this.pvpBattleNode.active = true;
		this.pvpBattle.setItem(battle);
	}

	endPVPBattle() {
		this.pvpBattle.setItem(null);
		this.pvpBattleNode.active = false;
	}

	// endregion

	@property(Prefab)
	loading: Prefab = null;

}
