import {PlayerJob} from "../Modules/EconomicModule/Data/PlayerJob";
import {playerMgr} from "../Modules/PlayerModule/Managers/PlayerManager";


const {ccclass, property} = cc._decorator;

@ccclass
export default class TestAutoProfit extends cc.Component {

    start () {

    }

    addTime() {
        playerMgr().getPlayerData(PlayerJob).autoProfitTime = 15000 + Math.max(Date.now(), playerMgr().getPlayerData(PlayerJob).autoProfitTime);
    }

}
