import {ItemDisplay} from "../../../../Modules/CoreModule/Components/ItemDisplay";
import Label = cc.Label;
import Node = cc.Node;
import Sprite = cc.Sprite;
import {PartInfo} from "../../../../Modules/HeroModule/Data/PartInfo";
import PartsSelection from "./PartsSelection";
import Color = cc.Color;
import {selectorScene} from "../../SelectorScene";
import PreviewPictureDisplay from "./PreviewPictureDisplay";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PartsDisplay extends ItemDisplay<PartInfo> {

	@property(Sprite)
	picture: Sprite = null;
	@property(Node)
	background: Node = null;
	@property(Node)
	frame: Node = null;
	@property(Number)
	padding: number = 20;

	@property(Node)
	editor: Node = null;
	@property(Label)
	id: Label = null;
	@property(Label)
	htId: Label = null;

	@property(Node)
	ban: Node = null;

	@property(PreviewPictureDisplay)
	preview: PreviewPictureDisplay = null;

	isSelected: boolean = false;

	// onLoad () {}

	//region 

	protected update(dt: number) {
		this.frame.active = this.isSelected;
		this.node.color = this.isSelected ? new Color(53, 48, 71) : new Color(36, 32, 47);
	}

	// endregion

	// region 

	/**
	 * 
	 */
	refresh() {
		super.refresh();
		if (this.item == null) return;
		this.isSelected = false;

		this.drawPicture();
		if (selectorScene().editorMode) {
			this.drawBan();
			this.drawEditorInfo();
		}
	}
	private drawEditorInfo() {
		if (!this.editor) return;
		this.editor.active = true;
		const id = this.item.partId, oriId = this.item.part.oriId;
		this.id.string = id == oriId ? `#${id}` : `#${id} (${oriId})`;
		this.htId.string = this.item.part.heroTypeId.toString();
	}
	private drawPicture(){
		let temp = this.item;
		this.picture.spriteFrame = null;
		this.item.part.picture().then(res => {
			if (this.item != temp) return;
			this.picture.spriteFrame = res?.clone();
			this.filterPicture();
		});
	}
	private filterPicture(){
		let k = Math.max(this.picture.node.width / (this.node.width - this.padding),
			this.picture.node.height / (this.node.height - this.padding));
		if (k > 1) {
			this.picture.node.width /= k;
			this.picture.node.height /= k;
		}
	}
	private drawBan() {
		this.ban.active = this.preview?.item?.calcScale(this.item.part) <= 0;
	}

	//endregion

	//region 

	public onSelectChanged(select: boolean){
		this.isSelected = select;
		if (this.item.stock <= 0)
			this.node.getComponent(PartsSelection)
				.selectableGroup.deselect(this.node.getComponent(PartsSelection).selectableGroup.currentSelection());
	}

	//endregion

}
