import {BaseManager, getManager, manager} from "./BaseManager";
import {loadingMgr, LoadingParam, LoadingOptions} from "./LoadingManager";
import {alertMgr, ErrorData} from "./AlertManager";
import {ParamCodec} from "../Utils/ParamCodec";

// const Host = "https://www.exermon.com:8088";
const Host = "https://zooworld-server-1836805-1255510304.ap-shanghai.run.tcloudbase.com";
// const Host = "http://localhost:8088"

const EncryptEnable = false;
const EncryptParamKey = "e_params";

export type Method = 'get' | 'post';

export type InterfaceLoading = LoadingParam | boolean;
	// falseLoading

export interface InterfaceOptions {
	method: Method;
	host?: string;
	route: string;
	useToken?: boolean;
	useEncrypt?: boolean;
	loading?: InterfaceLoading;
}

export class Interface implements InterfaceOptions {

	public method: Method;
	public host: string = Host;
	public route: string;
	public useToken: boolean = true;
	public useEncrypt: boolean = true;
	public loading: InterfaceLoading = true;

	public get isGet() {return this.method == 'get'}
	public get isPost() {return this.method == 'post'}

	private _loadingSetting: LoadingOptions = null;
	public get loadingSetting(): LoadingOptions {
		if (!this._loadingSetting) {
			let res: LoadingOptions = {};
			if (typeof this.loading == 'function')
				res = this.loading();
			else if (this.loading === null || this.loading === true)
				res = CoreLoading.Request(); // null
			else if (this.loading === false)
				res.enable = false;
			else
				res = this.loading;

			this._loadingSetting = res;
		}
		return this._loadingSetting;
	}

	constructor(options: InterfaceOptions) {
		Object.assign(this, options);
	}
}

export type PostErrHandlerFunc = ((errorData: RequestErrorData) => boolean) | (() => boolean);

/**
 * <p>
 *
 * 1login403<p>
 * <code>@postErrorHandler(Interface.Login, 0, 403)</code><p>
 *
 * 2updateToken2002<p>
 * <code>@postErrorHandler(Interface.UpdateToken, 2)</code>
 *
 *
 * @param interface_ Interface
 * @param code 
 * @param status http200
 */
export function requestErrorHandler(
	interface_: Interface, code: number, status: number = 200) {
	return (obj, key, desc) => {
		networkMgr().registerErrorHandler(interface_, code, status, desc.value);
	}
}

interface RequestErrorData extends ErrorData {
	interface_: Interface
	status: number
	code?: number
	errMsg?: string
	detail?: string
	data?: any
	handled?: boolean
}

class RequestErrorHandler {

	private funcDict: any = {};

	constructor() { }

	private getKey(interface_: Interface) {
		return interface_.host + interface_.route;
	}

	public register(func: PostErrHandlerFunc, interface_: Interface | RequestErrorData,
									status?: number, code?: number) {
		if (interface_ instanceof Interface) {
			const key = this.getKey(interface_);
			this.funcDict[key] ||= {};
			this.funcDict[key][status] ||= {};
			this.funcDict[key][status][code] = func;

		} else this.register(func, interface_.interface_,
			interface_.status, interface_.code);
	}

	public getHandler(interface_: Interface | RequestErrorData,
										status?: number, code?: number): PostErrHandlerFunc {
		if (interface_ instanceof Interface) {
			const key = this.getKey(interface_);
			if (!this.funcDict[key]) return null;
			if (!this.funcDict[key][status]) return null;
			return this.funcDict[key][status][code];

		} else return this.getHandler(interface_.interface_,
			interface_.status, interface_.code);
	}

	public handle(errorData: RequestErrorData): boolean {
		let func = this.getHandler(errorData);
		if (!func) return false;

		if (errorData.handled) return;

		const res = func(errorData);
		return errorData.handled = res == null ? true : res;
	}
}

export function networkMgr(): NetworkManager {
	return getManager(NetworkManager);
}

@manager
class NetworkManager extends BaseManager {

	private errorHandler: RequestErrorHandler = new RequestErrorHandler();

	/**
	 * 
	 */
	public async request<T = any>(interface_: Interface, data: object = {}): Promise<T> {

		data = DataLoader.convert(data);
		const dataStr = this.makeQueryParam(data, interface_.useEncrypt);

		let url = interface_.host + interface_.route;
		if (interface_.isGet) url += "?" + dataStr;

		const xhr = cc.loader.getXMLHttpRequest();
		xhr.open(interface_.method, url, true);
		xhr.setRequestHeader("Content-Type",
			"application/x-www-form-urlencoded");

		if (interface_.useToken)
			xhr.setRequestHeader("Token", gameMgr().getToken());

		return await this.getRequestPromise<T>(xhr, interface_, dataStr);
	}

	private getRequestPromise<T>(xhr: XMLHttpRequest, interface_: Interface, dataStr): Promise<T> {

		return new Promise<T>((resolve, reject) => {
			const loadingInfo = loadingMgr()
				.startLoading(interface_.loadingSetting);

			xhr.onreadystatechange = () => {
				if (xhr.readyState !== 4) return;

				const response = xhr.responseText;
				const status = xhr.status;

				console.log("Response: ", xhr, status, response);

				// response
				let json, code, data, decoded;
				try {
					json = JSON.parse(response);
					// JSONstring
					if (typeof json == "string")
						json = JSON.parse(decoded = ParamCodec.decode(json));

					// 500
					// if (!json) throw "Decode error!";
					// else json = JSON.parse(json);

					code = json["code"] || 0;
					data = json["data"] || json;

					console.log("Response JSON: ", json, code, data);
				} catch (e) {
					console.error("Parse JSON failed!", decoded, e);
				}

				// 
				if (status >= 200 && status < 300 && code === 0)
					resolve(data);
				// JSON
				else {
					// jsonjson
					let errMsg, detail = "";
					if (json) {
						errMsg = json["errMsg"];
						detail = json["detail"];
					} else {
						errMsg = "\\L[ERR_SYSTEM]" + status;
						data = response;
					}

					const errData: RequestErrorData = {
						interface_, status, code, errMsg, detail, data
					};
					this.handleError(errData);
					reject(errData);
				}

				loadingInfo?.finish();
			};

			if (interface_.isGet) xhr.send();
			else xhr.send(dataStr);
		})
	}

	private makeQueryParam(data, useEncrypt) {
		let res = Object.keys(data).reduce(
			(res, key) => (res + key + '=' +
				this.convertParam(data[key]) + '&'), '');
		if (res !== '')
			res = res.substr(0, res.lastIndexOf('&'));

		// 
		if (EncryptEnable && useEncrypt)
			return EncryptParamKey + "=" + ParamCodec.encode(res);

		return res;
	}

	private convertParam(data) {
		let res = data;
		if (typeof data === 'object') res = JSON.stringify(res);
		return encodeURIComponent(res);
	}

	private handleError(errorData: RequestErrorData) {
		let success = this.errorHandler.handle(errorData);
		if (!success) alertMgr().handleError(errorData);
	}

	public registerErrorHandler(interface_: Interface, code: number, status: number, func: PostErrHandlerFunc) {
		this.errorHandler.register(func, interface_, status, code);
	}
}

