import {ItemDisplay} from "../../../CoreModule/Components/ItemDisplay";
import Label = cc.Label;
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import Skeleton = sp.Skeleton;
import {HeroSkeleton} from "../../../../Utils/SpineUtils";
import Node = cc.Node;
import {battleScene} from "../../../../Scenes/BattleScene";
import {BaseData, field} from "../../../CoreModule/Data/BaseData";
import {QualityType} from "../../../HeroModule/Data/Quality";
import {Part, partRepo} from "../../../HeroModule/Data/Part";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PlayerCardDisplay extends ItemDisplay<RoomNFTInfo> {

    @property(Label)
    nameLabel: Label = null;
    @property(Label)
    qualityLabel: Label = null;
    @property(Label)
    levelLabel: Label = null;
    @property(Label)
    powerLabel: Label = null;
    @property(Sprite)
    qualityIcon: Sprite = null;
    @property(Node)
    frame: Node = null;

    @property([SpriteFrame])
    qualityPictures: SpriteFrame[] = [];

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    update (dt) {
        this.frame.active = battleScene().currentTargetSelect == this.item;
    }

    refresh() {
        super.refresh();
        this.updateLabels();
        this.updatePictures();
        this.drawHero();
    }

    quality: string[] = ["N", "R", "SR", "SSR", "UR"];

    updateLabels() {
        this.nameLabel.string = this.item.name;
        this.qualityLabel.string = this.quality[this.item.quality];
        this.levelLabel.string = this.item.level.toString();
        this.powerLabel.string = this.item.battlePoint.toString();
    }

    updatePictures() {
        this.qualityIcon.spriteFrame = this.qualityPictures[this.item.quality];
    }

    @property(Skeleton)
    hero: Skeleton = null;

    private size: number = 120;

    private drawHero(){
        this.hero.node.scale = 0;
        let node = new Node('node');
        let temp = node.addComponent(Skeleton);
        let hero = new HeroSkeleton(this.hero, temp, this.item.heroTypeId);
        this.item.parts.forEach(part => { hero.addPart(part); });
        hero.refresh().then(res => {
            node.destroy();
            this.sizeFilter();
        });
    }

    private sizeFilter() {
        let k = Math.max(
            this.hero.node.width / this.size,
            this.hero.node.height / this.size);
        if (k > 1) this.hero.node.scale = 1 / k;
        else this.hero.node.scale = 1;
        this.hero.node.y = - this.size / 2;
    }

    onClick() {
        if (battleScene().currentTargetSelect == this.item) return battleScene().currentTargetSelect = null;
        battleScene().currentTargetSelect = this.item;
    }

}

export class RoomNFTInfo extends BaseData {

    @field(String)
    public nftId: string;
    @field(String)
    public name: string;
    @field(Number)
    public heroTypeId: number;
    @field([Number])
    public partIds: number[];
    @field(Number)
    public level: number;
    @field(Number)
    public quality: QualityType;
    @field(Number)
    public battlePoint: number;

    /**
     * 
     */
    private _parts: Part[];
    public get parts() {
        return this._parts ||= this.partIds.map(
            pid => partRepo().getById(pid)) // DataUtils.part(pid, "id"));
    }

}
