import {ItemDisplay} from "../../../../Modules/CoreModule/Components/ItemDisplay";
import Label = cc.Label;
import Node = cc.Node;
import Sprite = cc.Sprite;
import {PartInfo} from "../../../../Modules/HeroModule/Data/PartInfo";
import Color = cc.Color;
import {selectorScene} from "../../SelectorScene";
import PartsDisplay from "../ItemPrefabs/PartsDisplay";
import Scrollbar = cc.Scrollbar;
import PreviewPictureDisplay from "../ItemPrefabs/PreviewPictureDisplay";
import {partMgr} from "../../../../Modules/HeroModule/Managers/PartManager";
import {configMgr} from "../../../../Modules/CoreModule/Managers/ConfigManager";
import PartConfig from "../../../../Modules/HeroModule/Configs/PartConfig";
import Slider = cc.Slider;
import PartsContainer from "../ItemContainers/PartsContainer";
import Component = cc.Component;
import Boolean = cc.Boolean;
import GlobalEditorCell from "./GlobalEditorCell";
import Prefab = cc.Prefab;
import GlobalEditor from "./GlobalEditor";
import {partTypeRepo} from "../../../../Modules/HeroModule/Data/PartType";
import {HeroType, heroTypeRepo} from "../../../../Modules/HeroModule/Data/HeroType";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GlobalEditorLine extends ItemDisplay<HeroType> {

	@property(Boolean)
	public isTitle: boolean = false;
	@property(Label)
	public title: Label = null;
	@property(Node)
	public cellContainer: Node = null;
	@property([GlobalEditorCell])
	public cells: GlobalEditorCell[] = [];
	@property(Prefab)
	public editPrefab: Prefab = null;
	@property(Prefab)
	public titlePrefab: Prefab = null;

	public globalEditor: GlobalEditor = null;
	private cellCreated = false;

	public get prefab() {
		return this.isTitle ? this.titlePrefab : this.editPrefab;
	}

	protected onLoad() {
	}

	refresh() {
		super.refresh();
		this.drawTitle();
		if (!this.cellCreated)
			this.createCells();
		else this.refreshCells();
	}
	private drawTitle() {
		if (!this.title || !this.item) return;
		this.title.string = `${this.item.id} ${this.item.getDisplayName()}`;
	}
	private createCells() {
		const hts = heroTypeRepo().list;
		hts.forEach(ht => this.createCell(ht));
		this.cellCreated = true;
	}
	private refreshCells() {
		this.cells.forEach(line => line.refresh());
	}
	private createCell(heroType: HeroType) {
		const node = cc.instantiate(this.prefab);
		const cell = node.getComponent(GlobalEditorCell);
		node.parent = this.cellContainer;

		// const sp = this.cellContainer.getComponent(Sprite);
		// sp.enabled = !this.isTitle;

		cell.line = this;
		cell.isTitle = this.isTitle;
		cell.setItem(heroType);

		this.cells.push(cell);
	}

}
