import HeroInTeamContainer from "../../../Modules/BattleModule/Components/Hero/HeroInTeamContainer";
import HeroGroupContainer from "../../../Modules/BattleModule/Components/Hero/HeroGroupContainer";
import {CopyType} from "../../../Modules/CopyModule/Data/Copy";
import {PlayerCopy} from "../../../Modules/CopyModule/Data/PlayerCopy";
import {CopyInterface} from "../../../Modules/CopyModule/CopyCommon";
import {Hero} from "../../../Modules/HeroModule/Data/Hero";
import {playerMgr, waitForLogin} from "../../../Modules/PlayerModule/Managers/PlayerManager";
import {waitForDataLoad} from "../../../Modules/CoreModule/Managers/DataManager";
import {heroMgr} from "../../../Modules/HeroModule/Managers/HeroManager";
import {copyMgr} from "../../../Modules/CopyModule/Managers/CopyManager";
import {battleScene} from "../../BattleScene";

const {ccclass, property} = cc._decorator;

@ccclass
export default class MyTeamFrame extends cc.Component {

    totalHeroes: Hero[] = [];

    @property(HeroInTeamContainer)
    heroesInTeamContainer: HeroInTeamContainer = null;

    @property(HeroGroupContainer)
    heroGroupContainer: HeroGroupContainer = null;

    start() {
        this.setup().then(res => {
            this.refreshTeam();
        });
    }

    @waitForDataLoad
    @waitForLogin
    private async setup() {
        const acc = playerMgr().player.account;
        this.totalHeroes = (await heroMgr().getListFromServer())
          .filter(h => h.ownerAccount == acc);
    }

    private refreshTeam() {
        battleScene().heroesInTeam = this.getHeroesInTeam();
        this.heroesInTeamContainer?.setItems(battleScene().heroesInTeam);
    }

    /**
     * 
     */
    private getHeroesInTeam() {
        let cfg = copyMgr().getMainPlayerCopySettings();
        return this.totalHeroes.filter(h => cfg.nftIds.find(c => c == h.nftId));
    }

    addTeamMember(nftId: string) {
        let cfg: PlayerCopy = copyMgr().getPlayerCopy();
        let teamHeroNftIds = cfg.mainSetting().nftIds;
        if (teamHeroNftIds.indexOf(nftId) >= 0) return this.removeTeamMember(nftId);
        if (teamHeroNftIds.length >= 3) return;
        teamHeroNftIds.push(nftId);
        this.refreshTeam();
    }

    removeTeamMember(nftId: string) {
        let cfg = copyMgr().getPlayerCopy();
        let teamHeroNftIds = cfg.mainSetting().nftIds;
        let idx = teamHeroNftIds.indexOf(nftId);
        if (idx != -1) {
            teamHeroNftIds.splice(idx, 1);
        }
        this.refreshTeam();
    }

    makeNewTeam(){
        let cfg = copyMgr().getPlayerCopy();
        let teamHeroNftIds = cfg.mainSetting().nftIds;
        CopyInterface.AdjustHeroes({copyType: CopyType.Main, nftIds: teamHeroNftIds});
    }
}
