import {BaseData, field} from "../../CoreModule/Data/BaseData";

export enum MotionType {
	None = "none",
	Move = "move",
	Rotate = "rotate",
	Scale = "scale",
	Jump = "jump",
	Shake = "shake",
	Shine = "shine",
	Reset = "reset",
	PlayCharAni = "playCharAni",
	PlayBattleAni = "playBattleAni",
	PlayNormalAni = "playNormalAni",
	PlaySE = "playSE",
	ApplyResult = "applyResult",
	ShowResult = "showResult",
	Wait = "ended",
	FadeOut = "fadeOut"
}

export enum TranslateType {
	Delta = 'delta',
	Value = 'value',
	Origin = 'origin',
	Target = 'target'
}

export enum TargetType {
	Self = 'self',
	Subject = 'subject',
	Target = 'target',
	Background = 'background',
	Screen = 'screen'
}

export enum AniOrigin {
	Subject = 'subject',
	Screen = 'screen',
	Target = 'target',
}

export class Motion extends BaseData {

	@field
	public duration: number = 0; // 
	@field
	public offset: number = 0; // 
	@field
	public wait: number = 0; // 
	@field
	public playType: 'before' | 'after' = 'after'; // 

	@field(String)
	public type: MotionType = MotionType.None;
	@field([Object])
	public params: any[] = [];

}
