import {ISelectableObject} from "./ISelectableObject";
import { SelectableGroup } from "./SelectableGroup";


export abstract class SelectableObject extends cc.Component implements ISelectableObject {
    selectableGroup: SelectableGroup;

    /**
     * 
     */
    public selectSelf(): void {
        this.selectableGroup?.select(this);
    }

    /**
     * 
     */
    public deselectSelf(): void {
        this.selectableGroup?.deselect(this);
    }

    public toggleSelection(): void {
        if (this.selectableGroup == null) return;
        if (this.selectableGroup!.isSelected(this)) {
            this.selectableGroup!.deselect(this);
        } else {
            this.selectableGroup.select(this);
        }
    }

    public removeSelf(): void {
        this.selectableGroup?.removeElement(this);
    }

    /**
     * 
     */
    abstract onSelected(): void;

    /**
     * 
     */
    abstract onDeselected(): void;

}