import {BaseManager, getManager, manager} from "../../CoreModule/Managers/BaseManager";
import {configMgr} from "../../CoreModule/Managers/ConfigManager";
import PartConfig from "../Configs/PartConfig";
import {EditorInterface} from "../Common/EditorCommon";
import {AlertDialogType, alertMgr} from "../../CoreModule/Managers/AlertManager";
import {showLoading} from "../../CoreModule/Managers/LoadingManager";
import {DateUtils} from "../../../Utils/DateUtils";

export function editorMgr() {
	return getManager(EditorManager);
}

export type ChangeType = "pHtScale" |
	"ptBptScale" | "ptBptOffset" | "ptBptHidden" |
	"clothes6Problem" | "weapon3Problem" | "footOffset";
export type ChangeValue =
	number | [number, number] | string[] | boolean;
export type ChangeInfo = {
	key: ChangeType
	path: number[] // 
	ori?: ChangeValue
	val: ChangeValue
}

// 
export enum FileType {
	DataUrl,// readAsDataURL, base64
	Text,// readAsText
	Binary,// readAsBinaryString
	ArrayBuffer,// readAsArrayBuffer
}

@manager
class EditorManager extends BaseManager {

	public changes: {[T: string]:ChangeInfo} = {};

	private _config;
	public get config() {
		return this._config ||= configMgr().config(PartConfig);
	}

	// region 

	private getMapValue(cKey: keyof PartConfig, id1, id2,
											default_, add = false) {
		const key = `${id1}-${id2}`;
		let res = this.config[cKey][key];

		if (res == undefined) {
			res = default_;
			if (add) this.config[cKey][key] = default_;
		}
		if (res instanceof Array) res = res.slice();
		return res;
	}
	private setMapValue(cKey, id1, id2, val) {
		const key = `${id1}-${id2}`;
		return this.config[cKey][key] = val;
	}

	public getPHtScale(ptId: number, fhtId: number, htId: number) {
		const res = this.config.pHtScale[ptId][fhtId][htId];
		return res === undefined ? 1 : res;
	}
	public getPtBptScale(pOriId, bpOriId,
											 default_ = 1,
											 add = false): number {
		return this.getMapValue("ptBptScale", pOriId, bpOriId, default_, add);
	}
	public getPtBptOffset(pOriId, bpOriId,
												default_: [number, number] = [0, 0],
												add = false): [number, number] {
		return this.getMapValue("ptBptOffset", pOriId, bpOriId, default_, add);
	}
	public getPtBptHidden(pOriId, bpOriId,
												default_: string[] = [],
												add = false): string[] {
		return this.getMapValue("ptBptHidden", pOriId, bpOriId, default_, add);
	}
	public getClothes6Problem(pOriId, hpOriId,
														default_ = false,
														add = false): boolean {
		return this.getMapValue("clothes6Problem", pOriId, hpOriId, default_, add);
	}
	public getWeapon3Problem(pOriId, hpOriId,
													 default_ = false,
													 add = false): boolean {
		return this.getMapValue("weapon3Problem", pOriId, hpOriId, default_, add);
	}
	public getFootOffset(bodyId, default_ = [0, 0],
											 add = false): [number, number] {
		let res = this.config.footOffset[bodyId];

		if (res == undefined) {
			res = default_;
			if (add) this.config.footOffset[bodyId] = default_;
		}
		return res.slice();
	}

	// public getPtBptScale(pOriId, bpOriId, default_: number = 1): number {
	// 	return this.getPtBptScaleConfig(pOriId, bpOriId, default_)[2];
	// }
	// public getPtBptOffset(pOriId, bpOriId, default_: [number, number] = [0, 0]): [number, number] {
	// 	return this.getPtBptOffsetConfig(pOriId, bpOriId, default_)[2].slice() as [number, number];
	// }
	// public getPtBptHidden(pOriId, bpOriId, default_: string[] = []): string[] {
	// 	return this.getPtBptHiddenConfig(pOriId, bpOriId, default_)[2].slice();
	// }

	private setPHtScale(ptId, fhtId, htId, val) {
		return this.config.pHtScale[ptId][fhtId][htId] = val;
	}
	private setPtBptScale(pOriId, bpOriId, val: number) {
		return this.setMapValue("ptBptScale", pOriId, bpOriId, val);
	}
	private setPtBptOffset(pOriId, bpOriId, val: [number, number]) {
		return this.setMapValue("ptBptOffset", pOriId, bpOriId, val);
	}
	private setPtBptHidden(pOriId, bpOriId, val: string[]) {
		return this.setMapValue("ptBptHidden", pOriId, bpOriId, val);
	}
	private setClothes6Problem(pOriId, hpOriId, val: boolean) {
		return this.setMapValue("clothes6Problem", pOriId, hpOriId, val);
	}
	private setWeapon3Problem(pOriId, hpOriId, val: boolean) {
		return this.setMapValue("weapon3Problem", pOriId, hpOriId, val);
	}
	private setFootOffset(bodyId, val: boolean) {
		return this.config.footOffset[bodyId] = val;
	}

	public getConfig(key: ChangeType, path: number[]) {
		switch (key) {
			case "pHtScale": // @ts-ignore
				return this.getPHtScale(...path);
			case "ptBptScale": // @ts-ignore
				return this.getPtBptScale(...path);
			case "ptBptOffset": // @ts-ignore
				return this.getPtBptOffset(...path);
			case "ptBptHidden": // @ts-ignore
				return this.getPtBptHidden(...path);
			case "clothes6Problem": // @ts-ignore
				return this.getClothes6Problem(...path);
			case "weapon3Problem": // @ts-ignore
				return this.getWeapon3Problem(...path);
			case "footOffset": // @ts-ignore
				return this.getFootOffset(...path);
		}
	}
	private setConfig(key: ChangeType, path: number[], val: ChangeValue) {
		switch (key) {
			case "pHtScale": // @ts-ignore
				return this.setPHtScale(...path, val);
			case "ptBptScale": // @ts-ignore
				return this.setPtBptScale(...path, val);
			case "ptBptOffset": // @ts-ignore
				return this.setPtBptOffset(...path, val);
			case "ptBptHidden": // @ts-ignore
				return this.setPtBptHidden(...path, val);
			case "clothes6Problem": // @ts-ignore
				return this.setClothes6Problem(...path, val);
			case "weapon3Problem": // @ts-ignore
				return this.setWeapon3Problem(...path, val);
			case "footOffset": // @ts-ignore
				return this.setFootOffset(...path, val);
		}
	}

	// endregion

	// region 

	public changePtBptScale(ptId, bptId, val) {
		this.addChange("ptBptScale", [ptId, bptId], val);
	}
	public changePtBptOffset(ptId, bptId, val) {
		this.addChange("ptBptOffset", [ptId, bptId], val);
	}
	public changePHtScale(ptId, htId, chtId, val) {
		this.addChange("pHtScale", [ptId, htId, chtId], val);
	}
	public changePtBptHidden(ptId, bptId, val) {
		this.addChange("ptBptHidden", [ptId, bptId], val);
	}
	public changeClothes6Problem(ptId, bptId, val) {
		this.addChange("clothes6Problem", [ptId, bptId], val);
	}
	public changeWeapon3Problem(ptId, bptId, val) {
		this.addChange("weapon3Problem", [ptId, bptId], val);
	}
	public changeFootOffset(bodyId, val) {
		this.addChange("footOffset", [bodyId], val);
	}

	private getKey(key: ChangeType, path: number[]) {
		return key + "." + path.join(",");
	}

	public getChange(key: ChangeType, path: number[]) {
		return this.changes[this.getKey(key, path)];
	}

	public addChange(changeOrKey: ChangeInfo | ChangeType,
									 path?: number[], val?: ChangeValue) {
		if (typeof changeOrKey == "string") { // key
			const key = changeOrKey;
			const cKey = this.getKey(key, path);
			const res = this.changes[cKey] ||= {
				key, path, val, ori: this.getConfig(key, path)
			};
			// const res = this.changes[cKey] ||= { key, path, val };
			// res.ori = res.ori === undefined ? this.getConfig(key, path) : res.ori;
			this.setConfig(key, path, res.val = val);
			return res;
		}
		// ChangeInfo
		const change = changeOrKey;
		const cKey = this.getKey(change.key, change.path);
		const oldChange = this.changes[cKey];
		if (oldChange) // 
			this.addChange(change.key, change.path,
				this.getMergeVal(change.key, oldChange.val, change.val))
		else
			this.addChange(change.key, change.path, change.val);
	}

	private getMergeVal(key: ChangeType,
											old: ChangeValue,
											new_: ChangeValue) {
		// let dist;
		// switch (key) {
		// 	case "pHtScale": case "ptBptScale":
		// 		// 1
		// 		dist = (val) => val == 0 ? Infinity :
		// 			val > 1 ? val - 1 : 1 / val - 1; break;
		// 	case "ptBptOffset":
		// 		// [0, 0]
		// 		dist = (val: [number, number]) =>
		// 			val[0] * val[0] + val[1] * val[1]; break;
		// 	case "ptBptHidden":
		// 		dist = (val: string[]) => val.length; break;
		// 	case "clothes6Problem":
		// 		dist = (val: boolean) => val ? 1 : 0; break;
		// }
		return new_; // dist(old) > dist(new_) ? old : new_;
	}

	public resetChange(key, path) {
		const cKey = this.getKey(key, path);
		const change = this.changes[cKey];
		if (!change) return;
		this.setConfig(key, path, change.ori);
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public exportAll() {
		console.log(this.config);
		const str = JSON.stringify(this.config);
		this.saveForBrowser(str, `export-all-${DateUtils.time2StrInFile(Date.now())}.json`);
		this.copyToClipboard(str);
		alertMgr().alert('',
			"", AlertDialogType.OK).then();
	}

	/**
	 * 
	 */
	public export() {
		console.log(this.changes);
		const str = JSON.stringify(this.changes);
		this.saveForBrowser(str, `export-${DateUtils.time2StrInFile(Date.now())}.json`);
		this.copyToClipboard(str);
		alertMgr().alert('',
			"", AlertDialogType.OK).then();
	}
	private copyToClipboard(str) {
		const input = document.createElement('input');
		document.body.appendChild(input);
		input.setAttribute('value', str);
		input.select();
		if (document.execCommand('copy')) {
			document.execCommand('copy');
		}
		document.body.removeChild(input);
	}

	/**
	 * 
	 */
	@showLoading
	public async import() {
		const file = await this.openLocalFile("*.json");
		const str = await this.readLocalFile(file, FileType.Text) as string;
		console.log("str:", str);
		const data = JSON.parse(str);
		console.log("data:", data);

		Object.values<any>(data).forEach(
			change => this.addChange(change));
	}

	/**
	 * 
	 */
	public async push() {
		await EditorInterface.PushConfig({changes: this.changes});
		alertMgr().alert('',
			"", AlertDialogType.OK).then();
	}

	/**
	 * 
	 */
	public async pull() {
		const res = await EditorInterface.PullConfig();
		console.log(res);

		Object.values<any>(res.changes).forEach(
			change => this.addChange(change));
		Object.assign(this.config, res.config);

		alertMgr().alert('',
			"", AlertDialogType.OK).then();
	}

	// endregion

	// region /

	// 
	// 
	// textToWrite - 
	// fileNameToSaveAs - 
	saveForBrowser(textToWrite, fileNameToSaveAs) {
		if (cc.sys.isBrowser) {
			console.log("");
			let textFileAsBlob = new Blob([textToWrite], {type:'application/json'});
			let downloadLink = document.createElement("a");
			downloadLink.download = fileNameToSaveAs;
			downloadLink.innerHTML = "Download File";
			if (window.webkitURL != null)
			{
				// Chrome allows the link to be clicked
				// without actually adding it to the DOM.
				downloadLink.href = window.webkitURL.createObjectURL(textFileAsBlob);
			}
			else
			{
				// Firefox requires the link to be added to the DOM
				// before it can be clicked.
				downloadLink.href = window.URL.createObjectURL(textFileAsBlob);
				// downloadLink.onclick = destroyClickedElement;
				downloadLink.style.display = "none";
				document.body.appendChild(downloadLink);
			}
			downloadLink.click();
		}
	}

	// TS
	// input
	openLocalFile(accept: string) {
		let inputEl: HTMLInputElement = <HTMLInputElement>document.getElementById('file_input');
		if (!inputEl) {
			inputEl = document.createElement('input');
			inputEl.id = 'file_input';
			inputEl.setAttribute('id', 'file_input');
			inputEl.setAttribute('type', 'file');
			inputEl.setAttribute('class', 'fileToUpload');
			inputEl.style.opacity = '0';
			inputEl.style.position = 'absolute';
			inputEl.setAttribute('left', '-999px');
			document.body.appendChild(inputEl);
		}

		accept = accept || ".*";
		inputEl.setAttribute('accept', accept);

		return new Promise<File>(resolve => {
			inputEl.onchange = (event) => {
				// console.log('xxx onchange2', event, inputEl.files);
				let files = inputEl.files
				console.log("event", event, "files", files);
				resolve(files[0]);
			}
			inputEl.click();
		})
	}

	// 
	readLocalFile(file: File, readType: FileType) {
		const reader = new FileReader();
		return new Promise<string | ArrayBuffer>(resolve => {
			reader.onload = function (event) {
				if (reader.readyState == FileReader.DONE)
					resolve(reader.result);
				else resolve(null);
			};
			switch (readType) {
				case FileType.DataUrl:
					reader.readAsDataURL(file);
					break;
				case FileType.Text:
					reader.readAsText(file);   //
					//reader.readAsText(file,'gb2312');   //utf-8txtANSI
					break;
				case FileType.Binary:
					reader.readAsBinaryString(file);
					break;
				case FileType.ArrayBuffer:
					reader.readAsArrayBuffer(file);
					break;
			}
		})
	}

	// base64Texture2D
	base64ToTexture2D(base64: string, callback: (this: void, texture: cc.Texture2D) => void) {
		if (base64) {
			var img = new Image();
			img.onload = function () {
				var texture = new cc.Texture2D();
				texture.initWithElement(img);
				texture.handleLoadedTexture();
				if (callback) callback(texture);
			}
			img.onerror = function (err) {
			}
			if ((<any>base64).startsWith !== undefined && (<any>base64).startsWith("data:image")) {
				img.src = base64;
			} else {
				img.src = "data:image/png;base64," + base64;
			}
		} else {
			if (callback) callback(null);
		}
	}

	// base64cc.SpriteFrame
	base64ToSpriteFrame(base64: string, callback: (this: void, spriteFrame: cc.SpriteFrame) => void) {
		this.base64ToTexture2D(base64, (texture: cc.Texture2D) => {
			if (texture) {
				var newframe = new cc.SpriteFrame(texture);
				if (callback) callback(newframe);
			} else {
				if (callback) callback(null);
			}
		});
	}

	// endregion
}

