import {ItemDisplay} from "../../../../Modules/CoreModule/Components/ItemDisplay";
import Label = cc.Label;
import Node = cc.Node;
import {PartInfo} from "../../../../Modules/HeroModule/Data/PartInfo";
import {selectorScene} from "../../SelectorScene";
import PartsDisplay from "../ItemPrefabs/PartsDisplay";
import PreviewPictureDisplay from "../ItemPrefabs/PreviewPictureDisplay";
import {configMgr} from "../../../../Modules/CoreModule/Managers/ConfigManager";
import PartConfig from "../../../../Modules/HeroModule/Configs/PartConfig";
import Slider = cc.Slider;
import PartsContainer from "../ItemContainers/PartsContainer";
import EditBox = cc.EditBox;
import {editorMgr} from "../../../../Modules/HeroModule/Managers/EditorManager";
import TransformUtil from "../../../../Utils/TransformUtils";
import {partTypeRepo} from "../../../../Modules/HeroModule/Data/PartType";
import Button = cc.Button;
import {partRepo} from "../../../../Modules/HeroModule/Data/Part";

const {ccclass, property} = cc._decorator;

const MaxScale = 4;
const MinScale = 0;

const OffsetRange = 256;
const OffsetDelta = 5;
const OffsetWait = 500;
const OffsetInterval = 50;

@ccclass
export default class SingleEditor extends ItemDisplay<PartInfo> {

	@property(PartsContainer)
	partsContainer: PartsContainer = null;

	@property(PartsDisplay)
	curPartDisplay: PartsDisplay = null;
	@property(PartsDisplay)
	basePartDisplay: PartsDisplay = null;

	@property(Node)
	scaleNode: Node = null;
	@property(Slider)
	scaleBar: Slider = null;
	@property(EditBox)
	scaleValue: EditBox = null;

	@property(Node)
	xNode: Node = null;
	@property(Slider)
	xBar: Slider = null;
	@property(EditBox)
	xValue: EditBox = null;

	@property(Node)
	yNode: Node = null;
	@property(Slider)
	yBar: Slider = null;
	@property(EditBox)
	yValue: EditBox = null;

	@property(Node)
	footXNode: Node = null;
	@property(Slider)
	footXBar: Slider = null;
	@property(EditBox)
	footXValue: EditBox = null;

	@property(Node)
	footYNode: Node = null;
	@property(Slider)
	footYBar: Slider = null;
	@property(EditBox)
	footYValue: EditBox = null;

	@property(Button)
	applyButton: Button = null;
	@property(Button)
	clothes6Button: Button = null;
	@property(Button)
	weapon3Button: Button = null;

	@property(PreviewPictureDisplay)
	preview: PreviewPictureDisplay = null

	@property(Node)
	touchNode: Node;

	public get globalEditor() {
		return selectorScene().globalEditor;
	}

	onLoad () {
		this.partsContainer.singleEditor = this;
		this.curPartDisplay.preview = this.preview;
		this.basePartDisplay.preview = this.preview;

		this.addKeyListeners();
	}

	addKeyListeners() {
		cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
		cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);

		this.touchNode.on("touchstart", this.onTouchStart, this);
	}

	// region 

	public get part() { return this.item?.part; }
	public get partType() { return this.part?.partType; }
	public get basePartType() { return this.partType?.basePartType; }
	public get basePart() { return this.basePartType ?
		this.preview.item?.findPart(this.basePartType) : null;
	}
	public get headPart() {
		return this.preview.item?.findPart(partTypeRepo().head);
	}
	public get bodyPart() {
		return this.preview.item?.findPart(partTypeRepo().body);
	}
	public get footPart() {
		return this.preview.item?.findPart(partTypeRepo().foot);
	}

	public get scale() {
		return editorMgr().getPtBptScale(this.part.oriId, this.basePart.oriId);
	}
	public set scale(val) {
		editorMgr().changePtBptScale(this.part.oriId, this.basePart.oriId, val);
	}

	public get offset() {
		return editorMgr().getPtBptOffset(this.part.oriId, this.basePart.oriId);
	}
	public set offset(val) {
		editorMgr().changePtBptOffset(this.part.oriId, this.basePart.oriId, val);
	}

	public get hidden() {
		return editorMgr().getPtBptHidden(this.part.oriId, this.basePart.oriId);
	}
	public set hidden(val) {
		editorMgr().changePtBptHidden(this.part.oriId, this.basePart.oriId, val);
	}

	public get clothes6() {
		return editorMgr().getClothes6Problem(this.part.oriId, this.headPart.oriId);
	}
	public set clothes6(val) {
		editorMgr().changeClothes6Problem(this.part.oriId, this.headPart.oriId, val);
	}

	public get weapon3() {
		return editorMgr().getWeapon3Problem(this.part.oriId, this.bodyPart.oriId);
	}
	public set weapon3(val) {
		editorMgr().changeWeapon3Problem(this.part.oriId, this.bodyPart.oriId, val);
	}

	public get footOffset() {
		return editorMgr().getFootOffset(this.footPart?.oriId);
	}
	public set footOffset(val) {
		const footPart = this.footPart;
		if (!footPart) return;
		editorMgr().changeFootOffset(footPart.oriId, val);
	}

	// endregion

	// region 

	refresh() {
		super.refresh();

		this.preview.refresh();
		this.partsContainer.refresh();

		if (this.item == null) return;

		this.globalEditor?.refresh();

		this.drawParts();
		this.drawScaleOffset();
		this.drawFootOffset();
		this.drawOthers();
	}
	private drawParts() {
		this.curPartDisplay.setItem(this.item);
		if (this.basePart) {
			this.basePartDisplay.node.active = true;
			this.basePartDisplay.setItem(this.basePart.toPartInfo());
		} else
			this.basePartDisplay.node.active = false;
	}
	private drawScaleOffset() {
		this.xNode.active = this.yNode.active =
			this.scaleNode.active = !!this.basePart;

		if (!this.basePart) return;

		const scale = this.scale;
		this.scaleBar.progress = (scale - MinScale) / (MaxScale - MinScale);
		this.scaleValue.string = scale.toFixed(3);

		this.drawOffsetBar(this.xBar, this.xValue,
			this.yBar, this.yValue, this.offset);
	}
	private drawFootOffset() {
		this.footXNode.active = this.footYNode.active = !!this.footPart;

		this.drawOffsetBar(this.footXBar, this.footXValue,
			this.footYBar, this.footYValue, this.footOffset);
	}
	private drawOffsetBar(xBar, xValue, yBar, yValue, offset) {
		xBar.progress = (offset[0] - -OffsetRange) / (OffsetRange * 2);
		xValue.string = offset[0].toFixed(1);
		yBar.progress = (offset[1] - -OffsetRange) / (OffsetRange * 2);
		yValue.string = offset[1].toFixed(1);
	}
	private drawOthers() {
		this.applyButton.interactable = !!this.basePart;
		this.clothes6Button.interactable = this.item.part.partType.isClothes;
		this.weapon3Button.interactable = this.item.part.partType.isWeapon;
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public setScale(scale) {
		this.scale = scale;
		this.refresh();
	}

	/**
	 * 
	 */
	public setOffset(x?: number, y?: number) {
		const ori = this.offset;
		if (x != undefined) ori[0] = x;
		if (y != undefined) ori[1] = y;
		this.offset = ori;

		this.refresh();
	}
	public setX(val: number) {return this.setOffset(val);}
	public setY(val: number) {return this.setOffset(undefined, val);}

	/**
	 * 
	 */
	public setFootOffset(x?: number, y?: number) {
		const ori = this.footOffset;
		if (x != undefined) ori[0] = x;
		if (y != undefined) ori[1] = y;
		this.footOffset = ori;

		this.refresh();
	}
	public setFootX(val: number) {return this.setFootOffset(val);}
	public setFootY(val: number) {return this.setFootOffset(undefined, val);}

	/**
	 * 
	 */
	public reset() {
		this.setScale(1);
		this.setOffset();
	}

	/**
	 * toggle
	 * @param name
	 */
	public toggleSlot(name) {
		const hidden = this.hidden;
		const idx = hidden.indexOf(name);
		if (idx >= 0) hidden.splice(idx, 1);
		else hidden.push(name);
		this.hidden = hidden;

		this.refresh();
	}

	/**
	 * toggle Clothes6
	 */
	public toggleClothes6() {
		this.clothes6 = !this.clothes6;
		this.refresh();
	}

	/**
	 * toggle Weapon3
	 */
	public toggleWeapon3() {
		this.weapon3 = !this.weapon3;
		this.refresh();
	}

	/**
	 * 
	 */
	private processDir(dir, rate = 1) {
		const delta = OffsetDelta * rate;
		const func = this.footMode ? "setFoot" : "set";
		const offset = this.footMode ? this.footOffset : this.offset;
		switch (dir) {
			case cc.macro.KEY.down: this[func+"Y"](offset[1] - delta); break;
			case cc.macro.KEY.left: this[func+"X"](offset[0] - delta); break;
			case cc.macro.KEY.right: this[func+"X"](offset[0] + delta); break;
			case cc.macro.KEY.up: this[func+"Y"](offset[1] + delta); break;
		}
		this.refresh();
	}

	/**
	 * 
	 */
	public applySameHeroType() {
		const curBp = this.basePart;
		if (!curBp) return;

		const curPart = this.part;
		const pOriId = curPart.oriId;
		const bhtId = curBp.heroTypeId, bptId = curBp.partTypeId;
		const baseParts = partRepo().findByTypes(bhtId, bptId, 1);

		baseParts.forEach(bp => {
			if (bp == curBp) return;
			editorMgr().changePtBptScale(pOriId, bp.oriId, this.scale);
			editorMgr().changePtBptOffset(pOriId, bp.oriId, this.offset);
			editorMgr().changePtBptHidden(pOriId, bp.oriId, this.hidden);
			// if (pt.isClothes)
			// 	editorMgr().changeClothes6Problem(bp.oriId,
			// 		this.basePart.oriId, this.clothes6);
		})

		// Body
		if (this.partType.isWeapon && this.weapon3 !== undefined) {
			const curBp2 = this.bodyPart
			const bhtId2 = curBp2.heroTypeId, bptId2 = curBp2.partTypeId;
			const bodyParts = partRepo().findByTypes(bhtId2, bptId2, 1);

			bodyParts.forEach(bp => {
				if (bp == curBp2) return;
				editorMgr().changeWeapon3Problem(pOriId, bp.oriId, this.weapon3);
			})
		}

		if (this.partType.isClothes && this.clothes6 !== undefined) {
			// Head
			const curHp = this.headPart
			const hhtId = curHp.heroTypeId, hptId = curHp.partTypeId;
			const headParts = partRepo().findByTypes(hhtId, hptId, 1);

			headParts.forEach(hp => {
				if (hp == curHp) return;
				editorMgr().changeClothes6Problem(pOriId, hp.oriId, this.clothes6);
			})
		}

		this.refresh();
	}

	// endregion

	// region 

	public onScaleSlide() {
		const rate = this.scaleBar.progress;
		this.setScale(rate * (MaxScale - MinScale) + MinScale);
	}
	public onScaleEdit() {
		const num = Number(this.scaleValue.string);
		if (isNaN(num)) this.setScale(this.scale);
		else this.setScale(num);
	}

	public onXSlide() {
		const rate = this.xBar.progress;
		this.setX(rate * (OffsetRange * 2) - OffsetRange);
	}
	public onXEdit() {
		const num = Number(this.xValue.string);
		if (isNaN(num)) this.setX(this.offset[0]);
		else this.setX(num);
	}

	public onYSlide() {
		const rate = this.yBar.progress;
		this.setY(rate * (OffsetRange * 2) - OffsetRange);
	}
	public onYEdit() {
		const num = Number(this.yValue.string);
		if (isNaN(num)) this.setY(this.offset[1]);
		else this.setY(num);
	}

	public onFootXSlide() {
		const rate = this.footXBar.progress;
		this.setFootX(rate * (OffsetRange * 2) - OffsetRange);
	}
	public onFootXEdit() {
		const num = Number(this.footXValue.string);
		if (isNaN(num)) this.setFootX(this.offset[0]);
		else this.setFootX(num);
	}

	public onFootYSlide() {
		const rate = this.footYBar.progress;
		this.setFootY(rate * (OffsetRange * 2) - OffsetRange);
	}
	public onFootYEdit() {
		const num = Number(this.footYValue.string);
		if (isNaN(num)) this.setFootY(this.offset[1]);
		else this.setFootY(num);
	}

	// region 

	private keyTimes = {};
	private footMode = false;
	private onKeyDown(e) {
		if (e.keyCode == cc.macro.KEY.shift)
			this.footMode = true;
		else {
			const keyCode = this.convertKeyCode(e.keyCode);
			this.keyTimes[keyCode] = {time: Date.now(), dt: 0};
			this.processDir(keyCode);
		}
	}
	private onKeyUp(e) {
		if (e.keyCode == cc.macro.KEY.shift)
			this.footMode = false;
		else {
			const keyCode = this.convertKeyCode(e.keyCode);
			delete this.keyTimes[keyCode];
		}
	}
	private convertKeyCode(code) {
		switch (code) {
			case cc.macro.KEY.s: return cc.macro.KEY.down;
			case cc.macro.KEY.a: return cc.macro.KEY.left;
			case cc.macro.KEY.d: return cc.macro.KEY.right;
			case cc.macro.KEY.w: return cc.macro.KEY.up;
			default: return code;
		}
	}

	private onTouchStart(e) {
		const skNode = this.preview.skeleton.node;
		const skPos = TransformUtil.getWorldPosition2(skNode);
		const touch: cc.Touch = e.currentTouch;
		const loc = touch.getLocation().clone();
		const dt = loc.sub(skPos).multiply(
			new cc.Vec2(1 / skNode.scaleX, 1 / skNode.scaleY));

		console.log("Delta", loc.x, loc.y, dt.x, dt.y, skNode, this.preview.skeleton);

		const names = this.item.part.slotNames;
		for (const name of names) {
			const slot = this.preview.item.slotTouchTest(name, dt);
			console.log("Slot: ", name, slot);
			if (slot) {
				this.toggleSlot(slot.data.name)
				return;
			}
		}
	}

	// endregion

	// endregion

	// region 

	protected update(dt: number) {
		const now = Date.now();

		Object.keys(this.keyTimes).forEach(key => {
			const data = this.keyTimes[key];
			if (!data || now - data.time < OffsetWait) return;

			if ((data.dt += dt) > OffsetInterval) {
				data.dt = 0; this.processDir(Number(key));
			}
		});
	}

	// endregion
}
