import {ItemDisplay} from "../../../../Modules/CoreModule/Components/ItemDisplay";
import Label = cc.Label;
import Node = cc.Node;
import Sprite = cc.Sprite;
import {PartInfo} from "../../../../Modules/HeroModule/Data/PartInfo";
import Color = cc.Color;
import {selectorScene} from "../../SelectorScene";
import PartsDisplay from "../ItemPrefabs/PartsDisplay";
import Scrollbar = cc.Scrollbar;
import PreviewPictureDisplay from "../ItemPrefabs/PreviewPictureDisplay";
import {partMgr} from "../../../../Modules/HeroModule/Managers/PartManager";
import {configMgr} from "../../../../Modules/CoreModule/Managers/ConfigManager";
import PartConfig from "../../../../Modules/HeroModule/Configs/PartConfig";
import Slider = cc.Slider;
import PartsContainer from "../ItemContainers/PartsContainer";
import Component = cc.Component;
import EditBox = cc.EditBox;
import {PartType, partTypeRepo} from "../../../../Modules/HeroModule/Data/PartType";
import GlobalEditorLine from "./GlobalEditorLine";
import {HeroType} from "../../../../Modules/HeroModule/Data/HeroType";
import {editorMgr} from "../../../../Modules/HeroModule/Managers/EditorManager";

const {ccclass, property} = cc._decorator;

const MaxScale = 4;
const MinScale = 0;

@ccclass
export default class GlobalEditorCell extends ItemDisplay<HeroType> {

	@property(Boolean)
	public isTitle: boolean = false;
	@property(Label)
	public title: Label = null;
	@property(Slider)
	public slider: Slider = null;
	@property(Label)
	public value: Label = null;
	@property(EditBox)
	public edit: EditBox = null;

	public line: GlobalEditorLine = null;

	// region 

	public get globalEditor() {
		return this.line.globalEditor;
	}
	public get singleEditor() {
		return selectorScene().singleEditor;
	}
	public get partType() {
		return this.globalEditor.item;
	}
	public get firstHeroType() {
		return this.line.item;
	}

	/**
	 * 
	 */
	public get scale() {
		const ptId = this.partType.id,
			fhtId = this.firstHeroType.id, htId = this.item.id;
		return editorMgr().getPHtScale(ptId, fhtId, htId);
	}
	public set scale(val) {
		const ptId = this.partType.id,
			fhtId = this.firstHeroType.id, htId = this.item.id;
		editorMgr().changePHtScale(ptId, fhtId, htId, val);
	}

	// endregion

	// region 

	public refresh() {
		if (this.isTitle) this.drawTitle();
		else this.drawValue();
	}
	private drawTitle() {
		// const curPart = this.singleEditor?.part;
		// const htId = this.item.id
		// const cHtId = curPart?.heroTypeId;
		//
		// const color = htId == cHtId ? Color.GREEN : Color.WHITE;

		this.title.string = `${this.item.id} ${this.item.getDisplayName()}`;
		// this.title.node.color = color;
	}
	private drawValue() {
		const config = configMgr().config(PartConfig);
		const ptId = this.partType.id,
			fhtId = this.firstHeroType.id, htId = this.item.id;
		const scale = config.pHtScale[ptId][fhtId][htId];

		this.slider.progress = (scale - MinScale) / (MaxScale - MinScale);
		this.value.string = scale.toFixed(3);
		this.edit.string = scale.toFixed(3);

		const editable = fhtId != htId && !this.partType.isBody;
		this.slider.node.active = editable;
		this.edit.node.active = editable;
		this.value.node.active = !editable;

		const curPart = this.singleEditor?.part;
		const basePart = this.singleEditor?.basePart;
		const cPtId = curPart?.partTypeId;
		const cHtId = curPart?.heroTypeId;
		const cBhtId = basePart ? basePart.heroTypeId :
			this.globalEditor.preview.item?.heroTypeId;

		const color = ptId == cPtId && fhtId == cBhtId && htId == cHtId ?
			Color.GREEN : Color.WHITE;

		this.value.node.color = this.edit.textLabel.node.color = color;
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public setScale(scale) {
		this.scale = scale;

		this.refresh();
		this.globalEditor.preview.refresh();
		this.globalEditor.partsContainer.refresh();
	}

	/**
	 * 
	 */
	public reset() {
		this.setScale(1);
	}

	// endregion

	// region 

	public onScaleSlide() {
		const rate = this.slider.progress;
		this.setScale(rate * (MaxScale - MinScale) + MinScale);
	}
	public onEdit() {
		const num = Number(this.edit.string);
		if (isNaN(num)) this.setScale(this.scale);
		else this.setScale(num);
	}

	// endregion
}
