import {ItemDisplay} from "../../CoreModule/Components/ItemDisplay";
import {Hero} from "../../HeroModule/Data/Hero";
import {HeroSkeleton} from "../../../Utils/SpineUtils";
import ccclass = cc._decorator.ccclass;
import property = cc._decorator.property;
import Skeleton = sp.Skeleton;
import Node = cc.Node;
import Bone = sp.spine.Bone;
import Slot = sp.spine.Slot;
import instantiate = cc.instantiate;
import BattleScene, {battleScene} from "../../../Scenes/BattleScene";

@ccclass
export default class HeroWithAvatar extends ItemDisplay<Hero> {

    start () {

    }

    protected onItemChanged() {
        super.onItemChanged();
        this.drawAvatar();
    }

    // region 

    @property(Skeleton)
    avatar: Skeleton = null;

    @property(Number)
    size: number = 100;

    private avatarHeight: number = 0;
    private avatarWidth: number = 0;
    private avatarOffsetX: number = 0;
    private avatarOffsetY: number = 0;

    private drawAvatar(){
        this.avatar.node.scale = 0;
        let loading = instantiate(battleScene().loading);
        loading.parent = this.avatar.node.parent;
        loading.scale = this.size / loading.width * 0.7;
        let node = new Node('node');
        let temp = node.addComponent(Skeleton);
        let hero = new HeroSkeleton(this.avatar, temp, this.item.heroTypeId);
        this.item.parts.forEach(part => { hero.addPart(part); });
        hero.refresh().then(res => {
            node.destroy();
            let bone: Bone = hero.findBone("head_1");

            hero.skeleton.updateWorldTransform();
            let offsetX = bone.worldX, offsetY = bone.worldY;
            while (bone.parent) bone = bone.parent;
            bone.x -= offsetX;
            bone.y -= offsetY;

            let slot: Slot = hero.findSlot("head_1");
            if (!slot.getAttachment()) return;
            this.avatarHeight = slot.getAttachment()["height"];
            this.avatarWidth = slot.getAttachment()["width"];
            this.avatarOffsetY = slot.getAttachment()["x"];
            this.avatarOffsetX = slot.getAttachment()["y"];
            this.sizeFilter();
            loading.destroy();
        });
    }

    private sizeFilter() {
        let k = Math.min(this.avatarWidth / this.size, this.avatarHeight / this.size);
        k *= 1.2;
        // this.avatar.node.x = - this.avatarOffsetX / k;
        this.avatar.node.y = - this.avatarOffsetY / k;
        if (k > 1) this.avatar.node.scale = 1 / k;
        else this.avatar.node.scale = 1;
    }

    // endregion

}
