import Label = cc.Label;
import {battleScene} from "../../../../Scenes/BattleScene";
import {playerMgr} from "../../../PlayerModule/Managers/PlayerManager";
import {PlayerMoney} from "../../../EconomicModule/Data/PlayerMoney";
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import {Hero} from "../../../HeroModule/Data/Hero";
import {HeroSkeleton} from "../../../../Utils/SpineUtils";
import {heroMgr} from "../../../HeroModule/Managers/HeroManager";
import Skeleton = sp.Skeleton;
import Node = cc.Node;
import Bone = sp.spine.Bone;
import Slot = sp.spine.Slot;
import {autoUnitConvert} from "../../../../Utils/NumberUtils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SelectedHeroDisplay extends cc.Component {

    @property(Label)
    nameLabel: Label = null;
    @property(Label)
    levelLabel: Label = null;
    @property(Label)
    qualityLabel: Label = null;
    @property(Label)
    powerLabel: Label = null;
    @property(Label)
    coinLabel: Label = null;

    @property(Sprite)
    qualitySprite: Sprite = null;
    @property(Sprite)
    restraintSprite: Sprite = null;

    @property(Node)
    playerInfoNode: Node = null;

    private hero: Hero = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    update (dt) {
        if (!battleScene().heroSelected) {
            this.playerInfoNode.active = true;
            this.node.active = false;
            return;
        }
        let hero = battleScene().currentPVPSelect;
        if (hero != this.hero) {
            this.hero = hero;
            this.onHeroChanged(this.hero);
        }
        this.updateLabels(this.hero);
    }

    private quality: string[] = ["N", "R", "SR", "SSR", "UR"];

    @autoUnitConvert
    updateLabels(hero: Hero) {
        this.levelLabel.string = "Lv." + hero.level.level;
        this.powerLabel.string = "99999999";
        this.coinLabel.string = playerMgr().getPlayerData(PlayerMoney).gold.toString();
    }

    @property([SpriteFrame])
    qualityPictures: SpriteFrame[] = [];
    @property([SpriteFrame])
    restraintPictures: SpriteFrame[] = [];

    onHeroChanged(hero: Hero) {
        this.nameLabel.string = hero.name;
        this.qualityLabel.string = this.quality[hero.quality];
        this.qualitySprite.spriteFrame = this.qualityPictures[hero.quality];
        this.restraintSprite.spriteFrame = this.restraintPictures[hero.restraint];
        this.drawAvatar(hero);
    }

    // region 

    @property(Skeleton)
    avatar: Skeleton = null;

    private size: number = 120;
    private avatarHeight: number = 0;
    private avatarWidth: number = 0;
    private avatarOffsetX: number = 0;
    private avatarOffsetY: number = 0;

    private drawAvatar(hero: Hero) {
        this.avatar.node.scale = 0;
        let node = new Node('node');
        let temp = node.addComponent(Skeleton);
        let skeleton = new HeroSkeleton(this.avatar, temp, heroMgr().getCacheOneFromServer(hero.nftId).heroTypeId);
        hero.parts.forEach(part => { skeleton.addPart(part); });
        skeleton.refresh().then(res => {
            node.destroy();
            let bone: Bone = skeleton.findBone("head_1");

            skeleton.skeleton.updateWorldTransform();
            let offsetX = bone.worldX, offsetY = bone.worldY;
            while (bone.parent) bone = bone.parent;
            bone.x -= offsetX;
            bone.y -= offsetY;

            let slot: Slot = skeleton.findSlot("head_1");
            if (!slot.getAttachment()) return;
            this.avatarHeight = slot.getAttachment()["height"];
            this.avatarWidth = slot.getAttachment()["width"];
            this.avatarOffsetY = slot.getAttachment()["x"];
            this.avatarOffsetX = slot.getAttachment()["y"];
            this.sizeFilter();
        });
    }

    private sizeFilter() {
        let k = Math.min(this.avatarWidth / this.size, this.avatarHeight / this.size);
        k *= 1.2;
        // this.avatar.node.x = - this.avatarOffsetX / k;
        this.avatar.node.y = - this.avatarOffsetY / k;
        if (k > 1) this.avatar.node.scale = 1 / k;
        else this.avatar.node.scale = 1;
    }

    // endregion
}
