import Skeleton = sp.Skeleton;
import Animation = cc.Animation;
import Vec2 = cc.Vec2;
import Node = cc.Node;
import Color = cc.Color;
import Prefab = cc.Prefab;
import ProgressBar = cc.ProgressBar;
import {ItemDisplay} from "../../CoreModule/Components/ItemDisplay";
import {Vec1} from "../Utils/Vec1";
import HitDamage, {HitType} from "./IndependentController/HitDamage";
import {HeroSkeleton} from "../../../Utils/SpineUtils";

const {ccclass, property} = cc._decorator;

// export type BattlerState = 'hidden' | 'idle' | 'playing' | 'dead';

const MaxElapsedTime = 99999;

@ccclass
export default class BattlerDisplay extends ItemDisplay<Battler> {

	@property
	public camp: number = 0;
	@property
	public index: number = 0;
	@property
	public heroScale: number = 0.2;

	// @property(Node)
	// public scaleNode: Node;

	@property(Node)
	public skeletonNode: Node;

	@property(Skeleton)
	public skeleton: Skeleton = null;
	@property(Animation)
	public animation: Animation = null;

	@property(ProgressBar)
	public hpBar: ProgressBar = null;

	@property(Node)
	public shadowNode: Node = null;

	public battle;

	public oriPos: Vec2;
	public oriAngle: Vec1;
	public oriScale: Vec2;

	public get pos() { return new Vec2(this.node.x, this.node.y); }
	public set pos(val: Vec2) { this.node.setPosition(val); }
	public get angle() { return new Vec1(this.skeletonNode.angle); }
	public set angle(val: Vec1) { this.skeletonNode.angle = val.x; }
	public get scale() { return new Vec2(this.skeletonNode.scaleX, this.skeletonNode.scaleY); }
	public set scale(val: Vec2) { this.skeletonNode.setScale(val); }
	public get color() { return this.skeletonNode.color; }
	public set color(val: Color) { this.skeletonNode.color = val; }

	/**
	 * 
	 */
	public get flip() { return this.camp == 1; }

	// region 

	protected start() {
		this.oriPos = new Vec2(this.node.x, this.node.y);
		this.oriAngle = new Vec1(this.node.angle);
		this.oriScale = new Vec2(this.node.scaleX, this.node.scaleY)

		// 
		this.node.scaleX = this.flip ? 1 : -1;
	}

	public refresh() {
		super.refresh();
		this.drawSkeleton(this.item.entity);
	}

	private drawSkeleton(entity: IEntity){
		if (!entity.parts) {
			this.skeleton.skeletonData = entity.skeleton;
			return;
		}

		let node = new Node('node');
		let temp = node.addComponent(Skeleton);
		let hero = new HeroSkeleton(this.skeleton, temp, heroMgr().getCacheOneFromServer(entity.nftId).heroTypeId);
		entity.parts.forEach(part => { hero.addPart(part); });
		hero.refresh().then(res => node.destroy());
	}

	// endregion

	// region 

	/**
	 * Display
	 */
	public get subjectDisplay() {
		return this.battle.getDisplay(this.item.subject);
	}
	public get targetDisplay() {
		return this.battle.getDisplay(this.item.target);
	}

	// endregion

	// region 

	/**
	 * 
	 */
	protected update(dt: number) {
		if (!this.item) return;
		this.updateTranslate();
		this.updateColor();
		this.updateOpacity();
		this.updateAnimation();
		this.updateResult();
		this.sizeFilter();
		this.updateHp();
		this.updateItemList();
	}

	/**
	 * 
	 */
	private updateTranslate() {
		this.scale = this.item.scale;
		this.angle = this.item.angle;

		// 
		const sPos = this.convertPosition(this.item.startPos);
		const tPos = this.convertPosition(this.item.targetPos);
		const curPos: Vec2 = new Vec2();

		Vec2.lerp(curPos, sPos, tPos, this.item.posRate.x);
		curPos.y += this.item.z;

		// 
		curPos.x += this.item.shakePos.x;
		curPos.y += this.item.shakePos.y;

		this.pos = curPos;
	}
	private convertPosition(pos: Position) {
		const offset = pos.offset.clone();
		if (this.flip) offset.x = -offset.x;

		if (pos.origin == "self")
			return this.oriPos.clone().add(offset);
		if (pos.origin == "target" && this.targetDisplay)
			return this.targetDisplay.oriPos.clone().add(offset);

		return pos.offset;
	}

	/**
	 * 
	 * @private
	 */
	private updateColor() {
		this.color = this.item.color;
	}

	/**
	 * 
	 * @private
	 */
	private updateOpacity() {
		this.skeletonNode.opacity = this.item.opacity;
		this.shadowNode.opacity = this.item.opacity;
		if (this.hpBar) this.hpBar.node.opacity = this.item.opacity;
	}

	/**
	 * TODO: 
	 * @private
	 */
	private updateAnimation() {
		switch (this.item.state) {
			case BattlerState.Idle:
				if (this.skeleton.animation == "stand") return;
				this.setAnimation("stand", true);
				return;
			case BattlerState.Dead:
				this.setAnimation(null, false);
				break;
		}
		this.updateNormalAnimation();
		let anim = this.item.animation;
		if (!anim) return;
		this.setAnimation(anim);
	}

	private updateNormalAnimation() {
		let times = this.item.normalAnimationTimes;
		this.item.normalAnimations.forEach((a, i) =>
			this.battle.playNormalAnimation(a, times[i],
				this.node.x + this.skeletonNode.x,
				this.node.y + this.skeletonNode.y + this.skeletonNode.height * this.heroScale / 2,
					  this.camp));
	}

	private setAnimation(name: string, loop: boolean = false){
		this.skeleton.clearTracks();
		if (!name) return;
		this.skeleton.setAnimation(0, name, loop);
	}

	@property(Prefab)
	public hit: Prefab = null;

	/**
	 * TODO: 
	 * @private
	 */
	private updateResult() {
		let result = this.item.result;
		if (!result) return;
		if(result.hpDamage >= 0)
			this.showDamage(result);
	}

	private showDamage(result: Result){
		let node = cc.instantiate(this.hit);
		node.parent = this.node.parent;
		let component = node.getComponent(HitDamage);
		component.setValue(result.hpDamage);
		if (result.isCritical) component.setType(HitType.Critical);
		if (result.isMiss) component.setType(HitType.Miss);
		component.setPosition(this.node.x + this.skeletonNode.x, this.node.y + this.skeletonNode.y + this.skeletonNode.height * this.heroScale);
		// component.setRange(this.skeletonNode.width * this.heroScale * 0.7, this.skeletonNode.height * this.heroScale * 0.7);
	}

	/**
	 * 
	 * @private
	 */
	private sizeFilter() {
		this.skeleton.node.scaleX = this.heroScale;
		this.skeleton.node.scaleY = this.heroScale;
		this.skeleton.node.y = - this.skeleton.node.height * this.heroScale / 3;
		if (this.shadowNode) {
			this.shadowNode.active = true;
			this.shadowNode.y = - this.skeleton.node.height * this.heroScale / 3;
			this.shadowNode.scale = this.skeleton.node.height * this.heroScale / this.shadowNode.width / 2;
		}
	}

	/**
	 * 
	 * @private
	 */
	private updateHp() {
		if (!this.hpBar) return;
		if (!this.skeleton.skeletonData) {
			this.hpBar.node.active = false;
			return;
		}
		this.hpBar.node.y = 20 + this.skeletonNode.height / 2 * this.skeletonNode.scaleY;
		this.hpBar.node.active = true;
		this.hpBar.node.scaleX = this.flip ? 1 : -1;
		this.hpBar.progress = cc.misc.lerp(this.hpBar.progress, this.item.hp / this.item.mhp, 0.1);
	}

	private itemList: Battler[] = [];

	private updateItemList() {
		if (this.item.isDead) {
			this.itemList.shift();
			if (this.itemList[0])
				this.setItem(this.itemList[0]);
		}
	}

	addItem(battler: Battler) {
		this.node.active = true;
		this.itemList.push(battler);
		if (this.itemList.length == 1) this.setItem(battler);
	}

	clearItems() {
		this.itemList = [];
		this.node.active = false;
	}

	// endregion

}

import {heroMgr} from "../../HeroModule/Managers/HeroManager";
import {Battler, BattlerState, IEntity, Position} from "../Data/Battler";
import {Result} from "../Data/Result";

// import BattleDisplay from "./BattleDisplay";
