import Sprite = cc.Sprite;
import Label = cc.Label;
import Skeleton = sp.Skeleton;
import SpriteFrame = cc.SpriteFrame;
import {autoUnitConvert} from "../../../../Utils/NumberUtils";
import {HeroSkeleton} from "../../../../Utils/SpineUtils";
import {heroMgr} from "../../../HeroModule/Managers/HeroManager";
import {DefaultStand} from "../../../../Scenes/SelectorScene/Components/ItemPrefabs/PreviewPictureDisplay";
import Node = cc.Node;
import CultivateWindowController from "./CultivateWindowController";
import {battleScene} from "../../../../Scenes/BattleScene";
import Animation = cc.Animation;

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroInfoDisplay extends cc.Component {

    @property(Label)
    nameLabel: Label = null;
    @property(Sprite)
    qualitySprite: Sprite = null;
    @property(Label)
    qualityLabel: Label = null;
    qualityStrings: string[] = ["N", "R", "SR", "SSR", "UR"];
    @property([Sprite])
    starSprites: Sprite[] = [];
    @property(Sprite)
    restraintSprite: Sprite = null;
    @property(Skeleton)
    heroSkeleton: Skeleton = null;

    @property([SpriteFrame])
    qualityPictures: SpriteFrame[] = [];
    @property([SpriteFrame])
    starPictures: SpriteFrame[] = [];
    @property([SpriteFrame])
    restraintPictures: SpriteFrame[] = [];

    @property(Animation)
    animation: Animation = null;
    // onLoad () {}

    private cultivateController: CultivateWindowController = null;

    start () {
        this.cultivateController = battleScene().cultivateWindow;
    }

    // region 

    update (dt) {}

    refresh() {
        if (!this.cultivateController)
            this.cultivateController = battleScene().cultivateWindow;
        this.updateLabels();
        this.updatePictures();
        this.drawSkeleton();
    }

    @autoUnitConvert
    updateLabels() {
        let hero = this.cultivateController.currentHero;
        if (!hero) return;
        this.nameLabel.string = hero.name;
        this.qualityLabel.string = this.qualityStrings[hero.quality];
    }

    updatePictures() {
        let hero = this.cultivateController.currentHero;
        if (!hero) return;
        this.qualitySprite.spriteFrame = this.qualityPictures[hero.quality];
        this.starSprites.forEach((s, i) => {s.spriteFrame = this.starPictures[i < hero.star.level ? 1 : 0];});
        this.restraintSprite.spriteFrame = this.restraintPictures[hero.restraint];
    }

    drawSkeleton() {
        let hero = this.cultivateController.currentHero;
        if (!hero) return;
        let node = new Node('node');
        let temp = node.addComponent(Skeleton);
        let heroSkeleton = new HeroSkeleton(this.heroSkeleton, temp, heroMgr().getCacheOneFromServer(hero.nftId).heroTypeId);
        hero.parts.forEach(part => { heroSkeleton.addPart(part); });
        heroSkeleton.refresh().then(res => {
            node.destroy();
            this.heroSkeleton.clearTracks();
            this.heroSkeleton.setAnimation(0, DefaultStand, true);
            this.sizeFilter();
        });
    }

    private size: number = 240;

    private sizeFilter() {
        let k = Math.max(
          this.heroSkeleton.node.width * this.heroSkeleton.node.scaleX / this.size,
          this.heroSkeleton.node.height * this.heroSkeleton.node.scaleY / this.size);
        if (k > 1) {
            this.heroSkeleton.node.scaleX /= k;
            this.heroSkeleton.node.scaleY /= k;
        }
        this.heroSkeleton.node.y = - 50 - this.size / 3;
    }

    // endregion

    playAnimation(name: string) {
        this.animation.play(name);
    }
}
