import {ItemContainer} from "../../../../CoreModule/Components/ItemContainer";
import {Hero} from "../../../../HeroModule/Data/Hero";
import HeroCompositingDisplay from "./HeroCompositingDisplay";
import {waitForDataLoad} from "../../../../CoreModule/Managers/DataManager";
import {playerMgr, waitForLogin} from "../../../../PlayerModule/Managers/PlayerManager";
import {heroMgr} from "../../../../HeroModule/Managers/HeroManager";
import {ScrollviewFilter} from "../../../../CoreModule/Utils/ScrollviewFilter";
import HeroQualitySiftSelectionGroup from "./HeroQualitySiftSelectionGroup";
import HeroQualitySiftSelection from "./HeroQualitySiftSelection";
import CompositingWindowDisplay from "./CompositingWindowDisplay";
import Node = cc.Node;
import Button = cc.Button;
import EventHandler = cc.Component.EventHandler;

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroCompositingContainer extends ItemContainer<Hero, HeroCompositingDisplay> {

    @property([HeroQualitySiftSelection])
    qualitySelections: HeroQualitySiftSelection[] = [];
    @property(CompositingWindowDisplay)
    compositing: CompositingWindowDisplay = null;
    private selectionGroup: HeroQualitySiftSelectionGroup = null;

    private totalHeroes: Hero[] = [];

    start () {
        this.selectionGroup = new HeroQualitySiftSelectionGroup(false);
        this.qualitySelections.forEach(s => {
            this.selectionGroup.addElement(s);
            s.container = this;
            s.selectableGroup = this.selectionGroup;
        })
        this.setup().then();
    }

    @waitForDataLoad
    @waitForLogin
    private async setup() {
        const acc = playerMgr().player.account;
        this.totalHeroes = (await heroMgr().getListFromServer())
          .filter(h => h.ownerAccount == acc);
        this.onSelectionChange();
    }

    update (dt) {
        this.itemsComponents.forEach(i => i.isSelected = i.item == this.compositing.mainHero.item || i.item == this.compositing.secondHero.item);
    }

    onItemsChanged() {
        super.onItemsChanged();
        let nodes : Node[] = [];
        this.itemsComponents.forEach((item, i) => {
            if(item.node.active == true)
                nodes.push(item.node);
            let event = new EventHandler();
            event.target = this.node;
            event.component = "HeroCompositingContainer";
            event.handler = "onItemClicked";
            event.customEventData = "" + i;
            item.getComponent(Button).clickEvents = [];
            item.getComponent(Button).clickEvents.push(event);
        })
        ScrollviewFilter.filter("vertical", this.container, nodes, 60, this.container.height, 60, 4);
    }

    onSelectionChange() {
        let type = (this.selectionGroup.currentSelection() as HeroQualitySiftSelection).quality;
        if (type == -1)
            this.setItems(this.totalHeroes);
        else
            this.setItems(this.totalHeroes.filter(h => h.quality == type));
    }

    onItemClicked(event: any, index: string) {
        if (this.items[index] == this.compositing.mainHero.item) {
            this.compositing.deselectMainHero();
            return;
        }
        if (this.items[index] == this.compositing.secondHero.item) {
            this.compositing.deselectSecondHero();
            return;
        }
        this.compositing.selectHero(this.items[index]);
    }

}
