import {data, StaticData} from "../../CoreModule/Data/StaticData";
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {Constructor} from "../../../Utils/BaseContext";
import {field} from "../../CoreModule/Data/BaseData";

const DefaultRoomPlayerCount = 8;

@data("PVPRoom")
export class PVPRoom extends StaticData {

	@field([Number])
	public levelRange: number[] = [0, 20];
	@field([Number])
	public rankRange: number[] = [-0.01, 0.05];
	@field
	public playerCount: number = DefaultRoomPlayerCount;

}

export function pvpRoomRepo() {
	return getRepository(PVPRoomRepo);
}

@repository
export class PVPRoomRepo extends BaseRepository<PVPRoom> {

	get clazz(): Constructor<PVPRoom> { return PVPRoom; }

}
