import {ItemContainer} from "../../../../Modules/CoreModule/Components/ItemContainer";
import {PartType, partTypeRepo} from "../../../../Modules/HeroModule/Data/PartType";
import PartsContainer from "./PartsContainer";
import {ScrollviewFilter} from "../../../../Modules/CoreModule/Utils/ScrollviewFilter";
import Node = cc.Node;
import PartTypeDisplay from "../ItemPrefabs/PartTypeDisplay";
import PartTypeSelectionGroup from "./PartTypeSelectionGroup";
import PartTypeSelection from "../ItemPrefabs/PartTypeSelection";
import {waitForLogin} from "../../../../Modules/PlayerModule/Managers/PlayerManager";
import {waitForDataLoad} from "../../../../Modules/CoreModule/Managers/DataManager";
import GlobalEditor from "../Editor/GlobalEditor";
import Boolean = cc.Boolean;

const {ccclass, property} = cc._decorator;

@ccclass
export default class PartTypeContainer extends ItemContainer<PartType, PartTypeDisplay> {

	@property(PartsContainer)
	partsContainer: PartsContainer = null;
	@property(Boolean)
	includeBackground: boolean = true;

	@property(PartTypeContainer)
	relative: PartTypeContainer = null;

	selectionGroup: PartTypeSelectionGroup = null;
	globalEditor: GlobalEditor = null;

	// region 

	isInclude(item: PartType): boolean {
		return super.isInclude(item) && (this.includeBackground || !item.isBackground);
	}

	private makeSelectionGroup(){
		this.selectionGroup = new PartTypeSelectionGroup(false);
		this.itemsComponents.forEach(item => {
			let selection = item.node.getComponent(PartTypeSelection)
			selection.display = selection.getComponent(PartTypeDisplay)
			selection.container = this;
			this.selectionGroup.addElement(selection);
		})
	}

	// endregion

	//region 

	protected start() {
		this.setup();
	}

	@waitForDataLoad
	setup() {
		this.setItems(partTypeRepo().list);
	}

	protected update(dt: number) {
		this.itemsComponents.forEach(i => i.node.active ? i.size = this.container.width / this.items.length : null);
		// let nodes : Node[] = [];
		// this.itemsComponents.forEach(item => { if(item.node.active == true) nodes.push(item.node) })
		// ScrollviewFilter.filter("horizontal", this.container, nodes, 10, this.node.width);
	}

	//endregion

	//region 

	onItemsChanged() {
		super.onItemsChanged();
		this.makeSelectionGroup();
	}

	public onSelectChanged(item: PartTypeDisplay) {
		if (this.relative) {
			const curSel = this.selectionGroup.currentSelection();
			const index = this.selectionGroup.indexOf(curSel);
			const relSelGroup = this.relative?.selectionGroup;
			relSelGroup.select(relSelGroup.getElementAt(index));
		}
		if (this.partsContainer) {
			this.partsContainer.optional = item.item.optional;
			this.partsContainer.currentPartType = item.item;
			this.partsContainer.onTypeChange().then();
		}
		this.globalEditor?.setItem(item.item);
	}

	//endregion

}
