import {BaseManager, getManager, manager} from "./BaseManager";

export function alertMgr(): AlertManager {
	return getManager(AlertManager);
}

export interface ErrorData {
	status?: number
	code?: number
	errMsg?: string
	detail?: string
	data?: any
	handled?: boolean
}

/**
 * throwFuncdetail
 * GameError
 * detaildetail
 * @param throwFunc
 * @param detail
 */
export function catchAsError(throwFunc: Function, detail?: string) {
	return (obj, key, desc) => {
		const oriFunc = desc.value;
		desc.value = async function (...p) {
			try {
				return await oriFunc.apply(this, p);
			} catch (e) { // {detail: string, data: string}
				console.error("Catch", {e});

				if (e instanceof Error && e["code"]) throw e;

				detail ||= typeof e == "string" ? e : e?.detail;
				const data = e?.data || e;

				throwFunc(detail, data);
			}
		}
	}
}

/**
 * 
 * @param objOrUseThrow 
 * @param key
 * @param desc
 */
export function handleError(
	objOrUseThrow: any | boolean, key?: string, desc?: any): any {

	let useThrow = false;
	const process = (obj, key, desc) => {
		const oriFunc = desc.value;
		desc.value = async function(...p) {
			try {
				return await oriFunc.apply(this, p);
			} catch (e) {
				alertMgr().handleError(e);
				if (useThrow) throw e;
			}
		}
	}
	if (typeof objOrUseThrow == "boolean") {
		useThrow = objOrUseThrow;
		return process;
	}
	process(objOrUseThrow, key, desc);
}

export class GameError extends Error implements ErrorData {

	public code: number;
	public errMsg: string;
	public detail: string;
	public data: any;
	public handled?: boolean;

	constructor(code: number, errMsg: string,
							detail?: string, data?: any) {
		super(code + ": " + errMsg);

		this.code = code;
		this.errMsg = errMsg;
		this.detail = detail;
		this.data = data;
	}
}

export enum AlertDialogType {
	Prompt,
	OK,
	OKCancel,
}

export interface AlertOptions {
	title?: string;
	message?: string;
	type?: AlertDialogType;
}

@manager
class AlertManager extends BaseManager {

	alertQueue: AlertTask[] = [];

	public handleError(error: ErrorData) {
		if (error.handled) return;
		error.handled = true;

		// Titleerror.errMsg
		const key = "ERR" + (error.status ?
			error.status + "_" : "") + error.code;
		const title = i18nMgr().makeStr(key, error.errMsg);

		// const exist = !!i18nMgr().getKey(key);
		// const title = exist ? `\\L[${key}]` : error.errMsg;

		// Detailsettingdetail
		const dKey = key + "_DETAIL";
		const detail = error.detail || i18nMgr().makeStr(dKey);

		// const dExist = !!i18nMgr().getKey(dKey);
		// const detail = error.detail || (dExist ? `\\L[${dKey}]` : "");

		console.error("Handled error: ", {error, title, detail});

		this.alert(title, detail, AlertDialogType.OK).then();
	}

	/**
	 * AlertWindow
	 */
	public alert(title: string | AlertOptions,
							 message?: string, type?: AlertDialogType): Promise<AlertResult> {
		if (typeof title !== "string")
			return this.alert(title.title, title.message, title.type);

		return new Promise<AlertResult>((resolve) => {
			let task = new AlertTask(title, message, type, resolve);
			this.alertQueue.push(task);
		});
	}

	/**
	 * 
	 */
	public hasAlert(): boolean {
		return this.alertQueue.length > 0;
	}

	/**
	 * 
	 */
	public popAlertTask(): AlertTask {
		if (this.alertQueue.length == 0) return null;
		let task = this.alertQueue[0];
		this.alertQueue.splice(0, 1);
		return task;
	}
}

export enum AlertResult {
	OK,
	CANCEL
}

export class AlertTask implements AlertOptions {

	title: string;
	message: string;
	type: AlertDialogType;

	private readonly promiseResolve: (value: AlertResult) => void;

	constructor(title: string, message: string, type: AlertDialogType, resolve: (value: AlertResult) => void) {
		this.title = title;
		this.message = message;
		this.type = type;
		this.promiseResolve = resolve;
	}

	public feedback(result: AlertResult): void {
		this.promiseResolve(result);
	}
}

import {i18nMgr} from "./I18nManager";
import {InterfaceOptions} from "./NetworkManager";
