import {ItemDisplay} from "../../../../Modules/CoreModule/Components/ItemDisplay";
import Label = cc.Label;
import Node = cc.Node;
import {PartInfo} from "../../../../Modules/HeroModule/Data/PartInfo";
import {selectorScene} from "../../SelectorScene";
import PartsDisplay from "../ItemPrefabs/PartsDisplay";
import PreviewPictureDisplay from "../ItemPrefabs/PreviewPictureDisplay";
import {configMgr} from "../../../../Modules/CoreModule/Managers/ConfigManager";
import PartConfig from "../../../../Modules/HeroModule/Configs/PartConfig";
import Slider = cc.Slider;
import PartsContainer from "../ItemContainers/PartsContainer";
import EditBox = cc.EditBox;
import {editorMgr} from "../../../../Modules/HeroModule/Managers/EditorManager";
import TransformUtil from "../../../../Utils/TransformUtils";
import {partTypeRepo} from "../../../../Modules/HeroModule/Data/PartType";
import Button = cc.Button;
import {partRepo} from "../../../../Modules/HeroModule/Data/Part";
import PartColorsContainer from "../ItemContainers/PartColorsContainer";

const {ccclass, property} = cc._decorator;

const MaxScale = 1.4;
const MinScale = 0.6;

@ccclass
export default class PartScaler extends ItemDisplay<PartInfo> {

	@property(PartColorsContainer)
	partsContainer: PartColorsContainer = null;

	@property(Label)
	partTypeName: Label = null;
	@property(Slider)
	scaleBar: Slider = null;

	@property(PreviewPictureDisplay)
	preview: PreviewPictureDisplay = null

	protected onLoad() {
		this.partsContainer.partScaler = this;
	}

	// region 

	public get part() { return this.item?.part; }
	public get partType() { return this.part?.partType; }
	public get basePartType() { return this.partType?.basePartType; }
	public get basePart() { return this.basePartType ?
		this.preview.item?.findPart(this.basePartType) : null;
	}

	public get partSetting() {
		return this.preview.item.getPartSetting(this.item.partId);
	}

	public get scale() { return this.partSetting.scale; }
	public set scale(val) { this.partSetting.scale = val; }

	// endregion

	// region 

	refresh() {
		super.refresh();

		this.preview.refresh();

		if (this.item == null) return;

		this.drawScale();
	}
	private drawScale() {
		const scale = this.scale;
		this.partTypeName.string = this.partType.getDisplayName();
		this.scaleBar.progress = (scale - MinScale) / (MaxScale - MinScale);
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public setScale(scale) {
		this.scale = scale;
		this.refresh();
	}

	/**
	 * 
	 */
	public reset() {
		this.setScale(1);
	}

	// endregion

	// region 

	public onScaleSlide() {
		const rate = this.scaleBar.progress;
		this.setScale(rate * (MaxScale - MinScale) + MinScale);
	}

	// endregion

}
