import Label = cc.Label;
import Sprite = cc.Sprite;
import Button = cc.Button;
import Node = cc.Node;
import Layout = cc.Layout;
import {autoUnitConvert} from "../../../../Utils/NumberUtils";
import SpriteFrame = cc.SpriteFrame;
import {battleScene} from "../../../../Scenes/BattleScene";
import HeroWithAvatar from "../HeroWithAvatar";

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroSelectDisplay extends HeroWithAvatar {

    @property(Label)
    nameLabel: Label = null;
    @property(Label)
    levelLabel: Label = null;
    @property(Label)
    qualityLabel: Label = null;

    @property(Label)
    attackLabel: Label = null;
    @property(Label)
    defenseLabel: Label = null;
    @property(Label)
    healthLabel: Label = null;

    @property(Sprite)
    qualityBackground: Sprite = null;
    @property(Sprite)
    scissorsIcon: Sprite = null;
    @property(Node)
    selectNode: Node = null;
    private selectPicture: Sprite = null;
    private selectButton: Button = null;
    @property([Node])
    starNodes: Node[] = [];
    @property(Layout)
    starLayout: Layout = null;
    @property(Node)
    selectFrame: Node = null;

    start () {
        this.setup();
    }

    private setup() {
        this.selectPicture = this.selectNode.getComponent(Sprite);
        this.selectButton = this.selectNode.getComponent(Button);
    }

    protected onItemChanged() {
        super.onItemChanged();
        this.nameLabel.string = this.item.name;
        this.qualityLabel.string = this.item.qualityData.shortName;
        this.qualityBackground.spriteFrame = this.qualityPictures[this.item.quality];
        this.scissorsIcon.spriteFrame = this.scissorsPictures[this.item.restraint];
    }

    update (dt) {
        if (!this.item) return;
        this.updateLabels();
        this.updatePictures();
        this.updateNodes();
    }

    @autoUnitConvert
    updateLabels() {
        this.levelLabel.string = "Lv." + this.item.level.level;
        this.attackLabel.string = this.item.atk.toString();
        this.defenseLabel.string = this.item.def.toString();
        this.healthLabel.string = this.item.mhp.toString();
    }

    @property([SpriteFrame])
    qualityPictures: SpriteFrame[] = [];
    @property([SpriteFrame])
    scissorsPictures: SpriteFrame[] = [];
    @property([SpriteFrame])
    buttonPictures: SpriteFrame[] = [];

    private static spacingX: number[] = [0, 0, -8, -18, -23];
    private static leftPending: number[] = [29, 12, 0, 0, 0];

    updatePictures() {

    }

    updateNodes() {
        if (this.item.level.level > battleScene().currentPVPRoomIndex * 20) {
            this.selectPicture.spriteFrame = this.buttonPictures[0];
            this.selectButton.interactable = true;
        } else {
            this.selectPicture.spriteFrame = this.buttonPictures[1];
            this.selectButton.interactable = false;
        }
        this.starNodes.forEach((n, i) => { n.active = i < this.item.star.level; });
        this.starLayout.paddingLeft = HeroSelectDisplay.leftPending[this.item.star.level - 1];
        this.starLayout.spacingX = HeroSelectDisplay.spacingX[this.item.star.level - 1];
        this.selectFrame.active = battleScene().currentPVPSelect == this.item;
    }

    selectBtn() {
        battleScene().currentPVPSelect = this.item;
        battleScene().heroSelected = true;
    }

    onAvatarClick() {
        battleScene().cultivateWindow.open(this.item);
    }
}
