import {BaseManager, getManager, manager} from "./BaseManager";
import {LocalizedLabel} from "../Components/LocalizedLabel";
import {LocalizedSprite} from "../Components/LocalizedSprite";
import SpriteFrame = cc.SpriteFrame;
import JsonAsset = cc.JsonAsset;
import {PromiseUtils} from "../../../Utils/PromiseUtils";
import {assetMgr, BundleName} from "./AssetBundleManager";

export function i18nMgr(): I18nManager {
	return getManager(I18nManager);
}

export type LanguageType = 'zh' | 'ko' | 'ja' | 'en';
export const Languages: LanguageType[] = [
	'zh','ko', 'ja', 'en'
];
export function getLanguage(type: LanguageType) {
	return Languages.indexOf(type);
}

const TranFileName = "translation";

@manager
class I18nManager extends BaseManager {

	private curLanguage: LanguageType = 'zh';

	private registeredLabels: LocalizedLabel[] = [];
	private registeredSprites: LocalizedSprite[] = [];
	private translation: any;
	private spriteTranslation: any;

	constructor() {
		super();
		this.loadTranslation().then();
	}

	async loadTranslation() {
		await PromiseUtils.waitFor(() => assetMgr && !!assetMgr());
		this.translation ||= (await assetMgr().loadAsset(
			JsonAsset, BundleName.Data, TranFileName))?.json;
	}

	public getKey(key: string) {
		return this.translation && this.translation[key];
	}

	public makeStr(key: string, default_: string = "") {
		return this.getKey(key) ? `\\L[${key}]` : default_;
	}

	/**
	 * 
	 * @param string
	 */
	public t(string: string): string {
		const index = getLanguage(this.curLanguage);
		let result: string = "";
		// 
		for (let i = 0; i < string.length; i++) {
			if (string.startsWith("\\L[", i)) {
				i += 3;
				let tag = "";
				while (string.charAt(i) != ']') {
					tag += string.charAt(i);
					i++;
				}
				if (this.translation == undefined) {
					result += "{!" + tag + "!}";
					continue;
				}
				let tr = this.translation[tag];
				if (tr == undefined || tr[index] == undefined) {
					result += "{!" + tag + "!}";
				} else {
					result += tr[index];
				}
			} else {
				result += string.charAt(i);
			}
		}
		return result;
	}

	/**
	 * 
	 * @param spriteName
	 */
	async ts(spriteName: string) {
		const index = getLanguage(this.curLanguage);
		return await this.loadSpriteFrame(BundleName.Sprites,
			spriteName + "$" + index) ||
			await this.loadSpriteFrame(
				BundleName.Sprites, spriteName);
	}

	private loadSpriteFrame(bundleName: string, assetPath: string) {
		/* if(object==null) {

		} else {
				const reg = /\${(.+?)}/g;
				const regRes = reg.exec(assetPath)
				// const replaceReg = new RegExp(regRes[0], 'g');
				const name = object[regRes[1]];
				if (name) assetPath = assetPath.replace(reg, name);
		} */
		return assetMgr().loadAsset<SpriteFrame>(SpriteFrame, bundleName, assetPath);
	}

	public registerLabel(label: LocalizedLabel): void {
		this.unregisterLabel(label);
		this.registeredLabels.push(label);
	}

	public unregisterLabel(label: LocalizedLabel): void {
		let idx = this.registeredLabels.indexOf(label);
		if (idx == -1) return;
		this.registeredLabels.splice(idx, 1);
	}

	public registerSprite(sprite: LocalizedSprite): void {
		this.unregisterSprite(sprite);
		this.registeredSprites.push(sprite);
	}

	public unregisterSprite(sprite: LocalizedSprite): void {
		let idx = this.registeredSprites.indexOf(sprite);
		if (idx == -1) return;
		this.registeredSprites.splice(idx, 1);
	}

	public setLanguage(language: LanguageType): void {
		if (this.curLanguage == language) return;
		this.curLanguage = language;
		this.updateUIs();
	}

	public getCurrentLanguage(): LanguageType {
		return this.curLanguage;
	}

	private updateUIs(): void {
		this.registeredLabels.forEach(l => l.updateText());
		this.registeredSprites.forEach(l => l.updateSprite());
	}
}
