import {field, GameData} from "../../CoreModule/Data/BaseData";
import {Constructor} from "../../../Utils/BaseContext";
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {data, StaticData} from "../../CoreModule/Data/StaticData";
import {i18nMgr} from "../../CoreModule/Managers/I18nManager";

export enum QualityType {
	N, R, SR, SSR, UR
}

@data("Quality")
export class Quality extends StaticData {

	@field
	public id: QualityType = QualityType.N;
	@field
	public color: string = "FFFFFF";
	@field
	public rate: number = 1;

	/**
	 * 
	 */
	public get shortName() { return QualityType[this.id]; }

	// 	return i18nMgr().makeStr(`QualityShort${this.id}`, QualityType[this.id]);
	// }
	// public get fullName() {
	// 	return i18nMgr().makeStr(`QualityFull${this.id}`, QualityType[this.id]);
	// }

}

export function qualityRepo() {
	return getRepository(QualityRepo);
}

@repository
export class QualityRepo extends BaseRepository<Quality> {

	get clazz(): Constructor<Quality> { return Quality; }

	// @ts-ignore
	getById(id: QualityType): Quality { }

	/**
	 * 
 	 */
	public n() { return this.getById(QualityType.N); }
	public r() { return this.getById(QualityType.R); }
	public sr() { return this.getById(QualityType.SR); }
	public ssr() { return this.getById(QualityType.SSR); }
	public ur() { return this.getById(QualityType.UR); }

}
