function waitForInit(objOrDefault: any, key?, desc?): any {
	let default_ = null;
	const process = (obj, key, desc) => {
		const oriFunc = desc.value;
		desc.value = function () {
			const args = arguments;
			return new Promise(async (resolve, reject) => {
				if (!Web3Enable) resolve(default_);
				else {
					try {
						const cond = await PromiseUtils.waitFor(() => this.initialized);
						if (cond) resolve(await oriFunc.apply(this, args));
						else reject("");
					} catch (e) {
						reject(e);
					}
				}
			})
		}
	}

	if (key === undefined && desc === undefined) { // 
		default_ = objOrDefault;
		return process;
	}
	process(objOrDefault, key, desc);
}

