	private async initialize() {
		await this.loadScripts();
		this.setupLanguage();
		this.createWeb3();

		console.log("NFT Managers", this);
	}

	private async loadScripts() {
		console.log("loadScripts");
		for (const script of scripts)
			await this.loadScript(script);
	}

	private loadScript(src): Promise<any> {
		const head = document.children[0].children[0];
		console.log({head});
		return new Promise(res => {
			let script = document.createElement('script');
			script.src = src;
			head.appendChild(script);
			script.onload = () => res(true);
			script.onerror = () => res(false);
		})
	}

	private setupLanguage() {
		PromiseUtils.waitFor(() => !!this.data.lang, 100, 100)
			.then(res => res && i18nMgr().setLanguage(this.data.lang));
	}

	private createWeb3() {
		// @ts-ignore
		this.w3 = new getWeb3Lib() as Web3;
		// this.init().then();
	}

	// endregion

	// region 

	update(dt: number) {
		super.update(dt);
		this.updateMessage();
	}

	private updateMessage() {
		const e = MessageQueue.message();
		e && this.processMessage(e);
	}

	private processMessage(e) {
		console.log("processMessage: ", e, e.data);
		if ((e.data.target as string)?.includes("metamask")) return;
		if (e.data.key == "createNFTProgressCallBack") return;
		if (e.data.nftData) this.dataRes(e.data.nftData);
		if (e.data.lang || e.data.windowSize ||
			e.data.inviteAddress || e.data.address) {
			this.data = Object.assign(this.data, e.data);
			window['defaultAccount'] = this.defaultAccount = this.data.address;
		}
	}

