	// region 

	public convertPrice(data: string | number): number {
		if (!data) return 0;
		if (typeof data == 'number') return data;

		const len = data.length;
		return Number(len < PriceZeroCount ? data :
			data.substr(0, len - PriceZeroCount));
	}

