
export function autoUnitConvert(obj, key, desc) {
    const oriFunc = desc.value;
    desc.value = function(...p) {
        const oriUC = NumberUtils.autoUnitConvert;
        try {
            NumberUtils.autoUnitConvert = true;
            return oriFunc.apply(this, p);
        } finally {
            NumberUtils.autoUnitConvert = oriUC;
        }
    }
}

const oriNumber_toString = Number.prototype.toString;
Number.prototype.toString = function (radix?: number) {
    return NumberUtils.autoUnitConvert ?
      NumberUtils.toString(this) :
      oriNumber_toString.call(this, radix);
}

export class NumberUtils {

    public static autoUnitConvert = false;

    private static units: string[] = ["", "K", "M", "B", "T", "aa"];
    public static toString(val: number): string {
        if (val < 1000) return val % 1 === 0 ? "" + val : val.toFixed(2);
        for (let i = 0; i < 5; i++) {
            if (val >= 1000) {
                val /= 1000;
                continue;
            }
            if (val > 100) return val.toFixed(1) + this.units[i];
            return val.toFixed(2) + this.units[i];
        }
        return val.toFixed(2) + this.units[5];
    }

}
