import {BaseData, field} from "../../CoreModule/Data/BaseData";
import {Action} from "./Action";

export type ResultType = "damage" | "recover" | "drain" | "miss";

export class Result extends BaseData {

	@field
	public hpDamage: number = 0; // HP
	@field
	public hpRecover: number = 0; // HP
	@field
	public hpDrain: number = 0; // HP

	@field
	public isMiss: boolean = false; // 
	@field
	public isCritical: boolean = false; // 

	// @field([StatusChange])
	// public statusChanges: StatusChange[] = [];

	public action: Action;

	constructor(index?, parent?) {
		super(index);
		this.action = parent;
	}

	public static create(action: Action, type: ResultType,
											 point = 0, isCritical = false) {
		const res = new Result(undefined, action);

		switch (type) {
			case "damage": res.hpDamage = point; break;
			case "recover": res.hpRecover = point; break;
			case "drain": res.hpDrain = point; break;
			case "miss": res.isMiss = true; break;
		}
		res.isCritical = isCritical;

		return res;
	}

	public static testData() {
		let res = new Result();
		res.hpDamage = Math.floor(Math.random() * 128);
		return res;
	}
}
