import {BaseManager, getManager, manager} from "./BaseManager";
import {BaseScene} from "../../../Scenes/BaseScene";
import Component = cc.Component;
import {Constructor} from "../../../Utils/BaseContext";

const CACHE_KEY = '__exermonCache__';

export function sceneMgr(): SceneManager {
	return getManager(SceneManager);
}

export function curScene(): BaseScene {
	return sceneMgr().curScene;
}

export function singletonComponent<T extends Component>(
	clazz: Constructor<T>) {

	let oriStart = clazz.prototype.start;
	let oriOnDestroy = clazz.prototype.onDestroy;

	clazz.prototype.start = function() {
		const scene = curScene();
		const key = findSceneSingletonTarget(scene, clazz);
		key && (scene[key] = this);

		oriStart?.call(this);
	}
	clazz.prototype.onDestroy = function() {
		const scene = curScene();
		const key = findSceneSingletonTarget(scene, clazz);
		key && (scene[key] = null);

		oriOnDestroy?.call(this);
	}
}

export function singletonTarget<T extends Component>(
	clazz: Constructor<T>) {

	return (obj, key) => {
		const cache = getOrCreateCache(obj.constructor);
		cache.targets[key] = clazz;
	}
}

export function getOrCreateCache<T extends BaseScene>(
	type: Constructor<T> | Object) {
	if (!type) return {};
	if (typeof type !== 'function')
		return getOrCreateCache(type.constructor);

	return type[CACHE_KEY] ||= {
		targets: {} // 
	};
}

function findSceneSingletonTarget(obj, clazz: Constructor) {
	const cache = getOrCreateCache(obj);

	for (const key in cache.targets)
		if (cache.targets[key] == clazz) return key;

	return null;
}

@manager
class SceneManager extends BaseManager {

	public curScene: BaseScene = null;

}
