import {ItemDisplay} from "../../../CoreModule/Components/ItemDisplay";
import {Battler} from "../../Data/Battler";
import Skeleton = sp.Skeleton;
import Label = cc.Label;
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import ProgressBar = cc.ProgressBar;
import {HeroSkeleton} from "../../../../Utils/SpineUtils";
import Node = cc.Node;
import Bone = sp.spine.Bone;
import Slot = sp.spine.Slot;

const {ccclass, property} = cc._decorator;

@ccclass
export default class PVPBattlerState extends ItemDisplay<Battler> {

    @property(Label)
    heroLevelLabel: Label = null;
    @property(Skeleton)
    heroAvatarSkeleton: Skeleton = null;
    @property(Sprite)
    heroQualitySprite: Sprite = null;
    @property(Sprite)
    heroRestraintSprite: Sprite = null;

    @property([SpriteFrame])
    heroQualityPictures: SpriteFrame[] = [];
    @property([SpriteFrame])
    heroRestraintPictures: SpriteFrame[] = [];

    @property(ProgressBar)
    heroHpProgressBar: ProgressBar = null;
    @property(Label)
    heroHpLabel: Label = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    protected update(dt: number) {
        if (!this.item) return;
        this.updateHpBars();
    }

    private updateHpBars() {
        this.heroHpLabel.string = this.item.hp + "/" + this.item.mhp;
        this.heroHpProgressBar.progress = this.item.hp / this.item.mhp;
    }

    refresh() {
        super.refresh();
        this.heroLevelLabel.string = this.item.hero().level.level.toString();
        this.heroQualitySprite.spriteFrame = this.heroQualityPictures[this.item.hero().quality];
        this.heroRestraintSprite.spriteFrame = this.heroRestraintPictures[this.item.hero().restraint];
        this.heroHpProgressBar.reverse = this.item.camp == 1;
        this.drawAvatar();
    }

    private size: number = 93;
    private avatarHeight: number = 0;
    private avatarWidth: number = 0;
    private avatarOffsetX: number = 0;
    private avatarOffsetY: number = 0;

    private drawAvatar(){
        let node = new Node('node');
        let temp = node.addComponent(Skeleton);
        let hero = new HeroSkeleton(this.heroAvatarSkeleton, temp, this.item.hero().heroTypeId);
        this.item.hero().parts.forEach(part => { hero.addPart(part); });
        hero.refresh().then(res => {
            node.destroy();
            let bone: Bone = hero.findBone("head_1");

            hero.skeleton.updateWorldTransform();
            let offsetX = bone.worldX, offsetY = bone.worldY;
            while (bone.parent) bone = bone.parent;
            bone.x -= offsetX;
            bone.y -= offsetY;

            let slot: Slot = hero.findSlot("head_1");
            if (!slot.getAttachment()) return;
            this.avatarHeight = slot.getAttachment()["height"];
            this.avatarWidth = slot.getAttachment()["width"];
            this.avatarOffsetY = slot.getAttachment()["x"];
            this.avatarOffsetX = slot.getAttachment()["y"];
            this.sizeFilter();
        });
    }

    private sizeFilter() {
        let k = Math.min(this.avatarWidth / this.size, this.avatarHeight / this.size);
        k *= 1.2;
        // this.avatar.node.x = - this.avatarOffsetX / k;
        this.heroAvatarSkeleton.node.y = - this.avatarOffsetY / k;
        if (k > 1) this.heroAvatarSkeleton.node.scale = 1 / k;
        else this.heroAvatarSkeleton.node.scale = 1;
        this.heroAvatarSkeleton.node.scaleX *= this.item.camp == 1 ? 1 : -1;
    }
}
