import Skeleton = sp.Skeleton;
import {SpineUtils} from "../Utils/SpineUtils";
import SpriteFrame = cc.SpriteFrame;
import Texture2D = cc.Texture2D;
import {partRepo} from "../Modules/HeroModule/Data/Part";
import {partTypeRepo} from "../Modules/HeroModule/Data/PartType";
import {heroTypeRepo} from "../Modules/HeroModule/Data/HeroType";
import EditBox = cc.EditBox;
import {EditorInterface} from "../Modules/HeroModule/Common/EditorCommon";

const {ccclass, property} = cc._decorator;

@ccclass
export default class TestSpine extends cc.Component {

	@property(Skeleton)
	sk: Skeleton = null;
	@property(SpriteFrame)
	pic1: SpriteFrame = null;
	@property(SpriteFrame)
	pic2: SpriteFrame = null;

	@property(EditBox)
	source: EditBox = null;
	@property(EditBox)
	out: EditBox = null;

	start () {
		// this.sk.setAnimation(0, "stand", true);
		// this.node.on("touchstart", this.onTouchStart, this);
	}

	// region Import

	public import() {
		EditorInterface.ImportParts({
			source: this.source.string,
			out: this.out.string,
			doImport: true
		})
	}

	// endregion

	onTouchStart(e) {
		const skPos = this.sk.node.getPosition();
		const touch: cc.Touch = e.currentTouch;
		const loc = touch.getLocation().clone();
		const dt = loc.sub(skPos);

		console.log("Delta", e, dt.x, dt.y, this.sk, cc);

		const sk: sp.spine.Skeleton = this.sk["_skeleton"];

		// const tSlot = sk.findSlot("hand_1");
		// // @ts-ignore
		// const tLoc = tSlot.bone.worldToLocal({x: dt.x, y: dt.y});
		// console.log("tLoc", tLoc, tLoc.x, tLoc.y);
		// const tAttach = tSlot.getAttachment() as sp.spine.RegionAttachment;
		// console.log("tAttach", tAttach, tAttach.x, tAttach.y, tAttach.width, tAttach.height);
		// const tALoc = {x: tLoc.x - tAttach.x, y: tLoc.y - tAttach.y};
		// const dist = Math.sqrt(tALoc.x * tALoc.x + tALoc.y * tALoc.y);
		// const ang = Math.atan(tALoc.y / tALoc.x) / Math.PI * 180;
		// const newAng = ang - tAttach.rotation;
		// const newRad = newAng / 180 * Math.PI;
		// const tNewLoc = {
		// 	x: dist * Math.cos(newRad), y: dist * Math.sin(newRad)
		// };
		// console.log("tNewLoc", tNewLoc);

		sk.slots.forEach((slot, i) => {
			// @ts-ignore
			const lLoc = slot.bone.worldToLocal({x: dt.x, y: dt.y});
			const attach = slot.getAttachment() as sp.spine.RegionAttachment;
			if (!attach) return;

			const aLoc = {x: lLoc.x - attach.x, y: lLoc.y - attach.y};

			// const newLoc = lLoc;

			// 
			const dist = Math.sqrt(aLoc.x * aLoc.x + aLoc.y * aLoc.y);
			const ang = Math.atan(aLoc.y / aLoc.x) / Math.PI * 180;
			const newAng = ang - attach.rotation;
			const newRad = newAng / 180 * Math.PI;
			const newLoc = {
				x: dist * Math.cos(newRad), y: dist * Math.sin(newRad)
			};

			// console.log("Slot1", slot.data.name, newLoc);
			// console.log("Slot2", slot.data.name, attach.x, attach.y, attach.width, attach.height);

			if (newLoc.x >= -attach.width / 2 && newLoc.x <= attach.width / 2
				&& newLoc.y >= -attach.height / 2 && newLoc.y <= attach.height / 2)
				slot.color.a = 0.5;
			else
				slot.color.a = 1;

			// if (newLoc.x >= attach.x && newLoc.x <= newLoc.x + attach.width
			// 	&& newLoc.y >= attach.y && newLoc.y <= newLoc.y + attach.height) {
			// 	slot.color.a = 0.5;
			// } else
			// 	slot.color.a = 1;
		});

	}

	public color1() {
		console.log(1);
		this.sk.findSlot("head_1").getAttachment()["setRegion"](this.CreateRegion(this.pic1.getTexture()));
	}

	public color2() {
		console.log(2);
		this.sk.findSlot("head_1").getAttachment()["setRegion"](this.CreateRegion(this.pic2.getTexture()));
	}

	CreateRegion(texture: Texture2D) {
		let skeletonTexture = new sp["SkeletonTexture"]();
		skeletonTexture.setRealTexture(texture);
		let page = new sp.spine.TextureAtlasPage();
		page.name = texture.name;
		page.uWrap = sp.spine.TextureWrap.ClampToEdge;
		page.vWrap = sp.spine.TextureWrap.ClampToEdge;
		page.texture = skeletonTexture;
		page.texture.setWraps(page.uWrap, page.vWrap);
		page.width = texture.width;
		page.height = texture.height;

		let region = new sp.spine.TextureAtlasRegion();
		region.page = page;
		region.width = texture.width;
		region.height = texture.height;
		region.originalWidth = texture.width;
		region.originalHeight = texture.height;

		region.rotate = false;
		region.u = 0;
		region.v = 0;
		region.u2 = 1;
		region.v2 = 1;
		region.texture = skeletonTexture;
		return region;
	}
}
