@ccclass
export default class JobDisplay extends ItemDisplay<PlayerJobItem> {

	@property(Label)
	profitLabel: Label = null;
	@property(Label)
	profitLabel2: Label = null;
	@property(Label)
	upgradeLabel: Label = null;
	@property(Label)
	upgradeTips: Label = null;
	@property(Label)
	titleLabel: Label = null;
	@property(Label)
	timerLabel: Label = null;
	@property(Label)
	levelLabel: Label = null;
	@property(Label)
	numberLabel: Label = null;

	@property(Node)
	infoNode: Node = null;
	@property(Node)
	profitNode: Node = null;
	@property(Node)
	iconNode: Node = null;
	@property(Node)
	autoIconNode: Node = null;
	@property(Node)
	iconFrameNode: Node = null;

	@property(Node)
	upgradeNode: Node = null;
	private upgradeButton: Button = null;

	@property(ProgressBar)
	progressBar: ProgressBar = null;
	@property(ProgressBar)
	levelProgress: ProgressBar = null;

	@property(Animation)
	levelUpAnimation: Animation = null;
	@property(Sprite)
	iconSprite: Sprite = null;

	// onLoad () {}

	start () {
		this.setup();
	}

	// private isLoad: boolean = false;
	private setup() {
		this.upgradeButton = this.upgradeNode.getComponent(Button);
		// this.isLoad = true;
	}

	// region 

	private get restSecond() {
		const sec = this.item.curLevel == 0 ? 0 : Math.floor((Date.now() - this.item.lastProfitTime) / 1000);
		return this.item.job.time - sec;
	}

	private get restSecondRate() {
		const sec = this.item.curLevel == 0 ? 0 : (Date.now() - this.item.lastProfitTime) / 1000;
		return 1 - Math.min(0.98, sec / this.item.job.time);
	}

	public refresh() {
		super.refresh();

		this.drawTitle();
		this.drawNodes();
		this.drawLabels();
		this.drawPictures();
		this.drawIcon();
	}

	private drawTitle() {
		this.titleLabel.string = this.item.job.getDisplayName();
		this.numberLabel.string = this.item.jobId.toString();
	}

	private drawNodes() {
		const auto = this.item.isAutoProfit;
		const enable = this.item.profitConditions().judge();

		this.autoIconNode.active = auto && this.item.curLevel > 0;
		this.profitNode.active = !auto && enable;
		this.infoNode.active = !enable;
		this.iconNode.active = !auto || this.item.curLevel <= 0;
		this.iconFrameNode.active = this.iconNode.active;
	}

	@autoUnitConvert
	private drawLabels() {
		const costs = this.item.upgradeCosts();
		const rewards = this.item.profitRewards();

		const isLocked = this.item.curLevel == 0;
		const isMax = this.item.curLevel >= this.item.job.maxLevel;

		this.levelLabel.string = "Lv." + this.item.curLevel;
		this.profitLabel.string = this.profitLabel2.string = rewards.gold.value.toString();
		this.upgradeLabel.string = (isMax ? 0 : costs.gold.value).toString();
		// TODO: []
		this.upgradeTips.string = isLocked ? "\\L[SJob7]" : (isMax ? "\\L[SJob10]" : "\\L[SJob3]");
		this.timerLabel.string = DateUtils.sec2Str(this.restSecond);
	}

	private drawPictures() {
		const enable = this.item.curLevel <= 0 ?
			this.item.unlockConditions().judge() : this.item.upgradeConditions().judge();
		if (this.upgradeButton)
			this.upgradeButton.interactable = enable;

		this.progressBar.progress = this.restSecondRate;
		this.levelProgress.progress = this.item.curLevel / this.item.job.maxLevel;
	}

	private drawIcon() {
		this.item.icon().then(res => this.iconSprite.spriteFrame = res);
	}

	// endregion

	// region 

	public profitBtn() {
		economicMgr().profit(this.item.jobId).then();
	}

	public upgradeBtn() {
		economicMgr().upgrade(this.item.jobId).then(() => this.levelUpAnimation.play());
	}

	// endregion

	// region 

	update(dt) {
		if (!this.item) return;

		this.updateDraw();
	}

	private updateDraw() {
		this.drawNodes();
		this.drawLabels();
		this.drawPictures();
	}

	// endregion

}
