import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import {Reward} from "../../../PlayerModule/Data/Reward";
import BattleRewardDisplay from "./BattleRewardDisplay";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BattleRewardContainer extends ItemContainer<Reward, BattleRewardDisplay> {

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    @property(Number)
    coldDownTime: number = 0.1;

    // update (dt) {}
    onItemsChanged() {
        super.onItemsChanged();
        this.items.forEach((e, i) => {
            this.itemsComponents[i].delayTime = this.coldDownTime * (i + 1);
        });
    }

}
