import {BaseManager, getManager, manager} from "../../CoreModule/Managers/BaseManager";
import {Web3NFTData} from "../../NFTModule/Utils/Web3";
import {
	chainId,
	ChainId,
	DefaultBt1,
	DefaultBt2,
	nftMgr,
	nftMakeId2PartId
} from "../../NFTModule/Managers/NFTManager";
import {HeroInterface} from "../Common/HeroCommon";
import {Hero, PartSetting} from "../Data/Hero";
import {DataLoader} from "../../CoreModule/Utils/DataLoader";
import {handleError} from "../../CoreModule/Managers/AlertManager";
import {heroTypeRepo} from "../Data/HeroType";
import {QualityType} from "../Data/Quality";

export const PageSize = 100;

const Web3NFTParamCount = 6;

export function heroMgr() {
	return getManager(HeroManager);
}

@manager
class HeroManager extends BaseManager {

	public data = {};

	// region 

	/**
	 * 
	 */
	public async getOne(bt1: number, bt2: number,
											id: string, refresh?: boolean) {
		let res = this.getCacheOne(bt1, bt2, id);
		if (!res || refresh)
			res = this.add(bt1, bt2, await nftMgr().getNFTDetail(id))
		return res;
	}

	/**
	 * 
	 */
	public async getList(bt1: number, bt2: number,
											 refresh?: boolean) {
		let res = this.getCacheList(bt1, bt2);

		// 
		if (!res || refresh) {
			const cnt = await this.getCount(bt1, bt2);
			const maxPage = Math.ceil(cnt.createCount / PageSize);
			//TODO:   

			for (let i = 1; i <= maxPage; i++)
				await this.getListWithPage(bt1, bt2, i, refresh);

			res = this.getCacheList(bt1, bt2);
		}

		return res;
	}

	/**
	 * 
	 * page1
	 */
	public async getListWithPage(bt1: number, bt2: number,
															 page: number, refresh?: boolean) {
		let res = this.getCacheListWithPage(bt1, bt2, page);

		// 
		if (!res || res.length <= 0 || refresh) {
			let tmp = await nftMgr().getMyNFTList(page, bt1, bt2, PageSize);
			tmp.forEach(d => this.add(bt1, bt2, d));
			res = this.getCacheList(bt1, bt2);
		}

		return res;
	}

	/**
	 * 
	 */
	public async getCount(bt1: number, bt2: number) {
		return await nftMgr().getNFTCount(bt1, bt2);
	}

	/**
	 * 
	 */
	public getCacheOne(bt1: number, bt2: number, id: string) {
		const list = this.getCacheList(bt1, bt2);
		return list?.find(d => d.nftId == id);
	}

	/**
	 * 
	 */
	public getCacheList(bt1: number, bt2: number): Hero[] {
		return this.data[this.getKey(bt1, bt2)];
	}
	public getCacheListWithPage(bt1: number, bt2: number, page: number): Hero[] {
		let res = this.getCacheList(bt1, bt2);
		return res?.splice(page * PageSize, (page + 1) * PageSize);
	}
	public getOrCreateCacheList(bt1: number, bt2: number): Hero[] {
		return this.data[this.getKey(bt1, bt2)] ||= [];
	}

	/**
	 * 
	 */
	private add(bt1: number, bt2: number, data: Web3NFTData) {
		const id = data.tokenId;
		const list = this.getOrCreateCacheList(bt1, bt2);

		// 
		let res = this.getCacheOne(bt1, bt2, id);
		// 
		if (!res) list.push(res = new Hero());
		// 
		return this.convert(data, res);
	}

	private getKey(bt1: number, bt2: number) {
		return bt1 + "_" + bt2;
	}

	public convert(data: Web3NFTData, res?: Hero): Hero {
		res ||= new Hero()
		res.bt1 = Number(data.seriesId) || DefaultBt1;
		res.bt2 = Number(data.gameId) || DefaultBt2;
		res.chainId = Number(data.chainId) as ChainId || chainId();
		res.nftId = data.tokenId.toString();
		res.name = data.tokenName;
		res.avatarUrl = data.tokenUrl;
		res.quality = QualityType.UR - Number(data.quality) + 1;
		res.price = nftMgr().convertPrice(data.currentPrice);
		res.creatorAccount = data.createAddress.toLowerCase();
		res.ownerAccount = data.ownerAddress.toLowerCase();
		res.addedParams = this.convertParams(data);
		res.partSettings = this.convertComponentIds(data.componentIds);
		res.heroTypeId = res.bodyPart.heroTypeId;
		res.rawData = data;

		return res;
	}

	private convertParams(extra: Web3NFTData) {
		const res = [];
		for (let i = 1; i <= Web3NFTParamCount; i++)
			// 10
			res[Number(extra["et" + i])] = Number(extra["ef" + i]) / 100;
		return res;
	}

	private convertComponentIds(cidsStr: string) {
		return cidsStr.split("-").map(
			cid => PartSetting.create(nftMakeId2PartId(Number(cid))));
	}

	/**
	 * NFT
	 */
	public async getLastNFT(hash: string) {
		const hero = await nftMgr().getLastestNFT(hash);
		return this.convert(hero);
	}

	public async synchronizeAllHeroes(){
		heroTypeRepo().list.forEach(t => {
			nftMgr().getMyNFTList(1, DefaultBt1, DefaultBt2, 9999).then(list => {
				list.forEach(d => {
					let hero = this.convert(d);
					HeroInterface.SyncHero({ hero }).then();
				});
			});
		})
	}

	// endregion

	// region 

	private serverKey: string = "server";

	/**
	 * 
	 */
	public async getOneFromServer(nftId: string, refresh?: boolean) {
		let res = this.getCacheOneFromServer(nftId);
		if (!res || refresh)
			res = this.addFromServer(await this._getOneFromServer(nftId));
		return res;
	}

	private async _getOneFromServer(nftId: string): Promise<Hero>{
		const res =  await HeroInterface.GetHero({nftId});
		return DataLoader.load(Hero, res['hero']);
	}

	/**
	 * 
	 */
	public async getListFromServer(refresh: boolean = true) {
		let res = this.getCacheListFromServer();

		// 
		if (!res || refresh) {
			const cnt = await this.getCountFromServer();
			const maxPage = Math.ceil(cnt / PageSize);
			// TODO:   

			for (let i = 0; i < maxPage; ++i)
				await this.getListWithPageFromServer(i, refresh);

			res = this.getCacheListFromServer();
		}

		return res;
	}

	private async _getListFromServer(offset = 0, count = 1000): Promise<Hero[]>{
		const res = await HeroInterface.GetHeroes({ offset, count });
		let heroes: Hero[] = [];
		res['heroes'].forEach(hero => {
			heroes.push(DataLoader.load(Hero, hero));
		})
		return heroes;
	}

	/**
	 * 
	 * page1
	 */
	public async getListWithPageFromServer(page: number, refresh?: boolean) {
		let res = this.getCacheListWithPageFromServer(page);

		// 
		if (!res || res.length <= 0 || refresh) {
			let tmp = await this._getListFromServer(page * PageSize, PageSize);
			tmp.forEach(d => this.addFromServer(d));
			res = this.getCacheListFromServer();
		}

		return res;
	}

	/**
	 * 
	 */
	public async getCountFromServer() {
		return this._getCountFromServer();
	}

	private async _getCountFromServer(): Promise<number>{
		const res =  await HeroInterface.GetHeroCount();
		return res['count'];
	}


	/**
	 * 
	 */
	public getCacheOneFromServer(nftId: string) {
		const list = this.getCacheListFromServer();
		return list?.find(d => d.nftId == nftId);
	}

	/**
	 * 
	 */
	public getCacheListFromServer(): Hero[] {
		return this.data[this.serverKey];
	}
	public getCacheListWithPageFromServer(page: number): Hero[] {
		let res = this.getCacheListFromServer();
		// return res?.splice(page * PageSize, (page + 1) * PageSize);
		return res?.slice(page * PageSize, (page + 1) * PageSize);
	}
	public getOrCreateCacheListFromServer(): Hero[] {
		return this.data[this.serverKey] ||= [];
	}

	/**
	 * 
	 */
	private addFromServer(data: Hero) {
		const list = this.getOrCreateCacheListFromServer();
		// 
		let res = this.getCacheOneFromServer(data.nftId);
		// 
		if (!res) list.push((res = data));
		// 
		return res;
	}

	// endregion

	// region 

	@handleError
	public async upgrade(hero: Hero) {
		await HeroInterface.UpgradeLevel({nftId: hero.nftId});
		hero.level.conditions().process();
		hero.level.upgrade();
	}

	@handleError
	public async break(hero: Hero) {
		await HeroInterface.BreakLevel({nftId: hero.nftId});
		hero.break.conditions().process();
		hero.break.upgrade();
	}

	@handleError
 	public async strengthen(hero: Hero) {
		await HeroInterface.UpgradeStrengthen({nftId: hero.nftId});
		hero.strengthen.conditions().process();
		hero.strengthen.upgrade();
	}

	@handleError
	public async starUp(hero: Hero) {
		await HeroInterface.UpgradeStar({nftId: hero.nftId});
		hero.star.conditions().process();
		hero.star.upgrade();
	}

	// endregion

}

