import {BaseData, field} from "../../CoreModule/Data/BaseData";
import {PlayerData} from "../../PlayerModule/Data/Player";
import {playerData} from "../../PlayerModule/Managers/PlayerManager";

export class PlayerJobItem extends BaseData {

	@field(Number)
	public jobId: number;
	@field
	public curLevel: number = 0;
	@field
	public maxLevel: number = 0;
	@field
	public lastProfitTime: number = 0;
	@field
	public profitCount: number = 0;

	public pJob: PlayerJob;

	constructor(index?, parent?) {
		super(index);
		this.pJob = parent;
	}

	/**
	 * 
	 */
	public get job() {
		return jobRepo().getById(this.jobId);
	}

	/**
	 * 
	 */
	public upgradeCosts() {
		const gold = this.job.upgradeGold(this.curLevel);
		return ConditionGroup.create(
			cGte(ConditionType.Gold, gold)
		);
	}

	/**
	 * 
	 */
	public upgradeConditions() {
		return ConditionGroup.create(
			cFun(() => this.curLevel > 0, EconomicThrow.JobNotUnlocked),
			cFun(() => this.curLevel < this.job.maxLevel, EconomicThrow.ReachJobMaxLevel),
			this.upgradeCosts()
		);
	}

	/**
	 * 
	 */
	public unlockCosts() {
		return ConditionGroup.create(
			cGte(ConditionType.Gold, this.job.unlockGold)
		);
	}

	/**
	 * 
	 */
	public unlockConditions() {
		return ConditionGroup.create(
			cFun(() => this.curLevel <= 0, EconomicThrow.JobAlreadyUnlocked),
			cGte(ConditionType.ReturnTime, this.job.unlockReturnTime),
			this.unlockCosts()
		);
	}

	/**
	 * 
	 */
	public get isAutoProfit() { return this.pJob.isAutoProfit; }

	/**
	 * 
	 */
	public get isValidProfitTime() {
		return Date.now() >= this.lastProfitTime + this.job.time * 1000;
	}

	/**
	 * 
	 */
	public profitConditions() {
		return ConditionGroup.create(
			cFun(() => this.curLevel > 0, EconomicThrow.JobNotUnlocked),
			cFun(() => this.isValidProfitTime, EconomicThrow.NotJobProfitTime)
		);
	}

	/**
	 * 
	 */
	public profitRewards() {
		const gold = this.job.profit(this.curLevel);
		return RewardGroup.create(r(RewardType.Gold, gold));
	}

	/**
	 * 
	 */
	public upgrade() {
		const maxLevel = this.job.maxLevel;
		if (this.curLevel >= maxLevel) return;

		// this.curLevel = MathUtils.clamp(this.curLevel + 1, 0, maxLevel);
		this.maxLevel = Math.max(this.maxLevel, ++this.curLevel);

		if (this.lastProfitTime == 0) // 
			this.lastProfitTime = Date.now();
	}

	/**
	 * 
	 */
	public profit() {
		this.lastProfitTime = Date.now();
		this.profitCount++;
	}

	private _icon: SpriteFrame;
	public async icon(): Promise<SpriteFrame> {
		return this._icon ||= await this.loadIcon();
	}
	private async loadIcon(): Promise<SpriteFrame> {
		return await assetMgr().loadAsset(SpriteFrame, BundleName.Sprites, `icons/job${this.jobId}`);
	}

}

@playerData("PlayerJob")
export class PlayerJob extends PlayerData {

	@field
	public id: number = -1;
	@field(Number)
	public returnTimes: number = 0; // 
	@field([PlayerJobItem])
	public jobs: PlayerJobItem[] = []; // 
	@field
	public autoProfitTime: number = 0; // 

	// region 

	/**
	 * 
	 */
	public job(jobId: number) {
		let res = this.jobs.find(j => j.jobId == jobId);
		if (!res) {
			res = new PlayerJobItem(this.jobs.length, this);
			res.jobId = jobId;

			this.jobs.push(res);
		}
		return res;
	}

	/**
	 * 
	 */
	public get isAutoProfit() { return Date.now() <= this.autoProfitTime; }

	// endregion

}
import {jobRepo} from "./Job";
import {cFun, cGte, ConditionGroup, ConditionType} from "../../PlayerModule/Data/Condition";
import {r, RewardGroup, RewardType} from "../../PlayerModule/Data/Reward";
import {EconomicThrow} from "../Common/EconomicCommon";
import {assetMgr, BundleName} from "../../CoreModule/Managers/AssetBundleManager";
import SpriteFrame = cc.SpriteFrame;
