import {ItemContainer} from "../../../../Modules/CoreModule/Components/ItemContainer";
import PartsDisplay from "../ItemPrefabs/PartsDisplay";
import PartsSelection from "../ItemPrefabs/PartsSelection";
import PartsSelectionGroup from "./PartsSelectionGroup";
import {ScrollviewFilter} from "../../../../Modules/CoreModule/Utils/ScrollviewFilter";
import PreviewPictureDisplay from "../ItemPrefabs/PreviewPictureDisplay";
import {PartInfo} from "../../../../Modules/HeroModule/Data/PartInfo";
import {PartType, SpecialPartType} from "../../../../Modules/HeroModule/Data/PartType";
import {HeroType} from "../../../../Modules/HeroModule/Data/HeroType";
import {partMgr} from "../../../../Modules/HeroModule/Managers/PartManager";
import {DefaultBt1, DefaultBt2} from "../../../../Modules/NFTModule/Managers/NFTManager";
import {selectorScene} from "../../SelectorScene";
import SingleEditor from "../Editor/SingleEditor";
import PartColorsContainer from "./PartColorsContainer";
import PartScaler from "../ItemPrefabs/PartScaler";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PartsContainer extends ItemContainer<PartInfo, PartsDisplay> {

	@property(PreviewPictureDisplay)
	preview: PreviewPictureDisplay = null;
	@property(PartColorsContainer)
	colors: PartColorsContainer = null;

	optional: boolean = false;
	selectionGroup: PartsSelectionGroup = null;
	singleEditor: SingleEditor = null;
	partScaler: PartScaler = null;

	// banList: number[] = [];

	// region 

	/**
	 * 
	 */
	public get hero() { return this.preview.item }

	/**
	 * ID
	 */
	public get partTypeId() { return this.items[0]?.partTypeId; }

	/**
	 * 
	 */
	private get lastSelection() {
		return this.items.length <= 0 ? null :
			this.hero?.parts.find(t => t.partTypeId == this.partTypeId);
	}

	/**
	 * 
	 * @param item
	 */
	isInclude(item: PartInfo): boolean {
		if (selectorScene().editorMode && !this.currentPartType.isBody) return true;
		return !this.preview.item || this.preview.item.calcScale(item.part) > 0;

		// if (!this.preview.item) {
		// 	const config = configMgr().config(PartConfig);
		// 	const ptId = item.partTypeId,
		// 		htId = item.part.heroTypeId,
		// 		chtId = this.currentHeroType.id;
		//
		// 	return config.pHtScale[ptId]?.[htId]?.[chtId] > 0 &&
		// 		!this.preview.banList.includes(item.part.oriId);
		// } else
		// 	return this.preview.item?.calcScale(item.part) > 0;
	}

	// endregion

	// region 

	/**
	 * 
	 */
	protected update(dt: number) {
		let nodes = this.itemsComponents
			.map(item => item.node).filter(node => node.active);
		ScrollviewFilter.filter("vertical", this.container,
			nodes, 20, this.node.height, 20,
			Math.floor(this.container.width / 120));

		if (this.preview.partsChanged) this.onPartsChanged();
	}

	//endregion

	// region 

	/**
	 * 
	 * @private
	 */
	private reselect() {
		if (this.currentPartType.special == SpecialPartType.Background) {
			const last = this.items.find(p => p.part == this.preview.backgroundPart);
			if (last)
				this.selectionGroup.select(this.selectionGroup.getElementAt(
					this.items.findIndex(item => item.partId == last.part.id)));
			else
				this.selectionGroup.select(this.selectionGroup.getElementAt(0));
			return;
		}
		const last = this.lastSelection;
		if (!last) // 
			this.selectionGroup.deselect(
				this.selectionGroup.currentSelection());
		else {
			this.selectionGroup.select(this.selectionGroup.getElementAt(
				this.items.findIndex(item => item.partId == last.oriId)));
			this.colors.reselect(last);
		}

	}

	/**
	 * Group
	 */
	private makeSelectionGroup(){
		this.selectionGroup = new PartsSelectionGroup(this.optional)
		this.itemsComponents.forEach(item => {
			let selection = item.node.getComponent(PartsSelection);
			selection.display = selection.getComponent(PartsDisplay);
			selection.container = this;
			this.selectionGroup.addElement(selection);
		})
	}

	// endregion

	// region 

	/**
	 * ItemDisplay
	 * @param display
	 */
	public onItemDisplayCreated(display: PartsDisplay) {
		display.preview = this.preview;
	}

	/**
	 * 
	 */
	public onItemsChanged() {
		super.onItemsChanged();
		const last = this.lastSelection;
		this.makeSelectionGroup();
		this.preview.addPart(last);
		this.reselect();
		if (this.colors)
			this.colors.node.active = false;
	}

	public currentHeroType: HeroType = null;
	public currentPartType: PartType = null;
	private currentTotalParts: PartInfo[] = [];

	/**
	 * 
	 */
	public async onHeroTypeChange() {
		await this.preview.setHeroType(this.currentHeroType);
	}

	/**
	 * 
	 */
	public async onTypeChange() {
		if (!this.currentHeroType || !this.currentPartType) return;
		this.currentTotalParts = await partMgr().getList(DefaultBt1, DefaultBt2, this.currentPartType);
		this.setItems(this.currentTotalParts.filter(p => p.part.id == p.part.oriId));

		// partMgr().getList(DefaultBt1, DefaultBt2,
		//   this.currentPartType).then((res) => {
		//     res = res.filter(p => this.currentPartType.exclusion
		//       [this.currentHeroType.id]?.find(t => t == p.part().heroTypeId) == undefined);
		//     this.setItems(res);
		// });
	}

	/**
	 * 
	 */
	public onPartsChanged() {
		this.reselect();
	}

	/**
	 * 
	 */
	public onSelectChanged(item: PartsDisplay) {
		this.preview.addPart(item.item.part);
		this.singleEditor?.setItem(item.item);
		this.partScaler?.setItem(item.item);
		if (this.colors) {
			this.colors.node.active = true;
			this.colors.setItems(this.currentTotalParts.filter(p => p.part.oriId == item.item.part.oriId && p.part.stock > 0));
		}
	}

	/**
	 * 
	 */
	public switchColorSelector() {
		if (!this.colors) return;
		this.colors.node.active = !this.colors.node.active;
	}

	/**
	 * 
	 */
	public onSelectCancel(item: PartsDisplay) {
		this.preview.removePart(item.item.part);
		this.singleEditor?.setItem(null);
		this.partScaler?.setItem(null);
		this.colors.node.active = false;
	}

	// endregion

}

