import {PlayerData} from "../../PlayerModule/Data/Player";
import {playerData} from "../../PlayerModule/Managers/PlayerManager";
import {field} from "../../CoreModule/Data/BaseData";

export type MoneyType = "gold" | "diamond" | "fragment" | "medal" | "token";

@playerData("PlayerMoney")
export class PlayerMoney extends PlayerData {

	@field
	public gold: number = 0;
	@field
	public diamond: number = 0;
	@field
	public fragment: number = 0;
	@field
	public medal: number = 0;

	// 
	@field
	public token: number = 0;

	/**
	 * 
	 */
	public gainGold(val: number) { return this.gain("gold", val); }
	public gainDiamond(val: number) { return this.gain("diamond", val); }
	public gainFragment(val: number) { return this.gain("fragment", val); }
	public gainMedal(val: number) { return this.gain("medal", val); }

	/**
	 * 
	 */
	public gain(type: MoneyType, val: number) {
		return this[type] = Math.max(this[type] + val, 0);
	}

}
