import {get, post} from "../../CoreModule/Common/CoreCommon";

export class HeroInterface {

	@post("/hero/hero/sync") static SyncHero(d?): any {}
	@post("/hero/hero/create") static CreateHero(d?): any {}
	@get("/hero/hero/get_one") static GetHero(d?): any {}
	@get("/hero/hero/count") static GetHeroCount(d?): any {}
	@get("/hero/hero/get_list") static GetHeroes(d?): any {}

	@post("/hero/level/upgrade") static UpgradeLevel(d?): any {}
	@post("/hero/level/break") static BreakLevel(d?): any {}
	@post("/hero/star/upgrade") static UpgradeStar(d?): any {}
	@post("/hero/strengthen/upgrade") static UpgradeStrengthen(d?): any {}

	@get("/hero/rank/refresh") static RefreshAllRank(d?): any {}

	@post("/core/data/clear_cache") static EmptyCache(d?): any {}

}
