import {ItemDisplay} from "../../../../Modules/CoreModule/Components/ItemDisplay";
import Label = cc.Label;
import Node = cc.Node;
import {PartInfo} from "../../../../Modules/HeroModule/Data/PartInfo";
import {selectorScene} from "../../SelectorScene";
import PartsDisplay from "../ItemPrefabs/PartsDisplay";
import PreviewPictureDisplay, {DefaultStand} from "../ItemPrefabs/PreviewPictureDisplay";
import {configMgr} from "../../../../Modules/CoreModule/Managers/ConfigManager";
import PartConfig from "../../../../Modules/HeroModule/Configs/PartConfig";
import Slider = cc.Slider;
import PartsContainer from "../ItemContainers/PartsContainer";
import EditBox = cc.EditBox;
import Component = cc.Component;
import {DataLoader} from "../../../../Modules/CoreModule/Utils/DataLoader";
import {AlertDialogType, alertMgr} from "../../../../Modules/CoreModule/Managers/AlertManager";
import Skeleton = sp.Skeleton;

const {ccclass, property} = cc._decorator;

@ccclass
export default class AnimationPlayer extends Component {

	@property(Skeleton)
	skeleton: Skeleton = null;
	@property
	aniName: string = "";

	// region 

	public play() {
		this.skeleton.clearTrack(0)
		this.skeleton.setAnimation(0, this.aniName, false);
		this.skeleton.addAnimation(0, DefaultStand, true);
	}

	// endregion

}
