import {field} from "../../CoreModule/Data/BaseData";
import {Motion} from "./Motion";
import {Constructor} from "../../../Utils/BaseContext";
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {data, StaticData} from "../../CoreModule/Data/StaticData";

export const AttackMotionGroupId = 0;
export const HurtMotionGroupId = 1;
export const EnterMotionGroupId = 2;
export const DieMotionGroupId = 3;
export const BossEnterMotionGroupId = 5;
export const SkillMotionGroupId = 6;
export const RecoverMotionGroupId = 7;

@data("MotionGroup")
export class MotionGroup extends StaticData {

	@field([Motion])
	public motions: Motion[] = []
}

export function motionGroupRepo() {
	return getRepository(MotionGroupRepo);
}

@repository
export class MotionGroupRepo extends BaseRepository<MotionGroup> {

	get clazz(): Constructor<MotionGroup> { return MotionGroup; }

	public attack() {
		return this.getById(AttackMotionGroupId);
	}
	public hurt() {
		return this.getById(HurtMotionGroupId);
	}
	public enter() {
		return this.getById(EnterMotionGroupId);
	}
	public die() {
		return this.getById(DieMotionGroupId);
	}
	public skill() {
		return this.getById(SkillMotionGroupId);
	}
	public recover() {
		return this.getById(RecoverMotionGroupId);
	}

}

