import {data, StaticData} from "../../CoreModule/Data/StaticData";
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {Constructor} from "../../../Utils/BaseContext";
import {field} from "../../CoreModule/Data/BaseData";
import {QualityType} from "./Quality";

// noinspection SpellCheckingInspection
export enum ParamType {
	CP, // Computing Power: 
	CPB, // Computing Power Bonus: 
	HID, // HIDden: 
	POW, // POWer: 
	AGI, // AGIle: 
	INT, // INTelligence: 
	POWB, // POWer Bonus: 
	AGIB, // AGIle Bonus: 
	INTB, // INTelligence Bonus: 
	ATK, // ATtacK: 
	CRI, // CRItical: 
	DMG, // Damage: 
	ATKB, // ATtacK Bonus: 
	CRIB, // CRItical Bonus: 
	DMGB, // Damage Bonus: 
	DEF, // DEFense: 
	AVO, // AVOidance: 
	EVA, // EVAsion: 
	DEFB, // DEFense Bonus: 
	AVOB, // AVOidance Bonus: 
	EVAB, // EVAsion Bonus: 
	MHP, // Max HP: 
	SPD, // SPeeD: 
	HIT, // HIT: 
	MHPB, // Max HP Bonus: 
	SPDB, // SPeeD Bonus: 
	HITB, // HIT Bonus: 
	AB1, // All Bonus 1: 1
	AB2, // All Bonus 2: 2
	AB3, // All Bonus 3: 3
	AB4, // All Bonus 4: 4

	// 
	BP, // Battle Point: 
	BPB, // Battle Point Bonus: 

}

@data("Param")
export class Param extends StaticData {

	@field(Number)
	public id: ParamType;
	@field(String)
	public attr: string;
	@field
	public default_: number = 0;
	@field
	public min: number = 0;
	@field
	public max: number = 999999;
	@field
	public isPercent: boolean = false;
	@field([Number])
	public bonus: number[];
	@field
	public battlePoint: number = 0;

	/**
	 * 
	 */
	public get shortName() { return ParamType[this.id]; }
}

export function paramRepo() {
	return getRepository(ParamRepo);
}

@repository
export class ParamRepo extends BaseRepository<Param> {

	get clazz(): Constructor<Param> { return Param; }

	getById(id: ParamType): Param {
		return super.getById(id);
	}

	// @ts-ignore
	getByAttr(attr: string): Param {}

}
