import {nftMgr} from "../../../Modules/NFTModule/Managers/NFTManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class WalletController extends cc.Component {

    @property(cc.Label)
    USTD: cc.Label = null;

    protected start() {
        nftMgr().getUSTDBalance().then(res => {
            this.USTD.string = res.toString();
        })
    }

}
