import {BaseManager, getManager, manager} from "./BaseManager";

import {CoreInterface} from "../Common/CoreCommon";

class Token {
	// TOKEN
	public static Duration = 30 * 60 * 1000;
	// TOKEN30
	public static UpdateDuration = 15 * 60 * 1000;

	public value: string;
	public time: number;

	// 
	public isOutOfDate() {
		const now = Date.now();
		return now > this.time + Token.Duration;
	}

	// 
	public isNeedUpdate() {
		const now = Date.now();
		return now > this.time + Token.UpdateDuration;
	}

	public static create(token) {
		const res = new Token();
		res.value = token;
		res.time = Date.now();
		return res;
	}

	public static invalid() {
		let token = new Token();
		token.time = -1000;
		token.value = "";
		return token;
	}
}

export function gameMgr(): GameManager {
	return getManager(GameManager);
}

@manager
class GameManager extends BaseManager {

	public token: Token = null; // Token.invalid();

	private lastCheckUpdToken: number = 0;

	public needUpdateToken = true;

	// region 

	update(dt: number) {
		super.update(dt);

		this.checkUpdateToken();
	}

	// endregion

	// region Token

	/**
	 * Token
	 */
	public getToken() {
		return this.token.value;
	}

	/**
	 * Token
	 * @param token
	 */
	public setToken(token: string) {
		this.token = Token.create(token);
	}

	/**
	 * Token
	 */
	public async updateToken() {
		const res = await CoreInterface.UpdateToken();
		this.setToken(res['token']);
	}

	// endregion

	private checkUpdateToken() { // 5
		if (this.needUpdateToken &&
			this.token?.isNeedUpdate() &&
			Date.now() - this.lastCheckUpdToken > 5000) {

			this.lastCheckUpdToken = 1e300; // 

			console.log("UpdateToken!!")
			// @ts-ignore
			this.updateToken().finally(() => {
				this.lastCheckUpdToken = Date.now();
			});
		}
	}
}

import {networkMgr} from "./NetworkManager";
