import {BaseData} from "../Data/BaseData";

const {ccclass, property} = cc._decorator;

@ccclass
export abstract class ItemDisplay<T extends BaseData> extends cc.Component {

	item: T = null;

	// isStarted = false;

	protected start() {
		// this.isStarted = true;
	}

	public setItem(item: T) {
		// if (!this.isStarted) this.start();
		this.item = item;
		this.onItemChanged();
	}

	protected onItemChanged() {
		this.refresh();
	}

	public refresh() {}

}
