import Label = cc.Label;
import Node = cc.Node;
import SpriteFrame = cc.SpriteFrame;
import Sprite = cc.Sprite;
import {battleScene} from "../../../../Scenes/BattleScene";
import HeroWithAvatar from "../HeroWithAvatar";

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroInBagDisplay extends HeroWithAvatar {

    @property(Label)
    nameLabel: Label = null;
    @property(Label)
    fragmentLabel: Label = null;

    @property(Sprite)
    qualityBackground: Sprite = null;

    @property(Node)
    fightingTips: Node = null;
    @property([Node])
    stars: Node[] = [];


    start () {

    }

    protected onItemChanged() {
        super.onItemChanged();
        this.nameLabel.string = this.item.name;
        this.qualityBackground.spriteFrame = this.qualityPictures[this.item.quality];
    }
    update (dt) {
        if (!this.item) return;
        this.updateLabels();
        this.updatePictures();
        this.updateNodes();
    }

    updateLabels() {
        this.fragmentLabel.string = "0/60";
    }

    @property([SpriteFrame])
    qualityPictures: SpriteFrame[] = [];

    updatePictures() {

    }

    updateNodes() {
        this.fightingTips.active = battleScene().heroesInTeam.find(h => h == this.item) != undefined;
        this.stars.forEach((s, i) => { s.active = i < this.item.star.level; });
    }

    onAvatarClick() {
        battleScene().cultivateWindow.open(this.item);
    }

}
