import {SelectableObject} from "../../../../Modules/CoreModule/Components/SelectableObject";
import HeroTypeDisplay from "./HeroTypeDisplay";
import HeroTypeContainer from "../ItemContainers/HeroTypeContainer";

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroTypeSelection extends SelectableObject {

    display: HeroTypeDisplay = null;
    container: HeroTypeContainer = null;

    //region 

    onDeselected(): void {
        this.display.onSelectChanged(false);
    }

    onSelected(): void {
        this.display.onSelectChanged(true);
        this.container.onSelectChanged(this.display).then();
    }

    //endregion

}
