import Label = cc.Label;
import {ItemDisplay} from "../../../CoreModule/Components/ItemDisplay";
import {Player} from "../../../PlayerModule/Data/Player";
import Sprite = cc.Sprite;
import {PlayerMoney} from "../../../EconomicModule/Data/PlayerMoney";
import {playerMgr, waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import {autoUnitConvert} from "../../../../Utils/NumberUtils";
import Node = cc.Node;
import {waitForDataLoad} from "../../../CoreModule/Managers/DataManager";
import {battleScene} from "../../../../Scenes/BattleScene";

const {ccclass, property} = cc._decorator;

@ccclass
export default class playerInfoDisplay extends ItemDisplay<Player> {

    @property(Label)
    playerName: Label = null;
    @property(Label)
    force: Label = null;
    @property(Sprite)
    avatar: Sprite = null;
    @property(Label)
    gold: Label = null;
    @property(Label)
    diamond: Label = null;
    @property(Label)
    stableCoin: Label = null;

    @property(Node)
    heroInfoNode: Node = null

    private money: PlayerMoney = null;

    // region 

    start () {
        this.setup();
    }

    @waitForLogin
    @waitForDataLoad
    private setup() {
        let player = playerMgr().player;
        this.setItem(player);
    }

    protected onItemChanged() {
        super.onItemChanged();
        this.money = playerMgr().getPlayerData(PlayerMoney);
    }

    // endregion

    protected update(dt: number) {
        if (battleScene().heroSelected) {
            this.heroInfoNode.active = true;
            this.node.active = false;
            return;
        }
        if (!this.item) return;
        this.updateLabels();
    }

    @autoUnitConvert
    private updateLabels() {
        this.playerName.string = this.item.name;
        // this.force.string = null;
        this.gold.string = this.money.gold.toString();
        this.diamond.string = this.money.diamond.toString();
        this.stableCoin.string = this.money.token.toString();
    }

}
