import {BaseData, field, PK, pkType} from "../../CoreModule/Data/BaseData";
import {Result} from "./Result";
import {Battle} from "./Battle";
import {NormalAttackId, Skill, skillRepo} from "../../HeroModule/Data/Skill";
import {Battler, SkillInfo} from "./Battler";
import {BattleConfig} from "../Configs/BattleConfig";
import {configMgr} from "../../CoreModule/Managers/ConfigManager";
import {MathUtils} from "../../../Utils/MathUtils";

export interface IBattlerInfo {
	camp: number; // 
	pos: number; // 
	battler: Battler;
	opponents: Battler[];
}

export class BattlerInfo extends BaseData implements IBattlerInfo {
	@field
	public camp: number = 0; // 
	@field
	public pos: number = 0; // 

	public action?: Action;

	constructor(index, parent) {
		super(index);
		this.action = parent;
	}

	public get battle() { return this.action?.battle; }
	public get battler() { return this.battle?.getBattler(this); }
	public get opponents() { return this.battle?.getOpponents(this); }

	/**
	 * 
	 * @param action
	 * @param battler
	 */
	public static create(action: Action, battler: IBattlerInfo) {
		const res = new BattlerInfo(null, action);
		res.camp = battler.camp;
		res.pos = battler.pos;
		return res;
	}

}

export class Action extends BaseData {

	@field
	public time: number = 0;
	@field(BattlerInfo)
	public subject: BattlerInfo; // 
	@field(BattlerInfo)
	public target: BattlerInfo; // 
	@field(Number)
	public skillId: number; // 
	@field(Result)
	public result: Result; // 

	public battle: Battle = null;

	// region 

	constructor(index, parent) {
		super(index);
		this.battle = parent;
	}

	public setSubject(battler: IBattlerInfo) {
		this.subject = BattlerInfo.create(this, battler);
	}
	public setTarget(battler: IBattlerInfo) {
		this.target = BattlerInfo.create(this, battler);
	}
	public setSkill(skillInfo: SkillInfo) {
		this._skillInfo = skillInfo;
		this.skillId = skillInfo.skillId;
	}

	// endregion

	// region 

	public get subjectBattler() {
		return this.battle.getBattler(this.subject);
	}
	public get targetBattler() {
		return this.battle.getBattler(this.target);
	}

	public get skill() { return this.skillInfo.skill; }

	private _skillInfo: SkillInfo;
	public get skillInfo() {
		return this._skillInfo ||= this.subjectBattler
			.skills.find(s => s.skillId == this.skillId);
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public apply() {
		this.skillInfo.use();
	}

	/**
	 * TODO: 
	 */
	public makeResult() {}

	// endregion

}
