import Label = cc.Label;
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import Node = cc.Node;
import Layout = cc.Layout;
import Button = cc.Button;
import {autoUnitConvert} from "../../../../Utils/NumberUtils";
import {heroMgr} from "../../../HeroModule/Managers/HeroManager";
import {battleScene} from "../../../../Scenes/BattleScene";
import HeroWithAvatar from "../HeroWithAvatar";

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroItemDisplay extends HeroWithAvatar {

	@property(Label)
	nameLabel: Label = null;
	@property(Label)
	levelLabel: Label = null;
	@property(Label)
	qualityLabel: Label = null;

	@property(Label)
	attackLabel: Label = null;
	@property(Label)
	defenseLabel: Label = null;
	@property(Label)
	healthLabel: Label = null;

	@property(Label)
	upgradeValueLabel: Label = null;
	@property(Label)
	upgradeTipsLabel: Label = null;

	@property(Sprite)
	qualityBackground: Sprite = null;
	@property(Sprite)
	scissorsIcon: Sprite = null;
	@property(Sprite)
	upgradeIcon: Sprite = null;
	@property(Node)
	upgradeNode: Node = null;
	private upgradePicture: Sprite = null;
	private upgradeButton: Button = null;
	@property([Node])
	starNodes: Node[] = [];
	@property(Layout)
	starLayout: Layout = null;

	start () {
		this.setup();
	}

	private setup() {
		this.upgradePicture = this.upgradeNode.getComponent(Sprite);
		this.upgradeButton = this.upgradeNode.getComponent(Button);
	}

	protected onItemChanged() {
		super.onItemChanged();
		this.nameLabel.string = this.item.name;
		this.qualityLabel.string = this.item.qualityData.shortName;
		this.qualityBackground.spriteFrame = this.qualityPictures[this.item.quality];
		this.scissorsIcon.spriteFrame = this.scissorsPictures[this.item.restraint];
	}

	update (dt) {
		if (!this.item) return;
		this.updateLabels();
		this.updatePictures();
		this.updateNodes();
	}

	@autoUnitConvert
	updateLabels() {
		this.levelLabel.string = "Lv." + this.item.level.level;
		this.attackLabel.string = this.item.atk.toString();
		this.defenseLabel.string = this.item.def.toString();
		this.healthLabel.string = this.item.mhp.toString();
		if (this.item.level.isMax()) {
			this.upgradeValueLabel.string = "0";
			this.upgradeTipsLabel.string = "\\L[SJob10]";
			return;
		}
		if (this.item.level.breakEnable()) {
			this.upgradeValueLabel.string =
				this.item.break.costs().diamond.value.toString();
			this.upgradeTipsLabel.string = "\\L[SHero3]";
		} else {
			this.upgradeValueLabel.string =
				this.item.level.costs().gold.value.toString();
			this.upgradeTipsLabel.string = "\\L[SJob3]";
		}
	}

	@property([SpriteFrame])
	qualityPictures: SpriteFrame[] = [];
	@property([SpriteFrame])
	scissorsPictures: SpriteFrame[] = [];
	@property([SpriteFrame])
	buttonPictures: SpriteFrame[] = [];
	@property([SpriteFrame])
	upgradeIcons: SpriteFrame[] = [];

	private static spacingX: number[] = [0, 0, -8, -18, -23];
	private static leftPending: number[] = [29, 12, 0, 0, 0];

	updatePictures() {
		this.upgradeIcon.spriteFrame = this.item.level.breakEnable() ?
			this.upgradeIcons[0] : this.upgradeIcons[1];
	}

	updateNodes() {
		const enable = (this.item.level.breakEnable() &&
			this.item.break.conditions().judge()) ||
			this.item.level.conditions().judge();

		this.upgradePicture.spriteFrame = this.buttonPictures[enable ? 0 : 1];
		this.upgradeButton.interactable = enable;

		const star = this.item.star.level;
		this.starNodes.forEach((n, i) => { n.active = i < star; });
		this.starLayout.paddingLeft = HeroItemDisplay.leftPending[star - 1];
		this.starLayout.spacingX = HeroItemDisplay.spacingX[star - 1];
	}

	upgradeBtn() {
		if (this.item.level.breakEnable())
			heroMgr().break(this.item).then();
		else
			heroMgr().upgrade(this.item).then();
	}

	onAvatarClick() {
		battleScene().cultivateWindow.open(this.item);
	}

}
