import {Reward, RewardGroup} from "../../PlayerModule/Data/Reward";
import {assetField, BaseData, field, GameData, PK, pkType} from "../../CoreModule/Data/BaseData";
import {Condition} from "../../PlayerModule/Data/Condition";
import {Constructor} from "../../../Utils/BaseContext";
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {Enemy, enemyRepo} from "./Enemy";
import {data, StaticData} from "../../CoreModule/Data/StaticData";
import {copyMgr} from "../Managers/CopyManager";
import {BundleName} from "../../CoreModule/Managers/AssetBundleManager";
import SpriteFrame = cc.SpriteFrame;
import {ParamType} from "../../HeroModule/Data/Param";

export enum CopyType {
	Main, Copy, Boss
}

const DefaultMaxRound = 100;

export class CopyLevel extends BaseData {

	@field
	public mapIdx: number = 0; // 
	@field([Number])
	public enemyIds: number[] = []; // 

	// 
	@field([Reward])
	public firstRewards: Reward[] = [];
	@field([Reward])
	public winRewards: Reward[] = [];
	@field([Reward])
	public loseRewards: Reward[] = [];

	public copy: Copy;

	constructor(index, parent) {
		super(index);
		this.copy = parent;
	}

	/**
	 * 
	 */
	public get displayName() {
		return `\\L[CopyLevel${this.mapIdx}]`;
	}

	/**
	 * 
	 */
	public enemies() {
		return this.enemyIds.map(eid => enemyRepo().getById(eid))
	}

	/**
	 * 
	 */
	public calcRewards(win: boolean, first = false) {
		const res = RewardGroup.create(
			...(win ? this.winRewards : this.loseRewards)
		);
		if (win && first)
			this.firstRewards.forEach(r => res.add(r));
		return res;
	}

	private get mapName() { switch (this.copy.type) {
		case CopyType.Main:
			return "main_" + this.mapIdx;
		case CopyType.Boss:
			return "boss_" + this.mapIdx;
		case CopyType.Copy:
			return "copy_" + this.mapIdx;
	} }
	@assetField(SpriteFrame, BundleName.Sprites,
		"battlemaps/${mapName}")
	public mapPicture: SpriteFrame;

}

@data("Copy")
export class Copy extends StaticData {

	@field
	public type: CopyType = CopyType.Copy;
	@field
	public pictureName: string = "";
	@field
	public maxRound: number = DefaultMaxRound;
	@field
	public maxLevel: number = 0;
	@field([CopyLevel])
	public levels: CopyLevel[] = []; // 0
	@field
	public difficulty: number = 1;
	@field([Condition])
	public conditions: Condition[] = [];

	public level(level: number): CopyLevel {
		return this.levels[(level + 1) % this.levels.length];
	}
}

export function copyRepo() {
	return getRepository(CopyRepo);
}

@repository
export class CopyRepo extends BaseRepository<Copy> {

	get clazz(): Constructor<Copy> { return Copy; }

	// @ts-ignore
	public getByType(type: CopyType): Copy { };

	public main() {
		return this.getByType(CopyType.Main);
	}

}
