import {getSingleton, singleton} from "../../Modules/CoreModule/Utils/SingletonUtils";
import {BaseScene} from "../BaseScene";
import GlobalEditor from "./Components/Editor/GlobalEditor";
import SingleEditor from "./Components/Editor/SingleEditor";
import director = cc.director;
import {gameMgr} from "../../Modules/CoreModule/Managers/GameManager";
import {editorMgr} from "../../Modules/HeroModule/Managers/EditorManager";
import {showLoading} from "../../Modules/CoreModule/Managers/LoadingManager";

const {ccclass, property} = cc._decorator;

export function selectorScene() {
	return getSingleton(SelectorScene);
}

@ccclass
@singleton
export default class SelectorScene extends BaseScene {

	@property
	public editorMode = false;
	@property(GlobalEditor)
	public globalEditor: GlobalEditor = null;
	@property(SingleEditor)
	public singleEditor: SingleEditor = null;

	protected get autoConnect() { return false; }

	protected onLoad() {
		super.onLoad();
		gameMgr().needUpdateToken = !this.editorMode;
		// nftMgr().init().then();
	}

	update (dt) {
		super.update(dt);
		// console.log("progress", dataMgr().loadingProgress());
	}

	public enterGame() {
		director.loadScene("BattleScene");
	}

	public exportAllConfigs() {
		editorMgr().exportAll();
	}

	public importConfigs() {
		editorMgr().import();
	}
	public exportConfigs() {
		editorMgr().export();
	}
	@showLoading
	public async pushConfigs() {
		await editorMgr().push();
	}
	@showLoading
	public async pullConfigs() {
		await editorMgr().pull();
		this.globalEditor.refresh();
		this.singleEditor.refresh();
	}

	public toggleGlobalEditor() {
		this.globalEditor.node.active = !this.globalEditor.node.active;
	}

}
