import gifshot = require('./gifshot')
import {PromiseUtils} from "./PromiseUtils";

export default class GIFUtils {

    static async makeGIF(images: string[]) {
        let passedOptions = this.getSelectedOptions();
        let temp = [];
        images.forEach(i => {
            let image = new Image();
            image.src = i;
            temp.push(image);
        })
        passedOptions.images = temp;
        let res = null;
        let made = false;

        gifshot['createGIF'](passedOptions, obj => {
            if (!obj.error) res = obj.image;
            made = true;
        });
        await PromiseUtils.waitFor(() => made);

        return res;
    }

    private static getSelectedOptions() {
        return {
            gifWidth: Number(400),
            gifHeight: Number(400),
            images: [],
            interval: Number(0.02),
            frameDuration: Number(1),
            sampleInterval: Number(10),
            numWorkers: Number(2)
        }
    }
}
