	// region 

	/**
	 * 
	 */
	public static scaleAttachment(bone: Bone, att: RegionAttachment,
																scaleX: number, scaleY: number) {
		att.scaleX = scaleX;
		att.scaleY = scaleY;

		this.updateAttachmentOffset(bone, att);
	}
	// RegionAttachment.updateOffset
	private static updateAttachmentOffset(bone: Bone, att: RegionAttachment) {
		const regionScaleX = att.width / att.region.originalWidth * att.scaleX;
		const regionScaleY = att.height / att.region.originalHeight * att.scaleY;

		const localX = -att.width / 2 * att.scaleX + att.region.offsetX * regionScaleX;
		const localY = -att.height / 2 * att.scaleY + att.region.offsetY * regionScaleY;
		const localX2 = localX + att.region.width * regionScaleX;
		const localY2 = localY + att.region.height * regionScaleY;
		const radians = att.rotation * Math.PI / 180;
		const cos = Math.cos(radians);
		const sin = Math.sin(radians);

		// const bonePos = this.boneDataWorldPos(bone);
		// const dx = att.x - bone.data.x;
		// const dy = att.y - bone.data.y;

		const x = att.scaleX * att.x; // + (att.scaleX - 1) * att.x;
		const y = att.scaleY * att.y; // + (att.scaleY - 1) * att.y;

		const localXCos = localX * cos + x;
		const localXSin = localX * sin;
		const localYCos = localY * cos + y;
		const localYSin = localY * sin;
		const localX2Cos = localX2 * cos + x;
		const localX2Sin = localX2 * sin;
		const localY2Cos = localY2 * cos + y;
		const localY2Sin = localY2 * sin;
		const offset = att.offset;
		offset[RegionAttachment.OX1] = localXCos - localYSin;
		offset[RegionAttachment.OY1] = localYCos + localXSin;
		offset[RegionAttachment.OX2] = localXCos - localY2Sin;
		offset[RegionAttachment.OY2] = localY2Cos + localXSin;
		offset[RegionAttachment.OX3] = localX2Cos - localY2Sin;
		offset[RegionAttachment.OY3] = localY2Cos + localX2Sin;
		offset[RegionAttachment.OX4] = localX2Cos - localYSin;
		offset[RegionAttachment.OY4] = localYCos + localX2Sin;
	}

