	/**
	 * 
	 */
	public static getBoneNames(partType: PartType) {

		const res = [];

		const config = configMgr().config(PartConfig);
		const orders = config.slotOrders;
		const map = config.slotNameMap;

		const slots = Object.keys(orders).map(
			key => [key, map[key] || key]);

		// 
		slots.forEach(slot => {
			if (partType.name == slot[1].split('_')[0])
				res.push(slot[0]);
		});

		return res;
	}

	/**
	 * 
	 */
	public static getBoneName(partType: PartType, index: number = 0) {
		return partType.name + "_" + index;
	}

