	/**
	 * 
	 */
	public static boneDataWorldPos(bone: Bone | BoneData) {
		if (bone instanceof Bone) bone = bone.data;
		const res = Vec2.ZERO.clone();
		do {
			res.x += bone.x; res.y += bone.y;
			bone = bone.parent;
		} while (bone.parent);
		return res;
	}

	/**
	 * Skeletonspine.Skeleton
	 */
	public static skeleton(sk: sp.Skeleton): Skeleton {
		return sk["_skeleton"];
	}

	/**
	 * 
	 */
	public static findBoneParent(bone: Bone, skeleton: Skeleton)
		: {bone?: Bone, parent?: Bone, sParent?: Bone} {
		const parent = bone.parent;
		if (!parent) return null;
		const sParent = skeleton.findBone(parent.data.name);
		return sParent ? {bone, parent, sParent} :
			this.findBoneParent(parent, skeleton);
	}

	/**
	 * 
	 */
	public static assignBone(bone: Bone, targetBone: Bone) {
		if (!targetBone || bone == targetBone) return targetBone;

		const dataKeys: (keyof BoneData)[] = [
			"x", "y", "length", "rotation", "scaleX", "scaleY",
			"shearX", "shearY", "color", "transformMode", "skinRequired"];

		targetBone.data = Object.assign({}, targetBone.data);
		// @ts-ignore
		dataKeys.forEach(key => targetBone.data[key] = bone.data[key]);
		targetBone.setToSetupPose();
		return targetBone;
	}

	/**
	 * 
	 */
	public static copyBoneToSkeleton(bone: Bone, skeleton: Skeleton) {
		// 
		const sBone = skeleton.findBone(bone.data.name);
		if (!sBone) { // 
			const parentInfo = this.findBoneParent(bone, skeleton);
			if (!parentInfo) return; // 

			// copycopy
			return this.copyBone(parentInfo.bone, parentInfo.sParent);
		} else // replace
			return this.replaceBone(bone, sBone, false);
	}

	/**
	 * 
	 */
	public static copyBone(bone: Bone, parent: Bone) {
		const target = parent.skeleton;

		const res = new Bone(bone.data, target, parent);
		bone.children.forEach(cBone => this.copyBone(cBone, res));

		parent.children.push(res);

		target.bones.push(res);
		target.sortBone(res);

		return res;
	}

	/**
	 * 
	 */
	public static replaceBone(bone: Bone, targetBone: Bone, assign: boolean = true) {
		if (!targetBone.parent) throw "";

		if (assign) this.assignBone(bone, targetBone);

		targetBone.active = true;
		// 
		targetBone.children.forEach(c => c.active = false);

		bone.children.forEach(c => {
			const name = c.data.name;
			const tc = targetBone.children.find(tc => tc.data.name == name);
			if (tc) // replace
				this.replaceBone(c, tc);
			else // copy
				this.copyBone(c, targetBone);
		});
	}

	/**
	 * 
	 */
	public static addPart(heroType: HeroType,
												target: Skeleton, source: Skeleton, part: Part) {
		const pHeroType = part.heroType;
		const scale = heroType.scale / pHeroType.scale;

		part.slotNames.forEach(name => {
			const sSlot = source.findSlot(name);
			let tSlot = target.findSlot(name), tBone;
			if (!tSlot) {
				tBone = target.findBone(sSlot.bone.data.name);
				if (!tBone) return;

				tSlot = this.createSlot(target, tBone, name);
			} else tBone = tSlot.bone;

			const att = sSlot.getAttachment().copy() as RegionAttachment;
			// this.scaleAttachment(tBone, att, scale, scale);
			tSlot.setAttachment(att);
		})
	}

	/**
	 * 
	 */
	public static removePart(target: Skeleton, part: Part) {
		part.slotNames.forEach(name =>
			target.findSlot(name)?.setAttachment(null)
		);
	}

