import {BaseData, field, GameData, PK, pkType} from "../../CoreModule/Data/BaseData";
import {ChainId, DefaultChainId, WalletType} from "../../NFTModule/Managers/NFTManager";

export type RankContent = {
	[key in RankType]?: number
}
export type RankType = 'battle' | 'gold' | 'diamond' | 'pvp';

export const RankTypes: RankType[] = ['battle', 'gold', 'diamond', 'pvp'];

export class RankItem extends BaseData {

	@field(String)
	public uuid: string;
	@field(Number)
	public value: number;

	@field
	public chainId?: ChainId = DefaultChainId; // ID
	@field
	public account?: string = ""; // 
	@field
	public wallet?: WalletType = 'Metamask'; // 

	@field
	public name?: string = ""; // 
	@field
	public avatarUrl?: string = ""; // URL

}

export class RankRecord extends GameData {

	@field(String)
	public type: RankType;
	@field(Number)
	public createdAt: number;
	@field([RankItem])
	public list: RankItem[] = [];

	/**
	 * 
	 */
	public static create(type: RankType, list: RankItem[]) {
		const res = new RankRecord();
		res.type = type;
		res.createdAt = Date.now();
		res.list = list;
		return res;
	}
}
