import {waitForDataLoad} from "../../../CoreModule/Managers/DataManager";
import {playerMgr, waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import {Hero} from "../../../HeroModule/Data/Hero";
import {ScrollviewFilter} from "../../../CoreModule/Utils/ScrollviewFilter";
import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import HeroSelectDisplay from "./HeroSelectDisplay";
import Node = cc.Node;

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroSelectContainer extends ItemContainer<Hero, HeroSelectDisplay> {

    start () {
        this.setup().then();
    }

    @waitForDataLoad
    @waitForLogin
    private async setup() {
        const acc = playerMgr().player.account;
        let items: Hero[] = (await heroMgr().getListFromServer()).filter(h => h.ownerAccount == acc);
        this.setItems(items);
    }

    update (dt) {
    }

    onItemsChanged() {
        super.onItemsChanged();
        let nodes : Node[] = [];
        this.itemsComponents.forEach(item => { if(item.node.active == true) nodes.push(item.node) })
        ScrollviewFilter.filter("vertical", this.container, nodes, 5, this.node.height, 170);
        battleScene().currentPVPSelect = this.items[0];
        battleScene().heroSelected = true;
    }
}

import {heroMgr} from "../../../HeroModule/Managers/HeroManager";
import {battleScene} from "../../../../Scenes/BattleScene";
