import {ItemDisplay} from "../../../CoreModule/Components/ItemDisplay";
import {Reward} from "../../../PlayerModule/Data/Reward";
import Sprite = cc.Sprite;
import Label = cc.Label;
import {autoUnitConvert} from "../../../../Utils/NumberUtils";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BattleRewardDisplay extends ItemDisplay<Reward> {

    @property(Sprite)
    icon: Sprite = null;
    @property(Label)
    value: Label = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    update (dt) {
        this.updateNodeAnimation(dt);
    }

    protected onItemChanged() {
        super.onItemChanged();
        this.updateLabels();
        this.resetTimers();
    }

    @autoUnitConvert
    updateLabels() {
        this.value.string = this.item.value.toString();
    }

    @property(Number)
    animationTime: number = 0.2;

    delayTime: number = 0;
    timer: number = 0;

    updateNodeAnimation(dt: number) {
        if (this.timer < this.delayTime + this.animationTime) {
            this.node.opacity = Math.max(0, 255 * (this.timer - this.delayTime) / this.animationTime);
            this.node.scale = Math.min(1.2, 1.2 - 0.2 * (this.timer - this.delayTime) / this.animationTime);
            this.timer += dt;
            return;
        }
        this.node.opacity = 255;
        this.node.scale = 1;
    }

    resetTimers() {
        this.timer = 0;
    }

}
