import Animation = cc.Animation;
import requireComponent = cc._decorator.requireComponent;
import menu = cc._decorator.menu;

const {ccclass, property} = cc._decorator;

@ccclass
@requireComponent(Animation)
@menu("/")
export default class RandomAnimation extends cc.Component {

    @property(Animation)
    animation: Animation = null;

    @property(Number)
    timeOffset: number = 0; // 
    @property(Number)
    timeOffsetAdd: number = 0; // 
    @property(Number)
    xOffset: number = 0; // X
    @property(Number)
    yOffset: number = 0; // Y
    @property(Number)
    speedUp: number = 0; // 
    @property(Boolean)
    resetPositionEveryLoop: boolean = true; // 

    // LIFE-CYCLE CALLBACKS:

    private centerX: number = 0;
    private centerY: number = 0;

    protected start() {
        this.reset();
        if (this.resetPositionEveryLoop)
            this.animation.on("lastframe", this.resetPosition, this);
    }

    protected onEnable() {
        this.centerX = this.node.x;
        this.centerY = this.node.y;
        this.reset();
    }

    private reset() {
        if (!this.animation)
            this.animation = this.getComponent(Animation);
        this.animation.play();
        this.animation.sample(this.animation.defaultClip.name);
        this.animation.stop();
        this.resetPosition();
        this.timer = Math.random() * this.timeOffset;
        this.animation.defaultClip.speed = 1 + Math.random() * this.speedUp;
        this.isPlaying = false;
    }

    private timer: number = 0;
    private isPlaying: boolean = false;

    protected update(dt: number) {
        if (!this.isPlaying) {
            if (this.timer <= 0) {
                this.animation.play();
                this.isPlaying = true;
            }
            this.timer -= dt;
        }
    }

    private resetPosition() {
        this.node.x = this.centerX + Math.random() * this.xOffset * 2 - this.xOffset;
        this.node.y = this.centerY + Math.random() * this.yOffset * 2 - this.yOffset;
    }

}
