import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import {ScrollviewFilter} from "../../../CoreModule/Utils/ScrollviewFilter";
import {Hero} from "../../../HeroModule/Data/Hero";
import {playerMgr, waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import {waitForDataLoad} from "../../../CoreModule/Managers/DataManager";
import HeroGroupDisplay from "./HeroGroupDisplay";
import {HeroGroup, HeroGroupType} from "../../Data/HeroGroup";
import {heroMgr} from "../../../HeroModule/Managers/HeroManager";
import Node = cc.Node;

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroGroupContainer extends ItemContainer<HeroGroup, HeroGroupDisplay> {
    currentGroupType: HeroGroupType = HeroGroupType.RESTRAINT;
    totalHeroes: Hero[] = [];

    start() {
        this.setup().then();
    }

    @waitForDataLoad
    @waitForLogin
    private async setup() {
        const acc = playerMgr().player.account;
        this.totalHeroes = (await heroMgr().getListFromServer())
          .filter(h => h.ownerAccount == acc);
        this.setItems(this.groupHeroes(this.totalHeroes));
    }

    update(dt) {
    }

    onItemsChanged() {
        super.onItemsChanged();
        let nodes: Node[] = [];
        this.itemsComponents.forEach(item => {
            if (item.node.active == true) nodes.push(item.node)
        })
        ScrollviewFilter.filter("vertical", this.container, nodes, 5, this.container.height, 180, 5);
    }

    /**
     * HeroHeroGroup
     */
    groupHeroes(heroes: Hero[], groupBy: HeroGroupType = this.currentGroupType): HeroGroup[] {
        this.currentGroupType = groupBy;
        let groups: HeroGroup[] = [];
        let maxIdx = 0;
        heroes.forEach((hero) => {
            let idx;//
            let val;//
            switch (groupBy) {
                case HeroGroupType.STAR:
                    idx = 6 - hero.star.level;
                    val = hero.star.level;
                    break;
                case HeroGroupType.QUALITY:
                    val = hero.quality;
                    idx = 6 - hero.quality;
                    break;
                case HeroGroupType.RESTRAINT:
                    val = idx = hero.restraint;
                    break;
            }
            groups[idx] ||= new HeroGroup(groupBy, val);
            groups[idx].heroes.push(hero);
            maxIdx = Math.max(maxIdx, idx);
        })
        for (let i = maxIdx; i >= 0; i--) {
            if (groups[i] == null)
                groups.splice(i, 1);
        }
        return groups;
    }

    /**
     * tab
     */
    setHeroGroupType(e, d) {
        let value = parseInt(d);
        this.setItems(this.groupHeroes(this.totalHeroes, value));
    }
}
