import BattleDisplay from "../Modules/BattleModule/Components/BattleDisplay";
import Prefab = cc.Prefab;

const {ccclass, property} = cc._decorator;

@ccclass
export default class TestGetHero extends cc.Component {

	@property(BattleDisplay)
	bd: BattleDisplay = null;
	@property(Prefab)
	hit: Prefab = null;
	@property(Boolean)
	shouldUpdate: boolean = false;

	public getOne(){
		console.log("One Hero:", heroMgr().getOneFromServer("140"));
	}

	public getList(){
		console.log("Hero List:", heroMgr().getListFromServer());
	}

	public getCount(){
		console.log("Hero Count", heroMgr().getCountFromServer());
	}

	public trySynchronize(){
		heroMgr().synchronizeAllHeroes().then();
	}

	public async testBattle() {

		await heroMgr().getOneFromServer("99");
		await heroMgr().getOneFromServer("118");
		await heroMgr().getOneFromServer("121");

		const battle = await battleMgr().startMainBattle();
		this.bd.setItem(battle);
	}

	public async testCopy() {
		await heroMgr().getOneFromServer("121");
		const battle = await battleMgr().startCopy();
		this.bd.setItem(battle);
	}

	public testHit(){
		let hitNode = cc.instantiate(this.hit);
		hitNode.parent = this.node;
		hitNode.getComponent(HitDamage).setRange(200, 200);
	}

	protected update(dt: number) {
		// if (this.shouldUpdate)
		// 	updateContext(dt);
	}

	public testRealBattle() {
		heroMgr().getListFromServer().then(res => {
			this.testBattle().then();
		})
	}

	public testRefreshRank() {
		// HeroInterface.RefreshAllRank().then(res => console.log(res));
		HeroInterface.EmptyCache().then(res => console.log(res));
	}
}

import {heroMgr} from "../Modules/HeroModule/Managers/HeroManager";
import {battleMgr} from "../Modules/BattleModule/Managers/BattleManager";
import {HeroInterface} from "../Modules/HeroModule/Common/HeroCommon";
import HitDamage from "../Modules/BattleModule/Components/IndependentController/HitDamage";
