import Prefab = cc.Prefab;
import Component = cc.Component;
import instantiate = cc.instantiate;
import Vec3 = cc.Vec3;

const {ccclass, property} = cc._decorator;

@ccclass
export default class ClickAnimation extends Component {

    @property(Prefab)
    clickObj: Prefab = null;

    isLoad: boolean = false;

    protected update(dt: number) {
        if (!this.isLoad) {
            this.node.on(cc.Node.EventType.TOUCH_END, e => { this.click(e); });
            (this.node as any)._touchListener.setSwallowTouches(false);
            this.isLoad = true;
        }
    }

    click (event: Event) {
        let node = instantiate(this.clickObj);
        node.parent = this.node;
        node.position = new Vec3(Number(event["touch"]["_point"]["x"]) - this.node.width / 2,
                                 Number(event["touch"]["_point"]["y"]) - this.node.height / 2,
                                 0);
        setTimeout(() => { node.destroy(); }, 400);
    }
}
