import {BaseData, field} from "../Modules/CoreModule/Data/BaseData";

const {ccclass, property} = cc._decorator;

@ccclass
export default class TestHeroItem extends BaseData {

    @field
    name: string = null;
    @field
    atk: number = 0;
    @field
    def: number = 0;
    @field
    mhp: number = 0;
    @field
    level: number = 1;

    @field
    quality: number = 0;
    @field
    stars: number = 1;
    @field
    scissorsType: number = 0;

    getRealUpgrade(): number {
        return this.level * 1000 + 2500;
    }

    getRealAttack(): number {
        return this.atk * this.level;
    }

    getRealDefense(): number {
        return this.def * this.level;
    }

    getRealHealth(): number {
        return this.mhp * this.level;
    }


    public static testData(): TestHeroItem {
        let res = new TestHeroItem();
        res.name = "Little Y";
        res.atk = 400;
        res.def = 120;
        res.mhp = 15000;
        res.quality = Math.floor(Math.random() * 4);
        res.stars = Math.floor(Math.random() * 6);
        res.scissorsType = Math.floor(Math.random() * 3);
        return res;
    }

}
