
import { updateContext} from "../Modules/CoreModule/Managers/BaseManager";
import Component = cc.Component;
import {curScene, sceneMgr} from "../Modules/CoreModule/Managers/SceneManager";
import {nftMgr, Web3Enable} from "../Modules/NFTModule/Managers/NFTManager";
import {playerMgr} from "../Modules/PlayerModule/Managers/PlayerManager";
const { ccclass, property } = cc._decorator;

export abstract class BaseScene extends Component {

	protected onLoad() {
		sceneMgr().curScene = this;
	}
	protected onDestroy() {
		sceneMgr().curScene = null;
	}

	protected get autoConnect() { return true; }

	protected start() {
		if (Web3Enable) nftMgr().init(this.autoConnect).then();
		else playerMgr().autoLogin().then();
	}

	update(dt: number) {
		updateContext(dt);
	}

}


