import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import {ScrollviewFilter} from "../../../CoreModule/Utils/ScrollviewFilter";
import Node = cc.Node;
import {Hero} from "../../../HeroModule/Data/Hero";

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroInTeamContainer extends ItemContainer<Hero, HeroInTeamDisplay> {

    start() {
    }

    private async setup() {
    }

    onItemsChanged() {
        super.onItemsChanged();
        this.modifyOnClickEvents();
    }

    modifyOnClickEvents() {
        this.itemsComponents.forEach(item => {
            let btn = item.node.getComponentInChildren(Button);
            btn.clickEvents = [];
            let clickEventHandler = new cc.Component.EventHandler();
            clickEventHandler.target = this.node;
            clickEventHandler.component = "HeroInTeamContainer";
            clickEventHandler.handler = "onTeamHeroClicked";
            clickEventHandler.customEventData = item.item.nftId;//nftId
            btn.clickEvents.push(clickEventHandler);
        });
    }

    onTeamHeroClicked(e, nftId: string) {
        battleScene().myTeamFrame.removeTeamMember(nftId);
    }
}

import HeroInTeamDisplay from "./HeroInTeamDisplay";
import Button = cc.Button;
import {battleScene} from "../../../../Scenes/BattleScene";
