import Component = cc.Component;
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import MainMenuItemSelectionGroup from "./MainMenuItemSelectionGroup";
import MainMenuItemSelection from "./MainMenuItemSelection";
import Node = cc.Node;
import Color = cc.Color;

const {ccclass, property} = cc._decorator;

@ccclass
export default class MainMenuController extends Component {

    @property([Sprite])
    backgroundSprites: Sprite[] = [];
    @property([Sprite])
    iconSprites: Sprite[] = [];
    @property([Node])
    textNodes: Node[] = [];
    @property([Node])
    playerWindows: Node[] = [];

    @property(SpriteFrame)
    backgroundSelect: SpriteFrame = null;
    @property([SpriteFrame])
    iconSelect: SpriteFrame[] = [];
    @property(SpriteFrame)
    backgroundUnselect: SpriteFrame = null;
    @property([SpriteFrame])
    iconUnselect: SpriteFrame[] = [];

    @property([Node])
    redPointTips: Node[] = [];


    // onLoad () {}

    isLoad: boolean = false;

    start () {

    }

    setup () {
        this.selectionGroup = new MainMenuItemSelectionGroup(false);
        this.selections.forEach(s => {
            this.selectionGroup.addElement(s);
            s.menu = this;
        });
        this.selectionGroup.select(this.selections[1]);
        this.selectionGroup.select(this.selections[0]);
        this.isLoad = true;
    }

    update (dt) {
        if (!this.isLoad) {
            this.setup();
            if (!this.isLoad) return;
        }
        this.updateTips();
    }

    updateTips () {
        // 
        this.redPointTips[2].active = Date.now() % 10000 >= 5000;
    }

    selectionGroup: MainMenuItemSelectionGroup = null;
    @property([MainMenuItemSelection])
    selections: MainMenuItemSelection[] = [];

    onSelectChanged (index: number) {
        this.backgroundSprites.forEach((bg, i) => {
            bg.spriteFrame = i == index ? this.backgroundSelect : this.backgroundUnselect;
        });
        this.iconSprites.forEach((ic, i) => {
            ic.spriteFrame = i == index ? this.iconSelect[i] : this.iconUnselect[i];
        });
        this.textNodes.forEach((n, i) => {
            n.color = i == index ? new Color(250, 235, 204) : new  Color(68, 58, 46);
        });
        this.playerWindows.forEach((n, i) => {
            n.active = i == index;
        })
    }

}
