import {BaseManager, getManager, manager} from "../../CoreModule/Managers/BaseManager";
import {PartType} from "../Data/PartType";
import {Web3NFTComponentData, Web3NFTData} from "../../NFTModule/Utils/Web3";
import {
	nftMgr,
	nftMakeId2PartId,
	PartTypeIdOffset,
	Web3Enable
} from "../../NFTModule/Managers/NFTManager";
import {PartInfo} from "../Data/PartInfo";
import {partRepo} from "../Data/Part";

const UseTestData = true;

export function partMgr() {
	return getManager(PartManager);
}

@manager
class PartManager extends BaseManager {

	public data = {};

	/**
	 * 
	 */
	public async getOne(bt1: number, bt2: number, partType: PartType,
											id: number, refresh?: boolean) {
		const list = await this.getList(bt1, bt2, partType, refresh);
		return list?.find(d => d.partId == id);
	}

	/**
	 * 
	 */
	public async getList(bt1: number, bt2: number,
											 partType: PartType, refresh?: boolean) {
		let res = this.getCacheList(bt1, bt2, partType);

		// 
		if (!res || refresh)
			if (UseTestData || !Web3Enable) {
				const parts = partRepo().findByPartTypeId(partType.id);
				res = this.data[this.getKey(bt1, bt2, partType)] =
					parts.map(p => p.toPartInfo());
			} else {
				let tmp = await nftMgr().getPartList(bt1, bt2, partType);
				tmp = tmp.filter(d => this.isValid(d));
				tmp.forEach(d => this.add(bt1, bt2, partType, d));
				res = this.getCacheList(bt1, bt2, partType);
			}

		return res;
	}

	/**
	 *
	 * 
	 */
	public getCacheOne(bt1: number, bt2: number, partType: PartType, id: number) {
		const list = this.getCacheList(bt1, bt2, partType);
		return list?.find(d => d.partId == id);
	}

	/**
	 * 
	 */
	public getCacheList(bt1: number, bt2: number, partType: PartType): PartInfo[] {
		return this.data[this.getKey(bt1, bt2, partType)];
	}
	public getOrCreateCacheList(bt1: number, bt2: number, partType: PartType): PartInfo[] {
		return this.data[this.getKey(bt1, bt2, partType)] ||= [];
	}

	/**
	 * 
	 */
	private add(bt1: number, bt2: number, partType: PartType,
							data: Web3NFTComponentData) {
		const pid = nftMakeId2PartId(Number(data.componentId));
		const list = this.getOrCreateCacheList(bt1, bt2, partType);

		// 
		let res = this.getCacheOne(bt1, bt2, partType, pid);
		// 
		if (!res) list.push(res = new PartInfo());
		// 
		return this.convert(data, res);
	}

	private getKey(bt1: number, bt2: number, partType: PartType) {
		return bt1 + "_" + bt2 + "_" + partType.id;
	}

	private isValid(data: Web3NFTComponentData) {
		const pid = nftMakeId2PartId(Number(data.componentId));
		return !!partRepo().getById(pid); // DataUtils.part(pid, "id");
	}

	public convert(data: Web3NFTComponentData, res?: PartInfo): PartInfo {
		res ||= new PartInfo();
		res.partId = nftMakeId2PartId(Number(data.componentId));
		res.name = data.name;
		res.partTypeId = Number(data.ct) - PartTypeIdOffset;
		res.price = nftMgr().convertPrice(data.prices[0].price);
		res.stock = Number(data.stock);
		res.rawData = data;
		return res;
	}

}
