import {BaseManager, getManager, manager} from "./BaseManager";

export type Platform = 'web' | 'android' | 'test';

export function getPlatformType() : Platform {
	switch (cc.sys.platform) {
		case cc.sys.DESKTOP_BROWSER:
		case cc.sys.MOBILE_BROWSER: return 'web';
		case cc.sys.ANDROID: return 'android';
		default: return 'test';
	}
}

export function platformMgr() : PlatformManager {
	return getManager(PlatformManager);
}

export function getPlatform() : BasePlatform {
	return platformMgr().getPlatform(getPlatformType());
}

@manager
class PlatformManager extends BaseManager {

	platformClasses = {};
	platforms = {};

	/**
	 * 
	 */
	public registerPlatform<T extends BasePlatform>(
		type: Platform, clazz: Constructor<T>) {
		this.platformClasses[type] = clazz;
	}

	/**
	 * 
	 */
	public getPlatform(type: Platform) : BasePlatform {
		const ctor = this.platformClasses[type];
		return this.platforms[type] ||= ctor ? new ctor(type) : null;
	}
}

import { BasePlatform } from '../../../Platforms/BasePlatform';
import {Constructor} from "../../../Utils/BaseContext";
