import Sprite = cc.Sprite;
import Label = cc.Label;
import Animation = cc.Animation;
import SpriteFrame = cc.SpriteFrame;
import Color = cc.Color;

const {ccclass, property} = cc._decorator;

const MaxQuality = 5;

@ccclass
export default class CreatedWindow extends cc.Component {

	@property(Sprite)
	picture: Sprite = null;
	@property(Sprite)
	light: Sprite = null;
	@property(Sprite)
	frame: Sprite = null;
	@property(Label)
	tips: Label = null;

	@property([SpriteFrame])
	sf: SpriteFrame[] = [];
	@property([String])
	tipContents: string[] = [
		"\\L[SCreate31]\\L[SCreate36]\\L[SCreate37]",
		"\\L[SCreate31]\\L[SCreate35]\\L[SCreate37]",
		"\\L[SCreate31]\\L[SCreate34]\\L[SCreate37]",
		"\\L[SCreate31]\\L[SCreate33]\\L[SCreate37]",
		"\\L[SCreate31]\\L[SCreate32]\\L[SCreate37]"
	];
	@property([Color])
	colors: Color[] = [
		new Color(35, 229, 160),
		new Color(23, 179, 226),
		new Color(160, 69, 246),
		new Color(216, 91, 225),
		new Color(254, 189, 74)
	];

	setPicture(base64: string){
		if(!this.picture) return;
		let img = new Image();
		img.src = base64;
		let self = this;
		img.onload = function(){
			let texture = new cc.Texture2D();
			texture.initWithElement(img);
			texture.handleLoadedTexture();
			self.picture.spriteFrame = new cc.SpriteFrame(texture);
		}
	}

	setQuality(quality: number) {
		const idx = MaxQuality - quality;
		this.light.spriteFrame = this.sf[idx];
		this.tips.string = this.tipContents[idx];
		this.frame.node.color = this.colors[idx];

		/*
		switch (quality){
				case 5:
						this.light.spriteFrame = this.sf[0];
						this.frame.node.color = new Color(35, 229, 160);
						this.tips.string = "\\L[SCreate30]";
						break;
				case 4:
						this.light.spriteFrame = this.sf[1];
						this.frame.node.color = new Color(23, 179, 226);
						this.tips.string = "\\L[SCreate30]";
						break;
				case 3:
						this.light.spriteFrame = this.sf[2];
						this.frame.node.color = new Color(160, 69, 246);
						this.tips.string = "\\L[SCreate30]";
						break;
				case 2:
						this.light.spriteFrame = this.sf[3];
						this.frame.node.color = new Color(216, 91, 225);
						this.tips.string = "\\L[SCreate30]";
						break;
				case 1:
						this.light.spriteFrame = this.sf[4];
						this.frame.node.color = new Color(254, 189, 74);
						this.tips.string = "\\L[SCreate30]";
						break;
		}
		*/
	}

	show(){
		this.light.node.getComponent(Animation).play();
		this.node.active = true;
	}

	shut(){
		this.node.active = false;
	}

	jump(){
		window.parent.location.href = "/mynft";
	}

}
