import {conditionProcessor, ConditionProcessor} from "../../PlayerModule/Utils/ConditionProcessor";
import {MoneyType, PlayerMoney} from "../Data/PlayerMoney";
import {EconomicThrow} from "../Common/EconomicCommon";
import {playerMgr} from "../../PlayerModule/Managers/PlayerManager";
import {ConditionType} from "../../PlayerModule/Data/Condition";
import {PlayerJob} from "../Data/PlayerJob";

abstract class MoneyConditionProcessor extends ConditionProcessor {

	protected pMoney: PlayerMoney;

	protected abstract get type(): MoneyType;

	protected get playerValue() { return this.pMoney[this.type] }
	protected get throwFunc(): Function { return EconomicThrow.NotEnoughMoney; }

	protected preprocess() {
		super.preprocess();
		this.pMoney = playerMgr().getPlayerData(PlayerMoney);
	}

	protected async doConsume() {
		this.pMoney.gain(this.type, -this.value);
	}

}

@conditionProcessor(ConditionType.Gold)
export class GoldConditionProcessor extends MoneyConditionProcessor {

	protected get type(): MoneyType { return "gold"; }

}

@conditionProcessor(ConditionType.Diamond)
export class DiamondConditionProcessor extends MoneyConditionProcessor {

	protected get type(): MoneyType { return "diamond"; }

}

@conditionProcessor(ConditionType.Medal)
export class MedalConditionProcessor extends  MoneyConditionProcessor {

	protected get type(): MoneyType { return "medal" }

}

@conditionProcessor(ConditionType.Fragment)
export class FragmentConditionProcessor extends MoneyConditionProcessor {

	protected get type(): MoneyType { return "fragment"; }
	protected get throwFunc(): Function { return EconomicThrow.NotEnoughFragment; }

}

@conditionProcessor(ConditionType.ReturnTime)
export class ReturnTimeConditionProcessor extends ConditionProcessor {

	protected pJob: PlayerJob;

	protected preprocess() {
		super.preprocess();
		this.pJob = playerMgr().getPlayerData(PlayerJob);
	}

	get consumable(): boolean { return false; }

	get playerValue() { return this.pJob.returnTimes }
	get throwFunc(): Function { return EconomicThrow.NotEnoughReturn; }

}
