import {field} from "../../CoreModule/Data/BaseData";
import {Effect} from "../../BattleModule/Data/Effect";
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {Constructor} from "../../../Utils/BaseContext";
import {HurtMotionGroupId, motionGroupRepo} from "../../BattleModule/Data/MotionGroup";
import {cache} from "../../../Utils/DecoratorUtils";
import {data, StaticData} from "../../CoreModule/Data/StaticData";
import {HeroRace} from "./HeroType";
import {Trait} from "../../BattleModule/Data/Trait";

export enum TargetType {
	Self, // 
	FriendsNotSelf, // 
	Friends, // 
	Enemies, // 
	AllNotSelf, // 
	All // 
}

// 1 => POW, 2 => INT, 3 => 
export type MainParam = 1 | 2 | 3;

export const NormalAttackId = 0;
export const PartAttackId = -1;

@data("Skill")
export class Skill extends StaticData {

	@field
	public freezeTime: number = -1; // -1
	@field
	public boostCost: number = 0; // 0

	@field(Number)
	public subjectMotionId: number = PartAttackId;
	@field(Number)
	public targetMotionId: number = HurtMotionGroupId;

	@field
	public race: HeroRace = HeroRace.None; // 
	@field
	public mainParam: MainParam = 3; // 

	// TODO: 
	@field
	public targetType: TargetType = TargetType.Enemies; // 
	@field(Number)
	public targetCount: number = 1; // 0

	@field([Effect])
	public effects: Effect[] = []; // 
	@field([Trait])
	public traits: Trait[] = []; // 

	@field
	public isPassive: boolean = false; // 
	@field
	public mustHit: boolean = false; // 

	@cache
	public subjectMotion() {
		return motionGroupRepo().getById(this.subjectMotionId);
	}
	@cache
	public targetMotion() {
		return motionGroupRepo().getById(this.targetMotionId);
	}

}

export function skillRepo() {
	return getRepository(SkillRepo);
}

@repository
export class SkillRepo extends BaseRepository<Skill> {

	get clazz(): Constructor<Skill> { return Skill; }

	public normalAttack() {
		return this.getById(NormalAttackId);
	}

}

