import executeInEditMode = cc._decorator.executeInEditMode;
import Color = cc.Color;
import director = cc.director;
import requireComponent = cc._decorator.requireComponent;
import RenderComponent = cc.RenderComponent;
import Director = cc.Director;
const {ccclass, property} = cc._decorator;

@ccclass
@executeInEditMode
@requireComponent(RenderComponent)
export default class ColorAssembler extends cc.Component {

    @property([Color])
    colors: Color[] = [
        null, null, null, null
    ]; // 

    onEnable() { director.once(Director.EVENT_AFTER_DRAW, this.updateColors, this); }

    onDisable() { director.off(Director.EVENT_AFTER_DRAW, this.updateColors, this); this.node[ '_renderFlag'] |= cc[ 'RenderFlow'].FLAG_COLOR; }

    updateColors() {
        const cmp = this.getComponent(cc.RenderComponent);
        if (!cmp) return;
        const _assembler = cmp['_assembler'];
        if (!(_assembler instanceof cc['Assembler2D'])) return;
        const uintVerts = _assembler._renderData.uintVDatas[0];
        if (!uintVerts) return;
        const color = this.node.color;
        const floatsPerVert = _assembler.floatsPerVert;
        const colorOffset = _assembler.colorOffset;
        let count = 0;
        for (let i = colorOffset, l = uintVerts.length; i < l; i += floatsPerVert)
            uintVerts[i] = (this.colors[count++] || color)['_val'];
    }
}
