import requireComponent = cc._decorator.requireComponent;
import VideoPlayer = cc.VideoPlayer;
import menu = cc._decorator.menu;

const {ccclass, property} = cc._decorator;

@ccclass
@requireComponent(VideoPlayer)
@menu("/")
export default class VideoAutoPlay extends cc.Component {

    player: VideoPlayer = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {
        this.player = this.getComponent(VideoPlayer);

        //

        this.player.node.on('ready-to-play', this.play, this);

        //

        this.player.node.on('completed', this.replay, this);

    }

    play() {
        if (this.player) {
            this.player.play();
        }
    }

    replay() {
        this.player.play();//
    }

    // onStopped () {
    //
    //     cc.Component.EventHandler.emitEvents(this.videoPlayerEvent, this, EventType.STOPPED);
    //
    //     this.node.emit('stopped', this);
    //
    // }

    // update (dt) {}
}
