import HeroInfoDisplay from "../HeroInfoDisplay";
import CultivateWindowController from "../CultivateWindowController";
import {playerMgr} from "../../../../PlayerModule/Managers/PlayerManager";
import {PlayerMoney} from "../../../../EconomicModule/Data/PlayerMoney";
import Label = cc.Label;
import HeroCompositingDisplay from "./HeroCompositingDisplay";
import {Hero} from "../../../../HeroModule/Data/Hero";
import {battleScene} from "../../../../../Scenes/BattleScene";

const {ccclass, property} = cc._decorator;

@ccclass
export default class CompositingWindowDisplay extends cc.Component {

    // region Background

    @property(HeroInfoDisplay)
    heroInfo: HeroInfoDisplay = null;
    @property(Label)
    lastTime: Label = null;
    @property(HeroCompositingDisplay)
    mainHero: HeroCompositingDisplay = null;
    @property(HeroCompositingDisplay)
    secondHero: HeroCompositingDisplay = null;

    // endregion

    // region Content

    @property(Label)
    priceValues: Label[] = [];
    @property(Label)
    balanceValues: Label[] = [];

    // endregion

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    private cultivateController: CultivateWindowController = null;

    start () {
        this.cultivateController = battleScene().cultivateWindow;
        this.mainHero.setItem(null);
        this.secondHero.setItem(null);
    }

    // region 

    update (dt) {
        this.updateLabels();
        this.updatePictures();
    }

    updateLabels() {
        this.priceValues[0].string = "1000";
        this.priceValues[1].string = "1";
        this.balanceValues[0].string = "\\L[SShop14]:" + playerMgr().getPlayerData(PlayerMoney).gold.toString();
        this.balanceValues[1].string = "\\L[SShop14]:" + playerMgr().getPlayerData(PlayerMoney).token.toString();
        this.lastTime.string = "16:22";
    }

    updatePictures() {


    }

    protected onEnable() {
        this.heroInfo.node.active = false;
    }

    protected onDisable() {
        this.mainHero.setItem(null);
        this.secondHero.setItem(null);
        this.mainHero.node.active = false;
        this.secondHero.node.active = false;
    }

    // endregion

    onHeroChange() {
        this.cultivateController.refreshHero = true;
    }

    // region 

    selectHero(hero: Hero) {
        if (!this.mainHero.item) {
            this.mainHero.node.active = true;
            this.mainHero.setItem(hero);
            return;
        }
        if (!this.secondHero.item) {
            this.secondHero.node.active = true;
            this.secondHero.setItem(hero);
            return;
        }
    }

    deselectMainHero() {
        this.mainHero.setItem(null);
        this.mainHero.node.active = false;
    }

    deselectSecondHero() {
        this.secondHero.setItem(null);
        this.secondHero.node.active = false;
    }

    compositingBtn() {

        this.onHeroChange();
    }

    // endregion

}
