import HeroInfoDisplay from "../HeroInfoDisplay";
import CultivateWindowController from "../CultivateWindowController";
import {autoUnitConvert} from "../../../../../Utils/NumberUtils";
import {playerMgr} from "../../../../PlayerModule/Managers/PlayerManager";
import {PlayerMoney} from "../../../../EconomicModule/Data/PlayerMoney";
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import Node = cc.Node;
import Label = cc.Label;
import {heroMgr} from "../../../../HeroModule/Managers/HeroManager";
import Button = cc.Button;
import {battleScene} from "../../../../../Scenes/BattleScene";

const {ccclass, property} = cc._decorator;

@ccclass
export default class StarUpWindowDisplay extends cc.Component {

    // region Background

    @property(HeroInfoDisplay)
    heroInfo: HeroInfoDisplay = null;

    // endregion

    // region Content

    @property([Sprite])
    currentStarSprites: Sprite[] = [];
    @property([Sprite])
    nextStarSprites: Sprite[] = [];
    @property(Label)
    tipsValue: Label = null;
    @property([SpriteFrame])
    starPictures: SpriteFrame[] = [];
    @property([Node])
    skillPictureNodes: Node[] = [];
    @property([Node])
    skillLevelNodes: Node[] = [];
    @property(Label)
    fragmentLabel: Label = null;
    @property(Node)
    starUpNode: Node = null;
    starUpSprite: Sprite = null;
    starUpButton: Button = null;
    @property([SpriteFrame])
    upgradePictures: SpriteFrame[] = [];

    // endregion

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    private cultivateController: CultivateWindowController = null;

    start () {
        this.cultivateController = battleScene().cultivateWindow;
        this.starUpSprite = this.starUpNode.getComponent(Sprite);
        this.starUpButton = this.starUpNode.getComponent(Button);
    }

    // region 

    update (dt) {
        if (this.cultivateController.refreshHero) {
            this.heroInfo.refresh();
            this.updateLabels();
            this.updatePictures();
            this.cultivateController.refreshHero = false;
        }
    }

    @autoUnitConvert
    updateLabels() {
        let hero = this.cultivateController.currentHero;
        if (!hero) return;
        this.tipsValue.string = hero.star.level * 20 + "%";
        const pDiamond = playerMgr().getPlayerData(PlayerMoney).diamond;
        if (hero.star.level == 5) {
            this.fragmentLabel.string = pDiamond.toString() + "/0";
            return;
        }
        const diamond = hero.star.costs().diamond.value;
        this.fragmentLabel.string = pDiamond.toString() + "/" + diamond.toString();
    }

    updatePictures() {
        let hero = this.cultivateController.currentHero;
        if (!hero) return;
        this.currentStarSprites.forEach((s, i) => {
            s.spriteFrame = this.starPictures[i < hero.star.level ? 1 : 0];
        });
        this.nextStarSprites.forEach((s, i) => {
            s.spriteFrame = this.starPictures[i < hero.star.level + 1 ? 1 : 0];
        });
        this.skillLevelNodes.forEach((s, i) => {
            s.active = i >= hero.star.level;
        });
        this.skillPictureNodes.forEach((s, i) => {
            s.active = i >= hero.star.level;
        });

        const cond = hero.star.conditions().judge();
        this.starUpButton.interactable = cond;
        this.starUpSprite.spriteFrame = this.upgradePictures[cond ? 0 : 1];
    }

    protected onEnable() {
        this.heroInfo.node.active = true;
        this.heroInfo.refresh();
    }

    // endregion

    onHeroChange() {
        this.cultivateController.refreshHero = true;
    }

    // region 

    starUpBtn() {
        heroMgr().starUp(this.cultivateController.currentHero).then(res => {
            this.onHeroChange();
            this.heroInfo.playAnimation("effectStarUp");
        });
    }

    // endregion

}
