import ProgressBar = cc.ProgressBar;
import Label = cc.Label;
import {loadingMgr, peekLoadingInfo} from "../Managers/LoadingManager";
import BlockInputEvents = cc.BlockInputEvents;

const {ccclass, property} = cc._decorator;

/**
 * 
 */
const WaitDuration: number = 100;
const MaskOpacity = 255;

@ccclass
export default class LoadingMask extends cc.Component {

	@property(ProgressBar)
	progressBar: ProgressBar = null;

	@property(Label)
	loadingLabel: Label;
	@property(Label)
	detailLabel: Label;

	@property(BlockInputEvents)
	inputBlocker: BlockInputEvents;

	start() {
		this.node.opacity = 0;
	}

	update(dt) {
		let info = peekLoadingInfo();
		if (!info) return;

		if (info.isNotStarted) {
			info.markStarted();
			this.loadingLabel.string = info.title;
			this.node.opacity = 0;
			this.blockInput();
		}

		if (info.isFinished) {
			loadingMgr().popLatestLoadingInfo();
			this.node.opacity = 0;
			this.unblockInput();
			// 
			let tmp = peekLoadingInfo();
			if (tmp != null) tmp.startTime -= 1e9;
			return;
		}

		const now = cc.director.getTotalTime();
		if (now - info.startTime > WaitDuration && !info.isFinished)
			this.node.opacity = MaskOpacity;
		if (this.progressBar) this.progressBar.progress = info.currentValue / info.maxValue;
		if (this.loadingLabel) this.loadingLabel.string = info.title;
		if (this.detailLabel) this.detailLabel.string = info.detail;
	}

	private blockInput() {
		this.inputBlocker.enabled = true;
	}

	private unblockInput() {
		this.inputBlocker.enabled = false;
	}
}
