import {BasePlatform, platform} from "./BasePlatform";
import {Platform} from "../Modules/CoreModule/Managers/PlatformManager";
import {MathUtils} from "../Utils/MathUtils";

const OpenidKey = "openid";

@platform('test')
export class TestPlatform extends BasePlatform {

	public constructor(type: Platform) {
		super(type);
	}

	// region 

	/**
	 * Openid
	 */
	public async getOpenid() : Promise<string> {
		let openid = cc.sys.localStorage.getItem(OpenidKey);
		if (!openid) {
			openid = "test_" + MathUtils.randomString()
			cc.sys.localStorage.setItem(OpenidKey, openid);
		}
		return openid;
	}

	/**
	 * 
	 */
	public async getUserInfo() : Promise<any> {
		return {}; // 
	}

	// endregion

	// region 

	// initAds() {}

	protected async doShowAds() : Promise<boolean> {
		if (!await super.doShowAds()) return false;

		return await new Promise((resolve) => {
			alert("2");
			// 2s
			setTimeout(() => resolve(true), 2000);
		});
	}

	// endregion

}
