import Node = cc.Node;

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroPageController extends cc.Component {

    @property([Node])
    pages: Node[] = [];
    @property([Node])
    titles: Node[] = [];

    protected onEnable() {
        this.onStateChange();
    }

    backBtn() {
        this.state = 0;
        this.onStateChange();
    }

    teamBtn() {
        this.state = 1;
        this.onStateChange();
    }

    bagBtn() {
        this.state = 2;
        this.onStateChange();
    }

    private state: number = 0;

    onStateChange() {
        this.pages.forEach((n, i) => { n.active = i == this.state; });
        this.titles.forEach((n, i) => { n.active = i == this.state; });
    }

}
