import requireComponent = cc._decorator.requireComponent;
import Button = cc.Button;
import Sprite = cc.Sprite;
import Node = cc.Node;

const {ccclass, property} = cc._decorator;

@ccclass
@requireComponent(Button)
@requireComponent(Sprite)
export default class ButtonFlash extends cc.Component {

    // @property(Node)
    // mask: Node = null;

    button: Button = null;
    sprite: Sprite = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {
        this.button = this.getComponent(Button);
        this.sprite = this.getComponent(Sprite);
        this.node.on(Node.EventType.TOUCH_START, () => this.lightOn());
        this.node.on(Node.EventType.TOUCH_CANCEL, () => this.lightOff());
        this.node.on(Node.EventType.TOUCH_END, () => this.lightOff());
    }

    // update (dt) { }

    lightOn() {
        this.sprite.dstBlendFactor = cc.macro.ONE;
        // this.mask.active = true;
    }

    lightOff() {
        this.sprite.dstBlendFactor = cc.macro.ONE_MINUS_SRC_ALPHA;
        // this.mask.active = false;
    }
}
