import {SelectableObject} from "../../../CoreModule/Components/SelectableObject";
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import CultivateWindowController from "./CultivateWindowController";
import Node = cc.Node;
import Color = cc.Color;

const {ccclass, property} = cc._decorator;

@ccclass
export default class CultivateSelection extends SelectableObject {


    @property(Sprite)
    picture: Sprite = null;
    @property(Node)
    labelNode: Node = null;
    @property(Node)
    redPoint: Node = null;

    @property([SpriteFrame])
    pictures: SpriteFrame[] = [];

    controller: CultivateWindowController = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    update (dt) {
        this.redPoint.active = false;
    }

    onDeselected(): void {
        this.refresh(false);
    }

    onSelected(): void {
        this.controller.onSelectionCardChange();
        this.refresh(true);
    }

    refresh(select: boolean) {
        this.picture.spriteFrame = this.pictures[select ? 1 : 0];
        this.labelNode.color = select ? new Color(0, 0, 0) : new Color(62, 50, 36);
        this.labelNode.scale = 1 + (select ? 0.07 : 0);
    }

}
