import {BaseManager, getManager, manager} from "../../CoreModule/Managers/BaseManager";
import {EconomicInterface} from "../Common/EconomicCommon";
import {PlayerJob, PlayerJobItem} from "../Data/PlayerJob";
import {handleError} from "../../CoreModule/Managers/AlertManager";

const {ccclass, property} = cc._decorator;

export function economicMgr() {
    return getManager(EconomicManager);
}

@manager
export default class EconomicManager extends BaseManager {

    playerJob: PlayerJob = null;
    jobItems: PlayerJobItem[] = [];

    update(dt: number) {
        super.update(dt);
        if (this.playerJob?.isAutoProfit)
            this.profitAutoCheck();
    }

    // region 

    @handleError
    public async profit(id : number) {
        const jobItem = this.jobItems.find(j => j.jobId == id);
        await EconomicInterface.ProfitJob({id: jobItem.jobId});
        jobItem.profitConditions().process();
        jobItem.profitRewards().invoke();
        jobItem.profit();
    }

    @handleError
    public async upgrade(id : number) {
        const jobItem = this.jobItems.find(j => j.jobId == id);
        if (jobItem.curLevel <= 0) { // 
            await EconomicInterface.UnlockJob({id: jobItem.jobId});
            jobItem.unlockConditions().process();
        } else {
            await EconomicInterface.UpgradeJob({id: jobItem.jobId});
            jobItem.upgradeConditions().process();
        }
        jobItem.upgrade();
    }

    // endregion

    // region 

    private profitAutoCheck() {
        this.jobItems.forEach(j => {
            if (!j.profitConditions().judge()) return;
            j.profitRewards().invoke();
            j.profit();
        })
    }

    // endregion

}
