import Animation = cc.Animation;
import {battleScene} from "../../../Scenes/BattleScene";
import Prefab = cc.Prefab;
import instantiate = cc.instantiate;

const {ccclass, property} = cc._decorator;

@ccclass
export default class BattleAnimationsController extends cc.Component {

    @property([Prefab])
    animationPrefabs: Prefab[] = [];

    private animations: Animation[] = [];
    private speeds: number[] = [];

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {
        // this.animations.forEach(a => this.speeds.push(1));
    }

    update (dt) {
        let speed = battleScene().battleSpeed;
        this.animations.forEach((a, i) => {
            a.getAnimationState(a.defaultClip.name).speed = speed * this.speeds[i];
            if (!a.getAnimationState(a.defaultClip.name).isPlaying) {
                a.node.destroy();
                this.animations.splice(i, 1);
                this.speeds.splice(i, 1);
            }
        })
    }

    play(name: string, time: number = 0, posX: number = 0, posY: number = 0, camp: number = 0) {
        if (!this.animationPrefabs[Animations[name]]) return;
        let node = instantiate(this.animationPrefabs[Animations[name]]);
        node.parent = this.node;
        let ani = node.getComponent(Animation);
        this.animations.push(ani);
        this.speeds.push(time > 0 ? ani.defaultClip["_duration"] / time : 1);
        ani.node.x = posX; ani.node.y = posY;
        ani.node.scaleX *= camp == 0 ? 1 : -1;
        ani.play();
    }
}

enum Animations {
    bossComing = 0,
    attack,
    death,
    recover,
    lightingSelf,
    lightingTarget
}
