import BattleRewardContainer from "./Main/BattleRewardContainer";
import {Reward} from "../../PlayerModule/Data/Reward";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SettlementWindowController extends cc.Component {

    @property(BattleRewardContainer)
    container: BattleRewardContainer = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    open(rewards: Reward[]) {
        if (this.node.active) return;
        this.node.active = true;
        this.container.setItems(rewards);
    }

    quit() {
        this.container.setItems([]);
        this.node.active = false;
    }

}
