import Node = cc.Node;
import {battleScene} from "../../../../Scenes/BattleScene";
import Label = cc.Label;
import {playerMgr, waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import {PlayerBattle} from "../../Data/PlayerBattle";
import SkillItemContainer from "./SkillItemContainer";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PVPWindowController extends cc.Component {

	@property(Node)
	roomsSelectNode: Node = null;
	@property(Node)
	playersSelectNode: Node = null;
	@property(Node)
	heroesSelectNode: Node = null;
	@property(Node)
	rankNode: Node = null;
	@property(Node)
	skillNode: Node = null;

	@property(Node)
	battleChanceNode: Node = null;
	@property(Label)
	battleChance: Label = null;

	fighting: boolean = false;
	heroSelecting: boolean = true;

	protected update(dt: number) {
		this.updateNodes();
		if (this.fighting && battleScene().pvpBattle.isSettled) {
			battleScene().endPVPBattle();
			this.fighting = false;
		}
	}

	updateNodes() {
		this.roomsSelectNode.active = !this.fighting && !battleScene().roomSelected;
		this.playersSelectNode.active = !this.fighting && battleScene().roomSelected;
		this.battleChanceNode.active = !this.fighting;
		this.heroesSelectNode.active = !this.fighting && this.heroSelecting;
		this.rankNode.active = !this.fighting && !this.heroSelecting;
		this.skillNode.active = this.fighting;
	}

	protected onEnable() {
		this.pvpBtn();
		// pvptrue
		if (battleScene().currentPVPSelect) battleScene().heroSelected = true;
		this.drawBattleChance();
	}

	@waitForLogin
	drawBattleChance() {
		const battle = playerMgr().getPlayerData(PlayerBattle)
		this.battleChance.string = battle.battleChance.toString();
	}

	protected onDisable() {
		// pvp
		battleScene().roomSelected = false;
		battleScene().heroSelected = false;
		battleScene().currentPVPRoomIndex = -1;
		battleScene().currentTargetSelect = null;
	}

	pvpBtn() {
		this.heroSelecting = true;
	}

	rankBtn() {
		this.heroSelecting = false;
	}

	confirmRoomBtn() {
		if (battleScene().currentPVPRoomIndex != -1)
			battleScene().roomSelected = true;
	}

	backBtn() {
		battleScene().roomSelected = false;
	}

	confirmBattleBtn() {
		battleScene().startPVPBattle().then(() => {
			this.skillNode.getComponent(SkillItemContainer)
				.setItems(battleScene().pvpBattle.selfBattler()
					.item.skills.filter(s => s.skillId > 1000).map(s => s.skill));
		});
		this.fighting = true;
	}

	refreshBtn() {
		// if (true) {
		//     let items = BattleInterface.GetPVPPlayers();
		//     this.playersSelectNode.getComponent(PlayerCardContainer).setItems();
		// }
	}
}
