import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import {ScrollviewFilter} from "../../../CoreModule/Utils/ScrollviewFilter";
import Node = cc.Node;
import {Hero} from "../../../HeroModule/Data/Hero";
import HeroInBagDisplay from "./HeroInBagDisplay";

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroGroupHeroContainer extends ItemContainer<Hero, HeroInBagDisplay> {

    start() {
    }

    private async setup() {
    }

    onItemsChanged() {
        super.onItemsChanged();
        let nodes: Node[] = [];
        this.itemsComponents.forEach(item => {
            if (item.node.active == true) nodes.push(item.node)
        })
        ScrollviewFilter.filter("vertical", this.container, nodes, 5, this.container.height, 40, 5);
        this.modifyOnClickEvents();
    }

    modifyOnClickEvents() {
        this.itemsComponents.forEach(item => {
            let btn = item.node.getComponentInChildren(Button);
            btn.clickEvents = [];
            let clickEventHandler = new cc.Component.EventHandler();
            clickEventHandler.target = this.node;
            clickEventHandler.component = "HeroGroupHeroContainer";
            clickEventHandler.handler = "onHeroClicked";
            clickEventHandler.customEventData = item.item.nftId;//nftId
            btn.clickEvents.push(clickEventHandler);
        });
    }

    async onHeroClicked(e, nftId: string) {
        battleScene().myTeamFrame.addTeamMember(nftId);
    }
}
import Button = cc.Button;
import {battleScene} from "../../../../Scenes/BattleScene";
import {HeroRace} from "../../../HeroModule/Data/HeroType";
