import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {Constructor} from "../../../Utils/BaseContext";
import {data, StaticData} from "../../CoreModule/Data/StaticData";
import {field} from "../../CoreModule/Data/BaseData";

export enum SpecialPartType {
	Normal, Body, Head, Clothes, Foot, Weapon, Background //,Suit
}

@data("PartType")
export class PartType extends StaticData {

	@field
	public name: string = "";
	@field
	public baseTypeId: number = -1; // ID
	@field
	public special: SpecialPartType = SpecialPartType.Normal;
	@field
	public optional: boolean = false;
	@field([String])
	public slotNames: string[] = [];

	public get isBody() {return this.special == SpecialPartType.Body;}
	public get isHead() {return this.special == SpecialPartType.Head;}
	public get isClothes() {return this.special == SpecialPartType.Clothes;}
	public get isFoot() {return this.special == SpecialPartType.Foot;}
	public get isWeapon() {return this.special == SpecialPartType.Weapon;}
	public get isBackground() {return this.special == SpecialPartType.Background;}

	/**
	 * 
	 */
	public get basePartType() {
		return this.baseTypeId < 0 ? null :
			partTypeRepo().getById(this.baseTypeId);
	}

	/**
	 * 
	 */
	public get subPartTypes() {
		return partTypeRepo().findByBaseTypeId(this.id);
	}
}

export function partTypeRepo() {
	return getRepository(PartTypeRepo);
}

@repository
export class PartTypeRepo extends BaseRepository<PartType> {

	get clazz(): Constructor<PartType> { return PartType; }

	// @ts-ignore
	public findByBaseTypeId(bptId: number): PartType[] {}
	// @ts-ignore
	public getBySpecial(special: SpecialPartType): PartType {}

	/**
	 * PartType
	 */
	public rootPartTypes() {
		return this.findByBaseTypeId(-1);
	}

	/**
	 * 
	 */
	public get body() { return this.getBySpecial(SpecialPartType.Body); }
	public get head() { return this.getBySpecial(SpecialPartType.Head); }
	public get clothes() { return this.getBySpecial(SpecialPartType.Clothes); }
	public get foot() { return this.getBySpecial(SpecialPartType.Foot); }
	public get weapon() { return this.getBySpecial(SpecialPartType.Weapon); }
	public get background() { return this.getBySpecial(SpecialPartType.Background); }
}
