import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import {ScrollviewFilter} from "../../../CoreModule/Utils/ScrollviewFilter";
import {waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import {waitForDataLoad} from "../../../CoreModule/Managers/DataManager";
import SkillItemDisplay from "./SkillItemDisplay";
import {Skill, skillRepo} from "../../../HeroModule/Data/Skill";
import {battleScene} from "../../../../Scenes/BattleScene";
import {PromiseUtils} from "../../../../Utils/PromiseUtils";

const {ccclass, property} = cc._decorator;


@ccclass
export default class SkillItemContainer extends ItemContainer<Skill, SkillItemDisplay> {

    // private isLoad: boolean = false;

    start() {
        this.setup();
    }

    // update (dt) {
    // 	if (!this.isLoad) {
    // 		this.setup();
    // 		if (!this.isLoad) return;
    // 	}
    // }

    @waitForDataLoad
    @waitForLogin
    private async setup() {
        await PromiseUtils.waitFor(() => battleScene().currentPVPSelect != null);
        this.updateSkills();
    }

    onItemsChanged() {
        super.onItemsChanged();
        const nodes = [];
        this.itemsComponents.forEach(item =>
            item.node.active && nodes.push(item.node)
        )
        ScrollviewFilter.filter("vertical", this.container,
            nodes, 5, this.node.height, 10);
    }

    public updateSkills() {
        let skillIds = battleScene().currentPVPSelect.skillIds;
        let skills = skillIds.map(id => skillRepo().getById(id));
        this.setItems(skills);
    }

}
