import Node = cc.Node;
import ScrollView = cc.ScrollView;

export class ScrollviewFilter {

	static filter(type: "horizontal" | "vertical", container: Node, children: Node[], spacing: number, minLength: number, padding: number = 0, step: number = 1){
		let length = -spacing + padding;
		let scroll: ScrollView = null;
		for (let i = container; i.parent ; i = i.parent) {
			let temp = i.getComponent(ScrollView);
			if (temp && temp.content == container) {
				scroll = temp;
				break;
			}
		}
		switch (type){
			case "horizontal":
				for (let i = 0; i < children.length; i++) {
					if (i % step != 0) continue;
					length += children[i].width + spacing;
				}
				if(length > minLength){
					if(length != container.width)
						if (scroll) scroll.scrollToLeft();
						else container.x = (length - minLength) / 2;
					container.width = length;
				}else{
					if (scroll) scroll.scrollToLeft();
					else container.x = 0;
					container.width = minLength;
				}
				break;
			case "vertical":
				for (let i = 0; i < children.length; i++) {
					if (i % step != 0) continue;
					length += children[i].height + spacing;
				}
				if(length > minLength){
					if(length != container.height)
						if (scroll) scroll.scrollToTop();
						else container.y = (minLength - length + container.height) / 2;
					container.height = length;
				}else{
					if (scroll) scroll.scrollToTop();
					else container.y = minLength / 2;
					container.height = minLength;
				}
				break;
		}
	}

}
