
import {ItemDisplay} from "./ItemDisplay";
import {BaseData} from "../Data/BaseData";

const {ccclass, property} = cc._decorator;

@ccclass
export class ItemContainer<T extends BaseData, D extends ItemDisplay<T>> extends cc.Component {

	@property(cc.Node)
	container: cc.Node = null;
	@property(cc.Prefab)
	prefab: cc.Prefab = null;

	public items: T[] = [];
	protected itemsComponents: D[] = [];
	protected itemCount: number = 0;

	public isInclude(item: T) { return true; }

	public setItems(items: T[]){
		this.items = items.filter(item => this.isInclude(item));
		this.onItemsChanged();
	}

	protected onItemsChanged(){
		this.refresh();
	}

	public refresh() {
		this.refreshComponents();
	}
	private refreshComponents() {
		for (let i = 0; i < this.items.length; i++) {
			if (i >= this.itemsComponents.length)
				this.createItemDisplay();
			else
				this.itemsComponents[i].node.active = true;
			this.itemsComponents[i].setItem(this.items[i]);
		}
		for (let i = this.items.length; i < this.itemsComponents.length; i++) {
			this.itemsComponents[i].node.active = false;
			//this.itemsComponents[i].setItem(null);
		}
		this.itemCount = this.items.length;
	}

	private createItemDisplay() {
		const node = cc.instantiate(this.prefab);
		node.parent = this.container;

		const res = node.getComponent(ItemDisplay) as D;
		this.onItemDisplayCreated(res);
		this.itemsComponents.push(res);

		return res;
	}

	protected onItemDisplayCreated(display: D) {

	}
}
