import {get, post} from "../../CoreModule/Common/CoreCommon";

export class PlayerInterface {

	@post("/player/player/login", undefined, false) static Login(d?): any {}
	@post("/player/player/logout") static Logout(d?): any {}
	@get("/player/player_info/get") static GetInfo(d?): any {}
	@post("/player/player_data/get") static GetData(d?): any {}
	@post("/player/player_info/edit") static EditInfo(d?): any {}

}
