@ccclass
export default class JobContainer extends ItemContainer<PlayerJobItem, JobDisplay> {

	// private isLoad: boolean = false;

	start () {
		this.setup();
	}

	// update (dt) {
	// 	if (!this.isLoad) {
	// 		this.setup();
	// 		if (!this.isLoad) return;
	// 	}
	// }

	@waitForDataLoad
	@waitForLogin
	private setup() {
		const playerJob = playerMgr().getPlayerData(PlayerJob);
		const jobs = jobRepo().list;

		this.setItems(jobs.map(j => playerJob.job(j.id)));
		economicMgr().playerJob = playerJob;
		economicMgr().jobItems = jobs.map(j => playerJob.job(j.id));
		// this.isLoad = true;
	}

	onItemsChanged() {
		super.onItemsChanged();
		const nodes = [];
		this.itemsComponents.forEach(item =>
			item.node.active && nodes.push(item.node)
		)
		ScrollviewFilter.filter("vertical", this.container,
			nodes, 5, this.node.height, 170);
	}

}
