import ValueType = cc.ValueType;
import misc = cc.misc;
import Mat4 = cc.Mat4;
import EPSILON = cc.EPSILON;

let _x: number = 0.0;
let _y: number = 0.0;

export interface IVec1Like {
	x: number;
}

/**
 * !#en Representation of 1D vectors and points.
 * !#zh  1D 
 *
 * @class Vec1
 * @extends ValueType
 */
export class Vec1 extends ValueType {
	// deprecated
	static sub   = Vec1.subtract;
	static mul   = Vec1.multiply;
	static scale = Vec1.multiplyScalar;
	static mag   = Vec1.len;
	static squaredMagnitude = Vec1.lengthSqr;
	static div = Vec1.divide;
	/**
	 * !#en Returns the length of this vector.
	 * !#zh 
	 * @method mag
	 * @return {number} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.mag(); // return 14.142135623730951;
	 */
	mag  = Vec1.prototype.len;
	/**
	 * !#en Returns the squared length of this vector.
	 * !#zh 
	 * @method magSqr
	 * @return {number} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.magSqr(); // return 200;
	 */
	magSqr = Vec1.prototype.lengthSqr;
	/**
	 * !#en Subtracts one vector from this. If you want to save result to another vector, use sub() instead.
	 * !#zh  sub() 
	 * @method subSelf
	 * @param {Vec1} vector
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.subSelf(cc.v2(5, 5));// return Vec1 {x: 5, y: 5};
	 */
	subSelf  = Vec1.prototype.subtract;
	/**
	 * !#en Subtracts one vector from this, and returns the new result.
	 * !#zh 
	 * @method sub
	 * @param {Vec1} vector
	 * @param {Vec1} [out] - optional, the receiving vector, you can pass the same Vec1 to save result to itself, if not provided, a new Vec1 will be created
	 * @return {Vec1} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.sub(cc.v2(5, 5));      // return Vec1 {x: 5, y: 5};
	 * var v1 = new Vec1;
	 * v.sub(cc.v2(5, 5), v1);  // return Vec1 {x: 5, y: 5};
	 */
	sub (vector: Vec1, out?: Vec1): Vec1 {
		return Vec1.subtract(out || new Vec1(), this, vector);
	}
	/**
	 * !#en Multiplies this by a number. If you want to save result to another vector, use mul() instead.
	 * !#zh  mul() 
	 * @method mulSelf
	 * @param {number} num
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.mulSelf(5);// return Vec1 {x: 50, y: 50};
	 */
	mulSelf  = Vec1.prototype.multiplyScalar;
	/**
	 * !#en Multiplies by a number, and returns the new result.
	 * !#zh 
	 * @method mul
	 * @param {number} num
	 * @param {Vec1} [out] - optional, the receiving vector, you can pass the same Vec1 to save result to itself, if not provided, a new Vec1 will be created
	 * @return {Vec1} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.mul(5);      // return Vec1 {x: 50, y: 50};
	 * var v1 = new Vec1;
	 * v.mul(5, v1);  // return Vec1 {x: 50, y: 50};
	 */
	mul (num: number, out?: Vec1): Vec1 {
		return Vec1.multiplyScalar(out || new Vec1(), this, num);
	}
	/**
	 * !#en Divides by a number. If you want to save result to another vector, use div() instead.
	 * !#zh  div() 
	 * @method divSelf
	 * @param {number} num
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.divSelf(5); // return Vec1 {x: 2, y: 2};
	 */
	divSelf  = Vec1.prototype.divide;
	/**
	 * !#en Divides by a number, and returns the new result.
	 * !#zh 
	 * @method div
	 * @param {number} num
	 * @param {Vec1} [out] - optional, the receiving vector, you can pass the same Vec1 to save result to itself, if not provided, a new Vec1 will be created
	 * @return {Vec1} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.div(5);      // return Vec1 {x: 2, y: 2};
	 * var v1 = new Vec1;
	 * v.div(5, v1);  // return Vec1 {x: 2, y: 2};
	 */
	div (num: number, out?: Vec1): Vec1 {
		return Vec1.multiplyScalar(out || new Vec1(), this, 1/num);
	}
	/**
	 * !#en Multiplies two vectors.
	 * !#zh 
	 * @method scaleSelf
	 * @param {Vec1} vector
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.scaleSelf(cc.v2(5, 5));// return Vec1 {x: 50, y: 50};
	 */
	scaleSelf = Vec1.prototype.multiply;
	/**
	 * !#en Multiplies two vectors, and returns the new result.
	 * !#zh 
	 * @method scale
	 * @param {Vec1} vector
	 * @param {Vec1} [out] - optional, the receiving vector, you can pass the same Vec1 to save result to itself, if not provided, a new Vec1 will be created
	 * @return {Vec1} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.scale(cc.v2(5, 5));      // return Vec1 {x: 50, y: 50};
	 * var v1 = new Vec1;
	 * v.scale(cc.v2(5, 5), v1);  // return Vec1 {x: 50, y: 50};
	 */
	scale (vector: Vec1, out?: Vec1): Vec1 {
		return Vec1.multiply(out || new Vec1(), this, vector);
	}
	/**
	 * !#en Negates the components. If you want to save result to another vector, use neg() instead.
	 * !#zh  neg() 
	 * @method negSelf
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.negSelf(); // return Vec1 {x: -10, y: -10};
	 */
	negSelf = Vec1.prototype.negate;
	/**
	 * !#en Negates the components, and returns the new result.
	 * !#zh 
	 * @method neg
	 * @param {Vec1} [out] - optional, the receiving vector, you can pass the same Vec1 to save result to itself, if not provided, a new Vec1 will be created
	 * @return {Vec1} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * var v1 = new Vec1;
	 * v.neg(v1);  // return Vec1 {x: -10, y: -10};
	 */
	neg (out?: Vec1): Vec1 {
		return Vec1.negate(out || new Vec1(), this);
	}

	/**
	 * !#en return a Vec1 object with x = 1 and y = 1.
	 * !#zh  Vec1 
	 * @property ONE
	 * @type Vec1
	 * @static
	 */
	static get ONE () { return new Vec1(1) };
	static readonly ONE_R = Vec1.ONE;

	/**
	 * !#en return a Vec1 object with x = 0 and y = 0.
	 * !#zh  x = 0  y = 0  Vec1 
	 * @property {Vec1} ZERO
	 * @static
	 */
	static get ZERO () { return new Vec1(0) };
	/**
	 * !#en return a readonly Vec1 object with x = 0 and y = 0.
	 * !#zh  x = 0  y = 0  Vec1 
	 * @property {Vec1} ZERO_R
	 * @readonly
	 * @static
	 */
	static readonly ZERO_R = Vec1.ZERO;

	/**
	 * !#en return a readonly Vec1 object with x = 1 and y = 0.
	 * !#zh  x = 1  y = 0  Vec1 
	 * @property {Vec1} RIGHT
	 * @static
	 */
	static get RIGHT () { return new Vec1(1) };
	/**
	 * !#en return a Vec1 object with x = 1 and y = 0.
	 * !#zh  x = 1  y = 0  Vec1 
	 * @property {Vec1} RIGHT_R
	 * @static
	 * @readonly
	 */
	static readonly RIGHT_R = Vec1.RIGHT;

	/**
	 * !#zh 
	 * @method clone
	 * @typescript
	 * clone <Out extends IVec1Like> (a: Out): Vec1
	 * @static
	 */
	static clone <Out extends IVec1Like> (a: Out) {
		return new Vec1(a.x);
	}

	/**
	 * !#zh 
	 * @method copy
	 * @typescript
	 * copy <Out extends IVec1Like> (out: Out, a: Out): Out
	 * @static
	 */
	static copy <Out extends IVec1Like> (out: Out, a: Out) {
		out.x = a.x;
		return out;
	}

	/**
	 * !#zh  
	 * @method set
	 * @typescript
	 * set <Out extends IVec1Like> (out: Out, x: number, y: number): Out
	 * @static
	 */
	static set <Out extends IVec1Like> (out: Out, x: number, y: number) {
		out.x = x;
		return out;
	}

	/**
	 * !#zh 
	 * @method add
	 * @typescript
	 * add <Out extends IVec1Like> (out: Out, a: Out, b: Out): Out
	 * @static
	 */
	static add <Out extends IVec1Like> (out: Out, a: Out, b: Out) {
		out.x = a.x + b.x;
		return out;
	}

	/**
	 * !#zh 
	 * @method subtract
	 * @typescript
	 * subtract <Out extends IVec1Like> (out: Out, a: Out, b: Out): Out
	 * @static
	 */
	static subtract <Out extends IVec1Like> (out: Out, a: Out, b: Out) {
		out.x = a.x - b.x;
		return out;
	}

	/**
	 * !#zh 
	 * @method multiply
	 * @typescript
	 * multiply <Out extends IVec1Like> (out: Out, a: Out, b: Out): Out
	 * @static
	 */
	static multiply <Out extends IVec1Like> (out: Out, a: Out, b: Out) {
		out.x = a.x * b.x;
		return out;
	}

	/**
	 * !#zh 
	 * @method divide
	 * @typescript
	 * divide <Out extends IVec1Like> (out: Out, a: Out, b: Out): Out
	 * @static
	 */
	static divide <Out extends IVec1Like> (out: Out, a: Out, b: Out) {
		out.x = a.x / b.x;
		return out;
	}

	/**
	 * !#zh 
	 * @method ceil
	 * @typescript
	 * ceil <Out extends IVec1Like> (out: Out, a: Out): Out
	 * @static
	 */
	static ceil <Out extends IVec1Like> (out: Out, a: Out) {
		out.x = Math.ceil(a.x);
		return out;
	}

	/**
	 * !#zh 
	 * @method floor
	 * @typescript
	 * floor <Out extends IVec1Like> (out: Out, a: Out): Out
	 * @static
	 */
	static floor <Out extends IVec1Like> (out: Out, a: Out) {
		out.x = Math.floor(a.x);
		return out;
	}

	/**
	 * !#zh 
	 * @method min
	 * @typescript
	 * min <Out extends IVec1Like> (out: Out, a: Out, b: Out): Out
	 * @static
	 */
	static min <Out extends IVec1Like> (out: Out, a: Out, b: Out) {
		out.x = Math.min(a.x, b.x);
		return out;
	}


	/**
	 * !#zh 
	 * @method max
	 * @typescript
	 * max <Out extends IVec1Like> (out: Out, a: Out, b: Out): Out
	 * @static
	 */
	static max <Out extends IVec1Like> (out: Out, a: Out, b: Out) {
		out.x = Math.max(a.x, b.x);
		return out;
	}

	/**
	 * !#zh 
	 * @method round
	 * @typescript
	 * round <Out extends IVec1Like> (out: Out, a: Out): Out
	 * @static
	 */
	static round <Out extends IVec1Like> (out: Out, a: Out) {
		out.x = Math.round(a.x);
		return out;
	}

	/**
	 * !#zh 
	 * @method multiplyScalar
	 * @typescript
	 * multiplyScalar <Out extends IVec1Like> (out: Out, a: Out, b: number): Out
	 * @static
	 */
	static multiplyScalar <Out extends IVec1Like> (out: Out, a: Out, b: number) {
		out.x = a.x * b;
		return out;
	}

	/**
	 * !#zh : A + B * scale
	 * @method scaleAndAdd
	 * @typescript
	 * scaleAndAdd <Out extends IVec1Like> (out: Out, a: Out, b: Out, scale: number): Out
	 * @static
	 */
	static scaleAndAdd <Out extends IVec1Like> (out: Out, a: Out, b: Out, scale: number) {
		out.x = a.x + (b.x * scale);
		return out;
	}

	/**
	 * !#zh 
	 * @method distance
	 * @typescript
	 * distance <Out extends IVec1Like> (a: Out, b: Out): number
	 * @static
	 */
	static distance <Out extends IVec1Like> (a: Out, b: Out) {
		return b.x - a.x;
	}

	/**
	 * !#zh 
	 * @method squaredDistance
	 * @typescript
	 * squaredDistance <Out extends IVec1Like> (a: Out, b: Out): number
	 * @static
	 */
	static squaredDistance <Out extends IVec1Like> (a: Out, b: Out) {
		_x = b.x - a.x;
		return _x * _x;
	}

	/**
	 * !#zh 
	 * @method len
	 * @typescript
	 * len <Out extends IVec1Like> (a: Out): number
	 * @static
	 */
	static len <Out extends IVec1Like> (a: Out) {
		return a.x;
	}

	/**
	 * !#zh 
	 * @method lengthSqr
	 * @typescript
	 * lengthSqr <Out extends IVec1Like> (a: Out): number
	 * @static
	 */
	static lengthSqr <Out extends IVec1Like> (a: Out) {
		return a.x * a.x;
	}

	/**
	 * !#zh 
	 * @method negate
	 * @typescript
	 * negate <Out extends IVec1Like> (out: Out, a: Out): Out
	 * @static
	 */
	static negate <Out extends IVec1Like> (out: Out, a: Out) {
		out.x = -a.x;
		return out;
	}

	/**
	 * !#zh  0  Infinity
	 * @method inverse
	 * @typescript
	 * inverse <Out extends IVec1Like> (out: Out, a: Out): Out
	 * @static
	 */
	static inverse <Out extends IVec1Like> (out: Out, a: Out) {
		out.x = 1.0 / a.x;
		return out;
	}

	/**
	 * !#zh  0  0
	 * @method inverseSafe
	 * @typescript
	 * inverseSafe <Out extends IVec1Like> (out: Out, a: Out): Out
	 * @static
	 */
	static inverseSafe <Out extends IVec1Like> (out: Out, a: Out) {
		_x = a.x;

		if (Math.abs(_x) < EPSILON) {
			out.x = 0;
		} else {
			out.x = 1.0 / _x;
		}

		return out;
	}

	/**
	 * !#zh 
	 * @method normalize
	 * @typescript
	 * normalize <Out extends IVec1Like, Vec1Like extends IVec1Like> (out: Out, a: Vec1Like): Out
	 * @static
	 */
	static normalize <Out extends IVec1Like, Vec1Like extends IVec1Like> (out: Out, a: Vec1Like) {
		out.x = 1;
		return out;
	}

	/**
	 * !#zh 
	 * @method dot
	 * @typescript
	 * dot <Out extends IVec1Like> (a: Out, b: Out): number
	 * @static
	 */
	static dot <Out extends IVec1Like> (a: Out, b: Out) {
		return a.x * b.x;
	}

	/**
	 * !#zh  A + t * (B - A)
	 * @method lerp
	 * @typescript
	 * lerp <Out extends IVec1Like> (out: Out, a: Out, b: Out, t: number): Out
	 * @static
	 */
	static lerp <Out extends IVec1Like> (out: Out, a: Out, b: Out, t: number) {
		_x = a.x;
		out.x = _x + t * (b.x - _x);
		return out;
	}

	/**
	 * !#zh 
	 * @method random
	 * @typescript
	 * random <Out extends IVec1Like> (out: Out, scale?: number): Out
	 * @static
	 */
	static random <Out extends IVec1Like> (out: Out, scale?: number) {
		scale = scale || 1.0;
		out.x = Math.random() * scale;
		return out;
	}

	/**
	 * !#zh  1
	 * @method transformMat3
	 * @typescript
	 * transformMat3 <Out extends IVec1Like, MatLike extends IMat3Like> (out: Out, a: Out, mat: IMat3Like): Out
	 * @static
	 */
	static transformMat3 <Out extends IVec1Like, MatLike extends IMat3Like> (out: Out, a: Out, mat: MatLike) {
		_x = a.x;
		let m = mat.m;
		out.x = m[0] * _x + m[3] * _y + m[6];
		return out;
	}

	/**
	 * !#zh  0 1
	 * @method transformMat4
	 * @typescript
	 * transformMat4 <Out extends IVec1Like, MatLike extends IMat4Like> (out: Out, a: Out, mat: MatLike): Out
	 * @static
	 */
	static transformMat4 <Out extends IVec1Like, MatLike extends IMat4Like> (out: Out, a: Out, mat: MatLike) {
		_x = a.x;
		let m = mat.m;
		out.x = m[0] * _x + m[4] * _y + m[12];
		return out;
	}

	/**
	 * !#zh 
	 * @method strictEquals
	 * @typescript
	 * strictEquals <Out extends IVec1Like> (a: Out, b: Out): boolean
	 * @static
	 */
	static strictEquals <Out extends IVec1Like> (a: Out, b: Out) {
		return a.x === b.x;
	}

	/**
	 * !#zh 
	 * @method equals
	 * @typescript
	 * equals <Out extends IVec1Like> (a: Out, b: Out,  epsilon?: number): boolean
	 * @static
	 */
	static equals <Out extends IVec1Like> (a: Out, b: Out,  epsilon = EPSILON) {
		return (
			Math.abs(a.x - b.x) <=
			epsilon * Math.max(1.0, Math.abs(a.x), Math.abs(b.x))
		);
	}

	/**
	 * !#zh 
	 * @method toArray
	 * @typescript
	 * toArray <Out extends IWritableArrayLike<number>> (out: Out, v: IVec1Like, ofs?: number): Out
	 * @static
	 */
	static toArray <Out extends IWritableArrayLike<number>> (out: Out, v: IVec1Like, ofs = 0) {
		out[ofs] = v.x;
		return out;
	}

	/**
	 * !#zh 
	 * @method fromArray
	 * @typescript
	 * fromArray <Out extends IVec1Like> (out: Out, arr: IWritableArrayLike<number>, ofs?: number): Out
	 * @static
	 */
	static fromArray <Out extends IVec1Like> (out: Out, arr: IWritableArrayLike<number>, ofs = 0) {
		out.x = arr[ofs];
		return out;
	}

	/**
	 * @property {Number} x
	 */
	x: number;

	// compatible with vec2
	y: number = 0;

	// compatible with vec3
	// z: number = 0;

	/**
	 * !#en
	 * Constructor
	 * see {{#crossLink "cc/Vec1:method"}}cc.v2{{/crossLink}} or {{#crossLink "cc/p:method"}}cc.p{{/crossLink}}
	 * !#zh
	 *  {{#crossLink "cc/Vec1:method"}}cc.v2{{/crossLink}}  {{#crossLink "cc/p:method"}}cc.p{{/crossLink}}
	 * @method constructor
	 * @param {Number} [x=0]
	 * @param {Number} [y=0]
	 */
	constructor (x: number | Vec1 = 0) {
		super();

		if (x && typeof x === 'object') {
			this.x = x.x || 0;
		} else {
			this.x = x as number || 0;
		}
	}

	/**
	 * !#en clone a Vec1 object
	 * !#zh  Vec1 
	 * @method clone
	 * @return {Vec1}
	 */
	clone (): Vec1 {
		return new Vec1(this.x);
	}

	/**
	 * !#en Sets vector with another's value
	 * !#zh 
	 * @method set
	 * @param {Vec1} newValue - !#en new value to set. !#zh 
	 * @return {Vec1} returns this
	 * @chainable
	 */
	set (newValue: Vec1): this {
		this.x = newValue.x;
		return this;
	}

	/**
	 * !#en Check whether two vector equal
	 * !#zh 
	 * @method equals
	 * @param {Vec1} other
	 * @return {Boolean}
	 */
	equals (other: Vec1): boolean {
		return other && this.x === other.x;
	}

	/**
	 * !#en Check whether two vector equal with some degree of variance.
	 * !#zh
	 * <br/>
	 *  2  true false
	 * @method fuzzyEquals
	 * @param {Vec1} other
	 * @param {Number} variance
	 * @return {Boolean}
	 */
	fuzzyEquals (other: Vec1, variance): boolean {
		return this.x - variance <= other.x && other.x <= this.x + variance;
	}

	/**
	 * !#en Transform to string with vector informations
	 * !#zh 
	 * @method toString
	 * @return {string}
	 */
	toString (): string {
		return "(" +
			this.x.toFixed(2) + ")"
			;
	}

	/**
	 * !#en Calculate linear interpolation result between this vector and another one with given ratio
	 * !#zh 
	 * @method lerp
	 * @param {Vec1} to
	 * @param {Number} ratio - the interpolation coefficient
	 * @param {Vec1} [out] - optional, the receiving vector, you can pass the same Vec1 to save result to itself, if not provided, a new Vec1 will be created
	 * @return {Vec1}
	 */
	lerp (to: Vec1, ratio: number, out?: Vec1): Vec1 {
		const x = this.x;
		out = out || new Vec1();
		out.x = x + (to.x - x) * ratio;
		return out;
	}

	/**
	 * !#en Clamp the vector between from float and to float.
	 * !#zh
	 * <br/>
	 *  max_inclusive  max_inclusive<br/>
	 *  min_inclusive  min_inclusive<br/>
	 * 
	 * @method clampf
	 * @param {Vec1} min_inclusive
	 * @param {Vec1} max_inclusive
	 * @return {Vec1}
	 * @example
	 * var min_inclusive = cc.v2(0, 0);
	 * var max_inclusive = cc.v2(20, 20);
	 * var v1 = cc.v2(20, 20).clampf(min_inclusive, max_inclusive); // Vec1 {x: 20, y: 20};
	 * var v2 = cc.v2(0, 0).clampf(min_inclusive, max_inclusive);   // Vec1 {x: 0, y: 0};
	 * var v3 = cc.v2(10, 10).clampf(min_inclusive, max_inclusive); // Vec1 {x: 10, y: 10};
	 */
	clampf (min_inclusive: Vec1, max_inclusive: Vec1): this {
		this.x = misc.clampf(this.x, min_inclusive.x, max_inclusive.x);
		this.y = misc.clampf(this.y, min_inclusive.y, max_inclusive.y);
		return this;
	}

	/**
	 * !#en Adds this vector.
	 * !#zh 
	 * @method add
	 * @param {Vec1} vector
	 * @param {Vec1} [out]
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.add(cc.v2(5, 5));// return Vec1 {x: 15, y: 15};
	 */
	add (vector: Vec1, out?: Vec1): Vec1 {
		out = out || new Vec1();
		out.x = this.x + vector.x;
		out.y = this.y + vector.y;
		return out;
	}

	/**
	 * !#en Adds this vector. If you want to save result to another vector, use add() instead.
	 * !#zh  add() 
	 * @method addSelf
	 * @param {Vec1} vector
	 * @return {Vec1} returns this
	 * @chainable
	 */
	addSelf (vector: Vec1): this {
		this.x += vector.x;
		this.y += vector.y;
		return this;
	}

	/**
	 * !#en Subtracts one vector from this.
	 * !#zh 
	 * @method subtract
	 * @param {Vec1} vector
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.subSelf(cc.v2(5, 5));// return Vec1 {x: 5, y: 5};
	 */
	subtract (vector: Vec1): this {
		this.x -= vector.x;
		this.y -= vector.y;
		return this;
	}

	/**
	 * !#en Multiplies this by a number.
	 * !#zh 
	 * @method multiplyScalar
	 * @param {number} num
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.multiply(5);// return Vec1 {x: 50, y: 50};
	 */
	multiplyScalar (num: number): this {
		this.x *= num;
		this.y *= num;
		return this;
	}

	/**
	 * !#en Multiplies two vectors.
	 * !#zh 
	 * @method multiply
	 * @param {Vec1} vector
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.multiply(cc.v2(5, 5));// return Vec1 {x: 50, y: 50};
	 */
	multiply (vector: Vec1): this {
		this.x *= vector.x;
		this.y *= vector.y;
		return this;
	}

	/**
	 * !#en Divides by a number.
	 * !#zh 
	 * @method divide
	 * @param {number} num
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.divide(5); // return Vec1 {x: 2, y: 2};
	 */
	divide (num: number): this {
		this.x /= num;
		this.y /= num;
		return this;
	}

	/**
	 * !#en Negates the components.
	 * !#zh 
	 * @method negate
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.negate(); // return Vec1 {x: -10, y: -10};
	 */
	negate (): this {
		this.x = -this.x;
		this.y = -this.y;
		return this;
	}

	/**
	 * !#en Dot product
	 * !#zh 
	 * @method dot
	 * @param {Vec1} [vector]
	 * @return {number} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.dot(cc.v2(5, 5)); // return 100;
	 */
	dot (vector: Vec1): number {
		return this.x * vector.x + this.y * vector.y;
	}

	/**
	 * !#en Cross product
	 * !#zh 
	 * @method cross
	 * @param {Vec1} [vector]
	 * @return {number} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.cross(cc.v2(5, 5)); // return 0;
	 */
	cross (vector: Vec1): number {
		return this.x * vector.y - this.y * vector.x;
	}

	/**
	 * !#en Returns the length of this vector.
	 * !#zh 
	 * @method len
	 * @return {number} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.len(); // return 14.142135623730951;
	 */
	len (): number {
		return Math.sqrt(this.x * this.x + this.y * this.y);
	}

	/**
	 * !#en Returns the squared length of this vector.
	 * !#zh 
	 * @method lengthSqr
	 * @return {number} the result
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.lengthSqr(); // return 200;
	 */
	lengthSqr (): number {
		return this.x * this.x + this.y * this.y;
	}

	/**
	 * !#en Make the length of this vector to 1.
	 * !#zh  1
	 * @method normalizeSelf
	 * @return {Vec1} returns this
	 * @chainable
	 * @example
	 * var v = cc.v2(10, 10);
	 * v.normalizeSelf(); // return Vec1 {x: 0.7071067811865475, y: 0.7071067811865475};
	 */
	normalizeSelf (): Vec1 {
		var magSqr = this.x * this.x + this.y * this.y;
		if (magSqr === 1.0)
			return this;

		if (magSqr === 0.0) {
			return this;
		}

		var invsqrt = 1.0 / Math.sqrt(magSqr);
		this.x *= invsqrt;
		this.y *= invsqrt;

		return this;
	}

	/**
	 * !#en
	 * Returns this vector with a magnitude of 1.<br/>
	 * <br/>
	 * Note that the current vector is unchanged and a new normalized vector is returned. If you want to normalize the current vector, use normalizeSelf function.
	 * !#zh
	 * <br/>
	 * <br/>
	 *  normalizeSelf 
	 * @method normalize
	 * @param {Vec1} [out] - optional, the receiving vector, you can pass the same Vec1 to save result to itself, if not provided, a new Vec1 will be created
	 * @return {Vec1} result
	 * var v = cc.v2(10, 10);
	 * v.normalize();   // return Vec1 {x: 0.7071067811865475, y: 0.7071067811865475};
	 */
	normalize (out?: Vec1): Vec1 {
		out = out || new Vec1();
		out.x = this.x;
		out.y = this.y;
		out.normalizeSelf();
		return out;
	}

	/**
	 * !#en Get angle in radian between this and vector.
	 * !#zh 
	 * @method angle
	 * @param {Vec1} vector
	 * @return {number} from 0 to Math.PI
	 */
	angle (vector: Vec1): number {
		var magSqr1 = this.magSqr();
		var magSqr2 = vector.magSqr();

		if (magSqr1 === 0 || magSqr2 === 0) {
			console.warn("Can't get angle between zero vector");
			return 0.0;
		}

		var dot = this.dot(vector);
		var theta = dot / (Math.sqrt(magSqr1 * magSqr2));
		theta = misc.clampf(theta, -1.0, 1.0);
		return Math.acos(theta);
	}

	/**
	 * !#en Get angle in radian between this and vector with direction.
	 * !#zh 
	 * @method signAngle
	 * @param {Vec1} vector
	 * @return {number} from -MathPI to Math.PI
	 */
	signAngle (vector: Vec1): number {
		let angle = this.angle(vector);
		return this.cross(vector) < 0 ? -angle : angle;
	}

	/**
	 * !#en rotate
	 * !#zh 
	 * @method rotate
	 * @param {number} radians
	 * @param {Vec1} [out] - optional, the receiving vector, you can pass the same Vec1 to save result to itself, if not provided, a new Vec1 will be created
	 * @return {Vec1} the result
	 */
	rotate (radians: number, out?: Vec1): Vec1 {
		out = out || new Vec1();
		out.x = this.x;
		out.y = this.y;
		return out.rotateSelf(radians);
	}

	/**
	 * !#en rotate self
	 * !#zh 
	 * @method rotateSelf
	 * @param {number} radians
	 * @return {Vec1} returns this
	 * @chainable
	 */
	rotateSelf (radians: number): Vec1 {
		var sin = Math.sin(radians);
		var cos = Math.cos(radians);
		var x = this.x;
		this.x = cos * x - sin * this.y;
		this.y = sin * x + cos * this.y;
		return this;
	}

	/**
	 * !#en Calculates the projection of the current vector over the given vector.
	 * !#zh  vector 
	 * @method project
	 * @param {Vec1} vector
	 * @return {Vec1}
	 * @example
	 * var v1 = cc.v2(20, 20);
	 * var v2 = cc.v2(5, 5);
	 * v1.project(v2); // Vec1 {x: 20, y: 20};
	 */
	project (vector: Vec1): Vec1 {
		return vector.multiplyScalar(this.dot(vector) / vector.dot(vector));
	}

	/**
	 * Transforms the Vec1 with a mat4. 3rd vector component is implicitly '0', 4th vector component is implicitly '1'
	 * @method transformMat4
	 * @param {Mat4} m matrix to transform with
	 * @param {Vec1} [out] the receiving vector, you can pass the same Vec1 to save result to itself, if not provided, a new Vec1 will be created
	 * @returns {Vec1} out
	 */
	transformMat4 (m: Mat4, out?: Vec1): Vec1 {
		out = out || new Vec1();
		Vec1.transformMat4(out, this, m);
		return out;
	}

	/**
	 * Returns the maximum value in x, y.
	 * @method maxAxis
	 * @returns {number}
	 */
	maxAxis (): number {
		return Math.max(this.x, this.y);
	}
}
