import menu = cc._decorator.menu;
import requireComponent = cc._decorator.requireComponent;
import Skeleton = sp.Skeleton;
import Animation = cc.Animation;

const {ccclass, property} = cc._decorator;

@ccclass
@requireComponent(Animation)
@requireComponent(Skeleton)
@menu("/")
export default class SkeletonAnimationBridge extends cc.Component {

    static SkeletonAnimationName: string = "attack";

    animation: Animation = null;
    skeleton: Skeleton = null;

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.animation = this.getComponent(Animation);
        this.skeleton = this.getComponent(Skeleton);
        this.createCallback();
    }

    start () {

    }

    update (dt) {
        this.skeleton.timeScale = this.animation.getAnimationState(this.animation.defaultClip.name).speed;
    }

    createCallback() {
        this.animation.on("play", this.startCallback, this);
    }

    startCallback() {
        this.skeleton.clearTracks();
        this.skeleton.setAnimation(0, SkeletonAnimationBridge.SkeletonAnimationName, false);
    }
}
