import {DynamicData} from "../../CoreModule/Data/DynamicData";
import {Web3NFTData} from "../../NFTModule/Utils/Web3";
import {ChainId, DefaultBt1, DefaultBt2, DefaultChainId} from "../../NFTModule/Managers/NFTManager";
import {qualityRepo, QualityType} from "./Quality";
import {Part, partRepo} from "./Part";
import {heroTypeRepo} from "./HeroType";
import {BreakData, LevelData, StarData, StrengthenData} from "./HeroSystem";
import {BaseData, dataPK, field} from "../../CoreModule/Data/BaseData";
import {paramRepo, ParamType} from "./Param";
import {MathUtils} from "../../../Utils/MathUtils";
import {IEntity} from "../../BattleModule/Data/Battler";
import {configMgr} from "../../CoreModule/Managers/ConfigManager";
import {HeroSystemConfig} from "../Configs/HeroSystemConfig";
import {SpecialPartType} from "./PartType";

export enum HeroState {
	NotCreated, Created, Destroyed
}

export enum RestraintType {
	// Red: , Green: Blue: 
	// 0  1, 1  2, 2  0
	// (x + 1) % 3 == y => x  y
	// (x + 2) % 3 == y => x  y 
	Red, Green, Blue
}

export const SuitPattern = [
	[[1,1,1,1,1]],[[2,1,1,1]],[[2,2,1],[3,1,1]],[[4,1],[3,2]],[[5]]
]

export class PartSetting extends BaseData {

	@field(Number)
	public partId: number;
	@field
	public scale: number = 1;

	private _part;
	public part() {
		return this._part ||= partRepo().getById(this.partId);
	}

	/**
	 * 
	 */
	public static create(partId: number, scale: number = 1) {
		const res = new PartSetting();
		res.partId = partId;
		res.scale = scale;
		return res;
	}
}

export class Hero extends DynamicData implements IEntity {

	@field @dataPK
	public nftId: string = "";

	@field
	public bt1: number = DefaultBt1;
	@field
	public bt2: number = DefaultBt2;
	@field
	public chainId: ChainId = DefaultChainId; // ID
	@field(Number)
	public heroTypeId: number;

	@field(String)
	public creatorAccount: string;
	@field(String)
	public ownerAccount: string;
	@field(String)
	public name: string;
	@field(String)
	public avatarUrl: string;
	@field(String)
	public description: string;

	@field(LevelData)
	public level: LevelData;
	@field(BreakData)
	public break: BreakData;
	@field(StrengthenData)
	public strengthen: StrengthenData;
	@field(StarData)
	public star: StarData;

	@field(Number)
	public quality: QualityType;
	@field
	public restraint: RestraintType = RestraintType.Red;
	@field
	public variation: boolean = false;
	@field(Number)
	public rank: number;

	@field([Number])
	public params: number[] = [];
	@field([Number])
	public addedParams: number[] = [];

	@field([Number])
	public skillIds: number[] = [];
	// @field([Trait])
	// public traits: Trait[] = [];

	@field([Number])
	public suitPattern: number[] = [];
	@field([PartSetting])
	public partSettings: PartSetting[] = [];
	@field(Number)
	public createdAt: number;

	@field
	public price: number = 0;

	@field
	public state: HeroState = HeroState.NotCreated;

	@field(Object)
	public rawData: Web3NFTData;

	onCreated() {
		super.onCreated();
		this.initHeroSystems();
	}

	// region 

	/**
	 * 
	 */
	public get heroType() {
		return heroTypeRepo().getById(this.heroTypeId);
	}

	/**
	 * 
	 */
	public get skeleton() {
		return this.heroType?.defaultSkeleton;
	}

	/**
	 * 
	 */
	private _parts: Part[];
	public get parts() {
		return this._parts ||= this.partSettings.map(
			ps => partRepo().getById(ps.partId)) // DataUtils.part(pid, "id"));
	}

	/**
	 * 
	 */
	public get bodyPart() {
		return this.parts.find(p => p.partType.special === SpecialPartType.Body)
	}
	public get weaponPart() {
		return this.parts.find(p => p.partType.special === SpecialPartType.Weapon)
	}

	/**
	 * 
	 */
	public get race() {return this.heroType.race; }

	// endregion

	// region 

	/**
	 * 
	 */
	public get qualityData() {
		return qualityRepo().getById(this.quality);
	}

	// endregion

	// region 

	/**
	 * 
	 */
	private initHeroSystems() {
		this.level ||= new LevelData(null, this);
		this.break ||= new BreakData(null, this);
		this.strengthen ||= new StrengthenData(null, this);
		this.star ||= new StarData(null, this);
	}

	/**
	 * 
	 */
	public get battlePoint() {
		return paramRepo().find().reduce((res, p) =>
			res + this.param(p.id) * p.battlePoint, 0);
	}

	/**
	 * 
	 */
	private _heroSystems;
	protected get heroSystems() {
		return this._heroSystems ||= [
			this.break, this.strengthen, this.star
		]
	}

	/**
	 * 
	 */
	public param(index: ParamType): number {
		const params = paramRepo().find();
		const param = paramRepo().getById(index);
		const bonusParams = params.filter(p => p.bonus.includes(index));

		const val = this.heroSystems.reduce(
			(res, p) => p.calc(res, index),
			this.baseParam(index)
		);
		const bonus = bonusParams.reduce((sum, p) =>
			sum * this.param(p.id), 1);

		let res = val * bonus;
		if (!param.isPercent) res = Math.round(res);
		return MathUtils.clamp(res, param.min, param.max);
	}

	/**
	 * 
	 */
	get battleLevel(): number {
		return this.level.level
	}

	/**
	 * 
	 */
	get battleParams(): number[] {
		const params = paramRepo().find();
		return params.map(p => this.param(p.id));
	}

	/**
	 * 
	 */
	public baseParam(index: ParamType) {
		const param = paramRepo().getById(index);
		let base = this.params[index] || param.default_;

		const config = configMgr().config(HeroSystemConfig).paramCalc;

		if (index in config) {
			const level = this.level.calc(0, index);
			const pow = this.pow, agi = this.agi, int = this.int;
			try {
				base += eval(config[index]);
			} catch (e) {
				console.error("", index, config, {
					level, pow, agi, int
				});
			}
		}

		// TODO: 
		// let add = this.addedParams[index + 1] || (param.isPercent ? 100 : 0);
		// if (param.isPercent) add = base * (add / 100 - 1);

		return base //+ add;
	}

	/**
	 * 
	 */
	public get cp() { return this.param(ParamType.CP) }
	public get cpb() { return this.param(ParamType.CPB) }
	public get hid() { return this.param(ParamType.HID) }
	public get pow() { return this.param(ParamType.POW) }
	public get agi() { return this.param(ParamType.AGI) }
	public get int() { return this.param(ParamType.INT) }
	public get powb() { return this.param(ParamType.POWB) }
	public get agib() { return this.param(ParamType.AGIB) }
	public get intb() { return this.param(ParamType.INTB) }
	public get atk() { return this.param(ParamType.ATK) }
	public get cri() { return this.param(ParamType.CRI) }
	public get dmg() { return this.param(ParamType.DMG) }
	public get atkb() { return this.param(ParamType.ATKB) }
	public get crib() { return this.param(ParamType.CRIB) }
	public get dmgb() { return this.param(ParamType.DMGB) }
	public get def() { return this.param(ParamType.DEF) }
	public get avo() { return this.param(ParamType.AVO) }
	public get eva() { return this.param(ParamType.EVA) }
	public get defb() { return this.param(ParamType.DEFB) }
	public get avob() { return this.param(ParamType.AVOB) }
	public get evab() { return this.param(ParamType.EVAB) }
	public get mhp() { return this.param(ParamType.MHP) }
	public get spd() { return this.param(ParamType.SPD) }
	public get hit() { return this.param(ParamType.HIT) }
	public get mhpb() { return this.param(ParamType.MHPB) }
	public get spdb() { return this.param(ParamType.SPDB) }
	public get hitb() { return this.param(ParamType.HITB) }
	public get ab1() { return this.param(ParamType.AB1) }
	public get ab2() { return this.param(ParamType.AB2) }
	public get ab3() { return this.param(ParamType.AB3) }
	public get ab4() { return this.param(ParamType.AB4) }
	public get bp() { return this.param(ParamType.BP) }
	public get bpb() { return this.param(ParamType.BPB) }

	// endregion

	// region 

	/**
	 * 
	 */
	public static create(account: string, heroTypeId: number,
											 partInfos: PartSetting[], name: string) {
		const res = new Hero();
		res.nftId = "LOC_" + MathUtils.randomString();
		res.creatorAccount = res.ownerAccount = account;
		res.heroTypeId = heroTypeId;
		res.partSettings = partInfos;
		res.name = name;

		return res;
	}

	public static createRandomQuality() {

	}

	// endregion

}
