import {SelectableObject} from "../../../CoreModule/Components/SelectableObject";
import MainMenuController from "./MainMenuController";

const {ccclass, property} = cc._decorator;

@ccclass
export default class MainMenuItemSelection extends SelectableObject {

    menu: MainMenuController = null;

    //region 

    onDeselected (): void {

    }

    onSelected (): void {
        this.menu?.onSelectChanged(this.selectableGroup.indexOf(this));
    }

    //endregion

}
