import {ItemContainer} from "../../../../Modules/CoreModule/Components/ItemContainer";
import {HeroType, heroTypeRepo} from "../../../../Modules/HeroModule/Data/HeroType";
import HeroTypeDisplay from "../ItemPrefabs/HeroTypeDisplay";
import HeroTypeSelection from "../ItemPrefabs/HeroTypeSelection";
import HeroTypeSelectionGroup from "./HeroTypeSelectionGroup";
import PartsContainer from "./PartsContainer";
import {waitForLogin} from "../../../../Modules/PlayerModule/Managers/PlayerManager";
import {waitForDataLoad} from "../../../../Modules/CoreModule/Managers/DataManager";
import {ScrollviewFilter} from "../../../../Modules/CoreModule/Utils/ScrollviewFilter";
import Node = cc.Node;

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroTypeContainer extends ItemContainer<HeroType, HeroTypeDisplay> {

	@property(PartsContainer)
	partsContainer: PartsContainer = null;

	selectionGroup: HeroTypeSelectionGroup = null;

	//region 

	@waitForDataLoad
	protected start() {
		this.setItems(heroTypeRepo().list);
	}

	protected update(dt: number) {
		this.itemsComponents.forEach(i => i.node.active ? i.size = this.container.width / this.items.length : null);
		// let nodes : Node[] = [];
		// this.itemsComponents.forEach(item => { if(item.node.active == true) nodes.push(item.node) })
		// ScrollviewFilter.filter("horizontal", this.container, nodes, 10, this.node.width);
	}

	private updateSelectionGroup(){
		this.selectionGroup = new HeroTypeSelectionGroup(false)
		this.itemsComponents.forEach(item => {
			let selection = item.node.getComponent(HeroTypeSelection)
			selection.display = selection.getComponent(HeroTypeDisplay)
			selection.container = this;
			this.selectionGroup.addElement(selection);
		})
	}

	//endregion

	//region 

	onItemsChanged() {
		super.onItemsChanged();
		this.updateSelectionGroup();
	}

	async onSelectChanged(item: HeroTypeDisplay){
		this.partsContainer.currentHeroType = item.item;
		await this.partsContainer.onHeroTypeChange();
		await this.partsContainer.onTypeChange();
	}

	//endregion

}
