import {BaseManager, getManager, manager} from "../../CoreModule/Managers/BaseManager";
import {CopyInterface} from "../CopyCommon";
import {PlayerCopy} from "../Data/PlayerCopy";
import {DataLoader} from "../../CoreModule/Utils/DataLoader";
import {copyRepo, CopyType} from "../Data/Copy";
import {playerMgr} from "../../PlayerModule/Managers/PlayerManager";
import {PlayerJob} from "../../EconomicModule/Data/PlayerJob";

export function copyMgr() {
	return getManager(CopyManager)
}

@manager
class CopyManager extends BaseManager {

	/**
	 * 
	 */
	public getMainPlayerCopy() {
		return playerMgr().getPlayerData(PlayerCopy).mainCopy();
	}

	public getMainPlayerCopySettings() {
		return playerMgr().getPlayerData(PlayerCopy)?.mainSetting();
	}

	public getPlayerCopy() {
		return playerMgr().getPlayerData(PlayerCopy);
	}
}
