import {Action} from "../Data/Action";
import {Battle, BattleState, BattleType, PlayerCamp} from "../Data/Battle";
import {BattleInterface} from "../BattleCommon";
import {DataLoader} from "../../CoreModule/Utils/DataLoader";
import {BattlerState, BonusParamType} from "../Data/Battler";
import {PromiseUtils} from "../../../Utils/PromiseUtils";
import {copyMgr} from "../../CopyModule/Managers/CopyManager";
import {copyRepo} from "../../CopyModule/Data/Copy";
import {playerMgr} from "../../PlayerModule/Managers/PlayerManager";
import {PlayerCopy} from "../../CopyModule/Data/PlayerCopy";
import {RewardGroup} from "../../PlayerModule/Data/Reward";

const SelfCamp = PlayerCamp; // 

export function battleProcessor(type: BattleType) {
	return (clazz) => {
		BattleProcessor.processorSettings[type] = clazz;
	}
}

export function createProcessor(battle: Battle): BattleProcessor {
	const clazz = BattleProcessor.processorSettings[battle.type];
	return clazz ? new clazz(battle) : null;
}

/**
 * 
 */
export abstract class BattleProcessor {

	public static processorSettings = {};

	public battle: Battle;
	public resultBattle: Battle;

	/**
	 * 
	 */
	public get isResult() { return this.battle.winner >= 0; }
	public get isWin() { return this.battle.winner == SelfCamp; }
	public get isLose() { return this.battle.winner == 1 - SelfCamp; }

	protected constructor(battle: Battle) {
		this.battle = battle;

		this.battle.onActionStartListener =
			a => this.onActionStart(a);
		this.battle.onActionEndListener =
			a => this.onActionEnd(a);
		this.battle.onBattleEndListener =
			w => this.onBattleEnd(w);
	}

	// region 

	/**
	 * 
	 */
	public start() {
		this.battle.start();

		this.onStarted();
	}

	/**
	 * 
	 */
	public terminate() {
		this.battle.terminate();

		this.onTerminated();
	}

	// endregion

	// region 

	/**
	 * 
	 */
	protected onStarted() { }

	/**
	 * 
	 */
	protected onTerminated() {
		this.processRewards();
	}

	/**
	 * 
	 */
	protected onActionStart(action: Action) { }

	/**
	 * 
	 */
	protected onActionEnd(action: Action) { }

	/**
	 * 
	 */
	protected onBattleEnd(winner) { }

	// endregion

	// region 

	/**
	 * 
	 */
	public rewards(): RewardGroup { return null; }

	/**
	 * 
	 */
	public processRewards() {
		if (!this.isResult) return;
		const rewards = this.rewards();
		if (!rewards) return;
		// 
		rewards.gold.value *= this.battle.campBonusParam(
			SelfCamp, BonusParamType.Gold);
		rewards.invoke();
	}

	// endregion


	// region 

	// endregion

	public update(dt) {
		this.updateBattle(dt);
	}

	protected updateBattle(dt) {
		this.battle.update(dt);
		// this.printBattle();
	}

	public printBattle() {
		console.log(`=== Battle ${this.battle.battleId} ===`);
		console.log("state: ", BattleState[this.battle.state]);
		console.log(
			"elapsedTime: ", this.battle.elapsedTime,
			"actionTime: ", this.battle.actionTime);

		console.log("== battlers ==");
		console.log("camp", "pos", "state", "hp", "action", "motions");
		this.battle.battlers.forEach(b =>
			console.log(b.camp, b.pos, BattlerState[b.state], b.hp, b.action, b.motions)
		)

		console.log("== actions ==");
		console.log("time", "subject", "target", "skillId");
		this.battle.actions.forEach(a =>
			console.log(a.time, a.subject, a.target, a.skillId)
		);
	}

}

/**
 * 
 */
abstract class RoundBattleProcessor extends BattleProcessor {

	/**
	 * 
	 */
	public get round() { return this.battle.actionTime; }

	/**
	 * 
	 */
	// protected isNextRound() {
	// 	// updateAction
	// 	return !this.battle.curAction;
	// }

	protected updateBattle(dt: number) {
		// const next = this.isNextRound();
		super.updateBattle(dt);
	}

}

@battleProcessor(BattleType.Main)
export class MainBattleProcessor extends RoundBattleProcessor {

	/**
	 * 
	 */
	public rewards() {
		const copy = copyRepo().main();
		const level = copy.level(this.battle.level);
		return level.calcRewards(this.isWin, this.battle.isFirst);
	}

	protected async onActionEnd(action: Action) {
		super.onActionEnd(action);

		// 
		// const res = await BattleInterface.NextTest({battleId: this.battle.battleId});
		const res = await PromiseUtils.waitForRetry(() => BattleInterface.StartMainBattleAction({battleId: this.battle.battleId}));
		this.resultBattle = DataLoader.load(Battle, res["battle"]);
		const nextAction = DataLoader.load(Action, res["action"]);

		this.battle.setActionTime(this.resultBattle.actionTime);
		this.battle.addAction(nextAction);

		// TODO: Battle
		// const res = await networkMgr().request(
		// 	BattleInterface.StartMainBattleAction, { action });
		// this.resultBattle = DataLoader.load(Battle, res["battle"]);
	}

	protected async onBattleEnd(winner) {
		super.onBattleEnd(winner);

		// 
		const res = await BattleInterface.EndMainBattle({battleId: this.battle.battleId});
		// copyMgr().getMainPlayerCopy().curLevel++;
		// copyMgr().getMainPlayerCopy().maxLevel =
		// 	Math.max(copyMgr().getMainPlayerCopy().curLevel, copyMgr().getMainPlayerCopy().maxLevel);
		this.resultBattle = DataLoader.load(Battle, res["battle"]);
		this.terminate();
	}

	/**
	 * 
	 */
	protected onTerminated() {
		super.onTerminated();
		const pCopy = playerMgr().getPlayerData(PlayerCopy);
		if (this.isWin) pCopy.mainCopy().next();
		if (this.isLose) pCopy.mainCopy().prev();
	}
}

@battleProcessor(BattleType.Copy)
export class CopyBattleProcessor extends BattleProcessor {
	protected onActionEnd(action: Action) {
		super.onActionEnd(action);
		if (this.battle.actions.find(a => a.time == action.time))
			this.battle.nextRound();
	}
}

@battleProcessor(BattleType.Boss)
export class BossBattleProcessor extends CopyBattleProcessor {

}

@battleProcessor(BattleType.PVP)
export class PVPBattleProcessor extends BattleProcessor {

	protected async onActionEnd(action: Action) {
		super.onActionEnd(action);

		const res = await PromiseUtils.waitForRetry(() => BattleInterface.StartPVPAction({battleId: this.battle.battleId}));
		this.resultBattle = DataLoader.load(Battle, res["battle"]);
		const nextAction = DataLoader.load(Action, res["action"]);

		this.battle.setActionTime(this.resultBattle.actionTime);
		this.battle.addAction(nextAction);

	}

	protected async onBattleEnd(winner) {
		super.onBattleEnd(winner);

		const res = await BattleInterface.EndPVP({battleId: this.battle.battleId});
		this.resultBattle = DataLoader.load(Battle, res["battle"]);
		this.terminate();
	}

	/**
	 * 
	 */
	protected onTerminated() {
		super.onTerminated();

	}

}

