import BattleDisplay from "../BattleDisplay";
import {battleScene} from "../../../../Scenes/BattleScene";
import PVPBattlerState from "./PVPBattlerState";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PVPBattleDisplay extends BattleDisplay {

    isSettled: boolean = false;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {
        // if (this.isStarted) return;
        super.start();
    }

    // region 

    selfBattler() {
        return this.battlers.find(b => b.camp == 0);
    }

    opponentBattler() {
        return this.battlers.find(b => b.camp == 1);
    }

    // endregion

    // region 

    @property([PVPBattlerState])
    battlerStates: PVPBattlerState[] = [];

    refresh() {
        this.isSettled = false;
        if (!this.item) return;
        super.refresh();
        this.setupHeroes();
    }

    setupHeroes() {
        this.battlerStates.forEach((b, i) => b.setItem(this.battlers[i].item));
    }

    // endregion

    // region 

    protected update(dt: number) {
        if (!this.item) return;
        super.update(dt);
        this.judgeCopySettlement();
    }

    judgeCopySettlement() {
        if (this.item.winner != 0 || this.isSettled) return;
        // let r = this.item.result.rewards;
        // console.log(123456, r);
        battleScene().settlementWindow.open([]);
        this.isSettled = true;
    }

    // endregion

}
