import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import {ScrollviewFilter} from "../../../CoreModule/Utils/ScrollviewFilter";
import {Hero} from "../../../HeroModule/Data/Hero";
import HeroInBagDisplay from "./HeroInBagDisplay";
import {playerMgr, waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import {waitForDataLoad} from "../../../CoreModule/Managers/DataManager";
import {heroMgr} from "../../../HeroModule/Managers/HeroManager";
import {HeroRace} from "../../../HeroModule/Data/HeroType";
import Node = cc.Node;

const {ccclass, property} = cc._decorator;

@ccclass
export default class HeroInBagContainer extends ItemContainer<Hero, HeroInBagDisplay> {

    private totalHeroes: Hero[] = [];
    private currentRace: HeroRace;

    start() {
        this.setup().then();
    }

    @waitForDataLoad
    @waitForLogin
    private async setup() {
        const acc = playerMgr().player.account;
        this.totalHeroes = (await heroMgr().getListFromServer())
          .filter(h => h.ownerAccount == acc);
        this.currentRace = undefined;
        this.onHeroRaceChange();
    }

    onItemsChanged() {
        super.onItemsChanged();
        let nodes : Node[] = [];
        this.itemsComponents.forEach(item => { if(item.node.active == true) nodes.push(item.node) })
        ScrollviewFilter.filter("vertical", this.container, nodes, 5, this.container.height, 170, 5);
    }


    /**
     * 
     */
    setHeroBagFilter(e, d) {
        this.currentRace = d == 'undefined' ? undefined : parseInt(d);
        this.onHeroRaceChange();
    }

    onHeroRaceChange() {
        let items = this.currentRace ? this.totalHeroes.filter(h => h.race == this.currentRace) : this.totalHeroes;
        this.setItems(items);
    }
}
