import {ItemDisplay} from "../../../CoreModule/Components/ItemDisplay";
import Label = cc.Label;
import Node = cc.Node;
import Button = cc.Button;
import Sprite = cc.Sprite;
import ProgressBar = cc.ProgressBar;
import SpriteFrame = cc.SpriteFrame;
import {autoUnitConvert} from "../../../../Utils/NumberUtils";
import {DateUtils} from "../../../../Utils/DateUtils";
import {Skill} from "../../../HeroModule/Data/Skill";
import {BattleInterface} from "../../BattleCommon";

const {ccclass, property} = cc._decorator;


@ccclass
export default class SkillItemDisplay extends ItemDisplay<Skill> {

    @property(Label)
    boostCostLabel: Label = null;
    @property(Label)
    boostTipsLabel: Label = null;
    @property(Label)
    nameLabel: Label = null;
    @property(Label)
    infoLabel: Label = null;
    @property(Label)
    coldTimeLabel: Label = null;
    @property(Label)
    levelLabel: Label = null;

    @property(Button)
    private boostButton: Button = null;

    @property(ProgressBar)
    coldTimeBar: ProgressBar = null;

    // onLoad () {}

    start() { }

    // region 

    private get restSecond() {
        return 1;
    }

    private get restSecondRate() {
        return 1;
    }

    public refresh() {
        super.refresh();

        this.drawLabels();
    }

    private drawLabels() {
        this.nameLabel.string = "\\L[SKILL_" + this.item.id + "]";
        this.infoLabel.string = "skill info";
        this.levelLabel.string = "";
    }

    private get canBoost() {
        return true;
    }

    // endregion

    // region 

    public async upgradeBtn() {
        await BattleInterface.StartTest()
        await BattleInterface.BoostPVP({battleId: "5", skillId: this.item.id});
    }

    // endregion

    // region 

    update(dt) {
        if (!this.item) return;

        this.updateLabels();
        this.updateNodes();
    }

    @autoUnitConvert
    private updateLabels() {
        this.boostCostLabel.string = this.item.boostCost.toString();
        this.boostTipsLabel.string = "\\L[SBattle23]";
        this.coldTimeLabel.string = "0";
    }

    private updateNodes() {
        this.boostButton.interactable = this.canBoost;
        this.coldTimeBar.progress = this.restSecondRate;
    }

    // endregion

}
