import {BaseData, field, PK, pkType} from "../../CoreModule/Data/BaseData";
import {PlayerData} from "../../PlayerModule/Data/Player";
import {playerData} from "../../PlayerModule/Managers/PlayerManager";
import {copyRepo, CopyType} from "./Copy";
import {heroMgr} from "../../HeroModule/Managers/HeroManager";

export class PlayerCopyItem extends BaseData {

	@field(Number)
	public copyId: number;
	@field
	public curLevel: number = 0;
	@field
	public maxLevel: number = 0

	public get copy() {
		return copyRepo().getById(this.copyId);
	}

	public next() {
		this.maxLevel = Math.max(this.curLevel, this.maxLevel);
		this.curLevel++;
	}

	public prev() {
		this.curLevel--;
	}

}

export class PlayerCopySetting extends BaseData {

	@field
	public copyType: CopyType = CopyType.Main;
	@field([String])
	public nftIds: string[] = []; // NFT
	@field
	public speedUpTime: number = 0;

	/**
	 * 
	 */
	public get battleSpeed() {
		return Date.now() > this.speedUpTime ? 1 : 2;
	}

	/**
	 * 
	 */
	public boost(time, force = false) {
		this.speedUpTime = force ? Date.now() + time :
			Math.max(this.speedUpTime, Date.now()) + time;
	}

}

@playerData("PlayerCopy")
export class PlayerCopy extends PlayerData {

	@field([PlayerCopyItem])
	public copies: PlayerCopyItem[] = [];

	@field([PlayerCopySetting])
	public settings: PlayerCopySetting[] = [];

	/**
	 * 
	 */
	public copy(copyId: number) {
		return this.copies.find(s => s.copyId == copyId);
	}

	/**
	 * 
	 */
	public setting(copyType: CopyType) {
		return this.settings.find(s => s.copyType == copyType);
	}

	/**
	 * 
	 */
	public mainCopy() {
		return this.copy(copyRepo().main().id);
	}

	/**
	 * 
	 */
	public mainSetting() {
		return this.setting(CopyType.Main);
	}

}
