import {ItemDisplay} from "../../../../Modules/CoreModule/Components/ItemDisplay";
import {PartType} from "../../../../Modules/HeroModule/Data/PartType";
import Label = cc.Label;
import Node = cc.Node;
import Color = cc.Color;
import {selectorScene} from "../../SelectorScene";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PartTypeDisplay extends ItemDisplay<PartType> {

	@property(Label)
	label: Label = null;
	@property(Node)
	background: Node = null;
	@property(Node)
	bottomLine: Node = null;

	public size: number = 0;

	private isSelected: boolean = false;

	start () { }

	//region 

	refresh(){
		super.refresh();
		if (this.item == null) return;
		this.isSelected = false;
		this.label.string = this.item.getDisplayName();
		if (selectorScene().editorMode)
			this.label.string = `${this.item.id} ${this.label.string}`;
	}

	protected update(dt: number) {
		this.node.width = Math.max(this.size, 40);
		this.bottomLine.width = this.node.width + 10;
		this.label.node.color = this.isSelected ? new Color(255, 255, 255) : new Color(159, 157, 180);
		this.bottomLine.opacity = this.isSelected ? 255 : 0;
		// this.background.opacity = this.isSelected ? 255 : 0;
	}

	//endregion

	//region 

	public onSelectChanged(select: boolean){
		this.isSelected = select;
	}

	//endregion

}
