import {Hero, RestraintType} from "../../HeroModule/Data/Hero";
import {QualityType} from "../../HeroModule/Data/Quality";
import {BaseData} from "../../CoreModule/Data/BaseData";


/**
 * 
 */
export enum HeroGroupType {
    RESTRAINT, QUALITY, STAR
}

/**
 * 
 */
export class HeroGroup extends BaseData{
    heroes: Hero[];

    groupBy: HeroGroupType;//
    value: RestraintType | QualityType | number;//HeroGroupheroesgroupByvalue

    constructor(groupBy: HeroGroupType, groupValue) {
        super();
        this.groupBy = groupBy;
        this.value = groupValue;
        this.heroes = [];
    }

}