import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import {Hero} from "../../../HeroModule/Data/Hero";
import {waitForDataLoad} from "../../../CoreModule/Managers/DataManager";
import {playerMgr, waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import HeroCompositingDisplay from "./Compositing/HeroCompositingDisplay";
import CultivateWindowController from "./CultivateWindowController";
import {heroMgr} from "../../../HeroModule/Managers/HeroManager";
import {battleScene} from "../../../../Scenes/BattleScene";
import Button = cc.Button;
import Node = cc.Node;
import EventHandler = cc.Component.EventHandler;

const {ccclass, property} = cc._decorator;

@ccclass
export default class BottomListHeroContainer extends ItemContainer<Hero, HeroCompositingDisplay> {

    private cultivateController: CultivateWindowController = null;
    private totalHeroes: Hero[] = [];
    private page: number = 0;

    start () {
        this.cultivateController = battleScene().cultivateWindow;
        this.setup().then();
    }

    @waitForDataLoad
    @waitForLogin
    private async setup() {
        const acc = playerMgr().player.account;
        this.totalHeroes = (await heroMgr().getListFromServer())
          .filter(h => h.ownerAccount == acc);
        this.onPageChange();
    }

    update (dt) {
        this.updateNodes();
    }

    @property(Node)
    heroFrame: Node = null;

    updateNodes() {
        let index = this.items.findIndex(i => i == battleScene().cultivateWindow.currentHero);
        if (index == -1) {
            this.heroFrame.active = false;
            return;
        }
        this.heroFrame.active = true;
        this.heroFrame.x = 125 * index - 250;
    }


    onItemsChanged() {
        super.onItemsChanged();
        this.itemsComponents.forEach((item, i) => {
            let event = new EventHandler();
            event.target = this.node;
            event.component = "BottomListHeroContainer";
            event.handler = "onItemClicked";
            event.customEventData = "" + i;
            item.getComponent(Button).clickEvents = [];
            item.getComponent(Button).clickEvents.push(event);
        })
    }

    nextPage() {
        if (this.totalHeroes.length > (this.page + 1) * 5) this.page++;
        this.onPageChange();
    }

    lastPage() {
        if (this.page > 0) this.page--;
        this.onPageChange();
    }

    onPageChange() {
        let items = this.totalHeroes.filter((h, i) => Math.floor(i / 5) == this.page);
        this.setItems(items);
    }

    onItemClicked(event: any, index: string) {
        this.cultivateController.currentHero = this.items[index];
        this.cultivateController.refreshHero = true;
    }
}
