import {battleScene} from "../../../../Scenes/BattleScene";
import Label = cc.Label;
import ProgressBar = cc.ProgressBar;
import {BattleInterface} from "../../BattleCommon";
import {copyMgr} from "../../../CopyModule/Managers/CopyManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SpeedButtonController extends cc.Component {

    @property(ProgressBar)
    speedUpProgress: ProgressBar = null;
    @property(Label)
    tipsLabel: Label = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    update (dt) {
        if (!copyMgr().getMainPlayerCopySettings()) return;
        let lastTime = (copyMgr().getMainPlayerCopySettings().speedUpTime - Date.now()) / 1000;
        if (lastTime < 0) {
            this.tipsLabel.string = "\\L[SMain1]";
            this.speedUpProgress.progress = 1;
            return;
        }
        this.tipsLabel.string = this.toTime(lastTime);
        this.speedUpProgress.progress = Math.max(lastTime / 120, 0.08);
    }

    speedUp() {
        if (Date.now() > copyMgr().getMainPlayerCopySettings().speedUpTime)
            BattleInterface.BoostMainBattle().then(res => copyMgr().getMainPlayerCopySettings().speedUpTime = Date.now() + 1000 * 120);
    }

    toTime(time: number) {
        let min = Math.floor(time / 60);
        let sec = Math.floor(time % 60);
        return (min < 10 ? "0" + min : min) + ":" + (sec < 10 ? "0" + sec : sec);
    }
}
