import {BaseManager, getManager, manager} from "../../CoreModule/Managers/BaseManager";
import {Player, PlayerData} from "../Data/Player";
import {ChainId, DefaultChainId, DefaultWalletType, WalletType} from "../../NFTModule/Managers/NFTManager";
import {DataLoader} from "../../CoreModule/Utils/DataLoader";
import {gameMgr} from "../../CoreModule/Managers/GameManager";

export function playerData<T extends PlayerData>(name: string) {
	return (clazz: Constructor<T>) =>
		playerMgr().registerPlayerData(clazz, name);
}

export function waitForLogin(obj, key, desc) {
	const oriFunc = desc.value;
	desc.value = async function (...p) {
		await PromiseUtils.waitFor(() => !!playerMgr().player);
		return oriFunc.apply(this, p);
	}
}

export function playerMgr() {
	return getManager(PlayerManager)
}

const AutoAccountKey = "autoAccount";

@manager
class PlayerManager extends BaseManager {

	public player: Player = null;

	public playerData: {[T: string]: PlayerData} = {};
	public playerDataClasses: {[T: string]: Constructor<any>} = {};

	public needLogin = true;

	// region 

	/**
	 * 
	 */
	public async login(account: string, chainId = DefaultChainId,
										 wallet = DefaultWalletType) {
		const res = await PlayerInterface.Login({
			chainId, account, wallet
		});
		this.player = DataLoader.load(Player, res['player']);
		this.setAllPlayerData(res['data']);
		gameMgr().setToken(res['token']);
	}

	/**
	 * TODO: 
	 */
	public async autoLogin() {
		if (!!this.player) return;
		let account = cc.sys.localStorage.getItem(AutoAccountKey);
		if (!account) cc.sys.localStorage.setItem(AutoAccountKey,
			account = MathUtils.randomString());
		console.log("AutoLogin: ", account);
		await this.login(account);
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public registerPlayerData<T extends PlayerData>(
		clazz: Constructor<T>, name: string) {
		this.playerDataClasses[name] = clazz;
	}

	/**
	 * 
	 */
	public getPlayerDataName<T extends PlayerData>(
		clazz: Constructor<T>) {
		for (const key in this.playerDataClasses) {
			const pdClazz = this.playerDataClasses[key];
			if (clazz == pdClazz) return key;
		}
	}

	/**
	 * 
	 */
	public getPlayerDataClass<T extends PlayerData>(name: string) {
		return this.playerDataClasses[name];
	}

	/**
	 * 
	 * @param clazz 
	 */
	public getPlayerData<T extends PlayerData>(clazz: Constructor<T>): T {
		return this.playerData[this.getPlayerDataName(clazz)] as T;
	}

	/**
	 * 
	 * @param clazz 
	 */
	public async refreshPlayerData<T extends PlayerData>(
		clazz?: Constructor<T>) {
		if (clazz) {
			const name = this.getPlayerDataName(clazz);
			const res = await PlayerInterface.GetData({name});
			this.setPlayerData(clazz, res["data"]);
		} else {
			const res = await PlayerInterface.GetData();
			this.setAllPlayerData(res['data']);
		}
	}

	/**
	 * 
	 */
	public setPlayerData<T extends PlayerData>(
		clazz: Constructor<T>, data: object) {
		const name = this.getPlayerDataName(clazz);
		if (name) this.playerData[name] = DataLoader.load(clazz, data);
	}

	/**
	 * 
	 */
	public setAllPlayerData<T extends PlayerData>(dict: object) {
		for (const key in dict) {
			const clazz = this.getPlayerDataClass(key);
			if (clazz) this.setPlayerData(clazz, dict[key]);
		}
	}

	// endregion

}

import {PlayerInterface} from "../Common/PlayerCommon";
import {Constructor} from "../../../Utils/BaseContext";
import {PromiseUtils} from "../../../Utils/PromiseUtils";
import {MathUtils} from "../../../Utils/MathUtils";
import {selectorScene} from "../../../Scenes/SelectorScene/SelectorScene";
