import {ItemDisplay} from "../../CoreModule/Components/ItemDisplay";
import BattleAnimationsController from "./BattleAnimationsController";
import Node = cc.Node;

const {ccclass, property} = cc._decorator;

export type BattleState = 'not_started' | 'preparing' | 'running' | 'finished';

@ccclass
export default class BattleDisplay extends ItemDisplay<Battle> {

	@property([Node])
	public battlerNodes: Node[] = [
		null, null, null, null
	];

	protected battlers: BattlerDisplay[] = [];

	start() {
		super.start();
	}

	onLoad() {
		this.battlerNodes.forEach(n => this.battlers.push(
			n.getComponent(BattlerDisplay)));
		this.battlers.forEach(b => b.battle = this);

		console.log("BattleDisplay onLoad", this, this.battlerNodes, this.battlers)
	}

	// region 

	/**
	 * 
	 */
	public getDisplay(info) {
		return info && this.battlers.find(
			b => b.camp == info.camp && b.index == info.pos);
	}

	// endregion

	// region 

	refresh() {
		super.refresh();
		console.log("BattleDisplay refresh", this, this.item)
		this.battlers.forEach(b => b.clearItems());
		this.item.battlers.forEach(b => this.getDisplay(b)?.addItem(b));
	}

	// endregion

	// region 

	protected update(dt: number) {
		if (!this.item) return;
        this.updateAnimationSpeed();
		this.updateBattleAnimation();
	}

	updateAnimationSpeed() {
		this.battlers.forEach(b => b.skeleton.timeScale = battleScene().battleSpeed);
	}

	updateBattleAnimation() {
		let times = this.item.getCurrentAnimationTimes();
		this.item.getCurrentAnimations().forEach((a, i) => this.animation.play(a, times[i]));
	}

	playNormalAnimation(name: string, time: number, posX: number, posY: number, camp: number) {
		this.animation.play(name, time, posX, posY, camp);
	}

	// endregion

	// region 

	@property(BattleAnimationsController)
	animation: BattleAnimationsController = null;

	// endregion

}

import {Battle} from "../Data/Battle";
import {battleScene} from "../../../Scenes/BattleScene";
import BattlerDisplay from "./BattlerDisplay";
