import {BaseManager, getManager, manager} from "../../CoreModule/Managers/BaseManager";

import {Constructor} from "../../../Utils/BaseContext";
import {DataLoader} from "../../CoreModule/Utils/DataLoader";

export function battleMgr() {
	return getManager(BattleManager);
}

@manager
export class BattleManager extends BaseManager {

	/**
	 * 
	 */
	public battles: BattleProcessor[] = [];

	/**
	 * 
	 */
	public async startMainBattle() {
		let iRes = await BattleInterface.StartMainBattle();
		const res = DataLoader.load(Battle, iRes["battle"]);
		res.battlers.forEach(b => b.isEntering && b.enter());

		await this.getBattleNFTs(res);

		const processor = this.startBattle(res);
		processor.start();

		// 
		battleMgr().startMainFirstAction(res).then()
		return res;
	}

	/**
	 * 
	 */
	public async startMainFirstAction(battle: Battle) {
		const res = await BattleInterface.StartMainBattleAction({
			battleId: battle.battleId });
		const nextAction = DataLoader.load(Action, res["action"]);

		battle.addAction(nextAction);
	}

	public async nextMainBattle() {

	}

	public async startCopy() {
		let iRes = await BattleInterface.StartCopy({copyId: 11});
		const res = DataLoader.load(Battle, iRes["battle"]);
		res.battlers.forEach(b => b.isEntering && b.enter());

		const processor = this.startBattle(res);
		processor.start();

		return res;
	}

	public test(battle) {
		const processor = this.startBattle(battle);
		processor.start();
	}

	private startBattle(battle: Battle) {
		const processor = createProcessor(battle);
		this.battles.push(processor);
		return processor;
	}

	/**
	 * NFT
	 */
	public async getBattleNFTs(battle: Battle) {
		const battlers = battle.battlers.filter(b => b.isHero);
		for (const battler of battlers) {
			await heroMgr().getOneFromServer(battler.nftId);
			battler.refresh();
		}
	}

	update(dt: number) {
		super.update(dt);
		this.battles.forEach(b => b.update(dt * battleScene().battleSpeed));
	}

	// region pvp

	public async startPVPBattle(selfNftId: string, roomId: number, enemyNftId: string) {
		let iRes = await BattleInterface.StartPVP({selfNftId: selfNftId, roomId: roomId, enemyNftId: enemyNftId});
		const res = DataLoader.load(Battle, iRes["battle"]);
		res.battlers.forEach(b => b.isEntering && b.enter());

		await this.getBattleNFTs(res);

		const processor = this.startBattle(res);
		processor.start();

		// 
		battleMgr().startPVPFirstAction(res).then()
		return res;
	}

	private async startPVPFirstAction(battle: Battle) {
		console.log(123456);
		const res = await BattleInterface.StartPVPAction({
			battleId: battle.battleId });
		const nextAction = DataLoader.load(Action, res["action"]);

		battle.addAction(nextAction);
	}

	// endregion
}

import {BattleProcessor, createProcessor} from "../Utils/BattleProcessor";
import {BattleInterface} from "../BattleCommon";
import {Battle} from "../Data/Battle";
import {Action} from "../Data/Action";
import {battleScene} from "../../../Scenes/BattleScene";
import {heroMgr} from "../../HeroModule/Managers/HeroManager";
