import Sprite = cc.Sprite;
import Label = cc.Label;
import SpriteFrame = cc.SpriteFrame;
import Node = cc.Node;
import {battleScene} from "../../../../Scenes/BattleScene";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PVPRoomDisplay extends cc.Component {

    @property(Number)
    index: number = -1;
    @property(Sprite)
    icon: Sprite = null;
    @property(Sprite)
    levelFrame: Sprite = null;
    @property(Label)
    levelValue: Label = null;
    @property(Label)
    tips1: Label = null;
    @property(Label)
    tips2: Label = null;
    @property(Node)
    frame: Node = null;
    @property(Node)
    lock: Node = null;

    start () {
        if (this.index >= 0) this.onIndexChange();
    }

    update (dt) {
        if (this.index == -1) this.node.active = false;
        this.frame.active = this.index == battleScene().currentPVPRoomIndex;
        this.lock.active = battleScene().currentPVPSelect ? battleScene().currentPVPSelect.level.level <= this.index * 20 : true;
    }

    setIndex(i: number) {
        this.index = i;
        this.onIndexChange();
    }

    @property([SpriteFrame])
    iconPictures: SpriteFrame[] = [];
    @property([SpriteFrame])
    framePictures: SpriteFrame[] = [];

    onIndexChange() {
        this.node.active = true;
        if (this.index < 0) return;
        this.icon.spriteFrame = this.iconPictures[this.index];
        this.levelFrame.spriteFrame = this.framePictures[this.index];
        this.levelValue.string = this.index == 0 ? "01-20" : (this.index * 20 + 1) + "-" + ((this.index + 1) * 20);
    }

    onClick() {
        if (battleScene().currentPVPRoomIndex == this.index) return battleScene().currentPVPRoomIndex = -1;
        if (!this.lock.active) battleScene().currentPVPRoomIndex = this.index;
    }
}
