import {ItemContainer} from "../../../CoreModule/Components/ItemContainer";
import PlayerCardDisplay, {RoomNFTInfo} from "./PlayerCardDisplay";
import {waitForDataLoad} from "../../../CoreModule/Managers/DataManager";
import {waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import ScrollView = cc.ScrollView;

const {ccclass, property} = cc._decorator;

@ccclass
export default class PlayerCardContainer extends ItemContainer<RoomNFTInfo, PlayerCardDisplay> {

    private currentDisplayRoomId: number = -1;
    private roomInfos: RoomNFTInfo[][] = [];

    start () {
        this.setup().then();
    }

    @waitForDataLoad
    @waitForLogin
    private async setup() {
        // const acc = playerMgr().player.account;
        // let items: Hero[] = (await heroMgr().getListFromServer()).filter(h => h.ownerAccount == acc);
        // items.forEach(h => {
        //     h.params = [1000, 400, 12500, 20];
        // })
        let nfts: [];
        let items: RoomNFTInfo[];
        for (let i = 0;i < 5;i++) {
            nfts = (await BattleInterface.GetPVPPlayers({ roomId: i })).nfts;
            items = nfts.map(nft => DataLoader.load(RoomNFTInfo, nft));
            this.roomInfos.push(items.filter(nft => !!nft));
        }
        this.currentDisplayRoomId = battleScene().currentPVPRoomIndex;
        this.setItems(this.roomInfos[this.currentDisplayRoomId]);
    }

    update (dt) {
        if (this.roomInfos.length < 5) return;
        if (this.currentDisplayRoomId != battleScene().currentPVPRoomIndex) {
            this.currentDisplayRoomId = battleScene().currentPVPRoomIndex;
            this.setItems(this.roomInfos[this.currentDisplayRoomId]);
        }
    }

    onItemsChanged() {
        super.onItemsChanged();
        this.getComponent(ScrollView).scrollToLeft();
    }

    protected onDisable() {
        battleScene().currentTargetSelect = null;
    }

}

import {battleScene} from "../../../../Scenes/BattleScene";
import {BattleInterface} from "../../BattleCommon";
import {DataLoader} from "../../../CoreModule/Utils/DataLoader";
