import Node = cc.Node;
import Label = cc.Label;
import Font = cc.Font;

const {ccclass, property} = cc._decorator;

@ccclass
export default class HitDamage extends cc.Component {

    @property(Number)
    public speedX: number = 0; // 0
    @property(Number)
    public speedY: number = 0; // 0
    @property(Number)
    public accelerationY: number = 200; // 200
    @property(Number)
    public timer: number = 0.5; // 
    @property(Number)
    public scaleSpeed: number = 2; // 21/2
    @property(Number)
    public disappearSpeed: number = 1000; // 10000.255
    @property(Number)
    public maxScale: number = 1;
    @property([Font])
    atlas: Font[] = [];
    @property(Node)
    miss: Node = null;

    start () {
        this.node.scaleX = 0;
        this.node.scaleY = 0;
    }

    protected update(dt: number) {
        if (this.timer <= 0 ) {
            this.node.opacity -= this.disappearSpeed * dt;
            if(this.node.opacity <= 0) this.node.destroy();
        }
        this.updateScale(dt);
        this.updatePosition(dt);
        this.timer -= dt;
    }

    private updateScale(dt: number) {
        this.node.scaleX = Math.min(this.maxScale, this.node.scaleX + 3 * dt * this.maxScale);
        this.node.scaleY = Math.min(this.maxScale, this.node.scaleY + 3 * dt * this.maxScale);
    }

    private updatePosition(dt: number) {
        this.node.x += this.speedX * dt;
        this.node.y += this.speedY * dt;
        this.speedY += this.accelerationY * dt;
    }

    public setValue(val: number) {
        this.getComponent(Label).string = "" + val;
    }

    public setPosition(x: number, y: number) {
        this.node.x = x;
        this.node.y = y;
    }

    public setRange(x: number, y: number) {
        let rx = Math.random() * x - x / 2, ry = Math.random() * y - y / 2;
        this.node.x += rx;
        this.node.y += ry;
        this.speedX = Math.min(150, Math.max(-150, rx));
    }

    public setType(type: number) {
        if (type == HitType.Miss) {
            this.miss.active = true;
            this.getComponent(Label).string = "";
            return;
        }
        this.miss.active = false;
        this.getComponent(Label).font = this.atlas[type];
    }

}

export enum HitType {
    Normal, Critical, Recover , Miss
}
