
import {BaseManager, getManager, manager} from "./BaseManager";
import {CoreLoading} from "../Common/CoreCommon";
import {showLoading} from "./LoadingManager";
import {Constructor} from "../../../Utils/BaseContext";

// export type AssetType<T extends cc.Asset> = typeof cc.Asset; // __private.cocos_core_asset_manager_shared_AssetType<T>;

export const BundleName = {
	Data: "data",
	// Prefabs: "prefabs",
	Sprites: "sprites"
}

export function assetMgr() : AssetBundleManager {
	return getManager(AssetBundleManager);
}

@manager
class AssetBundleManager extends BaseManager {

	private bundlePool = {};
	private assetPool = {};

	/**
	 * 
	 */
	constructor() {
		super();
		this.initializeBundles();
	}
	private initializeBundles() {
		for (const key in BundleName) {
			const name = BundleName[key];

			console.log("Loading Bundle: ", key, " -> ", name);
			this._loadBundle(name).then();
		}
	}

	/**
	 * Bundle
	 */
	@showLoading(CoreLoading.GetAsset)
	public async loadBundle(name: string) :
		Promise<cc.AssetManager.Bundle> {
		return this.bundlePool[name] || await this._loadBundle(name);
	}
	private async _loadBundle(name: string) {
		return await this._loadBundlePromise(name);
	}
	private _loadBundlePromise(name: string) {
		return new Promise((resolve, reject) => {

			cc.assetManager.loadBundle(name, (err, bundle) => {
				if (err) {
					console.error("Load Bundle: ", name, " ERROR! ", err);
					// reject(err);
					resolve(null);
				} else {
					console.log("Load Bundle: ", name, " FINISHED! ", bundle);
					resolve(this.bundlePool[name] = bundle);
				}
			})

		});
	}

	/**
	 * 
	 */
	@showLoading(CoreLoading.GetAsset)
	public async loadAsset<T extends cc.Asset>(
		type: Constructor<T>, bundleName: string, assetPath: string) : Promise<T> {

		const bundle = await this.loadBundle(bundleName);
		if (!bundle) return null;

		const key = this._getAssetKey(bundle, assetPath);
		return this.assetPool[key] ||
			await this._loadAsset(type, bundle, assetPath);
	}
	private _getAssetKey(bundle: cc.AssetManager.Bundle, path: string) {
		return bundle.name + "." + path;
	}
	private async _loadAsset<T extends cc.Asset>(
		type: Constructor<T>, bundle: cc.AssetManager.Bundle, path: string) : Promise<T> {
		return await this._loadAssetPromise(type, bundle, path);
	}
	private _loadAssetPromise<T extends cc.Asset>(
		type: Constructor<T>, bundle: cc.AssetManager.Bundle, path: string) : Promise<T> {

		return new Promise<T>((resolve, reject) => {
			// @ts-ignore
			bundle.load<T>(path, type, (err, res) => {
				if (err) {
					console.warn("Load Asset: ", bundle, " -> ", path, " ERROR! ", err);
					// reject(err);
					resolve(null);
				} else {
					console.log("Load Asset: ", bundle, " -> ", path, " FINISHED! ", res);
					let key = this._getAssetKey(bundle, path);
					// @ts-ignore
					resolve(this.assetPool[key] = res);
				}
			})
		});
	}
}
