import {get, post} from "../../CoreModule/Common/CoreCommon";

export class EditorInterface {

	@post("/editor/config/push", true, false) static PushConfig(d?): any {}
	@get("/editor/config/pull", true, false) static PullConfig(d?): any {}
	@post("/editor/config/save", true, false) static SaveConfig(d?): any {}
	@post("/editor/config/recalc", true, false) static RecalcConfig(d?): any {}
	@post("/editor/part/import", true, false) static ImportParts(d?): any {}

}
