import {Motion, MotionType, TargetType, TranslateType} from "../Data/Motion";
import {Vec1} from "./Vec1";
import {Battler} from "../Data/Battler";
import {Action} from "../Data/Action";
import {cache} from "../../../Utils/DecoratorUtils";
import Vec2 = cc.Vec2;
import v2 = cc.v2;
import Color = cc.Color;

export type MotionState = 'not_started' | 'playing' | 'ended' | 'finished';

export function motionProcessor(type: MotionType) {
	return (clazz) => {
		MotionProcessor.processorSettings[type] = clazz;
	}
}

export function createProcessor(
	battler: Battler, motion: Motion,
	action?: Action, isResult: boolean = false) {
	const clazz = MotionProcessor.processorSettings[motion.type];
	return clazz ? new clazz(battler, motion, action, isResult) : null;
}

export abstract class MotionProcessor {

	public static processorSettings = {};

	public motion: Motion;
	public action: Action;
	public battler: Battler;

	public isResult: boolean;

	public startTime: number = -1;
	public elapsedTime: number = -1;

	protected state: MotionState = 'not_started';

	protected constructor(battler: Battler, motion: Motion,
												action?: Action, isResult: boolean = false) {
		this.battler = battler; this.action = action;
		this.motion = motion; this.isResult = isResult;
	}

	/**
	 * 
	 */
	public get battle() { return this.action.battle; }

	public get subject() { return this.action.subject; }
	public get subjectBattler() { return this.subject.battler; }

	public get target() { return this.action.target; }
	public get targetBattler() { return this.target.battler; }

	public get isSubject() { return this.battler == this.subjectBattler }
	public get isTarget() { return this.battler == this.targetBattler }

	/**
	 * 
	 */
	public get isStarted() { return this.state != 'not_started'; }
	// public get isPlaying() { return this.state == 'playing'; }
	public get isEnded() { return this.state == 'ended' || this.isFinished; }
	public get isFinished() { return this.state == 'finished'; }
	public get isElapsedStarted() { return this.progress > 0; }
	// public get isElapsedPlaying() { return this.progress > 0 && this.progress < 1; }
	public get isElapsedEnded() { return this.progress >= 1; }
	public get isElapsedFinished() { return this.totalProgress >= 1; }

	public get duration() { return this.motion.duration; }
	public get endTime() { return this.startTime + this.duration; }
	public get totalEndTime() { return this.endTime + this.motion.wait; }
	public get totalDuration() { return this.totalEndTime - this.startTime; }
	public get progress() { return this.elapsedTime / this.duration; }
	public get totalProgress() { return this.elapsedTime / this.totalDuration; }

	public motionTarget(tType: TargetType) {
		switch (tType){
			case TargetType.Self:
				return this.battler;
			case TargetType.Subject:
				return this.subjectBattler;
			case TargetType.Target:
				return this.targetBattler;
		}
	}

	/**
	 * 
	 */
	public update() {
		// console.log("[Motion.Update]", this.motion.type);

		this.elapsedTime = this.battler.elapsedTime - this.startTime;

		if (!this.isStarted && this.isElapsedStarted) this.start();
		if (!this.isEnded && !this.isFinished &&
			this.isElapsedEnded) this.end();
		if (!this.isFinished && this.isElapsedFinished) this.finish();

		if (this.isStarted && !this.isEnded)
			this.onProgress(this.progress);
	}

	protected start() {
		this.state = 'playing';
		this.onStart();
		this.onProgress(0);
	}

	protected end() {
		this.state = 'ended';
		this.onProgress(1);
		this.onEnd();
	}

	protected finish() {
		this.state = 'finished';
		// this.onProgress(1);
		this.onFinish();
	}

	protected onProgress(p: number) { }

	protected onStart() {
		// console.log("[Motion.OnStart]", this.elapsedTime, this.motion.type);
	}

	protected onEnd() {
		// console.log("[Motion.OnEnd]", this.elapsedTime, this.motion.type);
	}

	protected onFinish() {
		// console.log("[Motion.OnFinish]", this.elapsedTime, this.motion.type);
	}
}

type TranslateProp = 'posRate' | 'angle' | 'scale';

abstract class TranslateProcessor extends MotionProcessor {

	public targetVal: Vec1 | Vec2;
	public startVal: Vec1 | Vec2;

	protected abstract get propKey(): TranslateProp;
	protected abstract get oriVal();
	protected get val() {
		return this.battler[this.propKey].clone();
	}

	/**
	 * 
	 */
	protected get tType(): TranslateType { return this.motion.params[0] }
	protected get delta(): number[] | number { return this.motion.params[1] }

	protected onStart() {
		super.onStart();
		this.setupStartVal();
		this.setupTargetVal();
	}

	protected setupStartVal() {
		this.startVal = this.val;
	}

	protected setupTargetVal() {
		this.targetVal = new Vec2();

		const dt = v2(this.delta[0] || this.delta, this.delta[1] || 0);

		switch (this.tType) {
			case TranslateType.Delta:
				Vec2.add(this.targetVal, this.val, dt); break;
			case TranslateType.Value:
				this.targetVal = dt; break;
			case TranslateType.Origin:
				Vec2.add(this.targetVal, this.oriVal, dt); break;
			default:
				this.targetVal = this.val; break;
		}
	}

	protected onProgress(p: number) {
		super.onProgress(p);
		const val = new Vec2();
		Vec2.lerp(val, this.startVal, this.targetVal, p);
		this.battler[this.propKey] = val;
	}
}

@motionProcessor(MotionType.Move)
class MoveProcessor extends TranslateProcessor {

	protected get propKey(): TranslateProp { return "posRate"; }

	protected get sPos() { return this.battler.startPos; }
	protected get tPos() { return this.battler.targetPos; }

	protected setupStartVal() {
		this.startVal = Vec1.ZERO;
	}

	protected setupTargetVal() {
		const dt = v2(this.delta[0] || 0, this.delta[1] || 0);

		switch (this.tType) {
			case TranslateType.Delta:
				this.tPos.origin = this.sPos.origin;
				Vec2.add(this.tPos.offset, this.sPos.offset, dt);
				break;
			case TranslateType.Value:
				this.tPos.origin = this.sPos.origin;
				this.tPos.offset = dt;
				break;
			case TranslateType.Origin:
				this.tPos.origin = "self";
				this.tPos.offset = dt;
				break;
			case TranslateType.Target:
				this.tPos.origin = "target";
				this.tPos.offset = dt;
				break;
		}
		this.targetVal = Vec1.ONE;
	}

	protected onFinish() {
		super.onFinish();
		this.sPos.origin = this.tPos.origin;
		this.sPos.offset = this.tPos.offset.clone();
	}

	// 
	protected get oriVal() { return null; }
}

@motionProcessor(MotionType.Scale)
class ScaleProcessor extends TranslateProcessor {
	protected get propKey(): TranslateProp { return "scale"; }
	protected get oriVal() { return Vec2.ONE; }
}

@motionProcessor(MotionType.Rotate)
class RotateProcessor extends TranslateProcessor {
	protected get propKey(): TranslateProp { return "angle"; }
	protected get oriVal() { return Vec1.ZERO; }
}

@motionProcessor(MotionType.Jump)
class JumpProcessor extends MotionProcessor {

}

@motionProcessor(MotionType.ApplyResult)
class ApplyResultProcessor extends MotionProcessor {

	protected onStart() {
		super.onStart();
		if (this.isResult)
			console.error("ApplyResult");
		else
			this.targetBattler.applyResult(this.action);
	}
}

@motionProcessor(MotionType.ShowResult)
class ShowResultProcessor extends MotionProcessor {

	protected onStart() {
		super.onStart();
		if (!this.isResult)
			console.error("ShowResult");
		else
			this.battler.showResult(this.action.result);
	}
}

@motionProcessor(MotionType.PlayCharAni)
class CharAniProcessor extends  MotionProcessor {
	/**
	 * 
	 */
	protected get name(): string { return this.motion.params[0] }

	protected onStart() {
		super.onStart();
		this.battler.animation = this.name;
	}

	public get duration(): number {
		return this.realDuration();
	}

	@cache
	private realDuration() {
		return this.battler.entity.skeleton.getRuntimeData()
			.animations.find(a => a.name == this.name)
			?.duration || this.motion.duration;
	}

	protected onEnd() {
		super.onEnd();
	}
}

@motionProcessor(MotionType.Shake)
class ShakeProcessor extends  MotionProcessor {

	protected get tt(): TargetType { return this.motion.params[0] }
	protected get range(): number { return this.motion.params[1] }

	protected onProgress(p: number) {
		super.onProgress(p);
		this.motionTarget(this.tt).shakePos.x = this.range * Math.sin(Math.PI * 4 * p);
	}

	protected onEnd() {
		super.onEnd();
		this.motionTarget(this.tt).shakePos = Vec2.ZERO;
	}

}

@motionProcessor(MotionType.Shine)
class ShineProcessor extends  MotionProcessor {

	protected get tt(): TargetType { return this.motion.params[0] }
	protected get colorCode(): string { return this.motion.params[1] }

	private realColor: Color = Color.WHITE;

	protected onStart() {
		super.onStart();
		this.realColor = new Color().fromHEX(this.colorCode);
	}

	protected onProgress(p: number) {
		super.onProgress(p);
		Color.lerp(this.motionTarget(this.tt).color, Color.WHITE, this.realColor, Math.sin(Math.PI * p));
	}

	protected onEnd() {
		super.onEnd();
		this.motionTarget(this.tt).color = Color.WHITE;
	}

}

@motionProcessor(MotionType.FadeOut)
class FadeOutProcessor extends MotionProcessor {

	protected get tt(): TargetType { return this.motion.params[0] }

	protected onStart() {
		super.onStart();
	}

	protected onProgress(p: number) {
		super.onProgress(p);
		this.motionTarget(this.tt).opacity = (1 - p) * 255;
	}

	protected onEnd() {
		super.onEnd();
	}

}

@motionProcessor(MotionType.PlayBattleAni)
class PlayBattleAniProcessor extends MotionProcessor {

	protected onStart() {
		this.motion.params.forEach(p => {
			this.battler.battle.battleAnimations.push(p[0]);
			this.battler.battle.battleAnimationTimes.push(p[1]);
		})
		super.onStart();
	}

	protected onProgress(p: number) {
		super.onProgress(p);
	}

	protected onEnd() {
		super.onEnd();
	}

}

@motionProcessor(MotionType.PlayNormalAni)
class PlayNormalAniProcessor extends MotionProcessor {

	protected onStart() {
		this.motion.params.forEach(p => {
			this.battler.normalAnimations = p[0];
			this.battler.normalAnimationTimes = p[1];
		})
		super.onStart();
	}

	protected onProgress(p: number) {
		super.onProgress(p);
	}

	protected onEnd() {
		super.onEnd();
	}

}
