import Label = cc.Label;
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import CultivateWindowController from "../CultivateWindowController";
import Node = cc.Node;
import Button = cc.Button;
import {playerMgr} from "../../../../PlayerModule/Managers/PlayerManager";
import {PlayerMoney} from "../../../../EconomicModule/Data/PlayerMoney";
import {autoUnitConvert} from "../../../../../Utils/NumberUtils";
import HeroInfoDisplay from "../HeroInfoDisplay";
import {heroMgr} from "../../../../HeroModule/Managers/HeroManager";
import {battleScene} from "../../../../../Scenes/BattleScene";

const {ccclass, property} = cc._decorator;

@ccclass
export default class UpgradeWindowDisplay extends cc.Component {

    // region Background

    @property(HeroInfoDisplay)
    heroInfo: HeroInfoDisplay = null;

    @property([Sprite])
    contentSelectCards: Sprite[] = [];
    @property([SpriteFrame])
    selectCardsPictures: SpriteFrame[] = [];

    // endregion

    // region Content

    @property(Node)
    heroProperty: Node = null;
    @property(Node)
    heroGrow: Node = null;
    @property(Node)
    skillsNode: Node = null;
    @property(Label)
    attackLabel: Label = null;
    @property(Label)
    defenceLabel: Label = null;
    @property(Label)
    healthLabel: Label = null;
    @property(Label)
    atkSpeedLabel: Label = null;
    @property(Label)
    criticalLabel: Label = null;
    @property(Label)
    evasionLabel: Label = null;
    @property([Label])
    skillNames: Label[] = [];
    @property([Label])
    skillLevels: Label[] = [];
    @property([Label])
    skillDescriptions: Label[] = [];
    @property(Label)
    medalLabel: Label = null;
    @property(Node)
    upgradeNode: Node = null;
    upgradeSprite: Sprite = null;
    upgradeButton: Button = null;
    @property([SpriteFrame])
    upgradePictures: SpriteFrame[] = [];

    // endregion

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    private cultivateController: CultivateWindowController = null;

    start () {
        this.cultivateController = battleScene().cultivateWindow;
        this.upgradeSprite = this.upgradeNode.getComponent(Sprite);
        this.upgradeButton = this.upgradeNode.getComponent(Button);
    }

    // region 

    update (dt) {
        if (this.cultivateController.refreshHero) {
            this.heroInfo.refresh();
            this.updateLabels();
            this.updatePictures();
            this.cultivateController.refreshHero = false;
        }
    }

    @autoUnitConvert
    updateLabels() {
        let hero = this.cultivateController.currentHero;
        if (!hero) return;
        this.attackLabel.string = hero.atk.toString();
        this.defenceLabel.string = hero.def.toString();
        this.healthLabel.string = hero.mhp.toString();
        this.atkSpeedLabel.string = hero.spd.toString();
        this.criticalLabel.string = hero.cri.toString();
        this.evasionLabel.string = hero.eva.toString();

        // this.skillNames.forEach((l, i) => { l.string = "Skill" + i; })
        // this.skillDescriptions.forEach((l, i) => { l.string = "Description" + i; })
        this.skillLevels.forEach(l => { l.string = "Lv." + hero.star.level; })

        this.medalLabel.string = playerMgr().getPlayerData(PlayerMoney).medal.toString();
    }

    updatePictures() {
        let hero = this.cultivateController.currentHero;
        if (!hero) return;

        const cond = hero.strengthen.conditions().judge();
        this.upgradeButton.interactable = cond;
        this.upgradeSprite.spriteFrame = this.upgradePictures[cond ? 0 : 1];
    }

    protected onEnable() {
        this.showHeroProperty();
        this.heroInfo.node.active = true;
        this.heroInfo.refresh();
    }

    // endregion

    onHeroChange() {
        this.cultivateController.refreshHero = true;
    }

    // region 

    showHeroProperty() {
        this.contentSelectCards[0].spriteFrame = this.selectCardsPictures[1];
        this.contentSelectCards[1].spriteFrame = this.selectCardsPictures[0];
        this.contentSelectCards[1].node.parent = this.node;
        this.contentSelectCards[0].node.parent = this.contentSelectCards[1].node;
        this.contentSelectCards[0].node.x = -150; this.contentSelectCards[0].node.y = 0;
        this.contentSelectCards[1].node.x = -60; this.contentSelectCards[1].node.y = -195;
        this.heroGrow.active = false;
        this.skillsNode.y = 170;
    }

    showHeroGrow() {
        this.contentSelectCards[0].spriteFrame = this.selectCardsPictures[0];
        this.contentSelectCards[1].spriteFrame = this.selectCardsPictures[1];
        this.contentSelectCards[0].node.parent = this.node;
        this.contentSelectCards[1].node.parent = this.contentSelectCards[0].node;
        this.contentSelectCards[0].node.x = -210; this.contentSelectCards[0].node.y = -195;
        this.contentSelectCards[1].node.x = 150; this.contentSelectCards[1].node.y = 0;
        this.heroGrow.active = true;
        this.skillsNode.y = 140;
    }

    upgradeBtn() {
        heroMgr().strengthen(this.cultivateController.currentHero).then(res => {
            this.onHeroChange();
            this.heroInfo.playAnimation("effectUpgrade");
        });
    }

    // endregion

}
