import Label = cc.Label;
import Animation = cc.Animation;
import Node = cc.Node;

const {ccclass, property} = cc._decorator;

@ccclass
export default class StageProgressController extends cc.Component {

    @property(Animation)
    animation: Animation = null;
    @property(Label)
    value: Label = null;
    @property([Node])
    enemies: Node[] = [];

    @property(Number)
    speed: number = 2.5; //  1s / speed
    stageIndex: number = 1;
    enemyIndex: number = 0;
    private isCurrent: boolean = false;
    private position: number = 2;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    init(index: number, position: number = 2) {
        this.stageIndex = index;
        this.value.string = this.stageIndex + "";
        this.position = position;
        this.node.x = 80 * position;
        this.node.y = 0;
    }

    update (dt) {
        this.updateNode(dt);
        this.updateAnimation();
        this.updateEnemies();
    }

    updateNode(dt: number) {
        if (this.node.x > 80 * this.position) this.node.x = Math.max(this.node.x - 80 * dt * this.speed, 80 * this.position);
        else this.node.x = Math.min(this.node.x + 80 * dt * this.speed, 80 * this.position);
        let currentPosition = Math.abs(this.node.x);
        this.node.scale = 1 - 3 * currentPosition / 1600;
        this.node.opacity = 255 - 255 * Math.pow(currentPosition / 160, 2);
    }

    updateAnimation() {
        if (Math.abs(this.node.x) <= 32) {
            if (!this.isCurrent) {
                this.animation.play("stageEnter", 0);
                this.isCurrent = true;
            }
        }
        else {
            if (this.isCurrent) {
                this.animation.play("stageLeave", 0);
                this.isCurrent = false;
            }
        }
    }

    updateEnemies() {
        this.enemies.forEach((e, i) => e.active = i < this.enemyIndex);
    }

    nextStage() {
        this.position--;
        if (this.position < -2 && this.node.opacity == 0) this.node.destroy();
    }

    lastStage() {
        this.position++;
        if (this.position > 2 && this.node.opacity == 0) this.node.destroy();
    }

}
