import {assetMgr, BundleName} from "../../CoreModule/Managers/AssetBundleManager";
import {assetField, field} from "../../CoreModule/Data/BaseData";
import {partTypeRepo} from "./PartType";
import SkeletonData = sp.SkeletonData;
import SpriteFrame = cc.SpriteFrame;
import {DefaultBt1, DefaultBt2} from "../../NFTModule/Managers/NFTManager";
import {heroTypeRepo} from "./HeroType";
import {
	BaseRepository,
	filterKeys,
	getRepository,
	repository,
	throwMessage
} from "../../CoreModule/Data/BaseRepository";
import {Constructor} from "../../../Utils/BaseContext";
import {data, StaticData} from "../../CoreModule/Data/StaticData";
import {PartInfo} from "./PartInfo";
import {SpineUtils} from "../../../Utils/SpineUtils";
import {AttackMotionGroupId} from "../../BattleModule/Data/MotionGroup";
import {blockLoading} from "../../CoreModule/Managers/LoadingManager";
import {configMgr} from "../../CoreModule/Managers/ConfigManager";
import PartConfig from "../Configs/PartConfig";

@data("Part")
export class Part extends StaticData {

	@field
	public bt1: number = DefaultBt1;
	@field
	public bt2: number = DefaultBt2;
	@field
	public oriId: number = -1;
	@field
	public heroTypeId: number = 0; // heroTypeId, bt2
	@field
	public partTypeId: number = 0; // partTypeId, ct

	@field
	public isEmpty: boolean = false;

	@field
	public name: string = "";
	@field
	public price: number = 0;
	@field
	public stock: number = 999999;

	@field
	public color: number = 1;

	@field([Number])
	public containCts: number[] = [];

	@field
	public skeletonName: string = "";

	@field
	public pictureName: string = "";

	@field(Number)
	public attackMotionId: number = AttackMotionGroupId;

	@field(Number)
	public nftMakeId: number;

	private _picture: SpriteFrame;
	public async picture(): Promise<SpriteFrame> {
		return this._picture ||= await this.loadPicture();
	}
	@blockLoading
	private async loadPicture(): Promise<SpriteFrame> {
		return await assetMgr().loadAsset(SpriteFrame, BundleName.Sprites, `partpictures/${this.pictureName}`);
	}

	private _allPicture: Map<string, SpriteFrame>;
	public async allPictures(): Promise<Map<string, SpriteFrame>> {
		return this._allPicture ||= await this.loadAllPictures();
	}
	@blockLoading
	private async loadAllPictures(): Promise<Map<string, SpriteFrame>> {
		const names = this.pictureName.split('_');
		const orders = configMgr().config(PartConfig).slotOrders;
		const keys = Object.keys(orders).filter(k => k.split('_')[0] == names[2]);
		let res = new Map<string, SpriteFrame>();
		for (const k of keys) {
			let sf = await assetMgr().loadAsset(SpriteFrame, BundleName.Sprites, `partpictures/${names[0]}_${names[1]}_${k}_${names[4]}`);
			if (sf) res.set(k, sf);
		}
		return res;
	}

	private _skeleton: SkeletonData;
	public async skeleton(): Promise<SkeletonData> {
		return this._skeleton ||= await this.loadSkeleton();
	}
	@blockLoading
	private async loadSkeleton(): Promise<SkeletonData> {
		return await assetMgr().loadAsset(SkeletonData, BundleName.Sprites, `parts/${this.skeletonName}`);
	}

	/**
	 * 
	 */
	public get partType() {
		return partTypeRepo().getById(this.partTypeId);
	}

	/**
	 * 
	 */
	public get heroType() {
		return heroTypeRepo().getById(this.heroTypeId);
	}

	/**
	 * 
	 */
	public get oriPart() {
		return partRepo().getById(this.oriId);
	}

	/**
	 * 
	 */
	private _slotNames: string[] = null;
	public get slotNames(): string[] {
		return this._slotNames ||= this.getSlotNames();
	}
	private getSlotNames() {
		const cts = this.containCts?.length > 0 ?
			this.containCts : [this.partTypeId];
		return cts.map(ct => partTypeRepo().getById(ct))
			.reduce((res, pt) => res.concat(
				SpineUtils.getSlotNames(pt)
			), [] as string[]);
	}

	/**
	 * 
	 */
	private _boneNames: string[] = null;
	public get boneNames(): string[] {
		return this._boneNames ||= this.getBoneNames();
	}
	private getBoneNames() {
		const cts = this.containCts?.length > 0 ?
			this.containCts : [this.partTypeId];
		return cts.map(ct => partTypeRepo().getById(ct))
			.reduce((res, pt) => res.concat(
				SpineUtils.getBoneNames(pt)
			), [] as string[]);
	}

	/**
	 * PartInfo
	 */
	public toPartInfo() {
		const res = new PartInfo();
		res.partId = this.id;
		res.name = this.name;
		res.partTypeId = this.partTypeId;
		res.price = this.price;
		res.stock = this.stock;

		return res;
	}
}

export function partRepo() {
	return getRepository(PartRepo);
}

@repository
export class PartRepo extends BaseRepository<Part> {

	get clazz(): Constructor<Part> { return Part; }

	// @ts-ignore
	public findByHeroTypeId(heroTypeId: number): Part[] {}
	// @ts-ignore
	public findByPartTypeId(partTypeId: number): Part[] {}
	// @ts-ignore
	public getByPartTypeId(partTypeId: number): Part {}
	// @ts-ignore
	public getByNftMakeId(nftMakeId: number): Part {}
	@filterKeys<Part>("heroTypeId", "partTypeId", "color") // @ts-ignore
	public findByTypes(htId: number, ptId: number, color: number): Part[] {}

}
