import {ItemDisplay} from "../../../../Modules/CoreModule/Components/ItemDisplay";
import Node = cc.Node;
import PreviewPictureDisplay from "../ItemPrefabs/PreviewPictureDisplay";
import PartsContainer from "../ItemContainers/PartsContainer";
import PartTypeContainer from "../ItemContainers/PartTypeContainer";
import {PartType, partTypeRepo} from "../../../../Modules/HeroModule/Data/PartType";
import Prefab = cc.Prefab;
import GlobalEditorLine from "./GlobalEditorLine";
import {HeroType, heroTypeRepo} from "../../../../Modules/HeroModule/Data/HeroType";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GlobalEditor extends ItemDisplay<PartType> {

	@property(PartTypeContainer)
	public partTypeContainer: PartTypeContainer = null;
	@property(Node)
	public lineContainer: Node = null;
	@property([GlobalEditorLine])
	public lines: GlobalEditorLine[] = [];

	@property(PartsContainer)
	public partsContainer: PartsContainer = null;
	@property(PreviewPictureDisplay)
	public preview: PreviewPictureDisplay = null;

	@property(Prefab)
	public prefab: Prefab = null;

	// region 

	protected onLoad() {
		this.partTypeContainer.globalEditor = this;
		this.partTypeContainer.setItems(partTypeRepo().list);
		this.createLines();
	}
	private createLines() {
		this.createLine(); // Title
		const hts = heroTypeRepo().list;
		hts.forEach(ht => this.createLine(ht));
	}
	private createLine(heroType?: HeroType) {
		const node = cc.instantiate(this.prefab);
		const line = node.getComponent(GlobalEditorLine);
		node.parent = this.lineContainer;

		line.globalEditor = this;
		line.isTitle = !heroType;
		line.setItem(heroType);

		this.lines.push(line);
	}

	// endregion

	// region 

	refresh() {
		super.refresh();
		this.lines.forEach(line => line.refresh());
	}

	// endregion

	// region 

	// endregion

}
