import BattleDisplay from "../BattleDisplay";
import StageProgressController from "../StageProgressController";
import {waitForLogin} from "../../../PlayerModule/Managers/PlayerManager";
import {waitForDataLoad} from "../../../CoreModule/Managers/DataManager";
import {copyMgr} from "../../../CopyModule/Managers/CopyManager";
import {copyRepo} from "../../../CopyModule/Data/Copy";
import Label = cc.Label;
import Prefab = cc.Prefab;
import Sprite = cc.Sprite;
import Node = cc.Node;
import instantiate = cc.instantiate;

const {ccclass, property} = cc._decorator;

@ccclass
export default class MainBattleDisplay extends BattleDisplay {

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {
        super.start();
        this.initProgress();
    }

    // region 

    refresh() {
        super.refresh();
        let index = (this.item.level - 1) % 15 + 1;
        const level = copyRepo().main().level(this.item.level - 1);
        this.mapName.string = level.displayName + (index < 10 ? "0" : "") + index;
        this.setMap();
    }

    // endregion

    // region 

    protected update(dt: number) {
        if (!this.item) return;
        super.update(dt);
        this.updateStageProgress();
    }

    updateStageProgress() {
        let level = copyMgr().getMainPlayerCopy().curLevel;
        if (level > this.currentLevel)
            this.nextStage();
        if (level < this.currentLevel)
            this.lastStage();
        this.enemies.forEach(e => e.enemyIndex = e.stageIndex < level ? 5 :
            e.stageIndex > level ? 0 : this.enemyIndex());
    }

    // endregion

    // region 

    @property(Label)
    mapName: Label = null;
    @property(Node)
    progressContainer: Node = null;
    @property(Prefab)
    progressPrefab: Prefab = null;
    @property(Sprite)
    backgroundSprite: Sprite = null;

    enemies: StageProgressController[] = [];
    enemyIndex() {
        return this.item && this.item.state < 5 ? 1 + this.item?.battlers
            .filter(b => (b.camp == 1) && b.isDead).length : 0;
    }
    currentLevel: number = 1;

    @waitForLogin
    @waitForDataLoad
    initProgress() {
        this.currentLevel = Math.max(1, copyMgr().getMainPlayerCopy().curLevel - 1);
        this.enemies = [];
        for (let i = 0; i < 2; i++) {
            let node = instantiate(this.progressPrefab);
            node.parent = this.progressContainer;
            let controller = node.getComponent(StageProgressController);
            controller.init(i + this.currentLevel, i);
            this.enemies.push(controller);
        }
    }

    nextStage() {
        this.currentLevel++;
        let node = instantiate(this.progressPrefab);
        node.parent = this.progressContainer;
        node.opacity = 0;
        let controller = node.getComponent(StageProgressController);
        controller.init(this.currentLevel + 1, 2);
        this.enemies.push(controller);
        this.enemies.forEach((e, i) => {
            if (e.node) e.nextStage();
            else this.enemies.splice(i, 1);
        });
    }

    lastStage() {
        if (this.currentLevel == 1) return;
        this.currentLevel--;
        if (this.currentLevel > 1) {
            let node = instantiate(this.progressPrefab);
            node.parent = this.progressContainer;
            node.opacity = 0;
            let controller = node.getComponent(StageProgressController);
            controller.init(this.currentLevel - 1, -2);
            this.enemies.push(controller);
        }
        this.enemies.forEach((e, i) => {
            if (e.node) e.lastStage();
            else this.enemies.splice(i, 1);
        });
    }

    setMap() {
        this.backgroundSprite.spriteFrame = copyRepo().main().level(this.item.level - 1).mapPicture;
    }

    // endregion

}
