import Label = cc.Label;
import Sprite = cc.Sprite;
import SpriteFrame = cc.SpriteFrame;
import {battleScene} from "../../../../Scenes/BattleScene";
import HeroWithAvatar from "../HeroWithAvatar";

const {ccclass, property} = cc._decorator;

@ccclass
export default class RankItemDisplay extends HeroWithAvatar {

    @property(Label)
    nameLabel: Label = null;
    @property(Label)
    powerLabel: Label = null;
    @property(Label)
    rankLabel: Label = null;
    @property(Label)
    qualityLabel: Label = null;
    @property(Label)
    levelLabel: Label = null;

    @property(Sprite)
    rankSprite: Sprite = null;
    @property(Sprite)
    restraintSprite: Sprite = null;
    @property(Sprite)
    qualitySprite: Sprite = null;
    @property([SpriteFrame])
    rankPictures: SpriteFrame[] = [];
    @property([SpriteFrame])
    restraintPictures: SpriteFrame[] = [];
    @property([SpriteFrame])
    qualityPictures: SpriteFrame[] = [];

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    private quality: string[] = ["N", "R", "SR", "SSR", "UR"];

    refresh() {
        super.refresh();
        this.nameLabel.string = this.item.name;
        this.powerLabel.string = this.item.battlePoint.toString();
        this.rankLabel.string = this.item.rank > 3 ? this.item.rank.toString() : "";
        this.qualityLabel.string = this.quality[this.item.quality];
        this.levelLabel.string = "Lv." + this.item.level.level.toString();

        this.rankSprite.spriteFrame = this.item.rank < 4 ? this.rankPictures[this.item.rank - 1] : null;
        this.restraintSprite.spriteFrame = this.restraintPictures[this.item.restraint];
        this.qualitySprite.spriteFrame = this.qualityPictures[this.item.quality];
    }

    fightBtn() {
        battleScene().startPVPBattle(this.item.nftId).then();
    }
}
