import {BaseData, field, PK, pkType} from "../../CoreModule/Data/BaseData";
import SkeletonData = sp.SkeletonData;
import Vec2 = cc.Vec2;
import Color = cc.Color;

import {Action, IBattlerInfo} from "./Action";
import {MathUtils} from "../../../Utils/MathUtils";
import {createProcessor, MotionProcessor} from "../Utils/MotionProcessor";
import {Vec1} from "../Utils/Vec1";
import {Result} from "./Result";
import {Part} from "../../HeroModule/Data/Part";

import {Battle} from "./Battle";
import {PartAttackId, Skill, skillRepo} from "../../HeroModule/Data/Skill";
import {
	AttackMotionGroupId, BossEnterMotionGroupId,
	DieMotionGroupId,
	EnterMotionGroupId,
	MotionGroup,
	motionGroupRepo
} from "./MotionGroup";
import {Enemy, enemyRepo, EnemyType} from "../../CopyModule/Data/Enemy";

export enum BonusParamType {
	ATK = 1, DEF, Gold, AVO
}

export interface IEntity {
	nftId?: string
	id?: number

	battleLevel: number
	battleParams: number[];

	restraint: RestraintType;

	parts?: Part[];
	skeleton: SkeletonData;
}

export enum BattlerState {
	Hidden, // 
	Entering, // 
	Idle, // 
	Acting, // 
	Frozen, // 
	Dying, // 
	Dead // 
}

type PositionOriginType = "self" | "target";

export type Position = {
	origin: PositionOriginType;
	offset: Vec2;
}

type MotionInfo = {
	motion: MotionGroup
	action?: Action
	isResult?: boolean
	played?: boolean
	onStart?: Function
	onFinish?: Function
}

const PartAniTag = "$";

export class SkillInfo extends BaseData {

	// region 

	@field
	public level: number = 1; // 
	@field
	public skillId: number = -1;
	@field
	public useCount: number = 0;
	@field
	public boostCount: number = 0;
	@field
	public lastTime: number = -1; // 

	public battler: Battler;

	// endregion

	// region 

	constructor(index?, parent?) {
		super(index);
		this.battler = parent;
	}

	// endregion

	// region /

	/**
	 * 
	 */
	public get skill(): Skill {
		return skillRepo().getById(this.skillId);
	}

	/**
	 * 
	 */
	public get boostCost() { return this.skill.boostCost; }

	/**
	 * 
	 */
	public get freezeTime() { return this.skill.freezeTime; }

	/**
	 * 
	 */
	public get frozenTime() {
		return this.battler.elapsedTime - this.lastTime
	}

	/**
	 * 
	 */
	public effects() {
		return this.skill.effects.map(e => e.applyLevel(this.level));
	}

	/**
	 * 
	 */
	public traits() {
		return this.skill.traits.map(t => t.applyLevel(this.level));
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public boostConditions() {
		return ConditionGroup.create(
			cGte(ConditionType.Gold, this.boostCost)
		);
	}

	/**
	 * 
	 */
	public get isUsable() {
		if (this.skill.freezeTime <= 0) return true;
		return this.frozenTime / this.freezeTime >= 1;
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public boost() {
		this.boostCount++;
		this.lastTime = 0;
	}

	/**
	 * 
	 */
	public use() {
		if (!this.isUsable) return false;
		this.lastTime = Date.now();
		this.useCount++;
		return true;
	}

	// endregion

	// region 

	public static testData(skill: Skill) {
		const res = new SkillInfo();
		res.skillId = skill.id;
		res.level = 1;

		return res;
	}

	// endregion

}

export class Battler extends BaseData implements IBattlerInfo {

	// region 

	@field(String)
	public nftId?: string; // NFTID
	@field(Number)
	public enemyId?: number; // 
	@field(Number)
	public enemyLevel?: number; // 

	@field
	public camp: number = 0; // 
	@field
	public pos: number = 0; // 

	@field
	public hp: number = 0;
	@field([Number])
	public params: number[] = [];
	@field([Number])
	public bonusParams: number[] = [];
	// @field([BattlerStatus])
	// public statuses: BattlerStatus[] = [];

	@field([SkillInfo])
	public skills: SkillInfo[] = [];

	@field
	public state: BattlerState = BattlerState.Hidden;

	public battle: Battle;
	public entity: IEntity;

	// endregion

	// region 

	/**
	 * 
	 */
	public startPos: Position = {
		origin: "self", offset: Vec2.ZERO
	}
	public targetPos: Position = {
		origin: "self", offset: Vec2.ZERO
	}
	public posRate: Vec1 = Vec1.ONE; // 0 -> pos, 1 -> targetPos
	public shakePos: Vec2 = Vec2.ZERO;
	public z: number = 0; // 

	/**
	 * 
	 */
	public scale: Vec2 = Vec2.ONE;

	/**
	 * 
	 */
	public angle: Vec1 = Vec1.ZERO;

	/**
	 * 
	 */
	public color: Color = Color.WHITE;

	/**
	 * 
	 */
	public opacity: number = 255;

	/**
	 * 
	 */
	private _animation: string;
	public get animation() {
		const res = this._animation;
		this._animation = null;
		return res;
	}
	public set animation(val) {
		this._animation = val;
	}
	private _normalAnimations: string[] = [];
	public get normalAnimations() {
		const res = this._normalAnimations;
		this._normalAnimations = [];
		return res;
	}
	public set normalAnimations(val) {
		// @ts-ignore
		this._normalAnimations.push(val); // setadd
	}
	private _normalAnimationTimes: number[] = [];
	public get normalAnimationTimes() {
		const res = this._normalAnimationTimes;
		this._normalAnimationTimes = [];
		return res;
	}
	public set normalAnimationTimes(val) {
		// @ts-ignore
		this._normalAnimationTimes.push(val); // setadd
	}

	// endregion

	// region 

	constructor(index?, parent?) {
		super(index);
		this.battle = parent;
	}

	onCreated() {
		super.onCreated();
		this.refreshEntity();
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public get battler() { return this; }
	public get opponents() { return this.battle?.getOpponents(this); }

	/**
	 * Hero
	 */
	public get isHero() { return !!this.nftId }

	/**
	 * 
	 */
	public get isEnemy() { return !!this.enemyId }

	/**
	 * 
	 */
	public hero(): Hero {
		return this.nftId && heroMgr().getCacheOneFromServer(this.nftId);
	}

	/**
	 * 
	 */
	public enemy(): Enemy {
		return this.enemyId && enemyRepo().getById(this.enemyId);
	}

	/**
	 * 
	 */
	public skill(skillId: number) {
		return this.skills.find(s => s.skillId == skillId);
	}

	/**
	 * 
	 */
	public traits() {
		const res = new TraitGroup();
		this.skills.forEach(s => res.add(...s.traits()));
		return res;
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public get isHidden() { return this.state == BattlerState.Hidden; }
	public get isEntering() { return this.state == BattlerState.Entering; }
	public get isIdle() { return this.state == BattlerState.Idle; }
	public get isActing() { return this.state == BattlerState.Acting; }
	public get isFrozen() { return this.state == BattlerState.Frozen; }
	public get isDying() { return this.state == BattlerState.Dying; }
	public get isDead() { return this.state == BattlerState.Dead; }

	// endregion

	// region 

	public get level() {
		const level = this.entity.battleLevel
		return this.isEnemy ? this.enemyLevel * level : level;
	}

	/**
	 * 
	 */
	public param(index: ParamType) {
		let res = this.params[index] || 0;
		switch (index) {
			case ParamType.ATK:
				res *= this.battle.campBonusParam(
					this.camp, BonusParamType.ATK); break;
			case ParamType.DEF:
				res *= this.battle.campBonusParam(
					this.camp, BonusParamType.DEF); break;
			case ParamType.AVO:
				res += this.bonusParam(BonusParamType.AVO) - 1;
				break;
		}
		return res;
	}

	/**
	 * 
	 */
	public bonusParam(index: BonusParamType) {
		let res = this.bonusParams[index] || 1;
		res += this.traitBonusParam(index);
		return res;
	}

	/**
	 * 
	 */
	public traitBonusParam(index: BonusParamType) {
		return this.traits().filter(TraitType.BonusParam).sum(0, index);
	}

	/**
	 * 
	 */
	public get cp() { return this.param(ParamType.CP) }
	public get cpb() { return this.param(ParamType.CPB) }
	public get hid() { return this.param(ParamType.HID) }
	public get pow() { return this.param(ParamType.POW) }
	public get agi() { return this.param(ParamType.AGI) }
	public get int() { return this.param(ParamType.INT) }
	public get powb() { return this.param(ParamType.POWB) }
	public get agib() { return this.param(ParamType.AGIB) }
	public get intb() { return this.param(ParamType.INTB) }
	public get atk() { return this.param(ParamType.ATK) }
	public get cri() { return this.param(ParamType.CRI) }
	public get dmg() { return this.param(ParamType.DMG) }
	public get atkb() { return this.param(ParamType.ATKB) }
	public get crib() { return this.param(ParamType.CRIB) }
	public get dmgb() { return this.param(ParamType.DMGB) }
	public get def() { return this.param(ParamType.DEF) }
	public get avo() { return this.param(ParamType.AVO) }
	public get eva() { return this.param(ParamType.EVA) }
	public get defb() { return this.param(ParamType.DEFB) }
	public get avob() { return this.param(ParamType.AVOB) }
	public get evab() { return this.param(ParamType.EVAB) }
	public get mhp() { return this.param(ParamType.MHP) }
	public get spd() { return this.param(ParamType.SPD) }
	public get hit() { return this.param(ParamType.HIT) }
	public get mhpb() { return this.param(ParamType.MHPB) }
	public get spdb() { return this.param(ParamType.SPDB) }
	public get hitb() { return this.param(ParamType.HITB) }
	public get ab1() { return this.param(ParamType.AB1) }
	public get ab2() { return this.param(ParamType.AB2) }
	public get ab3() { return this.param(ParamType.AB3) }
	public get ab4() { return this.param(ParamType.AB4) }
	public get bp() { return this.param(ParamType.BP) }
	public get bpb() { return this.param(ParamType.BPB) }

	public get isHPDead() { return this.hp <= 0; }

	/**
	 * HP
	 */
	public addHP(val) {
		this.hp += val;
		this.refreshHp();
	}

	/**
	 * HP
	 */
	public resetHp() {
		this.hp = this.mhp;
	}

	/**
	 * 
	 */
	public judgeDie() {
		if (!this.isDead && !this.isDying && this.isHPDead) this.die();
	}

	/**
	 * 
	 */
	public die() {
		this.addMotion(DieMotionGroupId,
			this.onDie.bind(this), this.onDead.bind(this));
	}

	/**
	 * 
	 */
	public onDie() {
		this.state = BattlerState.Dying;
	}

	/**
	 * 
	 */
	public onDead() {
		this.state = BattlerState.Dead;
		this.battle.enterNextBattler(this);
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public enter() {
		if (!this.isHidden) return;

		this.state = BattlerState.Entering;
	}

	/**
	 * 
	 * @private
	 */
	private onEntered() {
		this.state = BattlerState.Idle
	}

	/**
	 * 
	 */
	private updateEntering() {
		// Entering
		if (this.isEntering && !this.motion)
			this.addMotion(this.isEnemy && this.enemy().type == EnemyType.Boss ? BossEnterMotionGroupId : EnterMotionGroupId,
				null, this.onEntered.bind(this));
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public action: Action = null;

	/**
	 * 
	 */
	public get subject() { return this.action?.subject.battler; }
	public get target() { return this.action?.target.battler; }
	public get isSubject() { return this == this.subject; }
	public get isTarget() { return this == this.target; }

	/**
	 * Action
	 */
	public get canInvokeAction() { return this.isIdle; }

	/**
	 * 
	 */
	public inputAction(target?: IBattlerInfo,
										 skill?: SkillInfo, time?: number) {
		target ||= this.generateTarget();
		skill ||= this.generateSkill();

		return this.battle.addAction(this, target, skill, time);
	}
	private generateTarget() {
		return MathUtils.randomPick(this.opponents);
	}
	private generateSkill() {
		for (const skill of this.skills)
			if (skill.isUsable) return skill;
		return this.skills[this.skills.length - 1];
	}

	/**
	 * 
	 */
	public invokeAction(action: Action) {
		if (!this.canInvokeAction) return;
		this.action = action;
		action.apply();

		const skill = action.skill;
		if (skill.subjectMotionId == PartAttackId) {
			if (this.isHero) {
				let weapon = (this.entity as Hero).weaponPart;
				let motion = weapon ? weapon.attackMotionId : AttackMotionGroupId;
				this.addMotion(motion,
					this.onActionStart.bind(this),
					this.onActionEnd.bind(this), action);
			}
			if (this.isEnemy) this.addMotion(this.enemy().attackMotionId,
								this.onActionStart.bind(this),
								this.onActionEnd.bind(this), action);
		}
		else
			this.addMotion(skill.subjectMotionId,
				this.onActionStart.bind(this),
				this.onActionEnd.bind(this), action);
	}

	/**
	 * 
	 */
	// public updateAction() {
	// 	// 
	// 	if (this.isActing &&
	// 		this.isMotionsFinished &&
	// 		this.target.isMotionsFinished)
	// 		this.onActionEnd();
	// }

	/**
	 * 
	 */
	protected onActionStart() {
		this.state = BattlerState.Acting;
	}

	/**
	 * 
	 */
	protected onActionEnd() {
		this.state = BattlerState.Idle;
		this.action = null;
	}

	// endregion

	// region 

	/**
	 * 
	 */
	private _result: Result;
	public get result() {
		const res = this._result;
		this._result = null;
		return res;
	}

	/**
	 * 
	 */
	public get canApplyResult() { return this.isIdle; }

	/**
	 * 
	 */
	private processResult(result: Result) {

		// 
		this.addHP(-result.hpDamage);
		this.addHP(result.hpRecover);
		this.addHP(-result.hpDrain);

		result.action.subjectBattler.addHP(result.hpDrain);

		this.refresh();
	}

	/**
	 * 
	 * @param action
	 */
	public applyResult(action: Action) {
		if (!this.canApplyResult || action.result.isMiss)
			this.showResult(action.result);
		else
			this.addMotion(action.skill.targetMotionId,
				this.onResultApply.bind(this),
				this.onResultEnd.bind(this),
				action, true);
	}

	/**
	 * 
	 */
	public showResult(result: Result) {
		this.processResult(this._result = result);
		this.judgeDie();
	}

	/**
	 * 
	 */
	private onResultApply() {
		this.state = BattlerState.Frozen;
	}

	/**
	 * 
	 */
	private onResultEnd() {
		this.state = BattlerState.Idle;
	}

	/**
	 * 
	 */
	// public updateResult() {
	// 	if (this.isFrozen && this.isMotionsFinished)
	// 		this.state = BattlerState.Idle;
	// }

	// endregion

	// region 

	/**
	 * 
	 */
	public motions: MotionInfo[] = [];
	public get motion() { return this.motions[0]; }

	/**
	 * 
	 */
	public get curMotion() {
		return this.motion?.played ? null : this.motion;
	}

	/**
	 * 
	 */
	public motionProcessors: MotionProcessor[] = [];

	/**
	 * 
	 */
	private motionEndTime: number;
	private preparingMotion: boolean = false;

	/**
	 * 
	 */
	public get elapsedTime() { return this.battle.elapsedTime; }

	/**
	 * 
	 */
	public get isMotionsFinished() {
		return !this.preparingMotion && !this.motion;
		// this.motionProcessors.every(
		// 	p => this.elapsedTime >= p.endTime
		// );
		// this.elapsedTime >= this.motionEndTime;
	}

	/**
	 * 
	 */
	public get isCurMotionFinished() {
		return this.elapsedTime >= this.motionEndTime;
		// this.motionProcessors.every(
		// 	p => this.elapsedTime >= p.endTime
		// );
	}

	/**
	 * 
	 */
	public addMotion(id: number,
									 onStart?: Function,
									 onFinish?: Function,
									 action?: Action,
									 isResult: boolean = false) {
		return this.motions.push({
			motion: motionGroupRepo().getById(id),
			onStart, onFinish, action, isResult
		});
	}
	// public addMotion(id: number,
	// 								 state?: BattlerState,
	// 								 action?: Action,
	// 								 isResult: boolean = false) {
	// 	return new Promise(resolve => {
	// 		this.preparingMotion = true;
	// 		this.motions.push({
	// 			motion: motionGroupRepo().getById(id),
	// 			state, action, isResult,
	// 			onFinished: resolve
	// 		})
	// 		this.preparingMotion = false;
	// 	})
	// }

	/**
	 * 
	 */
	private playMotion(motion: MotionInfo) {
		this.motionProcessors = motion.motion.motions
			.map(m => createProcessor(this, m,
				motion.action, motion.isResult));

		this.scheduleMotions();
		this.onMotionStart(motion);

		// this.motionEndTime = this.calcMotionEndTime(motion.motion);
	}

	/**
	 * 
	 */
	// public playMotions(motions: Motion[], action?: Action,
	// 									 isResult: boolean = false) {
	// 	action ||= this.action;
	// 	this.motionProcessors = motions
	// 		.map(m => createProcessor(this, action, m, isResult))
	// 		.filter(p => !!p);
	//
	// 	this.scheduleMotions();
	// }

	/**
	 * 
	 */
	private scheduleMotions() {
		let startTime = this.elapsedTime;
		let endTime = this.elapsedTime;

		for (const processor of this.motionProcessors) {
			const playType = processor.motion.playType;
			if (playType == 'after') startTime = endTime;

			processor.startTime = startTime + processor.motion.offset;
			endTime = Math.max(endTime, processor.totalEndTime);
		}
		this.motionEndTime = endTime;
	}

	/**
	 * 
	 */
	private updateMotions() {
		if (!this.motion) return;

		// motion
		if (!this.motion.played)
			this.playMotion(this.motion);
		// 
		else if (this.isCurMotionFinished)
			this.onMotionFinish(this.motion);

		this.motionProcessors.forEach(p => p.update());

		// if (!this.isCurMotionFinished) return;
		// if (this.motion.played)
		// 	this.onMotionFinish(this.motion);
		// else
		// 	this.playMotion(this.motion);
	}

	/**
	 * 
	 */
	private onMotionStart(motion: MotionInfo) {
		motion.onStart && motion.onStart();
		motion.played = true;
	}

	/**
	 * 
	 */
	private onMotionFinish(motion: MotionInfo) {
		motion.onFinish && motion.onFinish();
		this.motions.shift();
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public refresh() {
		this.refreshEntity();
		this.refreshParams();
		this.refreshHp();
	}

	/**
	 * Entity
	 */
	private refreshEntity() {
		if (this.isHero) this.entity = this.hero();
		if (this.isEnemy) this.entity = this.enemy();
	}

	/**
	 * 
	 */
	private refreshParams() {
		this.params = this.entity.battleParams.slice(0);
		// TODO: 
		if (this.isEnemy) {
			this.params[ParamType.ATK] *= this.enemyLevel;
			this.params[ParamType.MHP] *= this.enemyLevel;
		}
	}

	/**
	 * HP
	 * @private
	 */
	private refreshHp() {
		this.hp = MathUtils.clamp(this.hp, 0, this.mhp);
	}

	// endregion

	// region 

	public update() {
		this.updateMotions();
		this.updateEntering();
	}

	// endregion

	// region 

	/**
	 * 
	 */
	public static testData(camp, index, nftId?, enemyId?) {
		const res = new Battler();

		res.camp = camp;
		res.pos = index;
		res.nftId = nftId;
		res.enemyId = enemyId;

		const skill = skillRepo().normalAttack();
		const sInfo = SkillInfo.testData(skill);

		res.skills.push(sInfo);
		sInfo.battler = res;

		res.enter();
		res.refreshEntity();

		// TODO: 
		res.entity.battleParams = [10, 125, 10, 5];
		res.refresh();
		res.resetHp();

		return res;
	}

	// endregion

}

import {Hero, RestraintType} from "../../HeroModule/Data/Hero";
import {heroMgr} from "../../HeroModule/Managers/HeroManager";
import {ParamType} from "../../HeroModule/Data/Param";
import {cGte, ConditionGroup, ConditionType} from "../../PlayerModule/Data/Condition";
import {TraitGroup, TraitType} from "./Trait";
