import Asset = cc.Asset;

import {assetMgr} from "../Managers/AssetBundleManager";
import {Constructor} from "../../../Utils/BaseContext";
import {DataLoader} from "../Utils/DataLoader";

export function pkType() {
	return String;
}
export type PK = string;

/**
 * 
 */
export function field<T>(
	typeOrObj?: Constructor<T> | Constructor<T>[] | Object, key?: string): any {

	// 
	const isDesc = key && (typeof typeOrObj !== 'function');
	// type
	let type = isDesc ? typeOrObj[key]?.constructor : typeOrObj;

	const process = (obj: any, key: string) => {
		const oType = obj.constructor;
		const setting = DataLoader.getSetting(oType);
		setting.default = new oType();
		setting.properties[key] = {
			type, default: setting.default[key]
		}
	}

	if (isDesc) process(typeOrObj, key);
	else return process;

	// const process = (obj: Object, key: string) => {
	// 	const cache = DataLoader.getOrCreateCache(obj);
	//
	// 	cache.proto.properties[key] = {
	// 		type, default: DataLoader.getDefaultValue(obj, key)
	// 	}
	// }
	//
	// if (key && typeof type !== 'function') {
	// 	const obj = type;
	// 	type = obj[key]?.constructor;
	// 	process(obj, key);
	// } else return process;
}

/**
 * 
 */
export function assetField<T extends Asset>(
	type: Constructor<T>, bundleName: string, assetPath: string) {

	return (obj, key) => {
		const setting = DataLoader.getSetting(obj.constructor)
		setting.assetFields[key] = {
			type, bundleName, assetPath
		}
	}
}

/**
 * 
 */
export function dataPK(obj: any, key: string) {
	const clazz = obj.constructor;
	const setting = DataLoader.getSetting(clazz);
	if (setting.dataPK)
		throw `${clazz.name}${setting.dataPK}${key}`;
	setting.dataPK = key;
}

export class BaseData {

	public index: number = -1; // 
	public isLoaded: boolean = false; // 

	private _loadTasks: Promise<Asset>[] = [];

	constructor(index = null) {
		this.index = index;
	}

	public onCreated() {}

	public loadTasks(): Promise<Asset>[] { return this._loadTasks; }

	public toJSON() { return DataLoader.convert(this); }

	/**
	 * 
	 */
	public clone(deep = false): this {
		// @ts-ignore
		const type: Constructor<any> = this.constructor;
		return deep ? DataLoader.load(type, DataLoader.convert(this)) :
			Object.assign(new type(), this);
	}

	/**
	 * 
	 */
	public addLoadTask<T extends Asset>(type: Constructor<T>,
		key: string, bundleName: string, assetPath: string) {
		this._loadTasks.push(this.loadAssetToField(
			type, key, bundleName, assetPath))
	}

	private async loadAssetToField<T extends Asset>(type: Constructor<T>,
		key: string, bundleName: string, assetPath: string) {
		const reg = /\${(.+?)}/g;
		const regRes = reg.exec(assetPath)
		// const replaceReg = new RegExp(regRes[0], 'g');
		const name = this[regRes[1]];

		if (name) assetPath = assetPath.replace(reg, name);

		return this[key] = await assetMgr().loadAsset<T>(type, bundleName, assetPath);
	}
}

export class GameData extends BaseData {

	@field(pkType())
	public _id: PK = null;
	// @field(Number)
	// public id?: number

	/**
	 * 
	 */
	public get dataId(): string | number {
		return this._id.toString();
	}
}
