
/**
 * /
 */
export function getMetaData<T>(type, key, default_?: T): T {
	let res = type[key] ||= default_;
	if (res?.type != type) {
		const keys = res ? Object.keys(res) : [];
		type[key] = keys.reduce((_res, key) => {
			const val = res[key];
			if (val instanceof Array)
				_res[key] = val.slice(0); // 
			else if (typeof val == "object")
				_res[key] = Object.assign({}, val); // 
			else
				_res[key] = val;
			return _res;
		}, {});
		type[key].type = type
	}
	return type[key];
}
