import Node = cc.Node;
import Widget = cc.Widget;
import {nftMgr} from "../../../Modules/NFTModule/Managers/NFTManager";
import {selectorScene} from "../SelectorScene";

const {ccclass, property} = cc._decorator;

@ccclass
export default class CanvasAdapter extends cc.Component {

    @property(Node)
    canvas: Node = null;
    @property(Widget)
    previewUI: Widget = null;
    @property(Widget)
    selectUI: Widget = null;
    @property(Widget)
    createUI: Widget = null;
    @property(Widget)
    descriptionUI: Widget = null;
    @property(Widget)
    main: Widget = null;
    @property(Widget)
    fixed: Widget = null;

    windowWidth: number = 1200;
    isDebug: boolean = true;

    @property(Number)
    testWidth: number = 1200; // 

    start () {

    }

    update (dt) {
        if (selectorScene().editorMode) return;

        this.fixed.top = this.canvas.y - 400;

        if (nftMgr().data.windowSize || !this.isDebug) {
            if (nftMgr().data.windowSize[0] == this.windowWidth) return;
            this.windowWidth = nftMgr().data.windowSize[0];
            this.windowWidth = Math.min(1200, this.testWidth);
            this.isDebug = false;
        }
        else {
            // this.testWidth = cc.view.getVisibleSize().width; // 
            this.testWidth = window.document.documentElement.clientWidth; // 
            if(this.testWidth == this.windowWidth) return;
            this.windowWidth = Math.min(1200, this.testWidth);
        }
        if (this.windowWidth >= 880) {
            this.canvas.width = this.windowWidth;
            this.canvas.height = this.getHeightByWidth();

            this.previewUI.isAlignLeft = true;
            this.previewUI.left = 0;

            this.selectUI.top = 150;
            this.selectUI.left = 450;

            this.createUI.top = 650;
            this.createUI.right = this.windowWidth - 450;

            this.descriptionUI.top = 760;
            this.descriptionUI.left = 450;
            this.descriptionUI.right = 30;
            this.descriptionUI.node.height = this.windowWidth == 1200 ? 330 : 570;
        }
        else {
            this.canvas.width = Math.max(450, this.windowWidth);
            this.canvas.height = this.getHeightByWidth();

            this.previewUI.isAlignHorizontalCenter = true;
            this.previewUI.horizontalCenter = 0;

            this.selectUI.top = 700;
            this.selectUI.left = 30;

            this.createUI.top = 1300;
            this.createUI.right = 0;

            this.descriptionUI.top = 1600;
            this.descriptionUI.left = 20;
            this.descriptionUI.right = 20;
            this.descriptionUI.node.height = this.windowWidth > 780 ? 330 : 570;
        }
        if (this.windowWidth < 450) this.canvas.scale = this.windowWidth / 450;
        else this.canvas.scale = 1;
    }

    getHeightByWidth(width: number = this.windowWidth): number{
        if (width >= 1200) return 1100;
        if (width >= 880) return 1350;
        if (width >= 780) return 1950;
        return 2200;
    }

}
